import * as React from 'react';
import { Grid, Typography, Button } from '@material-ui/core';
import { API } from 'Pages/API';
import { PNTable } from './PNTable';
import { CustomModal } from 'Pages/TicketList/ReassignTicketModal/CustomModal';

interface IProps {
    memberId: string;
    contractId: string;
    planType:string;
    setIsLoading: (isLoading: boolean) => void;
  }
  
  interface IStates {
    response: any;
    isOpen:boolean;
    // allMemberBenefits: PartialBenefitData[];
    // filteredMemberBenefits: PartialBenefitData[];
    globalFilter: string;
    columnFilters: any;
  }
  
  export class PartnerNetworks extends React.Component<IProps, IStates> {
    constructor(props: IProps) {
      super(props);
  
      this.state = {
        isOpen:false,
        response: null,
        // allMemberBenefits: [],
        // filteredMemberBenefits: [],
        globalFilter: '',
        columnFilters: [],
      };
    }
  
    componentDidMount() {
      const { memberId } = this.props;
      if (memberId && memberId.length > 0) {
        this.loadBenefitsData();
      }
    }
  
    componentDidUpdate(prevProps) {
      if (this.props.memberId !== prevProps.memberId) {
        this.loadBenefitsData();
      }
    }
  
    loadBenefitsData(): void {
      const { memberId, contractId, setIsLoading } = this.props;
      if (!memberId || memberId.length === 0) {
        return;
      }
  
      if (setIsLoading) {
        setIsLoading(true);
      }
      // API.findMemberBenefitsByContact(memberId, contractId).then(response => {
      API.getBenefitTree(contractId).then(response => {
        if (response && response.error === undefined) {
          console.log('member contract',response)
          this.setState(
            {
              response: response,
            },
            () => {
              if (setIsLoading) {
                setIsLoading(false);
              }
            },
          );
        } else {
          this.setState({
            isOpen:true
          })
          if (setIsLoading) {
            setIsLoading(false);
          }
        }
      });
    }
  
    render(): JSX.Element {
      const { response } = this.state;
      return (
        // <div className="MemberBenefitsPage section">
          <Grid container className="sub-section" spacing={5}>
            <Grid item xs={12}>
              {response === null ? null :<PNTable memberBenefits={response} planType={this.props.planType} /> }
              
            </Grid>
            <CustomModal
              isOpen={this.state.isOpen}
              header={<Typography variant='h5'>Error</Typography>}
              content={<Typography variant='body1'>Error Loading Benefits</Typography>}
              footer={<Button style={{height: '40px',
              minWidth: '125px',
              border: '1px solid #3AB77D',
              color: '#3AB77D',
              textTransform: 'capitalize',}} onClick={()=>{this.setState({isOpen:false})
              this.loadBenefitsData();}}>Confirm</Button>}
            />
          </Grid>
        //   <div className="section" />
        // </div>
      );
    }
  }
  