import * as React from 'react';
import { Grid } from '@material-ui/core';
import { PartialBenefitData } from './Models';
import { API } from '../../API';
import { BenefitsTable } from 'Pages/MemberContract/BenefitsTable';

interface IProps {
  memberId: string;
  contractId: string;
  setIsLoading: (isLoading: boolean) => void;
}

interface IStates {
  response: any;
  allMemberBenefits: PartialBenefitData[];
  filteredMemberBenefits: PartialBenefitData[];
  globalFilter: string;
  columnFilters: any;
}

export class MemberBenefits extends React.Component<IProps, IStates> {
  constructor(props: IProps) {
    super(props);

    this.state = {
      response: null,
      allMemberBenefits: [],
      filteredMemberBenefits: [],
      globalFilter: '',
      columnFilters: [],
    };
  }

  componentDidMount() {
    const { memberId } = this.props;
    if (memberId && memberId.length > 0) {
      this.loadBenefitsData();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.memberId !== prevProps.memberId) {
      this.loadBenefitsData();
    }
  }

  loadBenefitsData(): void {
    const { memberId, contractId, setIsLoading } = this.props;
    if (!memberId || memberId.length === 0) {
      return;
    }

    if (setIsLoading) {
      setIsLoading(true);
    }
    API.findMemberBenefitsByContact(memberId, contractId).then(response => {
      if (response && response.error === undefined) {
        const nodes = response.sort ((node1: any, node2: any) => {
          if (node1.plan_type_rank && node2.plan_type_rank) {
            if (node1.plan_type_rank < node2.plan_type_rank) return -1;
            if (node1.plan_type_rank > node2.plan_type_rank) return 1;
          }

          if (node1.plan_type_rank) return -1;
          if (node2.plan_type_rank) return 1;

          if (node1.sort_index > node2.sort_index) return -1;
          if (node1.sort_index < node2.sort_index) return 1;

          if (node1.name > node2.name) return 1;
          if (node1.name < node2.name) return -1;

          return 0;
        });

        this.setState(
          {
            response: nodes,
          },
          () => {
            if (setIsLoading) {
              setIsLoading(false);
            }
          },
        );
      } else {
        if (setIsLoading) {
          setIsLoading(false);
        }
      }
    });
  }

  render(): JSX.Element {
    const { response } = this.state;
    return (
      <div className="MemberBenefitsPage section">
        <Grid container className="sub-section" spacing={5}>
          <Grid item xs={12}>
            <BenefitsTable memberBenefits={response} />
          </Grid>
        </Grid>
        <div className="section" />
      </div>
    );
  }
}
