import React, { useState, useEffect } from 'react';
import { isNil } from 'lodash';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    IconButton,
    Grid,
    Typography,
    FormControlLabel,
    Switch,
    Checkbox,
    Paper,
    Table,
    TableHead,
    TableBody,
    TableRow,
    TableCell,
    InputLabel,
    TextField,
    Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/core/styles';

import moment from 'moment';
import { DatePicker } from './DatePicker';

import { API } from '../../../API';
import { Loader } from 'Components/UI/LoadingIndicator';

const dependentCheckboxStyle = {
    size: 12,
    padding: 0,
    color: '#272E4C',
    '&$checked': {
      color: '#272E4C',
    },
};

const useStyles = makeStyles(theme => ({
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    dialog: {
        align: 'center',
        justify: 'center',
        width: '95%',
        fontFamily: 'usual',
    },
    dialogContent: {
        paddingTop: '0px',
        paddingBottom: theme.spacing(3),
        minWidth: '250px',
        width: '55vw',
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 350,
    },
    dialogFirstTitle: {
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px',
    },
    dialogSecondTitle: {
        fontSize: '14px',
        color: '#272E4C',
    },
    dialogNotice: {
        fontSize: 14,
        fontWeight: 400,
        color: '#000',
    },
    dialogMemberName: {
        fontSize: 18,
        fontWeight: 700,
        color: '#272E4C',
    },
    dialogMemberOverall: {
        fontSize: 14,
        fontWeight: 500,
        color: '#272E4C',
    },
    suspensionDateLabel: {
        fontSize: 14,
        fontWeight: 500,
        color: 'rgb(132,132,132)',
    },
    suspensionDateValue: {
        fontSize: 14,
        fontWeight: 'bold',
        color: '#272E4C',
    },
    tblTitle: {
        fontSize: 14,
        fontWeight: 600,
        color: 'rgba(39, 49, 76, 1)',
    },
    tblSubtitle: {
        fontSize: 12,
        fontWeight: 400,
        color: 'rgba(39, 49, 76, 1)',
    },
    tblPaperWithDeps: {
        minHeight: 175,
        borderRadius: 0,
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    tblPaperNoDeps: {
        minHeight: 'auto',
        borderRadius: 0,
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    dialogAction: {
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(3),
    },
    cancelButton: {
        minWidth: 129,
        marginRight: 25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: '#FFFFFF',
    },
    nextMemberButton: {
        minWidth: 129,
        backgroundColor: '#3AB77D',
    },
    liftSuspensionButton: {
        minWidth: 129,
        backgroundColor: '#3AB77D',
        '&:hover': {
            backgroundColor: '#2e9565',
        },
    },
    labelRemarks: {
        fontSize: '14px',
        color: '#272E4C',
        fontWeight: 600,
        paddingTop: theme.spacing(2),
    }
}));

interface LiftSuspensionModalProps {
    id: string;
    isModalOpen: boolean;
    memberData: any[];
    maxDate?: Date;
    onStatusAction: (data: any[], isApplyToAll: boolean) => void;
    onClose: () => void;
}

export const LiftSuspensionModal: React.FC<LiftSuspensionModalProps> = (
    props: LiftSuspensionModalProps
): JSX.Element => {
    const classes = useStyles();
    const { id, isModalOpen, memberData } = props;

    const [isLoaded, setIsLoaded] = useState<boolean>(false);
    const [user, setUser] = useState<any>({});
    const [liftDatesData, setLiftDatesData] = useState<any>([]);
    const [memberDataHolder, setMemberDataHolder] = useState<any>([]);
    let [cnt, setCnt] = useState<number>(0);
    const [applyToAllSwitch, setApplyToAllSwitch] = useState<boolean>(false);
    const [fetchedDependents, setFetchedDependents] = useState<any>([]); 

    const [selectedDateLift, setSelectedDateLift] = useState<Date | null>(null);
    const [selectedDependents, setSelectedDependents] = useState<any[]>([]);
    const [remarks, setRemarks] = useState<string>('');
    const [liftDateError, setLiftDateError] = useState<boolean>(false);
    const [liftDateErrorMessage, setLiftDateErrorMessage] = useState<string>('');

    const [maxDate, setMaxDate] = useState<Date | undefined>(undefined);


    useEffect(() => {
        // reset components values on modal open
        if(props.isModalOpen) {
            resetData();
            applyAvailableLiftDate(memberData);
        }
    }, [props.isModalOpen]);
    
    useEffect(() => {
        if(isModalOpen === true){
            // console.log('current member index:',cnt)
            // console.log('apply to all:',applyToAllSwitch)
            // console.log('lift dates:',liftDatesData)

            if(applyToAllSwitch === false){
                setSelectedDateLift(liftDatesData[cnt]);
            } else {
                setSelectedDateLift(null);
            }
        }
    },[cnt,applyToAllSwitch,liftDatesData])

    useEffect(() => {
        async function getMemberDependents(){
            try {
                const userData = await API.getUserDataFromDb();
                setUser(userData);

                let dependentsArray: any[] = [];
                let promise = await memberData.map(data => API.getMemberStatusActionDependents(data.id)
                    .then(res => {
                        return res.map(res => {
                            res['principal_name'] = data.member_name;
                            return res;
                        })
                    })
                )
                const response = await Promise.all(promise);
                let filteredResponse = response.filter(res => res.length > 0)
                
                filteredResponse.forEach(filtRes => {
                    filtRes.forEach((dependent) => {
                        dependentsArray.push(dependent);
                    });
                })
                setFetchedDependents(dependentsArray);
                setIsLoaded(true);
            } catch(error){
                console.log(error)
                setIsLoaded(true);
            }
        }
        
        if(memberData.length > 0 && isModalOpen === true){
            getMemberDependents();
        }
    },[isModalOpen,cnt])

    useEffect(() => {
        if(isModalOpen === true){
            if(!isNil(props.maxDate)) {
                const maxDateMoment = moment(props.maxDate);
    
                if (!isNil(maxDateMoment) && maxDateMoment.isValid()) {
                    const dayBefore = maxDateMoment.add(-1, 'days').toDate();
                    setMaxDate(dayBefore);
                }
            }
        }
    },[props.maxDate])

    useEffect(() => {},[selectedDateLift,memberDataHolder,selectedDependents])


    const resetData = () => {
        setIsLoaded(false);
        setUser({});
        setLiftDatesData([]);
        setMemberDataHolder([]);
        setCnt(0);
        setApplyToAllSwitch(false);
        setFetchedDependents([]);

        setSelectedDateLift(null);
        setSelectedDependents([]);
        setRemarks('');
        setLiftDateError(false);
        setLiftDateErrorMessage('');
    }

    const applyAvailableLiftDate = (suspendedMemberData: any[]) => {
        let liftDatesArray: any[] =[];
        suspendedMemberData.forEach(smData => {
            if(smData['member_suspension'].suspension_lift_date !== undefined){
                liftDatesArray.push(smData['member_suspension'].suspension_lift_date)
            } else {
                liftDatesArray.push(null)
            }
        })

        setLiftDatesData(liftDatesArray);
    }

    function handleApplyToAllSwitchChange(){
        if(applyToAllSwitch === true){
            setApplyToAllSwitch(false);     
        } else {
            setApplyToAllSwitch(true);
        }
    };

    function handleDateLift(date: Date) {
        setSelectedDateLift(date);
        setLiftDateError(false);
        setLiftDateErrorMessage('');
    }

    function handleDependentsCheckboxChange(id: string) {
        if (selectedDependents.includes(id)) {
          setSelectedDependents(selectedDependents.filter(item => item !== id));
        } else {
          const newSelectedDependents = [...selectedDependents];
          newSelectedDependents.push(id);
          setSelectedDependents(newSelectedDependents);
        }
    }

    function handleNextMember(){
        if(selectedDateLift === null) {
            setLiftDateError(true);
            setLiftDateErrorMessage('Lift date is required');
        } else {
            let memberIdsArray: any[] = [];                
            memberIdsArray.push(memberData[cnt].id);
    
            if(selectedDependents.length > 0){
                selectedDependents.forEach(dependentId => {
                    memberIdsArray.push(dependentId);
                })
            }
            
            let memberDataForDisplay = {};           
            if(memberData[cnt].member_id !== undefined && memberData[cnt].member_id !== null){
                memberDataForDisplay['member_id'] = memberData[cnt].member_id
            } else {
                memberDataForDisplay['member_id'] = 'None';
            }
            if(memberData[cnt].member_name !== undefined && memberData[cnt].member_name !== null){
                memberDataForDisplay['member_name'] = memberData[cnt].member_name;
            } else {
                memberDataForDisplay['member_name'] = 'None';
            }
            if(memberData[cnt].corporates !== undefined && memberData[cnt].corporates !== null){
                memberDataForDisplay['corporates'] = memberData[cnt].corporates;
            } else {
                memberDataForDisplay['corporates'] = 'None';
            }            
    
            const data = {
                memberIds: memberIdsArray,
                suspension_start_date: memberData[cnt]['member_suspension'].suspension_start_date,
                suspension_lift_date: selectedDateLift,
                remarks: remarks,
                suspension_started_by: user.userName,
                apply_to_all: false,
                memberIdsForDisplay: [memberDataForDisplay['member_id']],
                memberNamesForDisplay : [memberDataForDisplay['member_name']],
                memberCorporatesForDisplay: [memberDataForDisplay['corporates']],
            };
    
            setMemberDataHolder([...memberDataHolder, data]);
    
            setSelectedDateLift(null);
            setSelectedDependents([]);
            setRemarks('');
    
            setCnt(++cnt);               
        }
    }

    function handleStatusAction() {
        if(selectedDateLift === null) {
            setLiftDateError(true);
            setLiftDateErrorMessage('Lift date is required');
        } else {
            let finalMemberData: any[] = [];
            let memberIdsArray: any[] = [];
            
            let memberIdsForDisplayArray: any[] = [];
            let memberNamesForDisplayArray: any[] = [];
            let memberCorporatesForDisplayArray: any[] = [];
            let memberSuspensionDatesForDisplayArray: any[] = [];

            let data = {};
    
            if(applyToAllSwitch === false){                
                finalMemberData = memberDataHolder;
        
                memberIdsArray.push(memberData[cnt].id);
                if(selectedDependents.length > 0){
                    selectedDependents.forEach(dependentId => {
                        memberIdsArray.push(dependentId);
                    })
                }
    
                if(memberData[cnt].member_id !== undefined && memberData[cnt].member_id !== null){
                    memberIdsForDisplayArray.push(memberData[cnt].member_id);
                } else {
                    memberIdsForDisplayArray.push('None');
                }
                if(memberData[cnt].member_name !== undefined && memberData[cnt].member_name !== null){
                    memberNamesForDisplayArray.push(memberData[cnt].member_name);
                } else {
                    memberNamesForDisplayArray.push('None');
                }
                if(memberData[cnt].corporates !== undefined && memberData[cnt].corporates !== null){
                    memberCorporatesForDisplayArray.push(memberData[cnt].corporates);
                } else {
                    memberCorporatesForDisplayArray.push('None');
                }

                data = {
                    memberIds: memberIdsArray,
                    suspension_start_date: memberData[cnt]['member_suspension'].suspension_start_date,
                    suspension_lift_date: selectedDateLift,
                    remarks: remarks,
                    suspension_started_by: user.userName,
                    apply_to_all: applyToAllSwitch,
                    memberIdsForDisplay: memberIdsForDisplayArray,
                    memberNamesForDisplay : memberNamesForDisplayArray,
                    memberCorporatesForDisplay: memberCorporatesForDisplayArray,
                };
            } else {
                memberIdsArray = memberData.map(member => {
                    return member['id'];
                })
    
                memberIdsForDisplayArray = memberData.map(member => {
                    if(member['member_id'] !== undefined || member['member_id'] !== null)
                        return member['member_id'];
                    else
                        return 'None';
                })
                memberNamesForDisplayArray = memberData.map(member => {
                    if(member['member_name'] !== undefined || member['member_name'] !== null)
                        return member['member_name'];
                    else
                        return 'None';
                })
                memberCorporatesForDisplayArray = memberData.map(member => {
                    if(member['corporates'] !== undefined || member['corporates'] !== null)
                        return member['corporates'];
                    else
                        return 'None';
                })
                memberSuspensionDatesForDisplayArray = memberData.map(member => {
                    return member['member_suspension'].suspension_start_date;
                })
                
                data = {
                    memberIds: memberIdsArray,
                    suspension_lift_date: selectedDateLift,
                    remarks: remarks,
                    suspension_started_by: user.userName,
                    apply_to_all: applyToAllSwitch,
                    memberIdsForDisplay: memberIdsForDisplayArray,
                    memberNamesForDisplay : memberNamesForDisplayArray,
                    memberCorporatesForDisplay: memberCorporatesForDisplayArray,
                    memberSuspensionDatesForDisplay: memberSuspensionDatesForDisplayArray,
                };
            }
    
            finalMemberData.push(data)
    
            if(props.onStatusAction){
                props.onStatusAction(finalMemberData, applyToAllSwitch);
                handleOnClose();
            }              
        }
    }

    function handleOnClose() {
        if(props.onClose){
            resetData();
            props.onClose();
        }
    }

    return (
        <div>
            <Dialog
                id={id}
                maxWidth={false}
                open={isModalOpen}
                scroll={'paper'}
                aria-labelledby="modal-title"
                className={classes.dialog}
            >
                {isLoaded === false ? <Loader/> :
                    (<>
                        <DialogTitle
                            className={classes.dialogTitle}
                            disableTypography={true}
                            id="modal-title"
                        >
                            <div className={classes.dialogFirstTitle}>Lift Suspension Details</div>
                            <IconButton
                                id='close_button'
                                data-cy='close_button'
                                aria-label="close"
                                className={classes.closeButton}
                                onClick={handleOnClose}
                            >
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                        <DialogContent className={classes.dialogContent}>
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                    <FormControlLabel
                                        control={
                                            <Switch
                                                id={'members_apply_to_all'}
                                                data-cy={'members_apply_to_all'}
                                                checked={applyToAllSwitch}
                                                onChange={handleApplyToAllSwitchChange}
                                                color="primary"
                                                inputProps={{ 'aria-label': 'primary checkbox' }}
                                            />
                                        }
                                        label="Apply to all selected members"
                                    />
                                </Grid>
                                {applyToAllSwitch === false ? 
                                    <>
                                        <Grid item xs={12} style={{paddingBottom: '1.5rem'}}>
                                            <Typography className={classes.dialogMemberName}>
                                                {memberData[cnt] !== undefined && memberData[cnt].member_name}
                                                {' '}
                                                ({memberData[cnt] !== undefined && memberData[cnt].member_type})
                                            </Typography>
                                            <Typography className={classes.dialogMemberOverall}>
                                                {`${applyToAllSwitch === false ? (cnt+1) : memberData.length} / ${memberData.length} members to lift suspension`}
                                            </Typography>
                                        </Grid>                        
                                        <Grid item xs={7}>       
                                            <Typography
                                                display={'block'}
                                                variant={'h3'}
                                                className={classes.suspensionDateLabel}
                                            >
                                                EFFECTIVE DATE OF SUSPENSION
                                            </Typography>
                                            <Typography
                                                display={'block'}
                                                variant={'subtitle1'}
                                                className={classes.suspensionDateValue}
                                                gutterBottom
                                            >
                                                {memberData[cnt] !== undefined && 
                                                memberData[cnt].member_suspension !== undefined &&
                                                memberData[cnt].member_suspension.suspension_start_date !== undefined &&
                                                moment(memberData[cnt].member_suspension.suspension_start_date).format('MMM DD, YYYY')}
                                            </Typography>
                                        </Grid>
                                    </>
                                : null}
                                <Grid item xs={7}>
                                    <DatePicker
                                        data-cy={'member_suspension_lift_picker'}
                                        id={'member_suspension_lift_picker'}
                                        placeholder="Effective Date of Lifting"
                                        value={selectedDateLift}
                                        error={liftDateError}
                                        error_message={liftDateErrorMessage}
                                        minDate={new Date()}
                                        maxDate={maxDate}
                                        minDateMessage="Date of lifting should not be before start of suspension."
                                        onChange={handleDateLift}
                                        isRequired={true}
                                    />
                                </Grid>
                                {fetchedDependents.length > 0 ?
                                <> 
                                    <Grid item xs={12}>
                                        <Typography className={classes.tblTitle}>Dependents</Typography>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Paper 
                                            className={fetchedDependents.length > 0 ? 
                                                classes.tblPaperWithDeps : classes.tblPaperNoDeps
                                            }
                                        >
                                            <Table
                                                data-cy={'member_suspension_dependents_table'}
                                                id={'member_suspension_dependents_table'}
                                                size="small"
                                            >
                                                <TableHead>
                                                    <TableRow>
                                                        {applyToAllSwitch === false ? 
                                                            <TableCell style={{ width: '5%' }}>
                                                                {/* Checkbox Column */}
                                                            </TableCell>
                                                        : null}
                                                        <TableCell style={{ width: '40%' }}>
                                                            Member Name
                                                        </TableCell>
                                                        <TableCell style={applyToAllSwitch === false ? { width: '25%' } : { width: '30%' }}>
                                                            Relationship
                                                        </TableCell>
                                                        {/* create code for showing additional column for Principal 
                                                            of the Dependent when Apply to All is selected 
                                                            for reference
                                                        */}
                                                        <TableCell style={{ width: '30%' }}>
                                                            Status
                                                        </TableCell>
                                                    </TableRow>
                                                </TableHead>
                                                <TableBody>
                                                {fetchedDependents.map(
                                                    (row, index) => {
                                                        return (
                                                            <TableRow key={index}>
                                                                {applyToAllSwitch === false ? 
                                                                    <TableCell style={{ width: '5%' }}>
                                                                        <Checkbox
                                                                            style={dependentCheckboxStyle}
                                                                            disabled={
                                                                                row['member_status'] === 'Suspended' ? 
                                                                                false : true                                       
                                                                            }
                                                                            indeterminate={
                                                                                row['member_status'] === 'Suspended' ? 
                                                                                false : true  
                                                                            }
                                                                            checked={selectedDependents.includes(
                                                                                row['_id']
                                                                            )}
                                                                            onChange={() => {
                                                                                handleDependentsCheckboxChange(row['_id']);
                                                                            }}
                                                                        />
                                                                    </TableCell>
                                                                : null}
                                                                <TableCell style={{ width: '40%' }}>
                                                                    {`${row['first_name']} ${row['middle_name']} ${row['last_name']}`}                                                                    
                                                                </TableCell>
                                                                <TableCell style={applyToAllSwitch === false ? { width: '25%' } : { width: '30%' }}>
                                                                    {row['relationship_to_principal']}
                                                                </TableCell>
                                                                <TableCell style={{ width: '30%' }}>
                                                                    {row['member_status']}
                                                                </TableCell>
                                                            </TableRow>
                                                        );
                                                    }
                                                )}
                                                </TableBody>
                                            </Table>
                                        </Paper>
                                    </Grid>
                                </>
                                : null}
                                <Grid item xs={12}>
                                    <InputLabel 
                                        className={classes.labelRemarks} 
                                        htmlFor="member_suspension_remarks_field"
                                    >
                                        Remarks
                                    </InputLabel>
                                    <TextField
                                        data-cy={'member_suspension_remarks_field'}
                                        id={'member_suspension_remarks_field'}
                                        style={{ marginTop: '8px', width: '100%' }}
                                        margin="normal"
                                        variant="outlined"
                                        multiline={true}
                                        rows={10}
                                        value={remarks}
                                        onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                            const { value } = e.target;
                                            setRemarks(value);
                                        }}
                                        inputProps={{ 'aria-label': 'bare' }}
                                    />
                                </Grid>                        
                            </Grid>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            <Grid
                                container
                                direction="row"
                                justify="space-evenly"
                                alignItems="stretch"
                            >
                            <Grid item xs={4} style={{ textAlign: 'center' }}>
                                <Button
                                    data-cy={'member_suspension_cancel_btn'}
                                    id={'member_suspension_cancel_btn'}
                                    className={classes.cancelButton}
                                    variant="contained"
                                    color="primary"
                                    onClick={handleOnClose}
                                >
                                    Cancel
                                </Button>
                            </Grid>
                            <Grid item xs={4} style={{ textAlign: 'center' }}>
                                <Button
                                    data-cy={'member_suspension_btn'}
                                    id={'member_suspension_btn'}
                                    className={cnt !== memberData.length-1 && applyToAllSwitch === false  ? classes.nextMemberButton : classes.liftSuspensionButton}
                                    variant="contained"
                                    color="primary"
                                    onClick={cnt !== memberData.length-1 && applyToAllSwitch === false ? handleNextMember : handleStatusAction}
                                >
                                    {cnt !== memberData.length-1 && applyToAllSwitch === false ? 'Next Member' : 'Lift Suspension'}
                                </Button>
                            </Grid>
                            </Grid>
                        </DialogActions>
                    </>)
                }
            </Dialog>            
        </div>
    )
}
