  handleChangeMemberData = (key: string, value: any) => {
    this.setState({
      memberData: Object.assign(this.state.memberData, { [key]: value }),
    });
  };

  handlePatchMemberData = (data: any) => {
    this.setState({
      loading_state: true,
    });
    // console.log('DATAS',data)
    const id = this.state.member_upload_data['_id'];
    // console.log('ID', id)
    const temp_id = this.state.memberData['temp_id'];

    let member_data = cloneDeep(this.state.member_upload_data['members']);
    let validated_members_data = cloneDeep(
      this.state.member_upload_data['validated_members'],
    );
    for (let i = 0; i < member_data.length; i = i + 1) {
      if (member_data[i]['temp_id'] === temp_id) {
        delete data['_id'];
        delete data['validation_status'];
        // console.log('MEMBER DATA BEFORE')
        member_data[i] = Object.assign(member_data[i], data);
        // console.log('MEMBER DATA AFTER')
      }
    }
    for (let i = 0; i < validated_members_data.length; i = i + 1) {
      if (validated_members_data[i]['temp_id'] === temp_id) {
        delete data['_id'];
        delete data['validation_status'];
        // console.log('BEFORE', validated_members_data[i])
        // console.log('TARGET', data)
        validated_members_data[i] = Object.assign(
          validated_members_data[i],
          data,
        );
        // console.log('AFTER', validated_members_data[i])
      }
    }
    // console.log('MEMBER_DATA', member_data)
    // console.log('VALIDATED_MEMBER', validated_members_data)
    let payload = this.state.member_upload_data;
    payload['members'] = member_data;
    payload['validated_members'] = validated_members_data;
    API.patchUploadMember(id, payload)
      .then(response => {
        // console.log('RESPONSE')
        if (response && response.error) {
          // console.log('PATCH SUCCESS!')
          console.log('error');
        } else {
          this.getValidatedMembers(validated_members_data);
          // console.log('PATCH SUCCESS!', this.state)
        }
        this.setState({
          loading_state: false,
        });
      })
      .catch(err => {
        console.log('error', err);
        this.setState({
          loading_state: false,
        });
      });
  };

