import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Button,
  IconButton,
  InputLabel,
  TextField,
  RadioGroup,
  Radio,
  FormControlLabel,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import { DatePicker } from 'Components/UI/DatePicker';
// import { InformationLabel } from 'Components/UI/InformationLabel';
import { CustomAutocomplete } from 'Components/UI/CustomAutocomplete';
import { TerminationData } from '../IViewMemberPage';
// import { GlobalFunction } from 'Components/UI/GlobalFunction';

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
    paddingLeft: '24px',
    paddingRight: '24px',
    width: '30rem',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    // minWidth: '680px',
    // width: 'auto',
    // width: '30rem',
    // height: '30rem'
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
    marginRight: '64px',
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
    paddingTop: theme.spacing(2),
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    width: 115,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  approveButton: {
    minWidth: 129,
    marginRight: 25,
    color: '#FFFFFF',
    backgroundColor: '#3AB77D',
  },
  label: {
    fontSize: '0.75rem',
    // color: '#272E4C',
    // fontWeight: 600,
    // paddingTop: theme.spacing(2),
  },
  previousButton: {
    minWidth: 120,
    fontWeight: 600,
  },
  nextButton: {
    minWidth: 130,
    fontWeight: 600,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  tab: {
    textAlign: 'left',
    alignItems: 'left',
  },
}));

interface IProps {
  isModalOpen: boolean;
  terminationData: TerminationData;
  refreshTerminationData: () => void;
  resetTerminationData: () => void;
  onClose: () => void;
  onSubmit: () => void;
}

export const TerminateMemberModal: React.FC<IProps> = (
  props: IProps,
): JSX.Element => {
  const [isModalOpen, setIsModalOpen] = useState(props.isModalOpen);

  const classes = useStyles();

  useEffect(() => {
    if (props.isModalOpen && props.resetTerminationData) {
      props.resetTerminationData();
    }

    setIsModalOpen(props.isModalOpen);
  }, [props.isModalOpen]);

  const renderMainContent = () => {
    return (
      <>
        <Grid item xs={6}>
          <DatePicker
            data-cy={'terminate_member_modal_terminate_picker'}
            id={'terminate_member_modal_terminate_picker'}
            placeholder="Date of Termination"
            labelSize="small"
            value={props.terminationData.terminationDate}
            minDate={new Date()}
            error={false}
            error_message={''}
            onChange={(date: Date) => {
              props.terminationData.terminationDate = date;
              props.refreshTerminationData();
            }}
          />
        </Grid>

        <Grid item xs={6}>
          <DatePicker
            data-cy={'terminate_member_modal_endorsed_picker'}
            id={'terminate_member_modal_endorsed_picker'}
            placeholder="Endorsed Date"
            labelSize="small"
            value={props.terminationData.endorsedDate}
            error={false}
            error_message={''}
            onChange={(date: Date) => {
              props.terminationData.endorsedDate = date;
              props.refreshTerminationData();
            }}
          />
        </Grid>

        {props.terminationData.source === 'Internal' ? (
          <Grid item xs={6}>
            <InputLabel
              className={classes.label}
              htmlFor="add_details_sender_name"
            >
              Endorsed by
            </InputLabel>
            <TextField
              data-cy={'terminate_member_modal_endorint_field'}
              id={'terminate_member_modal_endorint_field'}
              style={{ marginTop: '8px', width: '100%' }}
              margin="normal"
              variant="outlined"
              value={props.terminationData.endorsedBy}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                props.terminationData.endorsedBy = e.target.value;
                props.refreshTerminationData();
              }}
              inputProps={{ 'aria-label': 'bare' }}
            />
          </Grid>
        ) : (
          <Grid item xs={6}>
            <CustomAutocomplete
              data-cy={'terminate_member_modal_endorext_select'}
              id={'terminate_member_modal_endorext_select'}
              labelSize="small"
              options={[
                { value: 'Client', label: 'Client' },
                { value: 'Broker', label: 'Broker' },
              ]}
              label={'Endorsed by'}
              value={props.terminationData.endorsedType}
              onChange={(value: any) => {
                props.terminationData.endorsedType = value;
                props.refreshTerminationData();
              }}
              error={false}
              error_message={''}
            />
          </Grid>
        )}

        <Grid item xs={6}>
          <CustomAutocomplete
            data-cy={'terminate_member_modal_sentvia_select'}
            id={'terminate_member_modal_sentvia_select'}
            labelSize="small"
            options={[
              { value: 'Email', label: 'Email' },
              { value: 'Ticket', label: 'Ticket' },
              { value: 'Portal', label: 'Portal' },
              { value: 'Fax', label: 'Fax' },
            ]}
            label={'Sent via'}
            value={props.terminationData.sentVia}
            onChange={(value: any) => {
              props.terminationData.sentVia = value;
              props.refreshTerminationData();
            }}
            error={false}
            error_message={''}
          />
        </Grid>

        {props.terminationData.source === 'External' ? (
          <Grid item xs={6}>
            <InputLabel
              className={classes.label}
              htmlFor="add_details_sender_name"
            >
              Person who endorsed
            </InputLabel>
            <TextField
              data-cy={'terminate_member_modal_endorext_field'}
              id={'terminate_member_modal_endorext_field'}
              style={{ marginTop: '8px', width: '100%' }}
              margin="normal"
              variant="outlined"
              value={props.terminationData.endorsedBy}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                props.terminationData.endorsedBy = e.target.value;
                props.refreshTerminationData();
              }}
              inputProps={{ 'aria-label': 'bare' }}
            />
          </Grid>
        ) : null}

        {props.terminationData.source === 'External' ? (
          <Grid item xs={6}>
            <InputLabel
              className={classes.label}
              htmlFor="add_details_sender_name"
            >
              Email Address
            </InputLabel>
            <TextField
              data-cy={'terminate_member_modal_email_field'}
              id={'terminate_member_modal_email_field'}
              style={{ marginTop: '8px', width: '100%' }}
              margin="normal"
              variant="outlined"
              value={props.terminationData.email}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                props.terminationData.email = e.target.value;
                props.refreshTerminationData();
              }}
              inputProps={{ 'aria-label': 'bare' }}
            />
          </Grid>
        ) : null}

        <Grid item xs={6}>
          <CustomAutocomplete
            data-cy={'terminate_member_modal_reason_select'}
            id={'terminate_member_modal_reason_select'}
            labelSize="small"
            options={[
              { value: 'Death', label: 'Death' },
              { value: 'Resignation', label: 'Resignation' },
              { value: 'Non - affiliation', label: 'Non - affiliation' },
              {
                value: 'Enrolment to other HMO',
                label: 'Enrolment to other HMO',
              },
              { value: 'Other', label: 'Other' },
            ]}
            label={'Reason'}
            value={props.terminationData.reason}
            onChange={(value: any) => {
              props.terminationData.reason = value;
              props.refreshTerminationData();
            }}
            error={false}
            error_message={''}
          />
        </Grid>

        <Grid item xs={6}>
          <InputLabel
            className={classes.label}
            htmlFor="add_details_sender_name"
          >
            Ticket Number
          </InputLabel>
          <TextField
            data-cy={'terminate_member_modal_ticketno_field'}
            id={'terminate_member_modal_ticketno_field'}
            style={{ marginTop: '8px', width: '100%' }}
            margin="normal"
            variant="outlined"
            value={props.terminationData.ticketNo}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              props.terminationData.ticketNo = e.target.value;
              props.refreshTerminationData();
            }}
            inputProps={{ 'aria-label': 'bare' }}
          />
        </Grid>
      </>
    );
  };

  return (
    <Dialog
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 1200 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Termination Details</div>
        <IconButton
          data-cy={'terminate_member_modal_close_btn'}
          id={'terminate_member_modal_close_btn'}
          aria-label="close"
          className={classes.closeButton}
          onClick={() => props.onClose()}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <RadioGroup
              data-cy={'terminate_member_modal_radio_group'}
              id={'terminate_member_modal_radio_group'}
              value={props.terminationData.source}
              style={{ display: 'inline-block', paddingLeft: '5px' }}
              onChange={e => {
                // @ts-ignore
                props.terminationData.source = e.target.value;
                props.refreshTerminationData();
              }}
            >
              <FormControlLabel
                value="Internal"
                control={<Radio style={{ padding: '5px' }} color="primary" />}
                label="Internal"
                labelPlacement="end"
              />
              <FormControlLabel
                value="External"
                control={<Radio style={{ padding: '5px' }} color="primary" />}
                label="External"
                labelPlacement="end"
              />
            </RadioGroup>
          </Grid>
          {renderMainContent()}
        </Grid>
        <div style={{ paddingBottom: '4rem' }} />
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'terminate_member_modal_cancel_btn'}
              id={'terminate_member_modal_cancel_btn'}
              className={classes.cancelButton}
              variant="contained"
              color="secondary"
              onClick={() => props.onClose()}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'terminate_member_modal_btn'}
              id={'terminate_member_modal_btn'}
              className={classes.approveButton}
              style={{ background: '#FD5451' }}
              variant="contained"
              color="primary"
              // disabled={dropdownChoice.value === '' && props.action === 'Approve'}
              onClick={props.onSubmit}
            >
              Submit
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};
