import * as React from 'react';
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Button,
  IconButton,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import { Components } from '@hims/core';
import { CustomAutocomplete } from 'Components/UI/CustomAutocomplete';
import { LostInternetModal } from 'Components/UI';
import { get } from 'lodash';

type ISelectItem = Components.UI.InputSelect.ISelectItem;

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: 25,
    paddingBottom: 150,
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '680px',
    width: 'auto',
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
    minWidth: 350,
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    zIndex: 300,
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    minWidth: 129,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  submitButton: {
    minWidth: 129,
    backgroundColor: '#3AB77D',
  },
  previousButton: {
    minWidth: 120,
    fontWeight: 600,
  },
  nextButton: {
    minWidth: 130,
    fontWeight: 600,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  tab: {
    textAlign: 'left',
    alignItems: 'left',
  },
  error: {
    marginTop: 4,
    fontSize: '10px',
    color: '#FF0000',
  },
  dialogDatePicker: {
    marginBottom: 20,
  },
}));

interface GenerateReportModalProps {
  id: string;
  isModalOpen: boolean;
  clientList: any;
  onClose: () => void;
  onSubmit: (selectedAccount: ISelectItem) => void;
}

export const GenerateReportModal: React.FC<GenerateReportModalProps> = (
  props: GenerateReportModalProps,
): JSX.Element => {
  const { isModalOpen, id } = props;

  const [selectedAccount, setSelectedAccount] = useState<ISelectItem | null>(
    null,
  );
  const [accounts, setAccounts] = useState<ISelectItem[]>([]);
  const [selectedAccountError, setSelectedAccountError] = useState<boolean>(
    false,
  );
  const [checkInternetFlag, setCheckInternetFlag] = useState(false);
  const classes = useStyles();

  function handleOnClose() {
    setSelectedAccount(null);
    props.onClose();
  }

  function handleSubmit() {
    if (selectedAccount) {
      props.onSubmit(selectedAccount);
      setSelectedAccount(null);
    } else {
      setSelectedAccountError(true);
    }
  }

  function handleChangeAccount(item: ISelectItem) {
    setSelectedAccount(item);
  }

  const handleCloseLostInternetConnectionModal = () => {
    setCheckInternetFlag(false);
  };

  useEffect(() => {
    const clients: any[] = get(props, 'clientList', []);
    if (clients.length > 0) {
      setAccounts(
        clients.map(item => {
          const option = {
            id: item.client_id,
            value: item.client_id,
            label: item.client_name,
          };

          if (clients.length === 1) {
            setSelectedAccount(option);
          }

          return option;
        }),
      );
    } else {
      setAccounts([]);
    }
  }, [props.clientList]);

  return (
    <React.Fragment>
      <LostInternetModal
        id="Lost Internet Modal"
        isModalOpen={checkInternetFlag}
        onClose={handleCloseLostInternetConnectionModal}
      />

      <Dialog
        id={id}
        maxWidth={false}
        open={isModalOpen}
        scroll={'paper'}
        aria-labelledby="modal-title"
        className={classes.dialog}
        style={{ zIndex: 1299 }}
      >
        <DialogTitle
          className={classes.dialogTitle}
          disableTypography={true}
          id="modal-title"
        >
          <div className={classes.dialogFirstTitle}>Generate Report</div>
          <div className={classes.dialogSecondTitle}></div>
          <IconButton
            aria-label="close"
            className={classes.closeButton}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <Grid item xs={12}>
            <CustomAutocomplete
              data-cy={'generate_report_account_select'}
              id={'generate_report_account_select'}
              options={accounts}
              label={'Corporate Account *'}
              value={selectedAccount}
              error={selectedAccountError}
              error_message={
                selectedAccountError ? 'Please select a corporate account' : ''
              }
              addButton={false}
              addButtonLabel={''}
              onChange={handleChangeAccount}
            />
          </Grid>
        </DialogContent>
        <DialogActions className={classes.dialogAction}>
          <Grid
            container
            direction="row"
            justify="space-evenly"
            alignItems="stretch"
          >
            <Grid item xs={4} style={{ textAlign: 'center' }}>
              <Button
                data-cy={'cancel_generate_report_account_btn'}
                id={'cancel_generate_report_account_btn'}
                className={classes.cancelButton}
                variant="contained"
                color="primary"
                onClick={handleOnClose}
              >
                Cancel
              </Button>
            </Grid>
            <Grid item xs={4} style={{ textAlign: 'center' }}>
              <Button
                data-cy={'generate_report_account_btn'}
                id={'generate_report_account_btn'}
                className={classes.submitButton}
                variant="contained"
                color="primary"
                onClick={handleSubmit}
              >
                Generate
              </Button>
            </Grid>
          </Grid>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
};

GenerateReportModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
};
