  const getFilteredPNs = () => {
    if (globalFilter && globalFilter !== '') {
      const containsGlobalFilter = (target: any) => {
        return (
          target &&
          target !== undefined &&
          target !== null &&
          includes(toLower(target), toLower(globalFilter))
        );
      };

      const filtered = filter(partnerNetworks, partnerNetwork => {
        return (
          containsGlobalFilter(partnerNetwork.name) ||
          containsGlobalFilter(partnerNetwork.address) ||
          containsGlobalFilter(partnerNetwork.city) ||
          containsGlobalFilter(partnerNetwork.province)
        );
      });
      setFilteredPNs(filtered);
    } else {
        setFilteredPNs(partnerNetworks);
    }
  };

  const getPartnerNetworks = () => {
    console.log('props',props)
    // const planTypeCode = props.memberBenefits.find()
    const planTypeNodes = props.memberBenefits.filter(nodes =>
        nodes.tree_path[2]===props.planType &&
        nodes.tree_path.includes('NT'))
    console.log('nodes',planTypeNodes)
    if(planTypeNodes.length>0){
      const networkNode = planTypeNodes.find(nodes=>nodes.code==='NT')
      console.log('network',networkNode)
      const networkTypeNode = planTypeNodes.find(nodes=>nodes.code===networkNode['custom_metadata']['type'])
      console.log('networkType',networkTypeNode)

      const includedFacilities = networkTypeNode['custom_metadata']['included_facilities']
      const excludedFacilities = networkTypeNode['custom_metadata']['excluded_facilties']
      console.log('facilities',includedFacilities,excludedFacilities)
      const networkFacilities:any[]=[]
      includedFacilities.forEach(item=>{
        networkFacilities.push({
          name:item.name,
          address:'',
          city:'',
          province:'',
        })
      })
      excludedFacilities.forEach(item=>{
        networkFacilities.push({
          name:item.name,
          address:'',
          city:'',
          province:'',
        })
      })
      setPartnerNetworkType(networkTypeNode.name)
      if (networkNode.custom_metadata && networkNode.custom_metadata.groups){
        const netgroups:any[] = []
        networkGroups.forEach(item=>{
          netgroups.push({...item,
            checked:networkNode.custom_metadata.groups.includes(item.label)})
        })
        setNetworkGroups(netgroups)
      }
      setPartnerNetworks(networkFacilities)
    }

  };

