import * as React from 'react';
import { CustomModal } from './CustomModal';
import { CustomButton } from './CustomButton';
import { Dropdown } from './Dropdown';
import { API } from '../../../API';

interface DropdownChoice {
    value: string;
    label: string;
    params: any[];
}

interface IProps {
    mode: 'Assign' | 'Reassign';
    ticket: any;
    isOpen: boolean;
    onClose: () => void;
    onSuccess: (data: any) => void;
}

interface IStates {
    assignees: DropdownChoice[];
    assignee: DropdownChoice;
    isError: boolean;
    ticket: any;
}

export class ReassignTicketModal extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            ticket: props.ticket,
            assignees: [],
            assignee: { value: '', label: '', params: [] },
            isError: false,
        };
    }

    componentDidUpdate(prevProps) {
        if(prevProps.ticket !== this.props.ticket || prevProps.mode !== this.props.mode){
            this.setState({assignee: { value: '', label: '', params: [] }})
            const assign_or_reassign : any = this.props.mode === 'Reassign' ? this.props.ticket.assignee_pmaker_uid : undefined
            API.getTicketAssignees(this.props.ticket.ticket_type, assign_or_reassign).then(response =>{
                if(Array.isArray(response)){
                    const assignees = response.map(item => {
                        let params : any[] = [];
                        params.push(item.account.pmaker_uid)
                        params.push(item.account.username)
                        const name = item.account.first_name+' '+item.account.last_name
                        // const name = item.account.username
                        return ({
                            value: name,
                            label: name,
                            params: params
                        })
                    })
                    // console.log('ASS', assignees)
                    this.setState({assignees : assignees})
                }
            })
            .catch(err => console.log(err))
        }
    }

    onReassign = () => {
        const assignee = this.state.assignee;
        const ticket = this.props.ticket;
        if(!ticket) return;

        this.props.onSuccess(assignee)
        
    }

    
    render(): JSX.Element {
        
        return (
            <CustomModal
                className='modal'
                isOpen={this.props.isOpen}
                handleClose={() => this.props.onClose()}
                header={this.props.mode === 'Reassign' ? 'Re-assign Ticket' : 'Assign Ticket'}
                content={
                    <>
                    <div style={{padding: '0.5rem 0'}}>
                        {
                        this.props.mode === 'Reassign' ? `Re-assigning this ticket will change the ownership from 
                        ${this.props.ticket && this.props.ticket.assignee ? this.props.ticket.assignee : ''} to a new assignee.` : 
                        `Assigning this ticket will change the ownership to a new assignee.`
                        }
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <Dropdown 
                            id='reassign_select'
                            handleBlur={true}
                            placeholder='Select New Assignee'
                            choices={this.state.assignees}
                            value={this.state.assignee}
                            showAllResult
                            onChange={(value: DropdownChoice) => {
                                this.setState({assignee: value});
                            }} />
                    </div>
                    </>
                }
                footer={
                    <>
                    <CustomButton
                            id='cancel_button'
                            text='Cancel'
                            variant='outlined'
                            onClick={() => {
                                this.props.onClose()
                            }} />
                        <span style={{padding: '1rem'}} />
                    <CustomButton 
                        id='reassign_button'
                        text={this.props.mode === 'Reassign' ? 'Re-assign' : 'Assign'}
                        variant='contained'
                        onClick={() => {
                            this.onReassign();
                        }} />
                    </>
                } />
        );
    }
}