import React from 'react';
import { Grid, Typography } from '@material-ui/core/';
import { makeStyles } from '@material-ui/core/styles';
import moment from 'moment';

const informationItemStyles = makeStyles({
  infoName: {
    fontSize: '0.75rem',
    color: 'rgba(39, 46, 76, 0.6)',
    textTransform: 'uppercase',
    paddingRight: 32,
  },
  infoValue: {
    fontSize: '1rem',
    fontWeight: 600,
    color: 'rgb(39, 46, 76)',
  },
});

interface InformationItemProps {
  name?: string;
  value?: string;
  link?: string;
}

const InformationItem: React.FC<InformationItemProps> = (
  props: InformationItemProps,
): JSX.Element => {
  const classes = informationItemStyles(props);

  return (
    <Grid container alignItems="center">
      <Grid item xs={12}>
        <Typography
          data-cy={props.name ? `person_profile_${props.name.toLowerCase().replace(' ', '_')}_info` : ''}
          id={props.name ? `person_profile_${props.name.toLowerCase().replace(' ', '_')}_info` : ''}
          className={classes.infoName}
        >
          {props.name}
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <Typography
          data-cy={props.value ? `person_profile_${props.value.toLowerCase().replace(' ', '_')}_value` : ''}
          id={props.value ? `person_profile_${props.value.toLowerCase().replace(' ', '_')}_value` : ''}
          className={classes.infoValue}
        >{props.value}</Typography>
      </Grid>
    </Grid>
  );
};

const informationStyles = makeStyles({
  container: {
    paddingLeft: '32px',
  },
});

interface InformationProps {
  memberDetails: any;
}

export const Information: React.FC<InformationProps> = (
  props: InformationProps,
): JSX.Element => {
  const styles = informationStyles();
  const DEFAULT_STRING = 'N/A';
  const {
    first_name,
    middle_name,
    last_name,
    title,
    suffix,
    civil_status,
    date_of_birth,
    gender,
    email_address,
  } = props.memberDetails;

  const getDefaultedString = source => {
    return source || DEFAULT_STRING;
  };

  const getFormattedBirthdate = (dateOfBirth: string) => {
    let birthdate = DEFAULT_STRING;
    if (moment(dateOfBirth, 'MM/DD/YYYY', true).isValid()) {
      birthdate = moment(dateOfBirth, 'MM/DD/YYYY', true).format(
        'MMM DD, YYYY',
      );
    }

    if (moment(dateOfBirth, 'YYYY/MM/DD', true).isValid()) {
      birthdate = moment(dateOfBirth, 'MM/DD/YYYY', true).format(
        'MMM DD, YYYY',
      );
    }

    return birthdate;
  };

  const getAgeFromBirthdate = (dateOfBirth: string) => {
    const calculateDiffMs = (date: Date) => {
      return Date.now() - date.getTime();
    };

    let dateDiffMs = 0;
    if (moment(dateOfBirth, 'MM/DD/YYYY', true).isValid()) {
      dateDiffMs = calculateDiffMs(
        moment(dateOfBirth, 'MM/DD/YYYY', true).toDate(),
      );
    }

    if (moment(dateOfBirth, 'YYYY/MM/DD', true).isValid()) {
      dateDiffMs = calculateDiffMs(
        moment(dateOfBirth, 'MM/DD/YYYY', true).toDate(),
      );
    }

    if (dateDiffMs > 0) {
      const ageDate = new Date(dateDiffMs);
      return Math.abs(ageDate.getUTCFullYear() - 1970);
    }

    return 0;
  };

  return (
    <Grid
      container
      className={styles.container}
      spacing={4}
      direction="column"
      justify="flex-start"
      alignItems="stretch"
    >
      <Grid item xs={12}>
        <Grid
          container
          spacing={2}
          direction="column"
          justify="flex-start"
          alignItems="stretch"
        >
          <Grid item xs={12}>
            <InformationItem name={'PERSON ID'} value={'-'} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid
          container
          spacing={2}
          direction="row"
          justify="flex-start"
          alignItems="stretch"
        >
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'FIRST NAME'}
              value={getDefaultedString(first_name)}
            />
          </Grid>
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'MIDDLE NAME'}
              value={getDefaultedString(middle_name)}
            />
          </Grid>
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'LAST NAME'}
              value={getDefaultedString(last_name)}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid
          container
          spacing={2}
          direction="row"
          justify="flex-start"
          alignItems="stretch"
        >
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'TITLE/PREFFIX'}
              value={getDefaultedString(title)}
            />
          </Grid>
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'SUFFIX'}
              value={getDefaultedString(suffix)}
            />
          </Grid>
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'CIVIL STATUS'}
              value={getDefaultedString(civil_status)}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid
          container
          spacing={2}
          direction="row"
          justify="flex-start"
          alignItems="stretch"
        >
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'AGE'}
              value={`${getAgeFromBirthdate(date_of_birth) || DEFAULT_STRING}`}
            />
          </Grid>
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'DATE OF BIRTH'}
              value={getFormattedBirthdate(date_of_birth)}
            />
          </Grid>
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'GENDER'}
              value={getDefaultedString(gender)}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid
          container
          spacing={2}
          direction="row"
          justify="flex-start"
          alignItems="stretch"
        >
          <Grid item md={4} xs={12}>
            <InformationItem
              name={'EMAIL'}
              value={getDefaultedString(email_address)}
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};

Information.defaultProps = {
  memberDetails: {
    _id: '',
    temp_id: '',
    batch_name: '',
    member_id: '',
    title: '',
    last_name: '',
    middle_name: '',
    first_name: '',
    suffix: '',
    date_of_birth: '',
    gender: '',
    civil_status: '',
    email_address: '',
    client_id: '',
    member_type: '',
    plan_type: '',
    principal_name: '',
    contracts: [],
    hire_date: '',
    effectivity_date: '',
    formatted_effectivity_date: '',
    job_lvl: '',
    employee_id: '',
    site: '',
    site_address: '',
    floor: '',
    designation: '',
    supervisor: '',
    cost_center: '',
    financial_code: '',
    sub_company: '',
    department: '',
    is_vip: false,
    is_philhealth_rider: false,
    relationship_to_principal: '',
    member_status: '',
    registered_name: '',
    client_object: {
      registered_name: '',
      corporate_account_no: '',
    },
  },
};
