import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Typography,
  Grid,
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
    minWidth: '250px',
    width: '45vw',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '680px',
    width: 'auto',
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
  },
  desc: {
    fontSize: 14,
    fontWeight: 400,
    color: 'rgba(39, 49, 76, 1)',
  },
  deschighlight: {
    fontSize: 14,
    fontWeight: 600,
    color: 'rgba(39, 49, 76, 1)',
  },
  tblbox: {
    minHeight: 175,
    borderRadius: 0,
    boxShadow: '0px 3px 6px #272E4C19',
    padding: 0,
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    minWidth: 129,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  submitButton: {
    minWidth: 129,
    backgroundColor: '#3AB77D',
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
}));

interface ReactivateMemberConfirmModalProps {
  isModalOpen: boolean;
  id: string;
  memberFName?: string;
  memberName?: string;
  clientName?: string;
  dependentData?: any[];
  data?: any;
  onClose: () => void;
  onSubmit: (data: any) => void;
}

export const ReactivateMemberConfirmModal: React.FC<
  ReactivateMemberConfirmModalProps
> = (props: ReactivateMemberConfirmModalProps): JSX.Element => {
  const {
    isModalOpen,
    id,
    memberFName,
    memberName,
    clientName,
    dependentData,
    data,
  } = props;

  const classes = useStyles();

  const handleOnClose = () => {
    props.onClose();
  };

  const handleSubmit = () => {
    props.onSubmit(data);
  };

  let selectedMembers =
    data && data.selectedMembers ? data.selectedMembers : [];

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>
          Confirm Reactivation of Members
        </div>
        <div className={classes.dialogSecondTitle}></div>
        <IconButton
          data-cy={'reactive_member_conmod_close_btn'}
          id={'reactive_member_conmod_close_btn'}
          aria-label="close"
          className={classes.closeButton}
          onClick={props.onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          <Grid item xs={12} style={{ paddingTop: 24 }}>
            <Typography className={classes.desc}>
              Are you sure you want to reactivate the membership of{' '}
              <span className={classes.deschighlight}>{memberName}</span> in{' '}
              <span className={classes.deschighlight}>{clientName}</span>?
            </Typography>
          </Grid>
          <Grid item xs={12} style={{ marginTop: 24, marginBottom: 24 }}>
            <Typography className={classes.desc}>
              {memberFName}&apos;s dependents will have the following status
              after reactivation.
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Paper className={classes.tblbox}>
              <Table
                data-cy={'reactive_member_conmod_dependents_table'}
                id={'reactive_member_conmod_dependents_table'}
                size="small"
              >
                <TableHead>
                  <TableRow>
                    <TableCell style={{ width: '40%' }}>Member Name</TableCell>
                    <TableCell style={{ width: '30%' }}>Relationship</TableCell>
                    <TableCell style={{ width: '30%' }}>Status</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {dependentData && dependentData.length > 0 ? (
                    dependentData.map((row, index) => (
                      <TableRow key={index}>
                        <TableCell
                          component="th"
                          scope="row"
                          style={{ width: '40%' }}
                        >
                          {row['first_name'] +
                            ' ' +
                            (row['middle_name']
                              ? row['middle_name'].charAt(0) + '. '
                              : '') +
                            row['last_name']}
                        </TableCell>
                        <TableCell style={{ width: '30%' }}>
                          {row['relationship_to_principal']}
                        </TableCell>
                        <TableCell style={{ width: '30%' }}>
                          {selectedMembers.includes(row['_id'])
                            ? 'Awaiting Activation'
                            : row['member_status']}
                        </TableCell>
                      </TableRow>
                    ))
                  ) : (
                    <TableRow>
                      <TableCell
                        component="th"
                        scope="row"
                        style={{ textAlign: 'center' }}
                        colSpan={3}
                      >
                        No enrolled dependents
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </Paper>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid container direction="row" justify="center" alignItems="stretch">
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'reactive_member_conmod_cancel_btn'}
              id={'reactive_member_conmod_cancel_btn'}
              className={classes.cancelButton}
              variant="contained"
              color="primary"
              onClick={handleOnClose}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'reactive_member_conmod_btn'}
              id={'reactive_member_conmod_btn'}
              className={classes.submitButton}
              variant="contained"
              color="primary"
              onClick={handleSubmit}
            >
              Reactivate
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

ReactivateMemberConfirmModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
  memberFName: '',
  memberName: '',
  dependentData: [],
  data: {},
};
