import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  InputLabel,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
  Typography,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import moment from 'moment';
import { get, isNil, map } from 'lodash';

import { DatePicker } from './DatePicker';

interface SuspendMemberModalProps {
  id: string;
  isModalOpen: boolean;
  isPrincipal?: boolean;
  memberData?: any;
  dependents?: any[];
  maxDate?: Date;
  onSuspend: (data: any) => void;
  onClose: () => void;
}

const hmocheckboxStyle = {
  size: 12,
  padding: 0,
  color: '#272E4C',
  '&$checked': {
    color: '#272E4C',
  },
};

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
    minWidth: '250px',
    width: '55vw',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '680px',
    width: 'auto',
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
    minWidth: 350,
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
  },
  mnameLabel: {
    textTransform: 'uppercase',
    fontSize: 12,
    fontWeight: 400,
    color: 'rgba(39, 49, 76, 0.6)',
    paddingBottom: 4,
  },
  mnameValue: {
    fontSize: 14,
    fontWeight: 600,
    color: 'rgba(39, 49, 76, 1)',
  },
  tblTitle: {
    fontSize: 14,
    fontWeight: 600,
    color: 'rgba(39, 49, 76, 1)',
  },
  tblSubtitle: {
    fontSize: 12,
    fontWeight: 400,
    color: 'rgba(39, 49, 76, 1)',
  },
  tblbox: {
    minHeight: 175,
    borderRadius: 0,
    boxShadow: '0px 3px 6px #272E4C19',
    padding: 0,
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  label: {
    fontSize: '14px',
    color: '#272E4C',
    fontWeight: 600,
    paddingTop: theme.spacing(2),
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    minWidth: 129,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  suspendButton: {
    minWidth: 129,
    backgroundColor: '#FD5451',
  },
  previousButton: {
    minWidth: 120,
    fontWeight: 600,
  },
  nextButton: {
    minWidth: 130,
    fontWeight: 600,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  tab: {
    textAlign: 'left',
    alignItems: 'left',
  },
  error: {
    marginTop: 4,
    fontSize: '10px',
    color: '#FF0000',
  },
  dialogDatePicker: {
    marginBottom: 20,
  },
}));

export const SuspendMemberModal: React.FC<SuspendMemberModalProps> = (
  props: SuspendMemberModalProps,
): JSX.Element => {
  const classes = useStyles();

  const { isModalOpen, id } = props;
  const [memberName, setMemberName] = useState('');
  const [selectedDateStart, setSelectedDateStart] = useState<Date | null>(null);
  const [selectedDateLift, setSelectedDateLift] = useState<Date | null>(null);
  const [selectedDependents, setSelectedDependents] = useState<string[]>([]);
  const [remarks, setRemarks] = useState('');
  const [minDateLift, setMinDateLift] = useState<Date | null>(null);
  const [startDateError, setStartDateError] = useState<boolean>(false);
  const [liftDateError] = useState(false);
  const [startDateErrorMessage, setStartDateErrorMessage] = useState<string>(
    '',
  );
  const [liftDateErrorMessage] = useState('');

  const [maxDate, setMaxDate] = useState<Date | undefined>(undefined);

  useEffect(() => {
    if (props.isModalOpen) {
      setSelectedDateStart(null);
      setSelectedDateLift(null);
      setSelectedDependents([]);
      setRemarks('');
    }
  }, [props.isModalOpen]);

  useEffect(() => {
    if (props.memberData) {
      const firstName = get(props.memberData, 'first_name', null);
      const middleName = get(props.memberData, 'middle_name', null);
      const lastName = get(props.memberData, 'last_name', null);

      setMemberName(
        `${!isNil(firstName) ? firstName + ' ' : ''}${
          !isNil(middleName) ? middleName.charAt(0) + '. ' : ''
        }${!isNil(lastName) ? lastName : ''}`,
      );
    }
  }, [props.memberData]);

  useEffect(() => {
    if (!isNil(props.maxDate)) {
      const maxDateMoment = moment(props.maxDate);
      if (!isNil(maxDateMoment) && maxDateMoment.isValid()) {
        const dayBefore = maxDateMoment.add(-1, 'days').toDate();
        setMaxDate(dayBefore);
      }
    }
  }, [props.maxDate]);

  function handleSuspendMember() {
    if (selectedDateStart === null) {
      setStartDateError(true);
      setStartDateErrorMessage('Start date is required.');
    } else {
      const data = {
        selectedDateStart: selectedDateStart,
        selectedDateLift: selectedDateLift,
        selectedDependents: selectedDependents,
        remarks: remarks,
      };
      props.onSuspend(data);
      handleOnClose();
    }
  }
  function handleOnClose() {
    setSelectedDateStart(null);
    setSelectedDateLift(null);
    props.onClose();
  }

  function handleDateStart(date: Date) {
    if (date) {
      const tom = new Date();
      tom.setDate(date.getDate() + 1);

      if (maxDate && tom.getTime() > maxDate.getTime()) {
        setMinDateLift(maxDate);
      } else {
        setMinDateLift(tom);
      }
    }
    setSelectedDateStart(date);
  }

  function handleDateLift(date: Date) {
    setSelectedDateLift(date);
  }

  function handleCheckboxChange(id: string) {
    if (selectedDependents.includes(id)) {
      setSelectedDependents(selectedDependents.filter(item => item !== id));
    } else {
      const newSelectedDependents = [...selectedDependents];
      newSelectedDependents.push(id);
      setSelectedDependents(newSelectedDependents);
    }
  }

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 1299 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Suspension Details</div>
        <IconButton
          data-cy={'suspend_member_modal_close_btn'}
          id={'suspend_member_modal_close_btn'}
          aria-label="close"
          className={classes.closeButton}
          onClick={handleOnClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Typography className={classes.mnameLabel}>Principal</Typography>
            <Typography className={classes.mnameValue}>{memberName}</Typography>
          </Grid>
          <Grid item xs={6}>
            <DatePicker
              data-cy={'suspend_member_modal_suspend_picker'}
              id={'suspend_member_modal_suspend_picker'}
              placeholder="Effective Start of Suspension"
              value={selectedDateStart}
              error={startDateError}
              error_message={startDateErrorMessage}
              minDate={new Date()}
              maxDate={maxDate}
              onChange={handleDateStart}
            />
          </Grid>
          <Grid item xs={6}>
            <DatePicker
              data-cy={'suspend_member_modal_lift_picker'}
              id={'suspend_member_modal_lift_picker'}
              placeholder="Effective Date of Lifting"
              value={selectedDateLift}
              error={liftDateError}
              error_message={liftDateErrorMessage}
              minDate={minDateLift}
              maxDate={maxDate}
              minDateMessage="Date of lifting should not be before start of suspension."
              onChange={handleDateLift}
            />
          </Grid>
          {props.isPrincipal ? (
            <>
              <Grid item xs={12}>
                <Typography className={classes.tblTitle}>Dependents</Typography>
                <Typography className={classes.tblSubtitle}>
                  Choose the dependents to suspend.
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Paper className={classes.tblbox}>
                  <Table
                    data-cy={'suspend_member_modal_dependents_table'}
                    id={'suspend_member_modal_dependents_table'}
                    size="small"
                  >
                    <TableHead>
                      <TableRow>
                        <TableCell style={{ width: '40%' }}>
                          Member Name
                        </TableCell>
                        <TableCell style={{ width: '30%' }}>
                          Relationship
                        </TableCell>
                        <TableCell style={{ width: '30%' }}>Status</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {props.dependents && props.dependents.length > 0 ? (
                        map(props.dependents, (row, index) => {
                          // console.log('dependents',row)
                          return (
                            <TableRow key={index}>
                              <TableCell
                                component="th"
                                scope="row"
                                style={{ width: '30%' }}
                              >
                                <Grid container>
                                  <Grid xs={2}>
                                    <Checkbox
                                      style={hmocheckboxStyle}
                                      disabled={
                                        // get(row, 'member_status', null) !==
                                        //   'Active' &&
                                        // get(row, 'member_status', null) !==
                                        //   'Awaiting Activation'
                                        get(row, 'member_status', null) ===
                                          'Suspended' ||
                                        get(row, 'member_status', null) ===
                                          'Cancelled' ||
                                        get(row, 'member_status', null) ===
                                          'Terminated'
                                      }
                                      checked={selectedDependents.includes(
                                        row['_id'],
                                      )}
                                      onChange={() => {
                                        handleCheckboxChange(row['_id']);
                                      }}
                                    />
                                  </Grid>
                                  <Grid xs={1} />
                                  <Grid xs={9}>
                                    {row['first_name'] +
                                      ' ' +
                                      (row['middle_name']
                                        ? row['middle_name'].charAt(0) + '. '
                                        : '') +
                                      row['last_name']}
                                  </Grid>
                                </Grid>
                              </TableCell>
                              <TableCell style={{ width: '25%' }}>
                                {row['relationship_to_principal']}
                              </TableCell>
                              <TableCell style={{ width: '25%' }}>
                                {row['member_status']}
                              </TableCell>
                            </TableRow>
                          );
                        })
                      ) : (
                        <TableRow>
                          <TableCell
                            component="th"
                            scope="row"
                            style={{ textAlign: 'center' }}
                            colSpan={3}
                          >
                            No enrolled dependents
                          </TableCell>
                        </TableRow>
                      )}
                    </TableBody>
                  </Table>
                </Paper>
              </Grid>
            </>
          ) : null}
          <Grid item xs={12}>
            <InputLabel className="label" htmlFor="suspend_member_modal_remarks_field">
              Remarks
            </InputLabel>
            <TextField
              data-cy={'suspend_member_modal_remarks_field'}
              id={'suspend_member_modal_remarks_field'}
              style={{ marginTop: '8px', width: '100%' }}
              margin="normal"
              variant="outlined"
              multiline={true}
              rows={10}
              value={remarks}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const { value } = e.target;
                setRemarks(value);
              }}
              inputProps={{ 'aria-label': 'bare' }}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'suspend_member_modal_cancel_btn'}
              id={'suspend_member_modal_cancel_btn'}
              className={classes.cancelButton}
              variant="contained"
              color="primary"
              onClick={handleOnClose}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'suspend_member_modal_btn'}
              id={'suspend_member_modal_btn'}
              className={classes.suspendButton}
              variant="contained"
              color="primary"
              onClick={handleSuspendMember}
            >
              Suspend
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

SuspendMemberModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
};
