import React from 'react';
import { IProcessBatchesPage } from 'Pages/ProcessBatches/IProcessBatchesPage';
import { ProcessBatches } from './Components/ProcessBatches';

export class ProcessBatchesPage extends React.Component<
  IProcessBatchesPage.IProps,
  IProcessBatchesPage.IState
> {
  constructor(props) {
    super(props);
  }

  getBreadcrumbs() {
    const clientId = this.props.match.params.client_id;
    const clientName = this.props.match.params.client_name;

    const breadcrumbs = [
      {
        label: 'Clients',
        url: '#/membership/clients',
      },
      {
        label: clientName,
        url: '#/membership/clients-profile/' + clientId,
      },
      {
        label: 'Print ID Cards',
        url: '',
      },
    ];

    return breadcrumbs;
  }

  public render(): JSX.Element {
    return (
      <ProcessBatches
        caseId={this.props.match.params.case_id}
        clientId={this.props.match.params.client_id}
        clientName={this.props.match.params.client_name}
        breadcrumbs={this.getBreadcrumbs()}
      />
    );
  }
}
