const TerminateButton = withStyles({
  root: {
    color: '#FFFFFF',
    backgroundColor: '#FD5451',
    '&:hover': {
      backgroundColor: '#fd2724',
    },
  },
})(Button);

const CancelButton = withStyles({
  root: {
    '&:hover': {
      backgroundColor: '#3AB77D',
      color: '#FFFFFF',
    },
  },
})(Button);

export const TerminateMemberConfirmationModal: React.FC<IProps> = (
  props: IProps,
): JSX.Element => {
  const classes = useStyles();
  const [onViewDependents, setOnViewDependents] = React.useState<boolean>(false);
  const [activePrincipal, setActivePrincipal] = React.useState<string>('');
  const [dependentsData, setDependentsData] = React.useState<any>([]);

  function handleTerminateMember() {
    props.onTerminate();
    props.onClose();
  }
  console.log(props.clientData)

  const handleViewDependents = (row) => {
    setActivePrincipal(row.name);
    setOnViewDependents(true);

    API.viewMemberDependents(row.id).then(response => {
      setDependentsData(response);
    }).catch(err => {
      console.log(err);
    })
  }


  return (
    <Dialog
      maxWidth={false}
      open={props.isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        {
          !onViewDependents ? (
            <div className={classes.dialogFirstTitle}>
              Confirm Membership Termination
            </div>
          ) : (
              <Typography
                className={classes.prevButtonContainer}
                onClick={() => {
                  setOnViewDependents(false)
                }}>
                <FontAwesomeIcon size={'1x'} icon={faChevronLeft} className={classes.prevIcon} />
                <span className={classes.prevButton}>Back</span>
              </Typography>
            )
        }


        <div className={classes.dialogSecondTitle}></div>
        <IconButton
          data-cy={'terminate_member_conmod_close_btn'}
          id={'terminate_member_conmod_close_btn'}
          aria-label="close"
          className={classes.closeButton}
          onClick={props.onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>

      <DialogContent className={classes.dialogContent}>
        <Grid item xs={12}>
          {
            onViewDependents ?
              <Typography>{activePrincipal} (Principal)</Typography>
              : <Typography>
                Are you sure you want to terminate the membership of the following members
            in <b>{props.clientData[0] ? props.clientData[0].corporates : ''}</b>?
                                        These members and their dependents will no longer eligible to any benefits in
                                                                                                                            this contract.
          </Typography>
          }
        </Grid>
      </DialogContent>

      <DialogContent className={classes.dialogContent}>
        <div style={{ paddingBottom: '1rem' }} />
        <Paper className={classes.hmodependentsbox}>
          <Table
            size="small"
          >
            <TableHead>
              {
                onViewDependents ? (
                  <TableRow>
                    <TableCell>Dependent Name</TableCell>
                    <TableCell>Relationship</TableCell>
                    <TableCell>Status</TableCell>
                  </TableRow>
                ) : (
                    <TableRow>
                      <TableCell>Principal Name</TableCell>
                      <TableCell>Termination Date</TableCell>
                      <TableCell> </TableCell>
                    </TableRow>
                  )
              }

            </TableHead>
            <TableBody>
              {onViewDependents ? (
                dependentsData.length > 0 ? 
                  dependentsData.map((row, index) => (
                    <TableRow key={index}>
                      <TableCell component="th" scope="row">
                        <Link to={'#'} className={classes.hmodependentsbox_link}> 
                          {`${row.first_name} ${row.middle_name.charAt(1)} ${row.last_name}`}
                        </Link>
                      </TableCell>
                      <TableCell>{row.relationship_to_principal}</TableCell>
                      <TableCell>{row.member_status}</TableCell>
                    </TableRow>
                  ))
                 : (
                    <TableRow>
                      <TableCell
                        component="th"
                        scope="row"
                        style={{ textAlign: 'center' }}
                        colSpan={3}
                      >No enrolled dependents</TableCell>
                    </TableRow>
                  )
              ) : (
                  props.terminationData && props.terminationData.principal && props.terminationData.principal.length > 0 && (
                    props.terminationData.principal.map((row, index) => (
                      <TableRow key={index}>
                        <TableCell component="th" scope="row">
                          {row.name}
                        </TableCell>
                        <TableCell>{moment(props.terminationData.length > 1 ? props.terminationData[index].termination_date : props.terminationData.termination_date).format('MMMM DD, YYYY')}</TableCell>
                        <TableCell><Link to={'#'} onClick={() => { handleViewDependents(row) }} className={classes.hmodependentsbox_link}>View Dependents</Link></TableCell>
                      </TableRow>
                    )))
                )}
            </TableBody>
          </Table>
        </Paper>
      </DialogContent>

      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <CancelButton
              data-cy={'terminate_member_conmod_cancel_btn'}
              id={'terminate_member_conmod_cancel_btn'}
              className={classes.cancelButton}
              variant="contained"
              color="primary"
              onClick={props.onClose}
            >
              Cancel
            </CancelButton>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <TerminateButton
              data-cy={'terminate_member_conmod_btn'}
              id={'terminate_member_conmod_btn'}
              className={classes.terminateButton}
              variant="contained"
              color="primary"
              onClick={handleTerminateMember}
            >
              Terminate
            </TerminateButton>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

TerminateMemberConfirmationModal.defaultProps = {
  isModalOpen: false,
};
