export const ClientPage = () => {
  const [checkInternetFlag, setCheckInternetFlag] = useState(false);
  const [loading, setLoading] = useState(false);
  const [query, setQuery] = useState<string | null>(null);
  const [rows, setRows] = useState<any[]>([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(0);
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
  const [popoverOpen, setPopOverOpen] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [typingTimeout, setTypingTimeout] = useState<any>(0);
  const [sorting, setSorting] = useState<any[]>([]);
  const [defaultFilters, setDefaultFilters] = useState<any[]>([]);
  const [whereFilter, setWhereFilter] = useState<object>({});
  const [orderFilter, setOrderFilter] = useState<string[]>([]);
  const [exportData, setExportData] = useState('all_rows');
  const [exportFormat, setExportFormat] = useState('pdf');
  // const [appNumber, setAppNumber] = useState('');
  // const [appUID, setAppUID] = useState('');
  const [rowID, setRowID] = useState('');
  const [redirect, setRedirect] = useState(false);
  const [redirectType, setRedirectType] = useState('');
  // const [confirmationModal, setConfirmationModal] = useState(false);
  const classes = useStyles();
  // console.log(rowID)
  // const createProposal = () => {
  //  //create ticket for add member
  //  Processmaker.postWithToken('cases/', '',
  //    {
  //      pro_uid: "2523662725de63a19680ea5008281557",
  //      tas_uid: "6873263995de63a19889950032364241"
  //    }
  //  )
  //  .then(response =>{
  //    if(response.app_uid !== undefined && response.app_number !== undefined){
  //      Processmaker.put(
  //        'cases/'+response.app_uid+'/execute-trigger/2865638175de63a19cb32e1058550175','',{}
  //        ).then(response2 =>{
  //        console.log(" >>>>",response2);
  //        Processmaker.put(
  //          'cases/' + response.app_uid + '/execute-trigger/9174883615def0a44236984022777405','',{}
  //         ).then(response3 => {
  //          console.log(response3);
  //          setAppNumber(response.app_number);
  //          setAppUID(response.app_uid);
  //          handleConfirmationModalOpen();
  //          // window.location.href = '#/underwriting/create-proposal/'+response.app_uid;
  //        });
  //      })
  //    }
  //  });
  // }

  const handleRedirectToProposal = () => {
    setRedirectType('proposal');
    setRedirect(true);
  };

  // const handleConfirmationModalClose = (flag: boolean) => {
  //  if (flag === true) {
  //      setRedirectType('proposal');
  //      setRedirect(true);
  //  }else{
  //      setConfirmationModal(false);
  //  }
  // }

  // const handleConfirmationModalOpen = () => {
  //  setConfirmationModal(true);
  // }

  const handleLoadClientsFromAPI = () => {
    setLoading(true);
    setCheckInternetFlag(true);
    API.getClients(10, 0, whereFilter).then(response => {
      if (response && response.data && response.error === undefined) {
        setRows(response.data);
        setPage(0);
        setLimit(10);
        setTotalCount(response.total);
        setLoading(false);
        setCheckInternetFlag(false);
      } else {
        setLoading(false);
        setCheckInternetFlag(false);
        console.log('getbenefitlist api error: ', response);
      }
    });
  };

  const handleGenerateReport = () => {
    setCheckInternetFlag(true);
    API.generateClientReport(
      'PDF',
      exportData === 'filtered_rows' ? query : null,
      exportData === 'filtered_rows' ? whereFilter : {},
    ).then(response => {
      if (response) {
        const filename = 'Report-' + moment().format('MMM-DD-YYYY') + '.pdf';
        const samp = new File([response], filename);
        saveAs(samp);

        setPopOverOpen(false);
        setAnchorEl(null);
        setLoading(false);
        setCheckInternetFlag(false);
      } else {
        setLoading(false);
        setCheckInternetFlag(false);
        console.log('getbenefitlist api error: ', response);
      }
    });
  };

  const handleRowClick = (row: number) => {
    if (rows[row]['_id']) {
      setRowID(rows[row]['_id']);

      setRedirectType('client');
      setRedirect(true);
    }
    return null;
  };

  const handleSortChange = (sort: any) => {
    setSorting(sort);

    setOrderFilter(sort);

    setLoading(true);

    setCheckInternetFlag(true);
    if (query !== null) {
      API.searchClients(query, limit, limit * page, whereFilter, sort).then(
        response => {
          setCheckInternetFlag(false);
          if (response && response.data && response.error === undefined) {
            setRows(response.data);
            setTotalCount(response.total);
            setLoading(false);
          } else {
            setLoading(false);
            console.log('getbenefitlist api error: ', response);
          }
        },
      );
    } else {
      setCheckInternetFlag(true);
      API.getClients(limit, limit * page, whereFilter, sort).then(response => {
        setCheckInternetFlag(false);
        if (response && response.data && response.error === undefined) {
          setRows(response.data);
          setTotalCount(response.total);
          setLoading(false);
        } else {
          setLoading(false);
          console.log('getbenefitlist api error: ', response);
        }
      });
    }
  };

  const handlePageChange = (currentPage: number, skip: number) => {
    setPage(currentPage);

    changePageUpdate(limit, skip);
  };

  const handlePageSizeChange = value => {
    setLimit(value);

    changePageUpdate(value, value * page);
  };

  const handleSearch = (value: string | null) => {
    setPage(0);
    setQuery(value);
    if (typingTimeout) {
      clearTimeout(typingTimeout);
    }

    setTypingTimeout(
      setTimeout(function() {
        if (defaultFilters !== []) {
          setLoading(true);
          if (value !== null) {
            setCheckInternetFlag(true);
            API.searchClients(value, limit, 0, whereFilter, orderFilter).then(
              response => {
                setCheckInternetFlag(false);
                if (response && response.data && response.error === undefined) {
                  setRows(response.data);
                  setTotalCount(response.total);
                  setLoading(false);
                } else {
                  setLoading(false);
                  console.log('getbenefitlist api error: ', response);
                }
              },
            );
          } else {
            setCheckInternetFlag(true);
            API.getClients(limit, 0, whereFilter, orderFilter).then(
              response => {
                setCheckInternetFlag(false);
                if (response && response.data && response.error === undefined) {
                  setRows(response.data);
                  setTotalCount(response.total);
                  setLoading(false);
                } else {
                  setLoading(false);
                  console.log('getbenefitlist api error: ', response);
                }
              },
            );
          }
        } else {
          handleLoadClientsFromAPI();
        }
      }, 2000),
    );
  };

  const handleFilterByColumn = (filter: any) => {
    setDefaultFilters(filter);
    setPage(0);

    setWhereFilter(filter);

    if (typingTimeout) {
      clearTimeout(typingTimeout);
    }

    setTypingTimeout(
      setTimeout(function() {
        if (defaultFilters !== []) {
          setLoading(true);
          if (query !== null) {
            setCheckInternetFlag(true);
            API.searchClients(query, limit, 0, filter, orderFilter).then(
              response => {
                setCheckInternetFlag(false);
                if (response && response.data && response.error === undefined) {
                  setRows(response.data);
                  setTotalCount(response.total);
                  setLoading(false);
                } else {
                  setLoading(false);
                  console.log('getbenefitlist api error: ', response);
                }
              },
            );
          } else {
            console.log(JSON.stringify(whereFilter));
            setCheckInternetFlag(true);
            API.getClients(limit, 0, filter, orderFilter).then(response => {
              setCheckInternetFlag(false);
              if (response && response.data && response.error === undefined) {
                setRows(response.data);
                setTotalCount(response.total);
                setLoading(false);
              } else {
                setLoading(false);
                console.log('getbenefitlist api error: ', response);
              }
            });
          }
        } else {
          handleLoadClientsFromAPI();
        }
      }, 3000),
    );
  };

  const changePageUpdate = (limit: number, skip: number) => {
    setLoading(true);

    if (query !== null) {
      API.searchClients(query, limit, skip, whereFilter, orderFilter).then(
        response => {
          if (response && response.data && response.error === undefined) {
            setRows(response.data);
            setTotalCount(response.total);
            setLoading(false);
          } else {
            setLoading(false);
            console.log('getbenefitlist api error: ', response);
          }
        },
      );
    } else {
      API.getClients(limit, skip, whereFilter, orderFilter).then(response => {
        if (response && response.data && response.error === undefined) {
          setRows(response.data);
          setTotalCount(response.total);
          setLoading(false);
        } else {
          setLoading(false);
          console.log('getbenefitlist api error: ', response);
        }
      });
    }
  };

  const handleCloseLostInternetConnectionModal = () => {
    setCheckInternetFlag(false);
  };

  const handlePopoverClose = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    handleLoadClientsFromAPI();
  }, []);

  const columns: any = [
    {
      name: 'account_no',
      title: 'Account No.',
    },
    {
      name: 'name',
      title: 'Name',
    },
    {
      name: 'status',
      title: 'Status',
    },
    {
      name: 'effectivity_date',
      title: 'Effectivity Date',
      getCellValue: row =>
        row.effectivity_date
          ? GlobalFunction.toCommonDateString(new Date(row.effectivity_date))
          : '',
    },
    {
      name: 'termination_date',
      title: 'Expiry/Termination Date',
      getCellValue: row =>
        row.termination_date
          ? GlobalFunction.toCommonDateString(new Date(row.termination_date))
          : '',
    },
    {
      name: 'active_members_count',
      title: 'Active members',
    },
    {
      name: 'for_printing_count',
      title: 'For printing',
    },
  ];

  // const formattedColumns = {
  //  limit: ['limit'],
  //  limit_type: ['limit_type'],
  // };

  const columnExtensions = [
    {
      columnName: 'account_no',
      wordWrapEnabled: true,
    },
    {
      columnName: 'name',
      wordWrapEnabled: true,
    },
    {
      columnName: 'status',
      wordWrapEnabled: true,
    },
    {
      columnName: 'effectivity_date',
      wordWrapEnabled: true,
    },
    {
      columnName: 'termination_date',
      wordWrapEnabled: true,
    },
    {
      columnName: 'active_members_count',
      wordWrapEnabled: true,
    },
    {
      columnName: 'for_printing_count',
      wordWrapEnabled: true,
    },
  ];

  if (redirect === true) {
    if (redirectType === 'proposal') {
      // return <Redirect to={{pathname: '/underwriting/create-proposal/new'}} />
    } else if (redirectType === 'client') {
      return (
        <Redirect to={{ pathname: '/membership/clients-profile/' + rowID }} />
      );
    }
  }

  return (
    <div className={classes.main}>
      {loading ? <Loader /> : null}
      <Grid
        container
        justify="space-between"
        alignItems="center"
        className={classes.gridContainer}
      >
        <Grid item>
          <Typography className={classes.headerTitle}>Clients</Typography>
        </Grid>
        <Grid item>
          <Box
            display="flex"
            justifyContent="flex-end"
            alignItems="center"
            p={1}
            style={{ width: '100%', height: '100%' }}
          >
            <Box p={1} style={{ display: 'none' }}>
              <Link
                id='redirect_to_proposal'
                data-cy='redirect-to-proposal'
                style={{ color: '#3AB77D', fontSize: '0.875rem' }}
                onClick={handleRedirectToProposal}
              >
                <FontAwesomeIcon icon={faPlus} />
                &nbsp;&nbsp;Create Proposal
              </Link>
            </Box>

            <Box p={1}>
              <Link
                id='generate_report'
                data-cy='generate_report'
                style={{ color: '#3AB77D', fontSize: '0.875rem' }}
                onClick={(e: any) => {
                  const currentTarget = e.currentTarget;
                  setPopOverOpen(true);
                  setAnchorEl(currentTarget);
                }}
              >
                <FontAwesomeIcon icon={faDownload} />
                &nbsp;&nbsp;Generate Report
              </Link>
            </Box>
          </Box>
        </Grid>
      </Grid>
      <ClientsTable
        columns={columns}
        rows={rows}
        columnExtensions={columnExtensions}
        currentPage={page}
        limit={limit}
        totalCount={totalCount}
        onPageChange={handlePageChange}
        onPageSizeChange={handlePageSizeChange}
        onFilterChange={handleFilterByColumn}
        onSearchChange={handleSearch}
        onSortingChange={handleSortChange}
        sorting={sorting}
        defaultFilter={defaultFilters}
        onRowClick={handleRowClick}
      />
      <ReportPopover
        open={popoverOpen}
        anchorEl={anchorEl}
        exportData={exportData}
        exportFormat={exportFormat}
        onClose={handlePopoverClose}
        onChangeExportData={setExportData}
        onChangeExportFormat={setExportFormat}
        generateReport={handleGenerateReport}
      />
      {/*     <ConfirmationModal
                onClose={handleConfirmationModalClose}
                isModalOpen={confirmationModal}
                appNo={appNumber}
            />*/}
      <LostInternetModal
        id="Lost Internet Modal"
        isModalOpen={checkInternetFlag}
        onClose={handleCloseLostInternetConnectionModal}
      />
    </div>
  );
};

const mapStateToProps = (state: Store) => state.home;

const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(ClientActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
