import React from 'react';
import { 
    Grid, 
    Typography, 
    Box,
    Checkbox, 
} from '@material-ui/core/';
import { 
    makeStyles,
} from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import DragSortableList from 'react-drag-sortable';

// draggable item

const checkboxStyle = {
    size: 12,
    padding: 0,
    color: '#1E2071',
    '&$checked': {
        color: '#1E2071',
    },
};

const itemStyles = makeStyles({
    name: {
        fontSize: '0.875rem',
        color: '#272E4C',
        marginLeft: 16,
        paddingTop: 2,
        paddingBottom: 4,
    },
});

interface DraggableItemProps {
    index: number;
    value: string;
    label: string;
    checked: boolean;
    handleChange? : any;
}

export const DraggableItem: React.FC<DraggableItemProps> = (
	props: DraggableItemProps,
): JSX.Element => {

    const classes = itemStyles(props);

    let bgColor = props.index % 2 === 0 ? '#FFFFFF' : '#F6F7FA';

    return(
        <Grid container alignItems="center" style={{padding: '8px 4px', backgroundColor: bgColor}}>
            <Grid item xs={11}>
                <Box display="flex" flexDirection="row">
                    <Box>
                        <Checkbox
                            id={`checkbox_${props.value.replace(/\s/g,'_').toLocaleLowerCase()}`}
                            data-cy={`checkbox_${props.value.replace(/\s/g,'_').toLocaleLowerCase()}`}
                            style={checkboxStyle} 
                            checked={props.checked} 
                            onChange={() => {
                                props.handleChange(props.value, !props.checked);
                            }} />
                    </Box>
                    <Box>
                        <Typography className={classes.name}>
                            {props.label}
                        </Typography>
                    </Box>
                </Box>
            </Grid>
            <Grid item xs={1}>
                {
                    props.checked ?
                        (<MenuIcon style={{color: 'rgba(39, 46, 76, 0.5)'}} />)
                        : ''
                }
                
            </Grid>
        </Grid>
    );
}





// draggable list

interface DraggableListProps {
  list: any[];
  selectedList: string[];
  handleChange: any;
}

export const DraggableList: React.FC<DraggableListProps> = (
    props: DraggableListProps,
): JSX.Element => {

    const onSort = (sortedList) => {
        let filteredSortedList = sortedList.filter(item => {
            return (item['classes'].indexOf('no-drag') === -1);
        });

        let newSelectedList: any[] = [];
        if(filteredSortedList.length>0){
            newSelectedList =  filteredSortedList.map(item => {
                return item['content']['props']['value'];
            });
        }

        props.handleChange([...newSelectedList]);
    }

    const onCheckboxChange = (code: string, checked: boolean) => {
        if(checked){
            let newSelectedList = [...props.selectedList, code];
            props.handleChange([...newSelectedList]);
        }
        else{
            let newSelectedList = props.selectedList;
            let index = props.selectedList.indexOf(code);
            if(index !== -1) newSelectedList.splice(index, 1);
            props.handleChange([...newSelectedList]);
        }
    }

    let optionList = props.list.map(item => {
        item['checked'] = false;
        return item;
    });
    let draglist: any[] = [];
    if(props.selectedList.length > 0){
        props.selectedList.forEach(selectedCode => {
            let indx = optionList.findIndex(item => {
                return item.code === selectedCode;
            });
            if(indx !== -1){
                let dragObj = optionList.splice(indx, 1)[0];
                dragObj['checked'] = true;
                draglist.push(dragObj);
            }
        });
        draglist.push(...optionList);
    }
    else{
        draglist = optionList;
    }
    
    let draggableItems = draglist.map((item, index) => {
        return {
            content: (
                <DraggableItem 
                    index={index}
                    value={item.code}
                    label={item.label}
                    checked={item.checked}
                    handleChange={onCheckboxChange} />
            ), 
            classes: [(item.checked ? '' : 'no-drag')]
        };
    });

    return (
        <div>
            <Grid container>
                <Grid item xs={12} md={11} style={{boxShadow: '0px 3px 6px #00000029', marginRight: 24}}>
                    <DragSortableList 
                        items={draggableItems} 
                        moveTransitionDuration={0.3} 
                        onSort={onSort} 
                        type="vertical" />
                </Grid>
            </Grid>
        </div>
    );
};