import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Table, TableBody, TableCell,
    TableHead, TableRow, Paper} from '@material-ui/core';

import { AddSelect } from 'Components/UI/AddSelect';

interface PartialTableProps {
    rows: any[];
    columns: any[];
    id: string;
}

const useStyles = makeStyles({
    root: {
      width: '100%',
    },
    tableWrapper: {
      maxHeight: 350,
      overflow: 'auto',
    },
    tableHeader:{
        backgroundColor: "#272E4C"
    }
});
  

export const PartialTable: React.FC<PartialTableProps> = (
	props: PartialTableProps,
): JSX.Element => {
    const {
        rows,
        columns,
        id,
    } = props;

    const classes = useStyles();

    function getCell(col: any, row: any){
        let table_cell: any | null;
        const value = row[col.name]
        switch(col.type){
            case "select":
                if(col.options){
                    table_cell = (
                        <TableCell key={col.id} align={col.align}>
                            <AddSelect
                                id={"col_"+col.id}
                                options={col.options}
                                label={"Data Mapping"}
                                value={value}
                                addButton={false}
                                addButtonLabel={""}
                                onChange={col.callback}
                                error={false}
                                error_message={""}
                            />
                        </TableCell>
                    )
                }else{
                    table_cell = (
                        <TableCell key={col.id} align={col.align}>
                            {col.format && typeof value === 'number' ? col.format(value) : value}
                        </TableCell>
                    )
                }
            break;
            default:
                table_cell = (
                    <TableCell key={col.id} align={col.align}>
                        {col.format && typeof value === 'number' ? col.format(value) : value}
                    </TableCell>
                )

        }
        return table_cell;
    }

    return (
        <Paper className={classes.tableWrapper}>
            <Table id={id} stickyHeader>
                <TableHead className={classes.tableHeader}>
                    <TableRow>
                    {columns.map(column => (
                        <TableCell
                        key={column.id}
                        align={column.align}
                        style={{ minWidth: column.minWidth }}
                        >
                        {column.title}
                        </TableCell>
                    ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map(row => {
                    return (
                        <TableRow role="checkbox" tabIndex={-1} key={row.code} className={row.className ? row.className: ""}>
                        {columns.map(column => {
                            const value = row[column.name];
                            if(column.type){
                                return getCell(column, row);
                            }else{
                                return (
                                <TableCell key={column.id} align={column.align}>
                                    {column.format && typeof value === 'number' ? column.format(value) : value}
                                </TableCell>
                                );
                            }
                        })}
                        </TableRow>
                    );
                    })}
                </TableBody>
            </Table>
      </Paper>
    )
}

PartialTable.defaultProps = {
    rows: [],
    columns: []
};
