interface PopoverModalProps {
    data: any;
    memberFields: any;
    setSIData?: any;
    saveSIData: (data : any) => void;
    anchorElement: HTMLAnchorElement | null;
    handlePopoverClose: () => void;
    corporateAccountNo: any;
    registeredCompanyName: any;
    tradeCompanyName: any;
    // classes: any;
}

export const PopoverModal: React.FC<PopoverModalProps> = (props: PopoverModalProps): JSX.Element => {

    const [selectedTab, setSelectedTab] = useState(0);
    const [siData, setSIData] = useState((props.data ? props.data : {}));
    // const popoverOpen = Boolean(anchorEl);
    // const classes = STYLE_CONST.modalStyles();
    const classes = useStyles();

    const handleTabChange = (
        _event: React.ChangeEvent<HTMLDivElement>,
        tab: number,
    ) => {
        setSelectedTab(tab);
    };

    const updateSIDataByTab = (tabkey: string, value: any) => {
        let currSIData = siData;
        currSIData[tabkey] = value;
        setSIData({ ...currSIData });
        console.log('new siData ', siData);
        props.setSIData(currSIData);
    }

    // console.log('DATAAAA', props.data)

    useEffect(() => {
        setSIData(props.data)
      }, [props.data]);


    return (
        <Popover
            open={Boolean(props.anchorElement)}
            anchorEl={props.anchorElement}
            onClose={props.handlePopoverClose}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}
        >
            <Grid item sm={12} style={{ padding: '10px', minHeight: '80%' }}>
                <Dialog
                    maxWidth="md"
                    fullWidth={true}
                    open={true}
                    scroll={"body"}
                    style={{ width: '100%', transform: 'translate(0, 0)', alignContent: 'center', padding: '0px -100px 0px -100px' }}
                    aria-labelledby="modal-title"
                    classes={{ paper: classes.dialogPaper }}
                >
                    <DialogTitle id="form-dialog-title">
                        <Grid container alignItems="flex-end" justify="space-between">
                            <Grid item>
                                <Typography className={classes.dialogTitle}>Sorting and Printing Instructions</Typography>
                            </Grid>
                            <Grid item>
                                <span onClick={props.handlePopoverClose}>
                                    <FontAwesomeIcon icon={faTimes} />
                                </span>

                            </Grid>
                        </Grid>

                    </DialogTitle>
                    <DialogContent>
                        <Grid container style={{ marginRight: '23px', marginLeft: '23px'}}>
                            <Grid item sm={4}>
                                <Typography className={classes.clientInfoHeader}>
                                    CORPORATE ACCOUNT NO.
                                </Typography>
                                <Typography className={classes.clientInfoHeaderValue}>
                                    {props.corporateAccountNo}
                                </Typography>
                            </Grid>
                            <Grid item sm={4}>
                                <Typography className={classes.clientInfoHeader}>
                                    REGISTERED COMPANY NAME
                                </Typography>
                                <Typography className={classes.clientInfoHeaderValue}>
                                    {props.registeredCompanyName}
                                </Typography>
                            </Grid>
                            <Grid item sm={4}>
                                <Typography className={classes.clientInfoHeader}>
                                    TRADE COMPANY NAME
                                </Typography>
                                <Typography className={classes.clientInfoHeaderValue}>
                                    {props.tradeCompanyName}
                                </Typography>
                            </Grid>
                        </Grid>


                        <Grid container style={{ paddingTop : '20px', paddingBottom : '20px'}}>
                            <ServicingInstructionsTabs
                                value={selectedTab}
                                onChange={handleTabChange}
                                aria-label="page tab"
                            >
                                {page_tab_titles.map((title: string, index: number) => (
                                    <ServicingInstructionsTab
                                        label={title}
                                        id={'servicing_instructions_tab_' + index}
                                        aria-controls={'servicing-instructions-tabpanel-' + index}
                                        key={index}
                                    />
                                ))}
                            </ServicingInstructionsTabs>
                        </Grid>

                            {selectedTab === 0 ?
                                <SortingOfCards
                                    memberFields={props.memberFields}
                                    data={siData && siData['sorting_of_cards'] ? siData['sorting_of_cards'] : null}
                                    updateData={updateSIDataByTab} /> : null}
                            {selectedTab === 1 ?
                                <PrintingOfCards
                                    data={siData && siData['printing_of_cards'] ? siData['printing_of_cards'] : null}
                                    updateData={updateSIDataByTab} /> : null}

                        <Grid container item xs={12} alignItems="flex-end" style={{ padding: '10px 0 10px 0' }}>
                            <Grid item xs={10}></Grid>
                            <Grid item xs={2}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    fullWidth
                                    onClick={() => props.saveSIData(siData)}
                                >
                                    Save
				            </Button>
                            </Grid>

                        </Grid>
                    </DialogContent>
                </Dialog>
            </Grid>
        </Popover>

    )
}



const itemStyles = makeStyles({
    subheader: {
        fontSize: '1rem',
        color: '#272E4C'
    },
    description: {
        fontSize: '0.875rem',
        color: '#272E4C',
        paddingBottom: 16,
    },
    noDataMessage: {
        fontSize: '0.875rem',
        color: '#272E4C',
        fontWeight: 600,
        textAlign: 'center',
    },
    radio: {
        '&$checked': {
          color: '#272E4C'
        }
      },
    checked: {}
});

interface SortingOfCardsProps {
    memberFields: any[];
    data: any;
    updateData: any;
}

export const SortingOfCards: React.FC<SortingOfCardsProps> = (
    props: SortingOfCardsProps,
): JSX.Element => {

    const classes = itemStyles(props);

    const updateSortingData = (key: string, value: any) => {
        let currData = props.data ? props.data : {};
        currData[key] = value;
        props.updateData('sorting_of_cards', currData);
    };


    return (
        <div style={{ marginRight : '40px', marginLeft: '40px'}}>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <Typography className={classes.subheader}>
                        Card Sorting
                    </Typography>
                    <Typography className={classes.description}>
                        Choose category for sorting and arrange their hierarchy
                    </Typography>
                    {
                        props.memberFields && props.memberFields.length > 0 ?
                            (
                                <DraggableList
                                    list={props.memberFields}
                                    selectedList={props.data && props.data['card_sorting'] ? props.data['card_sorting'] : []}
                                    handleChange={newSelectedList => {
                                        updateSortingData('card_sorting', newSelectedList);
                                    }}
                                />
                            ) :
                            (
                                <Typography className={classes.noDataMessage} style={{ marginTop: 48, marginRight: 64 }}>
                                    No required fields to display.
                            </Typography>
                            )
                    }
                </Grid>
                <Grid item xs={12} md={6} style={{ paddingLeft: '20px' }}>
                    <Typography className={classes.subheader}>
                        Card Packing
                    </Typography>
                    <FormControl component="fieldset" margin="normal">
                        <RadioGroup
                            value={props.data && props.data['card_packing'] ? props.data['card_packing'] : ''}
                            onChange={(event) => {
                                updateSortingData('card_packing', event.target.value);
                            }}
                            name="card_packing">
                            <span style={{ paddingTop: '10px' }} />
                            <FormControlLabel
                                value="envelope"
                                control={
                                    <Radio
                                        name="card_packing" 
                                        classes={{root: classes.radio, checked: classes.checked}}/>
                                        
                                }
                                label={"Envelope"}
                            />
                            <span style={{ paddingTop: '10px' }} />
                            <FormControlLabel
                                value="plastic"
                                control={
                                    <Radio
                                        name="card_packing" 
                                        classes={{root: classes.radio, checked: classes.checked}}/>
                                }
                                label={"Plastic"}
                            />
                            <span style={{ paddingTop: '10px' }} />
                            <FormControlLabel
                                value="with flyer"
                                control={
                                    <Radio
                                        name="card_packing" 
                                        classes={{root: classes.radio, checked: classes.checked}}/>
                                }
                                label={"With Flyer"}
                            />
                        </RadioGroup>
                    </FormControl>
                    <Typography className={classes.subheader} style={{ marginTop: 32, marginBottom: 8 }}>
                        Pack according to
                    </Typography>
                    <SICheckboxItem
                        label="Team Leader"
                        checked={props.data && props.data['pack_accdg_to'] && props.data['pack_accdg_to'].indexOf('team_leader') > -1}
                        handleChange={(newValue) => {
                            if (newValue) {
                                if (props.data && props.data['pack_accdg_to']) {
                                    updateSortingData('pack_accdg_to', [...props.data['pack_accdg_to'], 'team_leader']);
                                }
                                else {
                                    updateSortingData('pack_accdg_to', ['team_leader']);
                                }
                            }
                            else {
                                if (props.data && props.data['pack_accdg_to']) {
                                    updateSortingData('pack_accdg_to', [...pull(props.data['pack_accdg_to'], 'team_leader')]);
                                }
                                else {
                                    updateSortingData('pack_accdg_to', []);
                                }
                            }
                        }} />
                    <SICheckboxItem
                        label="Family"
                        checked={props.data && props.data['pack_accdg_to'] && props.data['pack_accdg_to'].indexOf('family') > -1}
                        handleChange={(newValue) => {
                            if (newValue) {
                                if (props.data && props.data['pack_accdg_to']) {
                                    updateSortingData('pack_accdg_to', [...props.data['pack_accdg_to'], 'family']);
                                }
                                else {
                                    updateSortingData('pack_accdg_to', ['family']);
                                }
                            }
                            else {
                                if (props.data && props.data['pack_accdg_to']) {
                                    updateSortingData('pack_accdg_to', [...pull(props.data['pack_accdg_to'], 'family')]);
                                }
                                else {
                                    updateSortingData('pack_accdg_to', []);
                                }
                            }
                        }} />
                </Grid>
            </Grid>
        </div>
    );
};




interface PrintingOfCardsProps {
    data: any;
    updateData: any;
}

const itemStyles2 = makeStyles({
    reminder: {
        fontSize: '0.875rem',
        color: '#272E4C',
        paddingBottom: 16,
    },
});

export const PrintingOfCards: React.FC<PrintingOfCardsProps> = (
    props: PrintingOfCardsProps,
): JSX.Element => {

    const classes = itemStyles2(props);

    const handleCheckboxChange = (code: string, newValue: boolean) => {
        if (newValue) {
            if (props.data) {
                props.updateData('printing_of_cards', [...props.data, code]);
            }
            else {
                props.updateData('printing_of_cards', [code]);
            }
        }
        else {
            if (props.data) {
                props.updateData('printing_of_cards', [...pull(props.data, code)]);
            }
            else {
                props.updateData('printing_of_cards', []);
            }
        }
    };

    const isChecked = (code: string) => {
        return props.data && props.data.indexOf(code) > -1;
    };

    return (
        <div style={{ marginRight : '40px', marginLeft: '40px'}}>
            <Grid container>
                <Grid item xs={12} md={6} style={{ marginRight: 16 }}>

                    <Typography className={classes.reminder}>
                        Print in the card only the items that are checked below. Use the sample on the right to see layout of the card ID.
                    </Typography>
                    <SmallSICheckboxItem
                        label="1. Company Logo"
                        checked={isChecked('company logo')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('company logo', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="2. ID Picture"
                        checked={isChecked('id picture')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('id picture', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="3. Member Last Name, First Name, Middle Name, Extension (if any)"
                        checked={isChecked('full name')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('full name', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="4. Company Name"
                        checked={isChecked('company name')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('company name', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="5. Member Account No."
                        checked={isChecked('member account no')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('member account no', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="6. Card Number"
                        checked={isChecked('card no')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('card no', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="7. Month and Year of Birth (MM/YY)"
                        checked={isChecked('birthday')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('birthday', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="8. Sex"
                        checked={isChecked('sex')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('sex', newValue);
                        }} />
                    <SmallSICheckboxItem
                        label="9. Dental Coverage  Based on the members plan type in the contract"
                        checked={isChecked('dental coverage')}
                        handleChange={(newValue) => {
                            handleCheckboxChange('dental coverage', newValue);
                        }} />
                </Grid>
                <Grid item xs={12} md={5} style={{ paddingLeft: '30px', }} >
                    <img style={{ height: '330px', width: '330px', border: '1px solid #DDDFE3' }}
                        src={'si_sample_card.png'}
                        className={clsx('si-printing-of-card-sample-img')}
                        alt="Sample Reference Card" />
                </Grid>
            </Grid>
        </div>
    );
};