interface MemberCancelTerminationModalProps {
    id: string;
    isModalOpen: boolean;
    memberData: any[];
    clientData: any;
    onStatusAction: (payload: any) => void;
    onClose: () => void;
}

export const MemberCancelTerminationModal: React.FC<MemberCancelTerminationModalProps> = (
    props: MemberCancelTerminationModalProps
): JSX.Element => {
    const classes = useStyles();
    const { id, isModalOpen, memberData, clientData } = props;

    const [isLoaded, setIsLoaded] = useState<boolean>(false);
    const [user, setUser] = useState<any>({});
    const [dependentsCount, setDependentsCount] = useState({
        count: 0
    });
    const [fetchedDependents, setFetchedDependents] = useState<any>([]);
    const [memberDependents, setMemberDependents] = useState<any>({});
    const [activeLink, setActiveLink] = useState('Cancel Termination');
    const [reason, setReason] = useState<string>('');

    useEffect(() => {
        // reset components values on modal open
        if (props.isModalOpen) {
            resetData();
        }
    }, [props.isModalOpen]);

    useEffect(() => {
        async function getMemberDependents(){
            try{
                const userData = await API.getUserDataFromDb();
                setUser(userData);

                let dependentsArray: any[] = [];
                let markedDependentsArray: any[] = [];
                let promise = await memberData.map(data => API.getMemberStatusActionDependents(data.id)
                    .then(res => {
                        return res.map(res => {
                            res['principal_id'] = data.id;
                            return res;
                        })
                    })
                )
                const response = await Promise.all(promise);
                let filteredResponse = response.filter(res => res.length > 0)

                filteredResponse.forEach(filtRes => {
                    filtRes.forEach((dependent) => {                        
                        dependentsArray.push(dependent);

                        if(dependent['member_status'].toString().toLowerCase() === 'marked for termination'){
                            markedDependentsArray.push(dependent);
                        }
                    });
                })

                setDependentsCount({
                    count: markedDependentsArray.length
                })

                setFetchedDependents(dependentsArray);
                setIsLoaded(true);
            } catch(error){
                console.log(error)
                setIsLoaded(true);
            }
        }

        if (memberData.length > 0 && isModalOpen === true) {
            getMemberDependents();
        }
    }, [isModalOpen])

    useEffect(() => {},[memberDependents])

    const resetData = () => {
        setIsLoaded(false);
        setUser({});
        setDependentsCount({
            count: 0
        });
        setFetchedDependents([]);
        setMemberDependents({});
        setActiveLink('Cancel Termination');
        setReason('');
    }

    const handleMenuClick = (principalId, principalName, memberType) => {
        let memberDependents = fetchedDependents.filter(dependents => 
            dependents['principal_id'] === principalId);

        const data = {
            name: principalName,
            type: memberType,
            dependents: memberDependents
        }

        setMemberDependents(data)
        setActiveLink('View Dependents');
    }

    const handleBack = () => {
        setActiveLink('Cancel Termination');
    }

    const handleStatusAction = () => {
        let memberIdsArray: any[] = [];
        memberData.forEach(member => {
            console.log('marked member', member)
            memberIdsArray.push(member.id)
        })

        if(fetchedDependents.length > 0){
            fetchedDependents.forEach(dependent => {
                console.log('marked dependent', dependent)
                if(dependent['member_status'].toString().toLowerCase() === 'marked for termination'){
                    memberIdsArray.push(dependent['_id']);
                }
            })
        }

        const payload = {
            memberIds: memberIdsArray,
            remarks: reason !== '' ? reason : undefined,
            username: user.userName,
        }

        if(props.onStatusAction){
            props.onStatusAction(payload);
            handleOnClose();
        }
    }

    function handleOnClose() {
        if (props.onClose) {
            props.onClose();
        }
    }

    return (
        <div>
            <Dialog
                id={id}
                maxWidth={false}
                open={isModalOpen}
                scroll={'paper'}
                aria-labelledby="modal-title"
                className={classes.dialog}
            >
                {isLoaded === false ? <Loader/> : 
                    <>
                        {activeLink == 'Cancel Termination' ?
                            <>
                                <DialogTitle
                                    className={classes.dialogTitle}
                                    disableTypography={true}
                                    id="modal-title"
                                >
                                    <div className={classes.dialogFirstTitle}>Confirm Cancelling of Termination</div>
                                    <IconButton
                                        id='client_close_button'
                                        data-cy='client_close_button'
                                        aria-label="close"
                                        className={classes.closeButton}
                                        onClick={handleOnClose}
                                    >
                                        <CloseIcon />
                                    </IconButton>
                                </DialogTitle>
                                <DialogContent className={classes.dialogContent}>
                                    <Grid container spacing={2}>
                                        <Grid item xs={12}>
                                            <Typography className={classes.dialogNotice}>
                                                <span>Are you sure you want to cancel the termination of{' '}
                                                    <b>{memberData.length} principal{memberData.length > 1 ? 's' : null}
                                                    {' '}
                                                    {dependentsCount.count > 0 ? 
                                                        <span>and {dependentsCount.count} dependent{dependentsCount.count > 1 ? 's' : null}</span> 
                                                    : null}
                                                    {' '}in {clientData.brand_name}
                                                    </b>? Member(s) will continue to have their benefits coverage.
                                                </span>
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <Paper
                                                className={classes.tblPaperMembers}
                                            >
                                                <Table
                                                    data-cy={'client_cancel_termination_dependents_table'}
                                                    id={'client_cancel_termination_dependents_table'}
                                                    size="small"
                                                >
                                                    <TableHead>
                                                        <TableRow>
                                                            <TableCell style={{ width: '40%' }}>
                                                                Member Name
                                                            </TableCell>
                                                            <TableCell style={{ width: '30%' }}>
                                                                Termination Date
                                                            </TableCell>
                                                            <TableCell style={{ width: '30%' }}>
                                                                Dependents
                                                            </TableCell>
                                                        </TableRow>
                                                    </TableHead>
                                                    <TableBody>
                                                        {memberData.length > 0 ? memberData.map(
                                                            (row, index) => {
                                                                return (
                                                                    <TableRow key={index}>
                                                                        <TableCell style={{ width: '40%' }}>
                                                                            {`${row['member_name']}`}
                                                                        </TableCell>
                                                                        <TableCell style={{ width: '30%' }}>
                                                                        {row['member_termination']['termination_date'] !== '' ? 
                                                                        moment(row['member_termination']['termination_date']).format('MMM DD, YYYY') 
                                                                        : 'Not set'}
                                                                        </TableCell>
                                                                        <TableCell style={{ width: '30%' }}>
                                                                            {fetchedDependents.length > 0 ? 
                                                                                fetchedDependents.map(dependent => 
                                                                                    {return dependent['principal_id']}).indexOf(row['id']) !== -1 ?
                                                                                <Link
                                                                                    onClick={() => handleMenuClick(row['id'],row['member_name'],row['member_type'])}
                                                                                    className={classes.menuLink}>
                                                                                    View Dependents
                                                                                </Link>
                                                                                :
                                                                                <span className={classes.noDependentsLink}>
                                                                                    No Dependents
                                                                                </span>
                                                                            :                                                                         
                                                                            <span className={classes.noDependentsLink}>
                                                                                No Dependents
                                                                            </span>}
                                                                        </TableCell>
                                                                    </TableRow>
                                                                );
                                                            }
                                                        ) : (
                                                                <TableRow>
                                                                    <TableCell
                                                                        component="th"
                                                                        scope="row"
                                                                        style={{ textAlign: 'center' }}
                                                                        colSpan={5}
                                                                    >
                                                                        No available data
                                                                    </TableCell>
                                                                </TableRow>
                                                            )}
                                                    </TableBody>
                                                </Table>
                                            </Paper>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <InputLabel 
                                                className={classes.labelReason} 
                                                htmlFor="member_suspension_reason_field"
                                            >
                                                Reason
                                            </InputLabel>
                                            <TextField
                                                data-cy={'client_cancel_termination_reason_field'}
                                                id={'client_cancel_termination_reason_field'}
                                                style={{ marginTop: '8px', width: '100%' }}
                                                margin="normal"
                                                variant="outlined"
                                                multiline={true}
                                                rows={10}
                                                value={reason}
                                                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                    const { value } = e.target;
                                                    setReason(value);
                                                }}
                                                inputProps={{ 'aria-label': 'bare' }}
                                            />
                                        </Grid>
                                    </Grid>
                                </DialogContent>
                                <DialogActions className={classes.dialogAction}>
                                    <Grid
                                        container
                                        direction="row"
                                        justify="space-evenly"
                                        alignItems="stretch"
                                    >
                                        <Grid item xs={4} style={{ textAlign: 'center' }}>
                                            <Button
                                                data-cy={'client_cancel_termination_cancel_btn'}
                                                id={'client_cancel_termination_cancel_btn'}
                                                className={classes.cancelButton}
                                                variant="contained"
                                                color="primary"
                                                onClick={handleOnClose}
                                            >
                                                Cancel
                                            </Button>
                                        </Grid>
                                        <Grid item xs={4} style={{ textAlign: 'center' }}>
                                            <Button
                                                data-cy={'client_cancel_termination_btn'}
                                                id={'client_cancel_termination_btn'}
                                                variant="contained"
                                                color="primary"                                
                                                onClick={handleStatusAction}
                                            >
                                                Cancel Termination
                                            </Button>
                                        </Grid>
                                    </Grid>
                                </DialogActions>
                            </>
                        : null}


                        {activeLink == 'View Dependents' ?
                            <>
                                <DialogTitle
                                    className={classes.dialogTitle}
                                    disableTypography={true}
                                    id="modal-title"
                                >
                                    <div>
                                        <span
                                            className={classes.menuBackLink}
                                            onClick={() => handleBack()}                                        
                                        >
                                            <FontAwesomeIcon size={'1x'} icon={faChevronLeft} />
                                            <span className={classes.menuBack}>Back</span>
                                        </span>
                                    </div>
                                            <span className={classes.princpalTitle}>{memberDependents.name}&nbsp;{'('}{memberDependents.type}{')'}</span>
                                    <IconButton
                                        id='client_close_button'
                                        data-cy='client_close_button'
                                        aria-label="close"
                                        className={classes.closeButton}
                                        onClick={handleOnClose}
                                    >
                                        <CloseIcon />
                                    </IconButton>
                                </DialogTitle>
                                <DialogContent className={classes.dialogContent}>
                                    <Grid container spacing={2}>

                                        <Grid item xs={12}>
                                            <Paper
                                                className={classes.tblPaperDependents}
                                            >
                                                <Table
                                                    data-cy={'client_cancel_termination_dependents_table'}
                                                    id={'client_cancel_termination_dependents_table'}
                                                    size="small"
                                                >
                                                    <TableHead>
                                                        <TableRow>
                                                            <TableCell style={{ width: '40%' }}>
                                                                Dependent Name
                                                            </TableCell>
                                                            <TableCell style={{ width: '30%' }}>
                                                                Relationship
                                                            </TableCell>
                                                            <TableCell style={{ width: '30%' }}>
                                                                Status
                                                            </TableCell>
                                                        </TableRow>
                                                    </TableHead>
                                                    <TableBody>
                                                        {memberDependents.dependents.map(
                                                            (row) => {
                                                                return (
                                                                    <TableRow>
                                                                        <TableCell style={{ width: '40%' }}>
                                                                            {`${row['first_name']} ${row['middle_name']} ${row['lastname_name']}`}
                                                                        </TableCell>
                                                                        <TableCell style={{ width: '30%' }}>
                                                                            {row['relationship_to_principal']}
                                                                        </TableCell>
                                                                        <TableCell style={{ width: '30%' }}>
                                                                            {row['member_status']}
                                                                        </TableCell>
                                                                    </TableRow>
                                                                );
                                                            }
                                                        )}
                                                    </TableBody>
                                                </Table>
                                            </Paper>
                                        </Grid>
                                    </Grid>
                                </DialogContent>
                            </>
                        : null}
                    </>
                }
            </Dialog>
        </div>
    )
}
