interface IClientProfileComponentProps {
  data: any;
}

export const ClientsProfileComponent: React.FC<IClientProfileComponentProps> = (
  props: IClientProfileComponentProps,
) => {
  console.log(props);
  const { data } = props;
  const classes = useStyles();
  const [viewOnly, setViewOnly] = useState(true);
  const [loading, setLoading] = useState(false);
  const [activeItem, setActiveItem] = useState('Basic Information');
  const [redirectToClientsList, setRedirectToClientsList] = useState(false);
  console.log(data, 'client_info');
  console.log(data, 'basic infodog');
  console.log(setLoading, activeItem, loading);
  console.log(props);
  console.log(viewOnly);

  const sideBarItems = [
    {
      title: 'CLIENT INFORMATION',
      lists: [
        {
          name: 'Basic Information',
        },
        {
          disabled: true,
          name: 'Contracts/Branches',
        },
        {
          disabled: true,
          name: 'Member Fields',
        },
        {
          disabled: true,
          name: 'Servicing Instructions',
        },
      ],
    },
    {
      title: 'ACCOUNT',
      lists: [
        {
          disabled: true,
          name: 'HMO Information',
        },
        {
          name: 'Members',
        },
        {
          disabled: true,
          name: 'History',
        },
        {
          disabled: true,
          name: 'Utilization',
        },
      ],
    },
    {
      title: 'ACTIVITIES',
      lists: [
        {
          disabled: true,
          name: 'Tickets',
        },
      ],
    },
  ];

  const breadCrumbs = [
    'CLIENTS',
    data !== undefined ? data.registered_name : '',
  ];

  const activeSideBarItem = item => {
    setActiveItem(item);
    setViewOnly(true);
    APICalls();
  };

  const APICalls = () => {
    switch (activeItem) {
      case 'Members':
        break;
      default:
        break;
    }
  };

  const handleClientBreadcrumbRedirect = () => {
    setRedirectToClientsList(true);
  }

  if(redirectToClientsList === true){
    return(
      <Redirect to={{ pathname: '/membership/clients/list'}} />
    )
  }

  return (
    <>
      <SubHeader
        headingTitle={data !== undefined ? data.registered_name : ''}
        breadCrumbsArray={breadCrumbs.map((breadcrumb,i) => {
          if(i === 0){
            return(
              <Link onClick={handleClientBreadcrumbRedirect}>{breadcrumb}</Link>
            )
          } else {
            return(
              breadcrumb
            )
          }
        })}
        layoutType={'layout_1'}
        enableButton={false}
        enableStatus={true}
        setStatus={data && data.status ? data.status : ''}
      />
      <div className={classes.main}>
        <Grid container xs={12}>
          <Grid item xs={12} md={2} className={classes.sideBarContainer}>
            <SideMenu items={sideBarItems} activeItems={activeSideBarItem} />
          </Grid>
          <Grid item xs={12} md={10} className={classes.contentContainer}>
            {activeItem === 'Basic Information' ? (
              <BasicInformation classes={classes} data={data} />
            ) : null}
            {activeItem === 'Members' ? <MembersComponent data={data} /> : null}
          </Grid>
        </Grid>
      </div>
    </>
  );
};
