// React imports
import React, { useState, useEffect } from 'react';
// Mui imports
import { Grid, Typography, Button } from '@material-ui/core/';
// Relative imports
import { Modal } from '../../ProcessBatches/Components/Modal';
import MomentUtils from '@date-io/moment';
import { BasicSelect, BasicTextField } from 'Components/UI';
import {
  KeyboardDatePicker,
  MaterialUiPickersDate,
  MuiPickersUtilsProvider,
} from '@material-ui/pickers';
import {
  STYLE_CONST,
  SELECT_SENT_VIA,
  SELECT_REASON,
} from './EndorsementModal.const';
// Core imports
// Thirdparty imports

interface EndorsementModalProps {
  modalVisibility: boolean;
  onSubmit: (data: any) => void;
  modalClose: any;
}

export const EndorsementModal: React.FC<EndorsementModalProps> = (
  props: EndorsementModalProps,
): JSX.Element => {
  const classes = STYLE_CONST.batchesWrapper();
  const [endorsedDate, setEndorsedDate] = useState<Date | null>(new Date());
  const [endorsedDateError, setEndorsedDateError] = useState<boolean>(false);
  const [sentVia, setSentVia] = useState<string>('');
  const [sentViaError, setSentViaError] = useState<boolean>(false);
  const [endorsedBy, setEndorsedBy] = useState<string>('');
  const [endorsedByError, setEndorsedByError] = useState<boolean>(false);
  const [reason, setReason] = useState<string>('');
  const [reasonError, setReasonError] = useState<boolean>(false);

  useEffect(() => {
    if (props.modalVisibility) {
      reset();
    }
  }, [props.modalVisibility]);

  const reset = () => {
    setEndorsedDate(new Date());
    setEndorsedDateError(false);
    setSentVia('');
    setSentViaError(false);
    setReason('');
    setReasonError(false);
    setEndorsedBy('');
    setEndorsedByError(false);
  };

  const handleClose = () => {
    props.modalClose();
  };

  const handleNext = () => {
    if (endorsedDate === null) {
      setEndorsedDateError(true);

      return;
    }

    if (endorsedBy.length === 0) {
      setEndorsedByError(true);

      return;
    }

    if (sentVia.length === 0) {
      setSentViaError(true);

      return;
    }

    if (reason.length === 0) {
      setReasonError(true);

      return;
    }

    const data: any = {
      endorsed_date: endorsedDate.toISOString(),
      endorsed_by: endorsedBy,
      sent_via: sentVia,
      reason: reason,
    };

    if (props.onSubmit) {
      props.onSubmit(data);
    }
  };

  return (
    <Modal visibility={props.modalVisibility} close={handleClose}>
      <Typography className={classes.subHeading}>
        Endorsement Details
      </Typography>

      <Grid container>
        <Grid item sm={6}>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <Typography className={classes.formLabel}>Date Sent:</Typography>
            <KeyboardDatePicker
              data-cy={'endorse_modal_datesent_picker'}
              id={'endorse_modal_datesent_picker'}
              className={classes.datePicker}
              style={{
                marginTop: '10px',
                marginBottom: '20px',
                marginRight: '32px',
              }}
              maxDate={new Date()}
              variant="dialog"
              format="MMMM DD, YYYY"
              inputVariant="outlined"
              invalidDateMessage=""
              value={endorsedDate}
              initialFocusedDate={endorsedDate ? endorsedDate : new Date()}
              onChange={(date: MaterialUiPickersDate | null) => {
                const data = date ? date.toDate() : null;
                setEndorsedDate(data);
                setEndorsedDateError(data === null);
              }}
              error={endorsedDateError}
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid item sm={6}>
          <BasicSelect
            data-cy={'endorse_modal_sentvia_select'}
            id={'endorse_modal_sentvia_select'}
            marginTop="10px"
            marginBottom="10px"
            labelFontSize="14px"
            label="Sent via"
            menuItems={SELECT_SENT_VIA}
            width="85%"
            value={sentVia}
            onChange={(e: any) => {
              const value = e.target.value;
              setSentVia(value);
              setSentViaError(value.length === 0);
            }}
            error={sentViaError}
          />
        </Grid>
        <Grid item sm={6}>
          <BasicTextField
            data-cy={'endorse_modal_endorsed_field'}
            id={'endorse_modal_endorsed_field'}
            label="Endorsed by"
            labelFontSize="14px"
            value={endorsedBy}
            onChange={(e: any) => {
              const value = e.target.value;
              setEndorsedBy(value);
              setEndorsedByError(value.length === 0);
            }}
            error={endorsedByError}
            width="85%"
          />
        </Grid>
        <Grid item sm={6}>
          <BasicSelect
            data-cy={'endorse_modal_reason_select'}
            id={'endorse_modal_reason_select'}
            marginTop="10px"
            marginBottom="10px"
            labelFontSize="14px"
            label="Reason"
            menuItems={SELECT_REASON}
            value={reason}
            onChange={(e: any) => {
              const value = e.target.value;
              setReason(value);
              setReasonError(value.length === 0);
            }}
            error={reasonError}
            width="85%"
          />
        </Grid>
      </Grid>

      <Grid container className={classes.printButtons} spacing={5}>
        <Grid className={classes.modalButtonContainerLeft} item sm={6}>
          <Button
            data-cy={'endorse_modal_cancel_btn'}
            id={'endorse_modal_cancel_btn'}
            className={classes.modalButtonLeft}
            style={{ marginRight: '10px' }}
            variant="outlined"
            color="secondary"
            size="small"
            onClick={handleClose}
          >
            {' '}
            Cancel{' '}
          </Button>
        </Grid>
        <Grid className={classes.modalButtonContainerRight} item sm={6}>
          <Button
            data-cy={'endorse_modal_submit_btn'}
            id={'endorse_modal_submit_btn'}
            className={classes.modalButtonRight}
            style={{ marginRight: '10px' }}
            variant="contained"
            color="secondary"
            size="small"
            onClick={handleNext}
          >
            {' '}
            Submit{' '}
          </Button>
        </Grid>
      </Grid>
    </Modal>
  );
};
