import * as React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
        DialogActions, Grid, Button,
        IconButton, Typography, 
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

interface IProps {
    id: string;
    isModalOpen: boolean;
    memberData: any;
    clientRegisteredName: string;
    // terminationData: TerminationData;
    onClose: () => void;
    onCancelSuspension: (data: any) => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    hmodependentsbox: {
        height: 175,
        borderRadius: 0,
        overflow: 'auto',
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    hmodependentsbox_link: {
        color: 'rgb(39, 46, 76)',
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'95%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
        maxWidth: 500
    },
    dialogContentText :{
        fontSize: '0.875rem'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        maxWidth: '400px',
        width: 'auto'
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 500
    },
    dialogFirstTitle:{
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px'
    },
    dialogSecondTitle:{
        fontSize: '14px',
        color: '#272E4C'
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2)
    },
    boldText:{
        fontWeight: 'bold'
    },
    contentText:{
        fontWeight: 700
    },
    cancelButton: {
        minWidth:129,
        marginRight:25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    terminateButton: {
        minWidth:129,
        backgroundColor: '#24B070',
        color: '#24B070',
    },
    previousButton: {
        minWidth:120,
        fontWeight: 600
    },
    nextButton: {
        minWidth:130,
        fontWeight: 600
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    tab:{
        textAlign: 'left',
        alignItems: 'left'
    },
    error:{
        marginTop: 4,
        fontSize: '10px',
        color: '#FF0000'
    }
}));

const TerminateButton = withStyles({
  root: {
    color: '#FFFFFF',
    backgroundColor: '#24B070',
    '&:hover': {
      backgroundColor: '#25ba77',
    },
  },
})(Button);

const CancelButton = withStyles({
  root: {
    '&:hover': {
      backgroundColor: '#3AB77D',
      color: '#FFFFFF',
    },
  },
})(Button);
  


export const CancelSuspensionModal: React.FC<IProps> = (props: IProps): JSX.Element => {
    
    const classes = useStyles();

    function handleCancelSuspension() {

        props.onCancelSuspension(props.memberData['_id']);
        props.onClose();
    }

    return (
        <Dialog
            maxWidth={false}
            open={props.isModalOpen}
            scroll={"paper"}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                <div className={classes.dialogFirstTitle}>
                    Confirm Cancelling of Suspension
                </div>
                <div className={classes.dialogSecondTitle}>
                    
                </div>
                <IconButton
                    data-cy={'cancel_suspension_modal_close_btn'}
                    id={'cancel_suspension_modal_close_btn'}
                    aria-label="close" 
                    className={classes.closeButton} 
                    onClick={props.onClose}
                >
                    <CloseIcon />
                </IconButton>
            </DialogTitle>

            <DialogContent className={classes.dialogContent}>
                <Grid item xs={12}>
                    <Typography>
                        Are you sure you want to cancel the suspension of membership of
                        <span style={{fontWeight: 'bold'}}>
                            {` ${props.memberData ? props.memberData['first_name'] + ' ' + props.memberData['last_name'] : ''}`}
                        </span> in
                        <span style={{fontWeight: 'bold'}}>
                           {` ${props.clientRegisteredName ? props.clientRegisteredName : ''}? `}
                        </span>
                    </Typography>
                </Grid>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid 
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <CancelButton
                            data-cy={'cancel_suspension_modal_cancel_btn'}
                            id={'cancel_suspension_modal_cancel_btn'}
                            className={classes.cancelButton}
                            variant="contained"
                            color="primary"
                            onClick={props.onClose}>
                            Cancel
                        </CancelButton>
                    </Grid>
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <TerminateButton
                            data-cy={'cancel_suspension_modal_btn'}
                            id={'cancel_suspension_modal_btn'}
                            className={classes.terminateButton}
                            variant="contained"
                            color="primary"
                            onClick={handleCancelSuspension}>
                            Cancel Suspension
                        </TerminateButton>
                    </Grid>
                </Grid>
            </DialogActions>   
        </Dialog>
    )
}

CancelSuspensionModal.defaultProps = {
    isModalOpen: false,
};
