  handleLiftSuspensionConfirmModal = data => {
    let memberData = this.state.memberData;
    Utils.StorageService('user_data').then((result: any) => {
      if (result && result.length > 0) {
        let fullname = '';
        result.forEach(element => {
          if (element['key'] === 'first_name' && fullname !== '') {
            fullname = element['value'] + ' ' + fullname;
          } else if (element['key'] === 'first_name') {
            fullname = element['value'];
          }
          if (element['key'] === 'last_name' && fullname !== '') {
            fullname = fullname + ' ' + element['value'];
          } else if (element['key'] === 'last_name') {
            fullname = element['value'];
          }
        });

        let member_suspension = memberData
          ? memberData.member_suspension
            ? memberData.member_suspension
            : ''
          : '';
        const payload = {
          _id: memberData._id,
          delete_dt: data.delete_dt,
          suspension_started_by:
            member_suspension !== ''
              ? member_suspension.suspension_started_by
              : '',
          suspension_start_date:
            member_suspension !== ''
              ? member_suspension.suspension_start_date
              : moment().toISOString(),
          suspension_lifted_by: fullname,
          suspension_lift_date: data.suspension_lift_date,
        };

        this.setState({
          loading: true,
        });

        API.liftSuspensionMember(memberData._id, payload).then(response => {
          const resErr = _.get(response, 'error', null);
          if (_.isNil(resErr)) {
            this.setState({
              loading: false,
              isOpenModal: true,
              modalTitle: 'Lifting Suspension Success',
              modalMessage: `Successfully updated member's data`,
              isOpenLiftSuspensionConfirmModal: false,
            });
            this.getMemberData();
          } else {
            this.setState({
              loading: false,
              isOpenModal: true,
              modalTitle: 'Lifting Suspension failed',
              modalMessage: resErr.message,
              isOpenLiftSuspensionConfirmModal: false,
            });
          }
        });
      }
    });
  };

