  _generatePrincipalName = currEditMemberValues => {
    if (currEditMemberValues) {
      let cleanFirstName = currEditMemberValues['first_name']
        ? currEditMemberValues['first_name'].trim()
        : '';
      let cleanMiddleName = currEditMemberValues['middle_name']
        ? currEditMemberValues['middle_name'].trim()
        : '';
      let cleanLastName = currEditMemberValues['last_name']
        ? currEditMemberValues['last_name'].trim()
        : '';
      let cleanSuffix = currEditMemberValues['suffix']
        ? currEditMemberValues['suffix'].trim()
        : '';
      return (
        cleanLastName +
        ', ' +
        cleanFirstName +
        ', ' +
        cleanMiddleName +
        ', ' +
        cleanSuffix
      );
    } else {
      return '';
    }
  };

  _generateEditMemberValues = () => {
    let memberData = this.state.memberData;
    let memberDependents = this.state.memberDependents;
    let currEditMemberValues = _.cloneDeep(this.state.memberData);
    _.assign(currEditMemberValues, this.state.editMemberValues);
    let clientFields = this.state.clientFields;

    let is_vip =
      memberData &&
      memberData.is_vip !== undefined &&
      memberData.is_vip !== null &&
      memberData.is_vip === true
        ? true
        : false;
    let is_philhealth_rider =
      memberData &&
      memberData.is_philhealth_rider !== undefined &&
      memberData.is_philhealth_rider !== null &&
      memberData.is_philhealth_rider === true
        ? true
        : false;
    let plan_type = memberData ? memberData['plan_type'] : '';

    let suffix = '';
    if (memberData && memberData['suffix']) {
      switch (memberData['suffix']) {
        case 'Sr.':
        case 'Sr':
        case 'sr.':
        case 'sr':
        case 'sR':
        case 'sR.':
          suffix = 'Sr.';
          break;
        case 'Jr.':
        case 'Jr':
        case 'jr.':
        case 'jr':
        case 'jR':
        case 'jR.':
          suffix = 'Jr.';
          break;
        case 'I':
        case 'i':
          suffix = 'I';
          break;
        case 'II':
        case 'ii':
        case 'iI':
        case 'Ii':
          suffix = 'II';
          break;
        case 'III':
        case 'iii':
        case 'iIi':
        case 'iiI':
        case 'IiI':
        case 'Iii':
          suffix = 'III';
          break;
        case 'IV':
        case 'iV':
        case 'Iv':
        case 'iv':
          suffix = 'IV';
          break;
        case 'V':
        case 'v':
          suffix = 'V';
          break;
        case 'Others':
          suffix = 'Others';
          break;
        default:
          break;
      }
    }

    currEditMemberValues['first_name'] = memberData['first_name'];
    currEditMemberValues['middle_name'] = memberData['middle_name'];
    currEditMemberValues['last_name'] = memberData['last_name'];
    currEditMemberValues['suffix'] = suffix;
    currEditMemberValues['civil_status'] = memberData['civil_status'];
    currEditMemberValues['date_of_birth'] = memberData['date_of_birth'];
    currEditMemberValues['title'] = memberData['title'];
    currEditMemberValues['effectivity_date'] = memberData['effectivity_date'];
    currEditMemberValues['relationship_to_principal'] =
      memberData['relationship_to_principal'];
    currEditMemberValues['gender'] = memberData['gender'];
    currEditMemberValues['plan_type'] = plan_type;
    currEditMemberValues['is_vip'] = is_vip;
    currEditMemberValues['is_philhealth_rider'] = is_philhealth_rider;

    if (clientFields && clientFields.length > 0) {
      clientFields.forEach(field => {
        if (
          field.field_name !== 'regsitered_name' &&
          field.field_name !== 'name_on_card'
        ) {
          currEditMemberValues[field.field_name] = memberData[field.field_name];
        }
      });
    }

    let principal_name = this._generatePrincipalName(currEditMemberValues);

    let editMemberDependents = {};
    memberDependents.forEach((item, index) => {
      let key = item['_id'];
      let dependent_data = {
        index: index,
        last_name: item['last_name'],
        middle_name: item['middle_name'],
        first_name: item['first_name'],
        title: item['title'] || '',
        suffix: item['suffix'] || '',
        date_of_birth: item['date_of_birth'],
        gender: item['gender'],
        civil_status: item['civil_status'],
        contracts: item['contracts'],
        relationship_to_principal: item['relationship_to_principal'],
        principal_name: principal_name,
        principal_first_name: memberData['first_name'],
        principal_middle_name: memberData['middle_name'],
        principal_last_name: memberData['last_name'],
        principal_suffix: suffix,
      };

      editMemberDependents[key] = dependent_data;
    });
    currEditMemberValues['dependents'] = editMemberDependents;

    return currEditMemberValues;
  };

  openEditMember = () => {
    let currEditMemberValues = this._generateEditMemberValues();

    this.setState({
      edit_mode: true,
      sideNav: 'hmo_information',
      editMemberValues: currEditMemberValues,
      errorFields: [],
      dependentsErrorFields: {},
    });
  };

  _saveMemberDependents = callback => {
    let ins = this;
    let dependentPayloads = Object.assign(
      {},
      ins.state.editMemberValues['dependents'],
    );

    Object.keys(dependentPayloads).forEach(key => {
      let newDep = Object.assign({}, dependentPayloads[key]);

      delete newDep['index'];

      dependentPayloads[key] = newDep;
    });

    this.setState({
      check_internet_flag: true,
    });
    Promise.all(
      API.patchMemberDependents(
        Object.keys(dependentPayloads),
        dependentPayloads,
      ),
    ).then(responses => {
      this.setState({
        check_internet_flag: false,
      });
      let errors = responses.filter((error: string) => {
        return error.trim().length > 0;
      });
      callback(errors);
    });
    // this.setState({
    //   check_internet_flag: false,
    // })
  };

  _hasEditErrors = (callback: any) => {
    let errors = 0;
    let currEditMemberValues = this.state.editMemberValues;
    let currErrorFields = this.state.errorFields;

    //main field errors
    if (
      currEditMemberValues['first_name'] &&
      currEditMemberValues['first_name'].trim()
    ) {
      currErrorFields = currErrorFields.filter(item => item !== 'first_name');
    } else {
      if (currErrorFields.indexOf('first_name') === -1) {
        currErrorFields.push('first_name');
      }
      errors++;
    }

    if (
      currEditMemberValues['last_name'] &&
      currEditMemberValues['last_name'].trim()
    ) {
      currErrorFields = currErrorFields.filter(item => item !== 'last_name');
    } else {
      if (currErrorFields.indexOf('last_name') === -1) {
        currErrorFields.push('last_name');
      }
      errors++;
    }

    // client fields errors
    let clientFields = this.state.clientFields;
    if (clientFields.length > 0) {
      clientFields.forEach(item => {
        let cfkey = item.field_name;
        let cfval = currEditMemberValues[cfkey];
        if (item.input_type === 'date') {
          if (item.is_required) {
            if (cfval && this.isValidDate(cfval)) {
              currErrorFields = currErrorFields.filter(item => item !== cfkey);
            } else {
              if (currErrorFields.indexOf(cfkey) === -1) {
                currErrorFields.push(cfkey);
              }
              errors++;
            }
          } else {
            if (cfval) {
              if (this.isValidDate(cfval)) {
                currErrorFields = currErrorFields.filter(
                  item => item !== cfkey,
                );
              } else {
                if (currErrorFields.indexOf(cfkey) === -1) {
                  currErrorFields.push(cfkey);
                }
                errors++;
              }
            } else {
              currErrorFields = currErrorFields.filter(item => item !== cfkey);
            }
          }
        } else if (item.is_required) {
          if (cfval && cfval.trim()) {
            currErrorFields = currErrorFields.filter(item => item !== cfkey);
          } else {
            if (currErrorFields.indexOf(cfkey) === -1) {
              currErrorFields.push(cfkey);
            }
            errors++;
          }
        }
      });
    }

    // dependent errors
    let dependents = currEditMemberValues['dependents'];
    let dependentsErrorFields = this.state.dependentsErrorFields
      ? this.state.dependentsErrorFields
      : {};

    Object.keys(dependents).forEach(key => {
      let dependent = dependents[key];
      let dependentError: string[] = dependentsErrorFields[key]
        ? dependentsErrorFields[key]
        : [];

      let fn = dependent['first_name'];
      if (fn && fn.trim().length > 0) {
        dependentError = dependentError.filter(item => item !== 'first_name');
      } else {
        if (dependentError.indexOf('first_name') === -1) {
          dependentError.push('first_name');
        }
        errors++;
      }

      let ln = dependent['last_name'];
      if (ln && ln.trim().length > 0) {
        dependentError = dependentError.filter(item => item !== 'last_name');
      } else {
        if (dependentError.indexOf('last_name') === -1) {
          dependentError.push('last_name');
        }
        errors++;
      }

      let bd = dependent['date_of_birth'];
      if (bd && this.isValidDate(bd)) {
        dependentError = dependentError.filter(
          item => item !== 'date_of_birth',
        );
      } else {
        if (dependentError.indexOf('date_of_birth') === -1) {
          dependentError.push('date_of_birth');
        }
        errors++;
      }

      dependentsErrorFields[key] = dependentError;
    });

    this.setState(
      {
        errorFields: currErrorFields,
        dependentsErrorFields: dependentsErrorFields,
      },
      () => {
        callback(errors > 0);
      },
    );
  };

  handleEditSave = (data: any) => {
    let ins = this;

    ins._hasEditErrors(hasError => {
      if (hasError) {
        ins.setState({
          loading: false,
          isOpenModal: true,
          modalTitle: 'Error',
          modalMessage: 'Please fill out the required fields.',
        });
        return;
      }

      //submit
      let memberData = ins.state.memberData;
      let editMemberValues = ins.state.editMemberValues;

      let formatted_effectivity_date = '';
      if (
        editMemberValues &&
        editMemberValues['effectivity_date'] &&
        moment(editMemberValues['effectivity_date']).isValid()
      ) {
        formatted_effectivity_date = moment(
          editMemberValues['effectivity_date'],
        ).format('LL');
      }
      let payload = {
        last_name: editMemberValues['last_name']
          ? editMemberValues['last_name']
          : memberData['last_name'],
        middle_name: editMemberValues['middle_name']
          ? editMemberValues['middle_name']
          : memberData['middle_name'],
        first_name: editMemberValues['first_name']
          ? editMemberValues['first_name']
          : memberData['first_name'],
        suffix: editMemberValues['suffix']
          ? editMemberValues['suffix']
          : memberData['suffix'],
        civil_status: editMemberValues['civil_status']
          ? editMemberValues['civil_status']
          : memberData['civil_status'],
        gender: editMemberValues['gender']
          ? editMemberValues['gender']
          : memberData['gender'],
        date_of_birth: moment(editMemberValues['date_of_birth']).isValid()
          ? moment(editMemberValues['date_of_birth']).format('MM/DD/YYYY')
          : '',
        effectivity_date: moment(editMemberValues['effectivity_date']).isValid()
          ? moment(editMemberValues['effectivity_date']).format('MM/DD/YYYY')
          : '',
        formatted_effectivity_date: formatted_effectivity_date,
        title: editMemberValues['title'],
        relationship_to_principal:
          editMemberValues['relationship_to_principal'],
        principal_name: editMemberValues['principal_name'],
        plan_type: editMemberValues['plan_type'],
        is_vip: editMemberValues['is_vip'],
        is_philhealth_rider: editMemberValues['is_philhealth_rider'],
        contracts: memberData['contracts'],
      };

      let clientFields = this.state.clientFields;
      if (clientFields.length > 0) {
        clientFields.forEach(item => {
          if (
            item.field_name !== 'registered_name' &&
            item.field_name !== 'name_on_card'
          ) {
            if (item.input_type === 'date') {
              payload[item.field_name] = editMemberValues[item.field_name]
                ? new Date(editMemberValues[item.field_name]).toISOString()
                : '';
            } else {
              payload[item.field_name] = editMemberValues[item.field_name]
                ? editMemberValues[item.field_name]
                : '';
            }
          }
        });
      }

      // Add endorsement details to payload
      payload['endorsement_details'] = data;

      ins.setState(
        {
          loading: true,
          check_internet_flag: true,
        },
        () => {
          API.patchMemberData(ins.props.match.params.member_id, payload).then(
            response => {
              if (response.ok) {
                ins._saveMemberDependents(errors => {
                  if (errors.length > 0) {
                    let errorMsg = errors.join('; ');

                    ins.setState({
                      // check_internet_flag: false,
                      loading: false,
                      isOpenModal: true,
                      modalTitle: "Error saving dependents' information",
                      modalMessage: errorMsg,
                    });
                  } else {
                    ins.loadMemberInformation(isSuccess => {
                      if (isSuccess) {
                        if (ins.props.match.params.source_id) {
                          ins.setState({
                            redirectUrl: `/membership/view-member/${this.props.match.params.source_id}`,
                            redirectPage: true,
                          });
                        }
                        ins.setState({
                          check_internet_flag: false,
                          sideNav: 'hmo_information',
                          edit_mode: false,
                          editMemberValues: defaultEditMemberValues,
                          errorFields: [],
                          dependentsErrorFields: {},
                          isOpenModal: true,
                          modalTitle: 'Success',
                          modalMessage: "Successfully updated member's data",
                        });
                      } else {
                        if (ins.props.match.params.source_id) {
                          ins.setState({
                            redirectUrl: `/membership/view-member/${this.props.match.params.source_id}`,
                            redirectPage: true,
                          });
                        }
                        ins.setState({
                          isOpenModal: true,
                          modalTitle: 'Error fetching new information',
                          modalMessage:
                            'An error occurred while fetching updated information.',
                        });
                      }
                    });
                  }
                });
              } else {
                let error = response.error || {};
                ins.setState({
                  // check_internet_flag: false,
                  loading: false,
                  isOpenModal: true,
                  modalTitle: 'Error saving changes',
                  modalMessage: error.message,
                });
              }
            },
          );
        },
      );
    });
  };

  handleEditCancel = () => {
    if (this.props.match.params.source_id) {
      this.setState({
        redirectUrl: `/membership/view-member/${this.props.match.params.source_id}`,
        redirectPage: true,
      });
    }
    this.setState({
      sideNav: 'hmo_information',
      edit_mode: false,
      editMemberValues: defaultEditMemberValues,
      errorFields: [],
      dependentsErrorFields: {},
    });
  };

  handleEditTextChange = (key: string, value: string) => {
    const currEditMemberValues = this.state.editMemberValues;

    switch (key) {
      case 'first_name':
      case 'middle_name':
      case 'last_name':
        currEditMemberValues[key] = _.trim(value);

        const currDependents = currEditMemberValues['dependents'];
        const currDependentsKeys = Object.keys(currDependents);
        if (currDependentsKeys.length > 0) {
          const newPrincipalName = this._generatePrincipalName(
            currEditMemberValues,
          );
          currDependentsKeys.forEach(dependentKey => {
            currDependents[dependentKey]['principal_' + key] = value;
            currDependents[dependentKey]['principal_name'] = newPrincipalName;
          });
          currEditMemberValues['dependents'] = currDependents;
        }

        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
      default:
        currEditMemberValues[key] = value && value.trim() ? value.trim() : '';
        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
    }
  };

  handleEditClientFieldChange = (
    fieldname: string,
    value: string,
    type: string,
  ) => {
    let currEditMemberValues = this.state.editMemberValues;
    let currErrorFields = this.state.errorFields;

    if (type === 'date') {
      currEditMemberValues[fieldname] = value ? value : '';
    } else {
      currEditMemberValues[fieldname] =
        value && value.trim() ? value.trim() : '';
    }
    currEditMemberValues[fieldname] = value ? value : '';
    this.setState({
      errorFields: currErrorFields,
      editMemberValues: currEditMemberValues,
    });
  };

  handleEditSelectChange = (key: string, item: ISelectItem) => {
    let currEditMemberValues = this.state.editMemberValues;

    switch (key) {
      case 'suffix':
        currEditMemberValues[key] = item.value;

        let currDependents = currEditMemberValues['dependents'];
        let currDependentsKeys = Object.keys(currDependents);
        if (currDependentsKeys.length > 0) {
          let newPrincipalName = this._generatePrincipalName(
            currEditMemberValues,
          );
          currDependentsKeys.forEach(dependentKey => {
            currDependents[dependentKey]['principal_' + key] = item.value;
            currDependents[dependentKey]['principal_name'] = newPrincipalName;
          });
          currEditMemberValues['dependents'] = currDependents;
        }

        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
      default:
        currEditMemberValues[key] = item.value;
        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
    }
  };

  handleEditCheckboxChange = (key: string) => {
    let currEditMemberValues = this.state.editMemberValues;
    let currVal = currEditMemberValues[key];
    currEditMemberValues[key] = !currVal;
    this.setState({
      editMemberValues: currEditMemberValues,
    });
  };

  handleDependentsChange = (
    key: string,
    fieldName: string,
    fieldValue: string,
  ) => {
    let currEditMemberValues = this.state.editMemberValues;
    if (fieldName === 'date_of_birth') {
      currEditMemberValues['dependents'][key][fieldName] = new Date(
        fieldValue,
      ).toISOString();
    } else {
      currEditMemberValues['dependents'][key][fieldName] = fieldValue;
    }
    this.setState({
      editMemberValues: currEditMemberValues,
    });
  };

  handleCloseLostInternetConnectionModal = () => {
    this.setState({
      check_internet_flag: false,
    });
  };

  handleOpenLostInternetConnectionModal = () => {
    this.setState({
      check_internet_flag: true,
    });
  };

  onEditMemberSave = () => {
    this.setState({
      modalVisibility: true,
    });
  };

  onEndorsementModalSubmit = async (data: any) => {
    this.setState({
      modalVisibility: false,
    });

    if (!_.isNil(data)) {
      let createdBy: string = '';

      try {
        const userData = await API.getUserDataFromDb();
        createdBy = userData.userName;
      } catch (e) {
        console.log('Error getting user from IndexedDB.');
      }

      data.member_id = this.props.match.params.member_id;
      data.date_created = new Date().toISOString();
      data.created_by = createdBy;

      this.handleEditSave(data);
    }
  };

