import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
        DialogActions, Grid, Button,
        IconButton,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

interface TmpModalProps {
    isModalOpen: boolean;
    id: string;
    onClose: () => void;
    loadData: () => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'95%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
    },
    dialogContentText :{
        fontSize: '0.875rem'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        minWidth: '680px',
        width: 'auto'
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 500
    },
    dialogFirstTitle:{
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px'
    },
    dialogSecondTitle:{
        fontSize: '14px',
        color: '#272E4C'
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2)
    },
    boldText:{
        fontWeight: 'bold'
    },
    cancelButton: {
        minWidth:129,
        marginRight:25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    submitButton: {
        minWidth:129
    },
    previousButton: {
        minWidth:120,
        fontWeight: 600
    },
    nextButton: {
        minWidth:130,
        fontWeight: 600
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    tab:{
        textAlign: 'left',
        alignItems: 'left'
    },
    error:{
        marginTop: 4,
        fontSize: '10px',
        color: '#FF0000'
    }
}));
  

export const TmpDataModal: React.FC<TmpModalProps> = (
	props: TmpModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id,
    } = props;

    const classes = useStyles();

    return (
        <Dialog
            id={id}
            maxWidth={false}
            open={isModalOpen}
            scroll={"paper"}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                <div className={classes.dialogFirstTitle}>
                    Load previous data?
                </div>
                <div className={classes.dialogSecondTitle}>
                    
                </div>
                <IconButton aria-label="close" className={classes.closeButton} onClick={props.onClose}>
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid item xs={12}>
                </Grid>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid 
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <Button
                            className={classes.cancelButton}
                            variant="contained"
                            color="primary"
                            onClick={props.onClose}>
                            Cancel
                        </Button>
                    </Grid>
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <Button
                            className={classes.submitButton}
                            variant="contained"
                            color="primary"
                            onClick={props.loadData}>
                            Confirm
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>   
        </Dialog>
    )
}

TmpDataModal.defaultProps = {
    isModalOpen: false,
    id: 'modal-id',
};
