import React, { useState, useEffect } from 'react';
import { Grid, Box, TextField, InputAdornment } from '@material-ui/core/';
import { Search } from '@material-ui/icons/';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';
import { isNil } from 'lodash';

import { TableComponent } from 'Components/UI/TableComponent';

const columns: any = [
  { name: 'activity_id', title: 'Activity ID' },
  { name: 'description', title: 'Activity' },
  { name: 'user_name', title: 'User' },
  { name: 'revision_date', title: 'Date' },
  { name: 'revision_time', title: 'Time' },
];

const columnExtensions: any = [
  { columnName: 'description', wordWrapEnabled: true },
];

const formattedColumns: any = {
  //date: ['revision_date'],
  //time: ['revision_time'],
};

const defaultSorting: any = [
  {
    columnName: 'activity_id',
    direction: 'desc',
  },
];

interface AuditLogsProps {
  isSelected: boolean;
  data?: any;
  al_global_filter: string;
  alHandleSearch: any;
  fetchMemberLogs: () => void;
}

export const AuditLogs: React.FC<AuditLogsProps> = (
  props: AuditLogsProps,
): JSX.Element => {

  const [columnFilters, setColumnFilters] = useState<any>([]);

  const handleFilterChange = (filters: any) => {
    setColumnFilters(filters);
  };

  useEffect(() => {
    if (props.isSelected && !isNil(props.fetchMemberLogs)) {
      props.fetchMemberLogs();
    }
  }, [props.isSelected]);

  return (
    <Grid container style={{ paddingLeft: 32 }}>
      <Grid item xs={12} style={{ paddingTop: 16, paddingBottom: 16 }}>
        <Grid container>
          <Grid item xs={12} md={9}>
            <TextField
              // id="members_search"
              data-cy={'audit_logs_search'}
              id={'audit_logs_search'}
              style={{ width: '100%' }}
              placeholder="Search logs..."
              label=""
              margin="normal"
              variant="outlined"
              value={props.al_global_filter}
              onChange={props.alHandleSearch}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Search style={{ color: 'rgba(54, 54, 54, 0.4)' }} />
                  </InputAdornment>
                ),
              }}
            />
          </Grid>
          <Grid item xs={12} md={3}>
            <Box
              display="flex"
              justifyContent="flex-end"
              alignItems="center"
              p={1}
              style={{ width: '100%', height: '100%', display: 'none' }}
            >
              <Box p={1}>
                <Link
                  data-cy={'audit_logs_generate_report'}
                  id={'audit_logs_generate_report'}
                  style={{ color: '#0D5E40', fontSize: '0.875rem' }}
                  to={'#'}
                >
                  <FontAwesomeIcon icon={faDownload} />
                  &nbsp;&nbsp;Generate Report
                </Link>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <TableComponent
          // id="member-audit-logs"
          data-cy={'audit_logs_table'}
          id={'audit_logs_table'}
          message="No Logs Available"
          onClickRow={() => {}}
          rows={props.data}
          columns={columns}
          formattedColumns={formattedColumns}
          columnExtensions={columnExtensions}
          defaultSort={defaultSorting}
          query={props.al_global_filter}
          disableSearch
          disableSelect
          defaultFilter={columnFilters}
          onFilterChange={handleFilterChange}
        />
      </Grid>
    </Grid>
  );
};

AuditLogs.defaultProps = {
  data: {},
  isSelected: false,
  al_global_filter: '',
  alHandleSearch: function() {},
};
