//#region Global Imports
import React, { useState, useEffect } from 'react';
import { faSort, faSearch } from '@fortawesome/free-solid-svg-icons';
import Grid from '@material-ui/core/Grid';
import { PositionProperty } from 'csstype';
//#endregion Global Imports
import { Loader } from 'Components/UI/LoadingIndicator';
import { API } from '../../API';
import { makeStyles } from '@material-ui/styles';
import { TableCell, TableRow, TextField } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { CustomTable } from '../../ViewMember/TicketList/CustomTable';
import { IconButton } from '../../ViewMember/TicketList/IconButton';
import { InputField } from '../../ViewMember/TicketList/InputField';
import lodash from 'lodash';

interface IProps {
  contractID: string;
  planType: string;
}

const filtersForApi = {
  where: {},
};

let currentSortAttr = '';
const tablestyle = makeStyles(() => ({
  benefitTable: {
    boxShadow: '0px 3px 6px #00000029',
  },
  searchField: {
    backgroundColor: 'white',
    marginLeft: '4%',
    width: '92%',
  },
  searchInputAdornment: {
    color: '#00000061',
    marginRight: '8px',
  },
  planTypeSelected: {
    textDecoration: 'underline',
    color: '#3AB77D',
    fontSize: '0.875rem',
    fontWeight: 600,
    cursor: 'pointer',
    paddingRight: '20px',
  },
  planTypeNotSelected: {
    color: 'rgba(39, 46, 76, 0.5)',
    fontSize: '0.875rem',
    fontWeight: 400,
    cursor: 'pointer',
    paddingRight: '20px',
  },
}));

export const ExclusionsSection: React.FC<IProps> = (
  props: IProps,
): JSX.Element | null => {
  const { contractID } = props;
  const [exclusionList, setExclusionList] = useState<any[]>([]);
  const [renderTable, setRenderTable] = useState(true);
  const [filters, setFilters] = useState<any[]>([]);
  const [exclusionTable, setExclusionTable] = useState<any>();
  const [loader, setLoader] = useState(true);
  const [typingTimeout, setTypingTimeout] = useState<any>(0);
  const exclusionstyle = tablestyle();
  const [largeSearch, setLargeSearch] = useState('');
  const columns: any = [
    { name: 'parent_name', title: 'Type' },
    { name: 'name', title: 'Item' },
  ];

  const getFilteredTickets = (columns: any) => {
    let filteredTickets = exclusionList;
    if (largeSearch.trim() !== '') {
      filteredTickets = filteredTickets.filter(col => {
        return (
          col['name']
            .toString()
            .toLowerCase()
            .includes(largeSearch.toLocaleLowerCase().trim()) ||
          col['parent_name']
            .toString()
            .toLowerCase()
            .includes(largeSearch.toLocaleLowerCase().trim())
        );
      });
    }

    columns.map(column => {
      filteredTickets = filteredTickets.filter(ticket =>
        filters[column.name]
          ? filters[column.name] === '' ||
            (ticket[column.name] &&
              ticket[column.name]
                .toString()
                .toLowerCase()
                .includes(filters[column.name].toLowerCase()))
          : true,
      );
    });

    return filteredTickets;
  };

  const renderExclusionTable = () => {
    return (
      <CustomTable
        className="ticket-list-table"
        canOverflow={true}
        rows={getFilteredTickets(columns)}
        stickyHeader={true}
        renderHeader={() => renderCustomTableHeader(columns, columns)}
        renderRow={(row: any, index: number) =>
          renderCustomTableRow(row, index, columns)
        }
        rowsPerPageOptions={[5, 10, 15]}
      />
    );
  };

  const handleFiltersOnChange = () => {
    if (typingTimeout) {
      clearTimeout(typingTimeout);
    }

    setTypingTimeout(
      setTimeout(() => {
        setRenderTable(true);
      }, 2000),
    );
  };

  if (renderTable) {
    setExclusionTable(renderExclusionTable());
    setRenderTable(false);
  }

  const sortProposals = (attrName: string) => {
    let mask = '';
    if (attrName === 'account') {
      mask = 'client_registered_name';
    } else if (attrName === 'effectivity_date') {
      mask = 'custom_metadata.coverage_start_date';
    } else if (attrName === 'version') {
      mask = 'custom_metadata.version';
    } else if (attrName === 'date_created') {
      mask = 'custom_metadata.date_created';
    } else if (attrName === 'date_modified') {
      mask = 'custom_metadata.date_updated';
    }

    filtersForApi['order'] = {};
    filtersForApi['order'] =
      currentSortAttr === mask ? `${mask} desc` : `${mask} asc`;

    setRenderTable(true);
    currentSortAttr = mask;
  };

  const renderCustomTableHeader = (columns: any, fixedColumns: any) => {
    let dockedColumnsCounter = 0;
    return (
      <>
        {columns.map((column, index) => {
          const matchedColumn = fixedColumns.filter(
            col => col === column.name,
          )[0];
          let nextColumnIsFixed = true;
          if (matchedColumn && columns[index + 1]) {
            nextColumnIsFixed = fixedColumns.filter(
              col => col === columns[index + 1].name,
            )[0]
              ? true
              : false;
          }
          const style = {
            left: matchedColumn ? dockedColumnsCounter * 125 : 0,
            zIndex: matchedColumn ? 125 : 0,
            width: column.name === 'version' ? '100px' : '150px',
            paddingRight: '0px',
            paddingLeft: '10px',
            borderRight: !nextColumnIsFixed ? '#9EA3AC 2px solid' : undefined,
          };
          if (matchedColumn) {
            dockedColumnsCounter++;
          }
          return (
            <TableCell key={index} style={style}>
              <span className="header-2">{column.title}</span>
              <div style={{ float: 'right' }}>
                <IconButton
                  icon={faSort}
                  onClick={() => {
                    sortProposals(column.name);
                    setRenderTable(true);
                  }}
                />
              </div>
              <InputField
                value={filters[column.name]}
                onChange={(value: string) => {
                  filters[column.name] = value;
                  setFilters(filters);
                  handleFiltersOnChange();
                }}
              />
            </TableCell>
          );
        })}
      </>
    );
  };

  const handleLoadsFromAPI = () => {
    setLoader(true);
    API.getBenefitTree(contractID)
      .then(response => {
        setLoader(false);

        const resErr = lodash.get(response, 'error', null);
        if (lodash.isNil(resErr)) {
          const BASE_PLAN_TYPE = lodash.lowerCase('Base Plan Type');

          let basePlanTypeNode: any = null;
          let memberPlanTypeNode: any = null;

          // Get base plan and member plan type node
          lodash.forEach(response, benefitNode => {
            const planTypeName = lodash.lowerCase(benefitNode.name);

            if (planTypeName === BASE_PLAN_TYPE) {
              basePlanTypeNode = benefitNode;
            }

            if (planTypeName === lodash.lowerCase(props.planType)) {
              memberPlanTypeNode = benefitNode;
            }
          });

          if (lodash.isNil(basePlanTypeNode)) {
            return;
          }

          // filter exclusions by plan type
          let memberExclusionsNodes: any[] = [];
          if (memberPlanTypeNode !== null) {
            const rootPtExclTreeId = `${memberPlanTypeNode.tree_id}-EX`;

            memberExclusionsNodes = lodash.filter(response, benefitNode => {
              return (
                benefitNode.level > 4 &&
                lodash.includes(benefitNode.tree_id, rootPtExclTreeId)
              );
            });
          }

          // check if node exists in member exclusions nodes
          const existsInMemberExclusions = benefitNode => {
            if (!memberPlanTypeNode) {
              return false;
            }

            const treeId = lodash.replace(
              benefitNode.tree_id,
              basePlanTypeNode.code,
              memberPlanTypeNode.code,
            );

            const exists = lodash.some(
              memberExclusionsNodes,
              memberExclusionsNode => {
                return treeId === memberExclusionsNode.tree_id;
              },
            );

            return exists;
          };

          // filter base benefits
          const rootBaseExclTreeId = !lodash.isNil(memberPlanTypeNode) ? `${memberPlanTypeNode.tree_id}-EX` : '';
          const baseExclusionsNodes: any[] = lodash.filter(
            response,
            benefitNode => {
              return (
                benefitNode.level > 4 &&
                lodash.includes(benefitNode.tree_id, rootBaseExclTreeId) &&
                !existsInMemberExclusions(benefitNode)
              );
            },
          );

          // Combine base plan and plan type exclusions
          const combinedExclusionsNodes = [
            ...memberExclusionsNodes,
            ...baseExclusionsNodes,
          ];

          // Get parent nodes
          const parentExclusionsNodes = combinedExclusionsNodes.filter(
            element => {
              return element.level === 5;
            },
          );

          const tempexclusion: any[] = [];

          // Get list of compbined exclusions nodes
          lodash.forEach(parentExclusionsNodes, parentNode => {
            let pnTreeId = parentNode.tree_id;

            if (!lodash.isNil(memberPlanTypeNode)) {
              const rootPtExclTreeId = `${memberPlanTypeNode.tree_id}-EX`;

              pnTreeId = lodash.replace(
                pnTreeId,
                rootBaseExclTreeId,
                rootPtExclTreeId,
              );
            }

            lodash.forEach(combinedExclusionsNodes, childNode => {
              if (childNode.level === 6 && childNode.custom_metadata.is_excluded) {
                let childTreeId = childNode.tree_id;

                if (!lodash.isNil(memberPlanTypeNode)) {
                  const rootPtExclTreeId = `${memberPlanTypeNode.tree_id}-EX`;

                  childTreeId = lodash.replace(
                    childTreeId,
                    rootBaseExclTreeId,
                    rootPtExclTreeId,
                  );
                }

                if (lodash.startsWith(childTreeId, pnTreeId)) {
                  tempexclusion.push({
                    parent_name: parentNode.name,
                    parent_code: parentNode.code,
                    name: childNode.name,
                    code: childNode.code,
                  });
                }
              }
            });
          });

          setExclusionList(tempexclusion);
        } else {
          setLoader(false);
        }
      })
      .catch(error => {
        console.log('handleLoadsFromAPI Error', error);

        setLoader(false);
      });
  };

  useEffect(() => {
    handleLoadsFromAPI();
  }, []);

  useEffect(() => {
    setRenderTable(true);
  }, [exclusionList]);

  const rowcustomstyle = {
    left: 0,
    position: 'relative' as PositionProperty,
    fontSize: '12px',
    minWidth: '10px',
    maxWidth: '300px',
    paddingRight: '0px',
    paddingLeft: '10px',
  };

  const renderCustomTableRow = (row: any, index: number, columns: any) => {
    const backgroundColor = index % 2 === 0 ? '#FFFFFF' : '#F6F7FA';

    return (
      <TableRow style={{ backgroundColor: backgroundColor }}>
        {columns.map((column, index) => {
          return (
            <TableCell key={index} style={rowcustomstyle}>
              {row[column.name]}
            </TableCell>
          );
        })}
      </TableRow>
    );
  };

  return (
    <React.Fragment>
      {loader ? <Loader /> : null}
      <Grid item sm={12}>
        <TextField
          data-cy="history_search"
          id="history_search"
          placeholder="Search a benefit or benefit category"
          className={exclusionstyle.searchField}
          margin="dense"
          variant="outlined"
          fullWidth
          value={largeSearch}
          onChange={(event: any) => {
            setLargeSearch(event.target.value.trim());
            handleFiltersOnChange();
          }}
          inputProps={{ 'aria-label': 'bare' }}
          InputProps={{
            startAdornment: (
              <FontAwesomeIcon
                icon={faSearch}
                className={exclusionstyle.searchInputAdornment}
              />
            ),
          }}
        />
      </Grid>

      <Grid item sm={12}>
        <div style={{ marginLeft: '4%', width: '92%' }}>{exclusionTable}</div>
      </Grid>
    </React.Fragment>
  );
};
