import React /*useState*/ from 'react';
import { InputLabel, FormControl, FormHelperText } from '@material-ui/core/';
import { makeStyles } from '@material-ui/core/styles';
import Select from 'react-select';
//import Autocomplete from '@material-ui/lab/Autocomplete';
//import TextField from '@material-ui/core/TextField';
import './style.scss';

const SelectStyle = makeStyles({
  root: {
    borderTop: '0.5px solid rgba(60, 57, 74, 0.3)',
    color: '#0d5e40',
    fontSize: '14px',
    textDecoration: 'underline',
  },
  errorText: {
    marginTop: '5px',
    color: '#E53935',
  },
  autoComplete: {
    padding: 0,
  },
  '& div': {
    '& input': {
      padding: 0,
    },
  },
});

interface AutocompleteProps {
  options: any;
  value: any;
  label: string;
  id: string;
  addButton?: boolean;
  addButtonLabel?: string;
  disabled?: boolean;
  formatOptionLabel?: any;
  onChange: (value: any) => void;
  error: boolean;
  error_message: string;
  labelSize?: 'normal' | 'small';
}

export const CustomAutocomplete: React.FC<AutocompleteProps> = (
  props: AutocompleteProps,
): JSX.Element => {
  const classes = SelectStyle();

  //const [isFocused, setFocus] = useState(false);

  return (
    <div>
      <InputLabel
        style={
          props.labelSize === 'small' ? { fontSize: '0.75rem' } : undefined
        }
        htmlFor="data-map-select"
      >
        {props.label}
      </InputLabel>
      <FormControl
        style={{ width: '100%', margin: '8px 0' }}
        error={props.error}
      >
        <Select
          id={props.id}
          data-cy={props.id}
          className={`custom-autocomplete${props.labelSize ? '-small' : ''}`}
          classNamePrefix={`custom-autocomplete${
            props.labelSize ? '-small' : ''
          }`}
          formatOptionLabel={props.formatOptionLabel}
          value={props.value}
          options={props.options}
          onChange={(option: any) => {
            props.onChange(option);
          }}
          isDisabled={props.disabled}
        />
        {props.error === true ? (
          <FormHelperText
            id={props.id + '_error'}
            className={classes.errorText}
          >
            {props.error_message}
          </FormHelperText>
        ) : null}
      </FormControl>
    </div>
  );
};

CustomAutocomplete.defaultProps = {
  disabled: false,
  error: false,
  error_message: '',
};
