import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions, Grid,
    Button, IconButton, InputLabel, TextField,
    FormHelperText} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import { useState, useEffect} from 'react';

interface ConflictMemberNumberErrorModalProps {
    isModalOpen: boolean;
    id: string;
    title: string;
    state_data: any;
    onClose: () => void;
    onSave: ( member: any) => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'100%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
    },
    dialogContentText :{
        fontSize: '0.875rem'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        width: '350px'
    },
    dialogTitle: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600
    },
    dialogAction: {
        marginBottom: theme.spacing(2),
        marginTop: theme.spacing(2),
        justifyContent:'center'
    },
    dialogContentContainer: {
        width: '100%'
    },
    boldText:{
        fontWeight: 'bold'
    },
    saveButton: {
        minWidth:100
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    contentText:{
        fontWeight: 700
    },
    label: {
        fontSize:'1rem'
    },
    errorText2:{
        color: "#E53935",
        marginTop: '0px',
    },
    numberField:{
        marginTop: theme.spacing(2),
        paddingLeft: theme.spacing(6),
    }
}));
  

export const ConflictMemberNumberErrorModal: React.FC<ConflictMemberNumberErrorModalProps> = (
	props: ConflictMemberNumberErrorModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id
    } = props;
    const classes = useStyles();
    const [ number_member, setNumberMember] = useState<any>("");
    const [ error, setError] = useState(false);
    const [ error_message, setErrorMessage] = useState("");

    useEffect(()=>{
        setNumberMember(props.state_data["number_of_members"]);
    },[props.state_data])

    function handleNumberMember(e: React.ChangeEvent<HTMLInputElement>){
        const { value } = e.target;
        setNumberMember(value)
    }

    function handleSave(){
        if(number_member === "" || number_member === 0){
            setError(true);
            setErrorMessage("Number of members is required.")
            return;
        }
        if(parseInt(number_member) !== props.state_data["rows"]){
            setError(true);
            setErrorMessage("Number of members not equal to the rows of file.")
            return;
        }
        if(props.onSave){
            props.onSave(number_member);
        }
    }

    return (
        <Dialog
            id={id}
            open={isModalOpen}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs>
                        <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                            {props.title}
                            <IconButton
                                data-cy={'conflict_member_number_close_btn'}
                                id={'conflict_member_number_close_btn'}
                                aria-label="close" 
                                className={classes.closeButton} 
                                onClick={props.onClose}
                            >
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                    </Grid>
                    <Grid item xs className={classes.dialogContentContainer}>
                        <DialogContent className={classes.dialogContent} style={{padding: '0px 20px 10px 20px'}}>
                            <DialogContentText className={classes.dialogContentText}>
                                <span>This batch was set to have <span className={classes.contentText}>{new Intl.NumberFormat().format(props.state_data["number_of_members"])} members </span>
                                    but the file youre trying to upload has <span className={classes.contentText}>{new Intl.NumberFormat().format(props.state_data["rows"])} rows. </span>
                                    Please update the number of members in the batch upload.
                                </span>
                                <Grid container justify="center" alignItems="center" className={classes.numberField}>
                                    <Grid item xs={12}>
                                        <InputLabel
                                            className={classes.label}
                                            htmlFor="conflict_member_number_error_field">
                                            No. of Members
                                        </InputLabel>
                                        <TextField
                                            data-cy={'conflict_member_number_error_field'}
                                            id={'conflict_member_number_error_field'}
                                            style={{marginTop:'8px'}}
                                            type="number"
                                            margin="normal"
                                            variant="outlined"
                                            value={number_member}
                                            onChange={handleNumberMember}
                                            error={error}
                                            inputProps={{ 'aria-label': 'bare' }}
                                        />
                                        { error === true ?
                                            (<FormHelperText 
                                                id="add_details_number_of_members_error"
                                                className={classes.errorText2}
                                                >
                                                {error_message}
                                            </FormHelperText>):null
                                        }
                                    </Grid>
                                </Grid>
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            <Button
                                data-cy={'conflict_member_number_save_btn'}
                                id={'conflict_member_number_save_btn'}
                                className={classes.saveButton}
                                variant="contained"
                                color="primary" 
                                onClick={handleSave}
                            >
                                Save
                            </Button>
                        </DialogActions>
                    </Grid>
                </Grid>
            </div>
        </Dialog>
    )
}

ConflictMemberNumberErrorModal.defaultProps = {
    isModalOpen: false,
    id: 'modal_id'
};
