import React, { useState, useEffect } from 'react';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    IconButton,
    Grid,
    Typography,
    Paper,
    Table,
    TableHead,
    TableBody,
    TableRow,
    TableCell,
    Button,
} from '@material-ui/core';

import moment from 'moment';

import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/core/styles';

import { API } from '../../API';
import { Loader } from 'Components/UI/LoadingIndicator';

const useStyles = makeStyles(theme => ({
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    dialog: {
        align: 'center',
        justify: 'center',
        width: '100%',
        fontFamily: 'usual',
    },
    dialogContent: {
        paddingTop: '0px',
        minWidth: '250px',
        width: '55vw',
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 350,
    },
    dialogFirstTitle: {
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px',
    },
    dialogNotice: {
        fontSize: 14,
        fontWeight: 400,
        color: '#000',
    },
    tblPaperMembers: {
        minHeight: 175,
        borderRadius: 0,
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
    },
    cancelButton: {
        minWidth: 129,
        marginRight: 25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: '#FFFFFF',
    },
}));

interface MemberCancelTerminationModalProps {
    id: string;
    isModalOpen: boolean;
    memberData: any[];
    onStatusAction: (payload: any) => void;
    onClose: () => void;
}

export const MemberCancelTerminationModal: React.FC<MemberCancelTerminationModalProps> = (
    props: MemberCancelTerminationModalProps
): JSX.Element => {
    const classes = useStyles();
    const { id, isModalOpen, memberData } = props;

    const [isLoaded, setIsLoaded] = useState<boolean>(false);
    const [user, setUser] = useState<any>({});

    useEffect(() => {
        // reset components values on modal open
        if (props.isModalOpen) {
            resetData();
        }
    }, [props.isModalOpen]);

    useEffect(() => {
        async function getUser(){
            try{
                const userData = await API.getUserDataFromDb();
                setUser(userData);
                setIsLoaded(true);
            } catch(error){
                console.log(error)
                setIsLoaded(true);
            }
        }

        if (memberData.length > 0 && isModalOpen === true) {
            getUser();
        }
    }, [isModalOpen])


    const resetData = () => {
        setIsLoaded(false);
        setUser({});
    }

    const handleStatusAction = () => {
        let memberIdsArray: any[] = [];
        memberData.forEach(member => {
            console.log('marked member', member)
            memberIdsArray.push(member.id)
        })

        const payload = {
            memberIds: memberIdsArray,
            remarks: undefined,
            username: user.userName,
        }

        if(props.onStatusAction){
            props.onStatusAction(payload);
            handleOnClose();
        }
    }

    function handleOnClose() {
        if (props.onClose) {
            props.onClose();
        }
    }

    return (
        <div>
            <Dialog
                id={id}
                maxWidth={'md'}
                open={isModalOpen}
                scroll={'paper'}
                aria-labelledby="modal-title"
                className={classes.dialog}
            >
                {isLoaded === false ? <Loader/> :
                    (<>
                        <DialogTitle
                            className={classes.dialogTitle}
                            disableTypography={true}
                            id="modal-title"
                        >
                            <div className={classes.dialogFirstTitle}>Confirm Cancelling of Termination</div>
                            <IconButton
                                id='client_close_button'
                                data-cy='client_close_button'
                                aria-label="close"
                                className={classes.closeButton}
                                onClick={handleOnClose}
                            >
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                        <DialogContent className={classes.dialogContent}>
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                    <Typography className={classes.dialogNotice}>
                                        <span>Are you sure you want to cancel the termination of the following{' '}
                                            <b>{memberData.length} member{memberData.length > 1 ? 's' : null}
                                            </b>? Member(s) will continue to have their benefits coverage.
                                        </span>
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Paper
                                        className={classes.tblPaperMembers}
                                    >
                                        <Table
                                            data-cy={'client_cancel_termination_dependents_table'}
                                            id={'client_cancel_termination_dependents_table'}
                                            size="small"
                                        >
                                            <TableHead>
                                                <TableRow>
                                                    <TableCell style={{ width: '20%' }}>
                                                        Member ID
                                                    </TableCell>
                                                    <TableCell style={{ width: '20%' }}>
                                                        Member Name
                                                    </TableCell>
                                                    <TableCell style={{ width: '20%' }}>
                                                        Corporation
                                                    </TableCell>
                                                    <TableCell style={{ width: '20%' }}>
                                                        Type
                                                    </TableCell>
                                                    <TableCell style={{ width: '20%' }}>
                                                        Termination Date
                                                    </TableCell>
                                                </TableRow>
                                            </TableHead>
                                            <TableBody>
                                                {memberData.length > 0 ? memberData.map(
                                                    (row, index) => {
                                                        return (
                                                            <TableRow key={index}>
                                                                <TableCell style={{ width: '20%' }}>
                                                                    {row['member_id']}
                                                                </TableCell>
                                                                <TableCell style={{ width: '20%' }}>
                                                                    {row['member_name']}
                                                                </TableCell>
                                                                <TableCell style={{ width: '20%' }}>
                                                                    {row['corporates']}
                                                                </TableCell>
                                                                <TableCell style={{ width: '20%' }}>
                                                                    {row['member_type']}
                                                                </TableCell>
                                                                <TableCell style={{ width: '20%' }}>
                                                                    {row['member_termination']['termination_date'] !== '' ? 
                                                                    moment(row['member_termination']['termination_date']).format('MMM DD, YYYY') 
                                                                    : 'Not set'}
                                                                </TableCell>
                                                            </TableRow>
                                                        );
                                                    }
                                                ) : (
                                                        <TableRow>
                                                            <TableCell
                                                                component="th"
                                                                scope="row"
                                                                style={{ textAlign: 'center' }}
                                                                colSpan={5}
                                                            >
                                                                No available data
                                                            </TableCell>
                                                        </TableRow>
                                                    )}
                                            </TableBody>
                                        </Table>
                                    </Paper>
                                </Grid>
                            </Grid>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            <Grid
                                container
                                direction="row"
                                justify="space-evenly"
                                alignItems="stretch"
                            >
                                <Grid item xs={4} style={{ textAlign: 'center' }}>
                                    <Button
                                        data-cy={'client_cancel_termination_cancel_btn'}
                                        id={'client_cancel_termination_cancel_btn'}
                                        className={classes.cancelButton}
                                        variant="contained"
                                        color="primary"
                                        onClick={handleOnClose}
                                    >
                                        Cancel
                                    </Button>
                                </Grid>
                                <Grid item xs={4} style={{ textAlign: 'center' }}>
                                    <Button
                                        data-cy={'client_cancel_termination_btn'}
                                        id={'client_cancel_termination_btn'}
                                        variant="contained"
                                        color="primary"                                
                                        onClick={handleStatusAction}
                                    >
                                        Cancel Termination
                                    </Button>
                                </Grid>
                            </Grid>
                        </DialogActions>
                    </>)
                }
            </Dialog>
        </div>
    )
}
