import * as React from 'react';
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Button,
  IconButton,
  Tabs,
  Tab,
  RadioGroup,
  FormControlLabel,
  Radio,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faChevronRight,
  faChevronLeft,
} from '@fortawesome/free-solid-svg-icons';

// import { TableComponent } from 'Components/UI/TableComponent';
import { PartialTable } from './Table';

interface PartialMatchModalProps {
  isModalOpen: boolean;
  id: string;
  data: any[];
  row: number;
  onClose: () => void;
  onSave: (data: any) => void;
}

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '680px',
    width: 'auto',
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    width: 115,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  submitButton: {
    minWidth: 129,
  },
  previousButton: {
    minWidth: 120,
    fontWeight: 600,
  },
  nextButton: {
    minWidth: 130,
    fontWeight: 600,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  tab: {
    textAlign: 'left',
    alignItems: 'left',
  },
}));

export const PartialMatchModal: React.FC<PartialMatchModalProps> = (
  props: PartialMatchModalProps,
): JSX.Element => {
  const { isModalOpen, id, data, row } = props;
  const [tab, setSelectedTab] = useState(0);
  const [partialcount, setPartialCount] = useState(row);
  const [selectedpartial, setSelectedPartial] = useState<any>(data[row]);
  const [status, setStatus] = useState(data[row]['action']);
  const classes = useStyles();

  useEffect(() => {
    setSelectedPartial(data[row]);
    setStatus(data[row]['action']);
    setPartialCount(row);
    console.log('partialmatch1 row', row);
    console.log('partialmatch1 selected', selectedpartial);
    console.log('partialmatch1 data', data);
    console.log('partialmatch1 partialcount', partialcount);
    console.log('partialmatch1 status', status);
  }, [props.row]);

  useEffect(() => {
    setSelectedPartial(data[row]);
    setStatus(data[row]['action']);
    setPartialCount(row);
    console.log('partialmatch2 row', row);
    console.log('partialmatch2 selected', selectedpartial);
    console.log('partialmatch2 data', data);
    console.log('partialmatch2 partialcount', partialcount);
    console.log('partialmatch2 status', status);
  }, [data[row]['action']]);

  function handleNext() {
    if (partialcount !== data.length - 1) {
      var cnt = partialcount + 1;
      setPartialCount(cnt);
      setSelectedPartial(data[cnt]);
      setStatus(data[cnt]['action']);
    }
  }

  function handlePrevious() {
    var cnt = partialcount - 1;
    if (cnt >= 0) {
      setPartialCount(cnt);
      setSelectedPartial(data[cnt]);
      setStatus(data[cnt]['action']);
    }
  }

  function handleTabChange(event: any, newValue: number) {
    console.log('event >>>', event);
    if (newValue !== undefined) {
      setSelectedTab(newValue);
    }
  }

  function handleRadio(value: string) {
    data[partialcount]['action'] = value;
    setStatus(value);
  }

  function handleSave() {
    props.onSave(data);
  }

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Partial Match Details</div>
        <div className={classes.dialogSecondTitle}>
          Details in red are need to be verified if matched.
        </div>
        <IconButton
          id='close_modal'
          data-cy='close_modal'
          aria-label="close"
          className={classes.closeButton}
          onClick={props.onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Tabs
          value={tab}
          onChange={handleTabChange}
          aria-label="simple tabs example"
        >
          <Tab label="Person Profile" id="person-profile-tab" data-cy='person-profile-tab'/>
          <Tab label="Member Profile" id="member-profile-tab" data-cy='member-profile-tab'/>
          <span style={{ marginTop: '10px', marginLeft: '45px' }}>
            <RadioGroup
              aria-label="position"
              name="position"
              value={status}
              onChange={e => {
                e.stopPropagation();
                console.log('selectedpartial >>>', selectedpartial);
                handleRadio(e.target.value);
              }}
              row
            >
              <FormControlLabel
                key={0}
                value={'match'}
                control={<Radio id='radio_match' data-cy='radio_match' color="primary" />}
                label={'Match'}
                labelPlacement="end"
              />
              <FormControlLabel
                key={1}
                value={'not'}
                control={<Radio id='radio_not_match' data-cy='radio_not_match' color="primary" />}
                label={'Not Match'}
                labelPlacement="end"
              />
              <FormControlLabel
                key={2}
                value={'confirmation'}
                control={<Radio id='radio_confirmation' data-cy='radio_confirmation' color="primary" />}
                label={'For Confirmation'}
                labelPlacement="end"
              />
            </RadioGroup>
          </span>
        </Tabs>
        {tab === 0 ? (
          <PartialTable
            id="person-info-table"
            data-cy='person-info-table'
            rows={
              selectedpartial !== undefined &&
              selectedpartial['person_info'] !== undefined &&
              Object.keys(selectedpartial['person_info']).length > 0
                ? selectedpartial['person_info']['rows']
                : []
            }
            columns={
              selectedpartial !== undefined &&
              selectedpartial['person_info'] !== undefined &&
              Object.keys(selectedpartial['person_info']).length > 0
                ? selectedpartial['person_info']['columns']
                : []
            }
          />
        ) : tab === 1 ? (
          <PartialTable
            id="member-info-table"
            data-cy='member-info-table'
            rows={
              selectedpartial !== undefined &&
              selectedpartial['member_info'] !== undefined &&
              Object.keys(selectedpartial['member_info']).length > 0
                ? selectedpartial['member_info']['rows']
                : []
            }
            columns={
              selectedpartial !== undefined &&
              selectedpartial['member_info'] !== undefined &&
              Object.keys(selectedpartial['member_info']).length > 0
                ? selectedpartial['member_info']['columns']
                : []
            }
          />
        ) : null}
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4}>
            <Button
              id="previous_button"
              data-cy='previous_button'
              disabled={partialcount === 0 ? true : false}
              className={classes.previousButton}
              onClick={handlePrevious}
              style={{ textTransform: 'inherit' }}
            >
              <FontAwesomeIcon
                icon={faChevronLeft}
                style={{
                  color: partialcount === 0 ? 'rgba(0, 0, 0, 0.26)' : '#3C394A',
                  marginRight: '8px',
                }}
              />{' '}
              Prev Member
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              id="save_changes"
              data-cy='save_changes'
              className={classes.submitButton}
              variant="contained"
              color="primary"
              onClick={handleSave}
            >
              Save Changes
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'right' }}>
            <Button
              id="next_member"
              data-cy='next_member'
              disabled={partialcount === data.length - 1 ? true : false}
              className={classes.nextButton}
              onClick={handleNext}
              style={{ textTransform: 'inherit' }}
            >
              Next Member{' '}
              <FontAwesomeIcon
                icon={faChevronRight}
                style={{
                  color:
                    partialcount === data.length - 1
                      ? 'rgba(0, 0, 0, 0.26)'
                      : '#3C394A',
                  marginLeft: '8px',
                }}
              />
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

PartialMatchModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
  data: [],
};
