interface ReactivateMemberModalProps {
  isModalOpen: boolean;
  id: string;
  memberName?: string;
  dependentData?: any[];
  onClose: () => void;
  onSubmit: (data: any) => void;
}

export const ReactivateMemberModal: React.FC<ReactivateMemberModalProps> = (
  props: ReactivateMemberModalProps,
): JSX.Element => {
  const { isModalOpen, id, memberName, dependentData } = props;

  const classes = useStyles();

  const [ticketNum, setTicketNum] = useState<string>('');
  //const [ticketNumError, setTicketNumError] = useState<boolean>(false);
  const [selectedMembers, setSelectedMembers] = useState<string[]>([]);

  const handleOnClose = () => {
    props.onClose();
    setTicketNum('');
    setSelectedMembers([]);
  };

  const handleSubmit = () => {
    // let error = 0;

    // if(!(ticketNum)){
    //   error++;
    //   setTicketNumError(true);
    // }

    // if(error === 0){
    //   props.onSubmit({
    //     ticketNum: ticketNum,
    //     selectedMembers: selectedMembers,
    //   });
    //   setTicketNum('');
    //   setSelectedMembers([]);
    // }
    props.onSubmit({
      ticketNum: ticketNum,
      selectedMembers: selectedMembers,
    });
    setTicketNum('');
    setSelectedMembers([]);
  };

  function handleChangeTicketNum(value: string) {
    setTicketNum(value);
  }

  function handleCheckboxChange(id: string) {
    if (selectedMembers.includes(id)) {
      setSelectedMembers(selectedMembers.filter(item => item !== id));
    } else {
      let newSelectedMembers = [...selectedMembers];
      newSelectedMembers.push(id);
      setSelectedMembers(newSelectedMembers);
    }
  }

  function isValidDate(date: string) {
    const d = new Date(date);
    return d instanceof Date && !isNaN(d.getTime());
  }

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Re-activation Details</div>
        <IconButton
          data-cy={'reactive_member_modal_close_btn'}
          id={'reactive_member_modal_close_btn'}
          aria-label="close"
          className={classes.closeButton}
          onClick={props.onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          <Grid item xs={12} md={6}>
            <Typography className={classes.mnameLabel}>Principal</Typography>
            <Typography className={classes.mnameValue}>{memberName}</Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography className={classes.ticketNoLabel}>
              Ticket Number
            </Typography>
            {/* <InputTextField
              id={props.id+'-ticketno'}
              onChange={handleChangeTicketNum}
              error={ticketNumError}
              errorMessage={ticketNumError ? 'Please add ticket number' : ''}
              value={ticketNum}
            /> */}
            <InputTextField
              data-cy={props.id + '-ticketno'}
              id={props.id + '-ticketno'}
              onChange={handleChangeTicketNum}
              error={false}
              errorMessage={''}
              value={ticketNum}
            />
          </Grid>
          <Grid item xs={12} style={{ marginTop: 32, marginBottom: 16 }}>
            <Typography className={classes.tblTitle}>Dependents</Typography>
            <Typography className={classes.tblSubtitle}>
              Choose the dependents to reactivate
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Paper className={classes.tblbox}>
              <Table 
                data-cy={'reactive_member_modal_dependents_table'}
                id={'reactive_member_modal_dependents_table'}
                size="small"
              >
                <TableHead>
                  <TableRow>
                    <TableCell style={{ width: '30%' }}>Member Name</TableCell>
                    <TableCell style={{ width: '25%' }}>Relationship</TableCell>
                    <TableCell style={{ width: '20%' }}>
                      Termination Date
                    </TableCell>
                    <TableCell style={{ width: '25%' }}>
                      Status before Termination
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {dependentData && dependentData.length > 0 ? (
                    dependentData.map((row, index) => (
                      <TableRow key={index}>
                        <TableCell
                          component="th"
                          scope="row"
                          style={{ width: '30%' }}
                        >
                          <Grid container>
                            <Grid xs={2}>
                              <Checkbox
                                style={hmocheckboxStyle}
                                disabled={false}
                                checked={selectedMembers.includes(row['_id'])}
                                onChange={() => {
                                  handleCheckboxChange(row['_id']);
                                }}
                              />
                            </Grid>
                            <Grid xs={1}></Grid>
                            <Grid xs={9}>
                              {row['first_name'] +
                                ' ' +
                                (row['middle_name']
                                  ? row['middle_name'].charAt(0) + '. '
                                  : '') +
                                row['last_name']}
                            </Grid>
                          </Grid>
                        </TableCell>
                        <TableCell style={{ width: '25%' }}>
                          {row['relationship_to_principal']}
                        </TableCell>
                        <TableCell style={{ width: '20%' }}>
                          {row['member_status'] === 'Terminated' &&
                          row['member_termination'] &&
                          row['member_termination']['termination_date'] &&
                          isValidDate(
                            row['member_termination']['termination_date'],
                          )
                            ? moment(
                                row['member_termination']['termination_date'],
                              ).format('MMM DD, YYYY')
                            : '-'}
                        </TableCell>
                        <TableCell style={{ width: '25%' }}>
                          {row['member_status']}
                        </TableCell>
                      </TableRow>
                    ))
                  ) : (
                    <TableRow>
                      <TableCell
                        component="th"
                        scope="row"
                        style={{ textAlign: 'center' }}
                        colSpan={3}
                      >
                        No enrolled dependents
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </Paper>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid container direction="row" justify="center" alignItems="stretch">
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'reactive_member_modal_cancel_btn'}
              id={'reactive_member_modal_cancel_btn'}
              className={classes.cancelButton}
              variant="contained"
              color="primary"
              onClick={handleOnClose}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              data-cy={'reactive_member_modal_btn'}
              id={'reactive_member_modal_btn'}
              className={classes.submitButton}
              variant="contained"
              color="primary"
              onClick={handleSubmit}
            >
              Reactivate
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

ReactivateMemberModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
  memberName: '',
  dependentData: [],
};
