import * as React from 'react';
import { Grid } from '@material-ui/core/';

import { InformationLabel } from 'Components/UI/InformationLabel';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

interface MemberInformationProps {
  data: any;
  corporateAccountNo?: string;
  corporateAccountName?: string;
  contractName?: string;
}

export const MemberInformation: React.FC<MemberInformationProps> = (
  props: MemberInformationProps,
): JSX.Element => {
  const { data, corporateAccountNo, corporateAccountName, contractName } = props;
  
  return (
    <>
    <Grid
      container
      direction="row"
      alignItems="stretch"
      style={{ paddingTop: '35px' }}
      spacing={5}
    >
      <Grid item xs>
        <InformationLabel
          name={'CORPORATE ACCOUNT NO.'}
          value={
            (corporateAccountNo ? corporateAccountNo : '')
          }
        />
      </Grid>
      <Grid item xs>
        <InformationLabel
          name={'CORPORATE ACCOUNT NAME'}
          value={
            (corporateAccountName ? corporateAccountName : '')
          }
        />
      </Grid>
      <Grid item xs>
        <InformationLabel
          name={'CONTRACT'}
          value={
            (contractName ? contractName : '')
          }
        />
      </Grid>
      <Grid item xs>
        <InformationLabel
          name={'BATCH NO.'}
          value={data.batch_name ? data.batch_name : ''}
        />
      </Grid>
      <Grid item xs>
        <InformationLabel
          name={'NO. OF MEMBERS'}
          value={data.no_of_members ? data.no_of_members : 0}
        />
      </Grid>
    </Grid>
    <Grid
      container
      direction="row"
      alignItems="stretch"
      style={{ paddingTop: '35px' }}
      spacing={5}
    >
      <Grid item xs>
        <InformationLabel
          name={'CHANNEL USED'}
          value={data.channel ? data.channel : ''}
        />
      </Grid>
      <Grid item xs>
        <InformationLabel
          name={'SENDER'}
          value={data.sender_name ? data.sender_name : ''}
        />
      </Grid>
      <Grid item xs>
        <InformationLabel
          name={'EMAIL ADDRESS'}
          value={data.sender_email ? data.sender_email : ''}
        />
      </Grid>
      <Grid item xs>
        <InformationLabel
          name={'DATE SENT'}
          value={
            data.date_sent
              ? GlobalFunction.toCommonDateString(new Date(data.date_sent))
              : ''
          }
        />
      </Grid>
      <Grid item xs>
      </Grid>
    </Grid>
    </>
  );
};

MemberInformation.defaultProps = {
  data: {},
  corporateAccountNo: '',
  corporateAccountName: '',
  contractName: '',
};
