// React imports
import React, { useState } from 'react';
// Mui imports
import { Grid, Typography, Checkbox, Button, Radio, RadioGroup, FormControl, FormControlLabel } from '@material-ui/core/';
// Relative imports
import { Modal } from '../Modal';
import MomentUtils from '@date-io/moment';
import { BasicSelect, BasicTextField } from 'Components/UI'
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import {
  STYLE_CONST,
  SELECT_SENT_VIA,
  SELECT_REASON,
} from '../../ProcessBatches.const';
import Dropzone from 'react-dropzone';
import {
  faFileUpload,
} from '@fortawesome/free-solid-svg-icons';
import { MaterialUiPickersDate } from '@material-ui/pickers';
// Core imports
// Thirdparty imports
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

interface BatchReprintingModalProps {
  modalVisibility: boolean;
  modalClose: any;
  modalNext: any;
  isExternal: boolean;
  setIsExternal: any;
  currentMember: any;
  lastMember:boolean;
}

export const BatchReprintingModal: React.FC<BatchReprintingModalProps> = (
  props: BatchReprintingModalProps,
): JSX.Element => {
  const classes = STYLE_CONST.batchesWrapper();
  const [dateSent, setDateSent] = useState<any>(new Date());
  const [dateSentError, setDateSentError] = useState<boolean>(false);
  const [dateRecieved, setDateRecieved] = useState<any>(new Date());
  const [dateRecievedError, setDateRecievedError] = useState<boolean>(false);
  const [sentVia, setSentVia] = useState<string>('');
  const [sentViaError, setSentViaError] = useState<boolean>(false);
  const [senderName, setSenderName] = useState<string>('');
  const [senderNameError, setSenderNameError] = useState<boolean>(false);
  const [senderEmail, setSenderEmail] = useState<string>('');
  const [ticketNumber, setTicketNumber] = useState<string>('');
  const [reason, setReason] = useState<string>('');
  const [reasonError, setReasonError] = useState<boolean>(false);
  const [reasonOthers, setReasonOthers] = useState<string>('');
  const [personWhoEndorsed, setPersonWhoEndorsed] = useState<string>('');
  const [personWhoEndorsedError, setPersonWhoEndorsedError] = useState<boolean>(false);
  const [endorsedBy, setEndorsedBy] = useState<string>('');
  const [endorsedByError, setEndorsedByError] = useState<boolean>(false);
  const [cardReturned, setCardReturned] = useState<boolean>(false);
  const [generateNewCard, setGenerateNewCard] = useState<boolean>(false);
  const [uploadedFile, setUploadedFile] = useState<any[]>([])

  const reset = () => {
    setDateSent(new Date());
    setDateSentError(false);
    setDateRecieved(new Date());
    setDateRecievedError(false);
    setSentVia('');
    setSentViaError(false);
    setSenderName('');
    setSenderNameError(false);
    setSenderEmail('');
    setTicketNumber('');
    setReason('');
    setReasonError(false);
    setReasonOthers('');
    setPersonWhoEndorsed('');
    setPersonWhoEndorsedError(false);
    setEndorsedBy('');
    setEndorsedByError(false);
    setCardReturned(false);
    setGenerateNewCard(false);
    setUploadedFile([]);
  };

  const handleClose = () => {
    reset();
    props.modalClose();
  }

  const handleNext = () => {
    let errors = 0;

    console.log(uploadedFile);

    if (sentVia.length === 0) {
      setSentViaError(true);
      errors++;
    }

    if (dateSent === null) {
      setDateSentError(true);
      errors++;
    }

    if (dateRecieved === null) {
      setDateRecievedError(true);
      errors++;
    }

    if (reason.length === 0) {
      setReasonError(true);
      errors++;
    }

    if (props.isExternal) {
      if (personWhoEndorsed.length === 0) {
        setPersonWhoEndorsedError(true);
        errors++;
      }

      if (endorsedBy.length === 0) {
        setEndorsedByError(true);
        errors++;
      }
    } else {
      if (senderName.length === 0) {
        setSenderNameError(true);
        errors++;
      }
    }

    if(errors === 0){
      let data: object = {
        member_id:props.currentMember._id ? props.currentMember._id :'', 
        sent_via: sentVia,
        reason: reason, 
        date_sent: dateSent,
        date_received: dateRecieved,
        card_returned: cardReturned,
        generate_new_card: generateNewCard,
        is_external: props.isExternal
      };

      if (senderEmail.length > 0) {
        data['sender_email'] = senderEmail;
      }

      if (ticketNumber.length > 0) {
        data['ticket_no'] = ticketNumber;
      }

      if (props.isExternal) {
        if (personWhoEndorsed.length > 0) {
          data['person_who_endorsed'] = personWhoEndorsed; 
        }     
        if (endorsedBy.length > 0) {
          data['endorsed_by'] = endorsedBy;
        }
      } else {
        if (senderName.length > 0) {
          data['sender_name'] = senderName;
        }
        if (reasonOthers.length > 0) {
          data['reason_others'] = reasonOthers;
        }
      }

      reset();
      props.modalNext(data, uploadedFile);
    }
    // reset();
  }

  const member = props.currentMember;

  let files = uploadedFile && uploadedFile.length  ? 
    uploadedFile.map((file: any) => (
      <li key={file.path}>
        {!file.name ? file.path : file.name}
      </li>
    )) : [];

  return (
    <Modal visibility={props.modalVisibility} close={handleClose}>
      <Typography className={classes.subHeading}>
        Batch Details
      </Typography>
      <Typography className={classes.modalInfo}>
        {member['first_name'] ? member['first_name'] + ' ' : ''}
        {member['middle_name'] ? member['middle_name'] + ' ' : ''}
        {member['last_name'] ? member['last_name'] : ''}
      </Typography>
      <Grid container>
          <Grid item sm={12}>
          <FormControl component="fieldset">
            <RadioGroup name="source" className={classes.radioGroup}>
              <Typography className={classes.radioInfo}>Source</Typography>
              <FormControlLabel
                className={classes.formRadioLabel}
                control={ 
                      <Radio 
                        id='radio_internal'
                        data-cy='radio_internal'
                        name="internal"
                        className={classes.radio}
                        checked={props.isExternal === false}
                        onClick={(e) => {
                          e.stopPropagation();
                          reset();
                          props.setIsExternal(false);
                        }}
                      />
                    }
                label="Internal"
              />
              <FormControlLabel
                className={classes.formRadioLabel}
                control={ 
                      <Radio 
                        id='radio_external'
                        data-cy='radio_external'
                        name="external"
                        className={classes.radio}
                        checked={props.isExternal === true}
                        onClick={(e) => {
                          e.stopPropagation();
                          reset();
                          props.setIsExternal(true);
                        }}
                      />
                    }
                label="External"
              />
            </RadioGroup>
          </FormControl>
        </Grid>
      </Grid>

      { !props.isExternal ? 
          <Grid container>
            <Grid item sm={4}>
                <BasicSelect
                  marginTop='10px'
                  marginBottom='10px'
                  labelFontSize='14px'
                  id='sent-via'
                  data-cy='sent-via'
                  label='Sent via*'
                  menuItems={SELECT_SENT_VIA}
                  width="85%"
                  value={sentVia}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setSentVia(value);
                    setSentViaError(value.length === 0);
                  }}
                  error={sentViaError}
                />
            </Grid>
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Date Sent:</Typography>
                <KeyboardDatePicker
                  id='date_sent'
                  data-cy='date_sent'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  maxDate={new Date(dateRecieved)}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateSent}
                  initialFocusedDate={dateSent ? dateSent : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateSent(data);
                    setDateSentError(data === null);
                  }}
                  error={dateSentError}
                />
              </MuiPickersUtilsProvider>
            </Grid> 
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Date Recieved:</Typography>
                <KeyboardDatePicker
                  id='date_received'
                  data-cy='date_received'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateRecieved}
                  initialFocusedDate={dateRecieved ? dateRecieved : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateRecieved(data);
                    setDateRecievedError(data === null);
                  }}
                  error={dateRecievedError}
                />
              </MuiPickersUtilsProvider>
            </Grid> 
            <Grid item sm={4}>
                <BasicTextField
                  id='sender-name'
                  data-cy='sender-name'
                  label='Sender Name*'
                  labelFontSize='14px'
                  value={senderName}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setSenderName(value);
                    setSenderNameError(value.length === 0);
                  }}
                  error={senderNameError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='sender-email'
                  data-cy='sender-email'
                  label='Sender Email (Optional)'
                  labelFontSize='14px'
                  value={senderEmail}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setSenderEmail(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='ticket-number'
                  data-cy='ticket-number'
                  label='Ticket Number (Optional)'
                  labelFontSize='14px'
                  value={ticketNumber}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setTicketNumber(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicSelect
                  marginTop='10px'
                  marginBottom='10px'
                  labelFontSize='14px'
                  id='reason'
                  data-cy='reason'
                  label='Reason*'
                  menuItems={SELECT_REASON}
                  value={reason}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setReason(value);
                    setReasonError(value.length === 0);
                  }}
                  error={reasonError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='reason-others'
                  data-cy='reason-others'
                  label='If Others, please specify'
                  labelFontSize='14px'
                  value={reasonOthers}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setReasonOthers(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item xs={4}>
              <Dropzone
                onDropAccepted={acceptedFiles => setUploadedFile(acceptedFiles)}
              >
                {({ getRootProps, getInputProps }) => (
                  <section>
                    <div className={classes.fileupload} {...getRootProps()}>
                      <input id="upload_file_member" 
                        data-cy='upload_file_member' {...getInputProps()} />
                      <FontAwesomeIcon
                        className="fa-lg"
                        icon={faFileUpload}
                        style={{ color: 'rgba(39, 46, 76, 0.56)' }}
                      />
                      <p
                        style={{
                          color: 'rgba(39, 46, 76, 0.56)',
                          fontWeight: 400,
                          fontSize: '14px',
                        }}
                      >
                        Upload OR
                      </p>
                    </div>
                    <aside>
                      {files.length > 0 ? (
                        <div>
                          <Typography>Uploaded File</Typography>
                          <ul>{files}</ul>
                        </div>
                      ) : null}
                    </aside>
                  </section>
                )}
              </Dropzone>
            </Grid>
            <Grid item sm={12}>
              <FormControl component="fieldset">
                <RadioGroup name="card" className={classes.radioGroup}>
                  <FormControlLabel
                    className={classes.formLabel}
                    control={ 
                          <Checkbox 
                            id='checkbox_card_returned'
                            data-cy='checkbox_card_returned'
                            name="card_returned"
                            className={classes.checkbox}
                            checked={cardReturned}
                            onChange={e => {
                              setCardReturned(e.target.checked);
                            }}
                          />
                        }
                    label="Card Returned"
                  />
                  <FormControlLabel
                    className={classes.formLabel}
                    control={ 
                          <Checkbox 
                            id='checkbox_generate_new_card'
                            data-cy='checkbox_generate_new_card'
                            name="generate_new_card"
                            className={classes.checkbox}
                            checked={generateNewCard}
                            onChange={e => {
                              setGenerateNewCard(e.target.checked);
                            }}
                          />
                        }
                    label="Generate New Card No."
                  />
                </RadioGroup>
              </FormControl>
            </Grid>
          </Grid> : null
      }

      { props.isExternal ? 
          <Grid container>
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Date Sent:</Typography>
                <KeyboardDatePicker
                  id='date_sent_input'
                  data-cy='date_sent_input'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  maxDate={new Date(dateRecieved)}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateSent}
                  initialFocusedDate={dateSent ? dateSent : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateSent(data);
                    setDateSentError(data === null);
                  }}
                  error={dateSentError}
                />
              </MuiPickersUtilsProvider>
            </Grid> 
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Date Recieved:</Typography>
                <KeyboardDatePicker
                  id='date_received_input'
                  data-cy='date_received_input'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateRecieved}
                  initialFocusedDate={dateRecieved ? dateRecieved : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateRecieved(data);
                    setDateRecievedError(data === null);
                  }}
                  error={dateRecievedError}
                />
              </MuiPickersUtilsProvider>
            </Grid> 
            <Grid item sm={4}>
                <BasicTextField
                  id='ticket-number'
                  data-cy='ticket-number'
                  label='Ticket Number (Optional)'
                  labelFontSize='14px'
                  value={ticketNumber}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setTicketNumber(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='person-endorse'
                  data-cy='person-endorse'
                  label='Person Who Endorsed*'
                  labelFontSize='14px'
                  value={personWhoEndorsed}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setPersonWhoEndorsed(value);
                    setPersonWhoEndorsedError(value.length === 0);
                  }}
                  error={personWhoEndorsedError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='sender-email'
                  data-cy='sender-email'
                  label='Sender Email (Optional)'
                  labelFontSize='14px'
                  value={senderEmail}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setSenderEmail(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicSelect
                  marginTop='10px'
                  marginBottom='10px'
                  labelFontSize='14px'
                  id='sent-via'
                  data-cy='sent-via'
                  label='Sent via*'
                  menuItems={SELECT_SENT_VIA}
                  width="85%"
                  value={sentVia}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setSentVia(value);
                    setSentViaError(value.length === 0);
                  }}
                  error={sentViaError}
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='endorsed-by'
                  data-cy='endorsed-by'
                  label='Endorsed By*'
                  labelFontSize='14px'
                  value={endorsedBy}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setEndorsedBy(value);
                    setEndorsedByError(value.length === 0);
                  }}
                  error={endorsedByError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicSelect
                  marginTop='10px'
                  marginBottom='10px'
                  labelFontSize='14px'
                  id='reason'
                  data-cy='reason'
                  label='Reason*'
                  menuItems={SELECT_REASON}
                  value={reason}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setReason(value);
                    setReasonError(value.length === 0);
                  }}
                  error={reasonError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='reason-others'
                  data-cy='reason-others'
                  label='If Others, please specify'
                  labelFontSize='14px'
                  value={reasonOthers}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setReasonOthers(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
              <FormControl component="fieldset">
                <RadioGroup name="card">
                  <FormControlLabel
                    className={classes.formLabel}
                    control={ 
                          <Checkbox 
                            id='checkbox_card_returned'
                            data-cy='checkbox_card_returned'
                            name="card_returned"
                            className={classes.checkbox}
                            checked={cardReturned}
                            onChange={e => {
                              setCardReturned(e.target.checked);
                            }}
                          />
                        }
                    label="Card Returned"
                  />
                  <FormControlLabel
                    className={classes.formLabel}
                    control={ 
                          <Checkbox 
                            id='checkbox_generate_new_card'
                            data-cy='checkbox_generate_new_card'
                            name="generate_new_card"
                            className={classes.checkbox}
                            checked={generateNewCard}
                            onChange={e => {
                              setGenerateNewCard(e.target.checked);
                            }}
                          />
                        }
                    label="Generate New Card No."
                  />
                </RadioGroup>
              </FormControl>
            </Grid>
            <Grid item xs={8}>
              <Dropzone
                onDropAccepted={acceptedFiles => setUploadedFile(acceptedFiles)}
              >
                {({ getRootProps, getInputProps }) => (
                  <section>
                    <div className={classes.fileupload} {...getRootProps()}>
                      <input id="upload_file_member"
                            data-cy='upload_file_member' {...getInputProps()} />
                      <FontAwesomeIcon
                        className="fa-lg"
                        icon={faFileUpload}
                        style={{ color: 'rgba(39, 46, 76, 0.56)' }}
                      />
                      <p
                        style={{
                          color: 'rgba(39, 46, 76, 0.56)',
                          fontWeight: 400,
                          fontSize: '14px',
                        }}
                      >
                        Upload OR
                      </p>
                    </div>
                    <aside>
                      {files.length > 0 ? (
                        <div>
                          <Typography>Uploaded File</Typography>
                          <ul>{files}</ul>
                        </div>
                      ) : null}
                    </aside>
                  </section>
                )}
              </Dropzone>
            </Grid>
          </Grid> : null
      }

      <Grid container className={classes.printButtons} spacing={5}>
        <Grid className={classes.modalButtonContainerLeft} item sm={6}>
          <Button
            id='cancel_reprint_button'
            data-cy='cancel_reprint_button'
            className={classes.modalButtonLeft}
            style={{ marginRight: '10px' }}
            variant="outlined"
            color="secondary"
            size="small"
            onClick={handleClose}
          >
            {' '}
            Cancel{' '}
          </Button>
        </Grid>
        <Grid className={classes.modalButtonContainerRight} item sm={6}>
          <Button
            id='finish_member_button'
            data-cy='finish_member_button'
            className={classes.modalButtonRight}
            style={{ marginRight: '10px' }}
            variant="contained"
            color="secondary"
            size="small"
            onClick={handleNext}
          >
            {' '}
            {props.lastMember ? 'Print' : 'Next Member'}
            
            {' '}
          </Button>
        </Grid>
      </Grid>
    </Modal>
  );
};
