import * as React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { Link } from 'react-router-dom';

interface InformationLabelProps {
	name?: string;
	value?: string;
	nameFontSize?: number;
	valueFontSize?: number;
	link?: string;
}

const useStyles = makeStyles({
	root: {
        padding: '5px 5px'
	},
	name: (props: any) => ({
        fontSize: props.nameFontSize,
        paddingBottom: '5px',
		color: 'rgba(39, 46, 76, 0.6)',
	}),
	value: (props: any) => ({
		fontSize: props.valueFontSize,
		color: 'rgb(39, 46, 76)',
		fontWeight: 600
	}),
	link: (props: any) => ({
		fontSize: props.valueFontSize,
		color: 'rgb(58, 183, 125)',
		fontWeight: 600,
		textDecoration: 'underline',
	}),
});

export const InformationLabel: React.FC<InformationLabelProps> = (
	props: InformationLabelProps,
): JSX.Element => {
	const classes = useStyles(props);

	if(props.link && props.link.length > 0){
		let lnk: string = props.link ? props.link : '#';
		return (
			<div className={classes.root}>
				<Typography className={classes.name}>{props.name}</Typography>
				<Link to={lnk} className={classes.link}>{props.value}</Link>
			</div>
		);
		
	}
	else{
		let val: string = props.value ? props.value : '-'
		return (
			<div className={classes.root}>
				<Typography className={classes.name}>{props.name}</Typography>
				<Typography className={classes.value}>{val}</Typography>
			</div>
		);
	}
};

InformationLabel.defaultProps = {
	nameFontSize: 12,
	valueFontSize: 16,
	link: '',
}
