import React from 'react';
import {
    Grid,
    Table,
    TableHeaderRow,
    TableSelection
} from '@devexpress/dx-react-grid-material-ui';

import {
    SelectionState,
    IntegratedSelection,
    // DataTypeProvider,
} from '@devexpress/dx-react-grid';

import * as MaterialUI from '@material-ui/core';

import { STYLE_CONST } from '../../ProcessBatches/ProcessBatches.const';

interface ReactivateTableProps {
    id:string,
    rows: any,
    columns: any,
    selection: any,
    selectall:boolean,
    disableSelect?: boolean;
    onSelectAll:(selected:boolean)=>void;
    onRowSelect: (selected: any) => void; 
    
}

export const ReactivateTable: React.FC<ReactivateTableProps> = (
    props: ReactivateTableProps
): JSX.Element => {
    const {
        rows,
        columns,
        onRowSelect,
        selection,
        selectall,
        onSelectAll,
        disableSelect,
        id,
    } = props;
    const classes = STYLE_CONST.tableStyles();
    
    const getRowId = row => row._id;

    const handleSelection = (newSelection: any) => {
        onRowSelect(newSelection);
    }


    const headerselectAll = () =>{
        return (
            <MaterialUI.Checkbox
                color="primary"
                // classes={{
                //     colorPrimary: classes.checkbox,
                // }}
                data-cy={'print_check_all_checkbox'}
                checked={selectall}
                onChange={()=>{onSelectAll(!selectall)}}
            />
        )
    }

    
    return (
        <div className={classes.container}>
            <Grid
                rows={rows}
                columns={columns}
                getRowId={getRowId}>

                {!disableSelect? <SelectionState 
                    selection={selection} 
                    onSelectionChange={handleSelection} />
                    :null}
                
                    {!disableSelect? <IntegratedSelection /> :null}
                

                <Table 
                data-cy={id} />
                
                {!disableSelect?
                    <TableSelection
                    selectByRowClick
                    showSelectionColumn={true}
                    showSelectAll={true}
                    headerCellComponent={headerselectAll}
                /> 
                :null}
                

                <TableHeaderRow
                    showSortingControls={ false} />
                
                


                
            </Grid>
        </div>
    )
}
