import * as React from 'react';
import { Link, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useState } from 'react';
import classNames from 'classnames';

const useStyles = makeStyles({
	sideBarLists: {
		padding: '0 0 20px',
		margin: 0,
		listStyle: 'none'
	},
	sideBarLink: {
		color: "#272e4c",
        margin: "0",
        padding: "0 0 10px",
        textDecoration: "none",
        display: "block",
        fontSize: "14px",
        '&:hover': {
            color: "#2C2E8E",
        }
    },
    sideTitle: {
        color:'#b7bac6',
        fontSize: '14px',
        paddingBottom: '10px',
        fontWeight: 600
    },
    LinkActive: {
        color: "#2C2E8E",
        fontWeight: "bold",
        textDecoration: "underline"
    },
    disabledLink: {
        pointerEvents: 'none',
        color: '#ccc'
    }
});

export interface ISideMenuProps {
    items: any,
    activeItems: (item) => void,
}

export const SideMenu : React.FC<ISideMenuProps> = (props: ISideMenuProps): JSX.Element => {
    const {items , activeItems } = props;
    const [activeClass, setActiveClass] = useState('00');
    const classes = useStyles();

    const sideBarlinkClick = (itemName, index) => {
        activeItems(itemName)
        setActiveClass(index)
    }

    return (
        <>
         {
            items.map((item, i) => (
                <ul className={classes.sideBarLists}>
                        <li>
                            <Typography className={classes.sideTitle}>{item.title}</Typography>
                        </li>
                        {
                            item.lists && item.lists.map((_item, _i) => (
                                <li>
                                    {
                                        _item.label !== undefined &&
                                        <Typography variant={'subtitle2'} style={{color:'#929292', fontSize: '12px'}}>{ _item.label }</Typography>
                                    }
                                    <Link id={'sidemenu_' + _item.name} data-cy={'sidemenu_' + _item.name} className={classNames(classes.sideBarLink,_item.disabled && classes.disabledLink, activeClass === String(i)+String(_i) ? classes.LinkActive : null)} 
                                        onClick={() => sideBarlinkClick(_item.name, String(i) + String(_i))}
                                    >
                                        { _item.name }
                                    </Link>
                                </li>
                            ))
                        }
                    </ul>
                ))
            }
        </>
    )
}
