import { Grid, IconButton, Typography, Drawer } from '@material-ui/core/';
import { makeStyles } from '@material-ui/core/styles';
import CloseIcon from '@material-ui/icons/Close';
import Dropzone from 'react-dropzone';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faFileUpload,
  faTimesCircle,
  faFile,
} from '@fortawesome/free-solid-svg-icons';
import { replace } from 'lodash';

import { API, BASE_API_URL } from '../../../API';
// import { useState } from 'react';

// import { InformationLabel } from 'Components/UI/InformationLabel';

import { SnackbarNotification } from 'Components/UI/SnackbarNotification';

const useStyles = makeStyles(theme => ({
  list: {
    width: 380,
    position: 'fixed',
    right: '0px',
    top: '0px',
    height: '100%',
    backgroundColor: '#F5F7FB',
    boxShadow: '0px 3px 6px #00000029',
    zIndex: 9990,
  },
  container: {
    padding: '42px 30px',
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  fullList: {
    width: 'auto',
  },
  value: {
    fontSize: '16px',
    color: 'rgb(39, 46, 76)',
    fontWeight: 600,
  },
  name: {
    fontSize: '12px',
    paddingBottom: '5px',
    color: 'rgba(39, 46, 76, 0.6)',
  },
  itemPadding: {
    paddingTop: '32px',
  },
  fileupload: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    borderWidth: '2px',
    borderRadius: '5px',
    borderColor: '#7b8094',
    borderStyle: 'dashed',
    color: '#7b8099',
    outline: 'none',
    cursor: 'pointer',
    backgroundColor: '#E6E8EE',
    transition: 'border .24s ease-in-out',
    padding: '8px',
    width: '100%',
    height: '65px',
    textAlign: 'center',
  },
  upload: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    borderWidth: '2px',
    borderRadius: '5px',
    borderColor: '#7b8094',
    borderStyle: 'dashed',
    color: '#7b8099',
    outline: 'none',
    cursor: 'pointer',
    backgroundColor: '#E6E8EE',
    transition: 'border .24s ease-in-out',
    padding: '25px',
    width: '135px',
    height: '125px',
    textAlign: 'center',
  },
  image: {
    height: '110px',
    width: '120px',
    borderRadius: '5px',
    border: '2px solid #1E20714D',
  },
}));

interface ViewDocumentsProps {
  open: boolean;
  memberId: string;
  row_data: any;
  row_id: string;
  all_data: any;
  showHideDrawer: (value: boolean) => void;
  showHideLoader: (flag: boolean) => void;
  updateMemberDocuments: (uploadDocs: any) => void;
  generateDocumentData: (data: any, disabled: boolean) => void;
  handleOpenLostInternetConnectionModal: () => void;
  handleCloseLostInternetConnectionModal: () => void;
}

export const ViewDocuments: React.FC<ViewDocumentsProps> = (
  props: ViewDocumentsProps,
): JSX.Element => {
  const classes = useStyles();

  const [memberId, setMemberId] = React.useState(props.memberId);
  const [isSnackbarNotifOpen, setIsSnackbarNotifOpen] = React.useState(false);
  const [snackbarNotifMsg, setSnackbarNotifMsg] = React.useState(
    'File successfully uploaded.',
  );
  const [snackbarNotifVariant, setSnackbarNotifVariant] = React.useState(
    'success',
  );

  React.useEffect(() => {
    setMemberId(props.memberId);
  }, [props.memberId]);

  function onClose(event: React.SyntheticEvent<Element>) {
    console.log('event >>>', event, ' ==== ', event.type);
    props.showHideDrawer(false);
  }

  function handleFileUpload(acceptedFile: any) {
    if (acceptedFile.length > 0) {
      console.log('>>>>', acceptedFile);
      const reader = new FileReader();
      reader.onabort = () => console.log('file reading was aborted');
      reader.onerror = () => console.log('file reading has failed');
      reader.onload = () => {
        const file = acceptedFile[0];
        uploadDocument(file);
      };
      reader.readAsDataURL(acceptedFile[0]);
    }
  }

  function uploadDocument(file: File) {
    props.showHideLoader(true);
    const { _id: type } = props.row_data;
    props.handleOpenLostInternetConnectionModal();
    API.uploadMemberDocument(memberId, type, file)
      .then((uploadRes: any) => {
        props.showHideLoader(false);
        if (uploadRes && uploadRes.url) {
          props.handleCloseLostInternetConnectionModal()
          const apiBaseUrl = BASE_API_URL || '';
          const newUrl = replace(uploadRes.url, 'public/', apiBaseUrl);
          uploadRes.url = newUrl;

          let all_data: any = props.all_data;
          for (const cnt in all_data) {
            const data = all_data[cnt];
            if (data['_id'] === props.row_id) {
              if (
                data['files'] &&
                data['files'] !== undefined &&
                data['files'] !== null
              ) {
                data['files'].push(uploadRes);
              } else {
                data['files'] = [];
                data['files'].push(uploadRes);
              }
              break;
            }
          }
          props.generateDocumentData(all_data, false);
          setIsSnackbarNotifOpen(true);
          setSnackbarNotifMsg('File successfully uploaded.');
          setSnackbarNotifVariant('success');
          /**
           * Moved patching member data on 'Save'
           */
          // if (props.updateMemberDocuments) {
          //   props.updateMemberDocuments(uploadRes);
          // }
        } else {
          setIsSnackbarNotifOpen(true);
          setSnackbarNotifMsg('File not uploaded.');
          setSnackbarNotifVariant('error');
        }
      })
      .catch(e => {
        console.log('uploadDocument Error', e);

        props.showHideLoader(false);
        setIsSnackbarNotifOpen(true);
        setSnackbarNotifMsg('File not uploaded.');
        setSnackbarNotifVariant('error');
      });
  }

  function handleDelete(e: React.MouseEvent<HTMLSpanElement>, row: number) {
    console.log('e >>>', e, ' ==== ', row);
    let all_data: any = props.all_data;
    for (const cnt in all_data) {
      const data = all_data[cnt];
      if (data['_id'] === props.row_id) {
        if (
          data['files'] &&
          data['files'] !== undefined &&
          data['files'] !== null
        ) {
          data['files'].splice(row, 1);
        }
        break;
      }
    }
    props.generateDocumentData(all_data, false);
  }

  // function onOpen(event: React.SyntheticEvent<Element>){
  //     console.log("event >>>", event, " ==== ", event.type);
  //     props.showHideDrawer(true);
  // }
  return (
    <Drawer
      open={props.open}
      anchor="right"
      ModalProps={{
        hideBackdrop: true,
        keepMounted: true,
        open: false,
      }}
    >
      <div className={classes.list} role="presentation">
        <Grid container className={classes.container}>
          <Grid item xs={12}>
            <IconButton
              data-cy={'view_docs_close_btn'}
              id={'view_docs_close_btn'}
              aria-label="close"
              className={classes.closeButton}
              onClick={onClose}
            >
              <CloseIcon />
            </IconButton>
          </Grid>
          <Grid item xs={12}>
            <Typography className={classes.value}>
              {props.row_data && props.row_data.name ? props.row_data.name : ''}
            </Typography>
          </Grid>
          <Grid item xs={12} className={classes.itemPadding}>
            <div>
              <Typography className={classes.name}>
                {'PURPOSE OF DOCUMENT'}
              </Typography>
              <Typography className={classes.value}>
                {props.row_data && props.row_data.purpose
                  ? props.row_data.purpose
                  : ''}
              </Typography>
            </div>
          </Grid>
          <Grid item xs={12} className={classes.itemPadding}>
            <Typography className={classes.name}>{'FILES'}</Typography>
          </Grid>
          <Grid item xs={12} style={{ paddingTop: '10px' }}>
            <Dropzone
              onDrop={acceptedFiles => handleFileUpload(acceptedFiles)}
              accept="image/*, .pdf"
            >
              {({ getRootProps, getInputProps }) => (
                <section>
                  <div className={classes.fileupload} {...getRootProps()}>
                    <input 
                      data-cy={'view_docs_upload_file'}
                      id={'view_docs_upload_file'}
                      {...getInputProps()} 
                    />
                    <FontAwesomeIcon
                      className="fa-lg"
                      icon={faFileUpload}
                      style={{ color: 'rgba(39, 46, 76, 0.56)' }}
                    />
                    <p
                      style={{
                        color: 'rgba(39, 46, 76, 0.56)',
                        fontWeight: 400,
                        fontSize: '11px',
                      }}
                    >
                      Click or drag/drop additional files
                    </p>
                  </div>
                </section>
              )}
            </Dropzone>
          </Grid>
          <Grid
            item
            xs={12}
            style={{ height: '45vh', overflow: 'auto', marginTop: '20px' }}
          >
            <Grid container style={{ paddingTop: '15px' }}>
              {props.row_data &&
              props.row_data['files'] &&
              props.row_data['files'] !== undefined &&
              props.row_data['files'] !== null
                ? props.row_data['files'].map((item: any, idx: number) => {
                    if (
                      item['name'] &&
                      item['name'] !== null &&
                      item['name'] !== undefined &&
                      (item['name'].split('.').pop() === 'jpeg' ||
                        item['name'].split('.').pop() === 'jpg' ||
                        item['name'].split('.').pop() === 'png')
                    ) {
                      return (
                        <Grid item xs={6} style={{ marginBottom: '10px' }}>
                          <span>
                            <img src={item['url']} className={classes.image} />
                            <div>
                              <span>
                                <span
                                  data-cy={'view_docs_delete_file'}
                                  id={'view_docs_delete_file'}
                                  style={{
                                    paddingRight: '5px',
                                    fontSize: '10px',
                                  }}
                                  onClick={e => handleDelete(e, idx)}
                                >
                                  <FontAwesomeIcon
                                    icon={faTimesCircle}
                                    style={{ color: '#B0AFB8' }}
                                  />
                                </span>
                                <span style={{ fontSize: '10px' }}>
                                  {item['name']}
                                </span>
                              </span>
                            </div>
                          </span>
                        </Grid>
                      );
                    } else if (
                      item['type'] &&
                      item['type'] !== null &&
                      item['type'] !== undefined &&
                      (item['type'] === 'image/jpeg' ||
                        item['type'] === 'image/jpg' ||
                        item['type'] === 'image/png')
                    ) {
                      return (
                        <Grid item xs={6} style={{ marginBottom: '10px' }}>
                          <span>
                            <img
                              src={item['source']}
                              className={classes.image}
                            />
                            <div>
                              <span>
                                <span
                                  data-cy={'view_docs_delete_file'}
                                  id={'view_docs_delete_file'}
                                  style={{
                                    paddingRight: '5px',
                                    fontSize: '10px',
                                  }}
                                  onClick={e => handleDelete(e, idx)}
                                >
                                  <FontAwesomeIcon
                                    icon={faTimesCircle}
                                    style={{ color: '#B0AFB8' }}
                                  />
                                </span>
                                <span style={{ fontSize: '10px' }}>
                                  {item['filename']}
                                </span>
                              </span>
                            </div>
                          </span>
                        </Grid>
                      );
                    } else {
                      return (
                        <Grid item xs={6} style={{ marginBottom: '10px' }}>
                          <span>
                            <div>
                              <FontAwesomeIcon
                                className={'fa-7x'}
                                icon={faFile}
                                style={{ color: '#B0AFB8' }}
                              />
                            </div>
                            <div>
                              <span>
                                <span
                                  data-cy={'view_docs_delete_file'}
                                  id={'view_docs_delete_file'}
                                  style={{
                                    paddingRight: '5px',
                                    fontSize: '10px',
                                  }}
                                  onClick={e => handleDelete(e, idx)}
                                >
                                  <FontAwesomeIcon
                                    icon={faTimesCircle}
                                    style={{ color: '#B0AFB8' }}
                                  />
                                </span>
                                <span style={{ fontSize: '10px' }}>
                                  {item['filename']
                                    ? item['filename']
                                    : item['name']}
                                </span>
                              </span>
                            </div>
                          </span>
                        </Grid>
                      );
                    }
                  })
                : null}
            </Grid>
          </Grid>
        </Grid>
        <SnackbarNotification
          isOpen={isSnackbarNotifOpen}
          message={snackbarNotifMsg}
          variant={snackbarNotifVariant}
          onSnackbarClose={() => {
            setIsSnackbarNotifOpen(false);
          }}
        />
      </div>
    </Drawer>
  );
};

ViewDocuments.defaultProps = {
  open: false,
};
