const Select = ({ items, ...restProps }: any) => {
  return (
    <MaterialUI.Select
      variant="outlined"
      style={{ width: '100%' }}
      renderValue={(selected: any) => {
        if (selected != null) {
          const value: any = items.find((item: any) => {
            return item.value === selected;
          });
          if (value) {
            return value.label;
          } else {
            return '';
          }
        }
        return '';
      }}
      {...restProps}
    >
      {items.map((item: any, i: number) => (
        <MaterialUI.MenuItem key={i} value={item.value}>
          {item.label}
        </MaterialUI.MenuItem>
      ))}
    </MaterialUI.Select>
  );
};

const TableFilterCell = (props: any) => {
  const { column, filter, onFilter, filteringEnabled, classes } = props;
  const datePickerClasses = datePickerStyles();
  if (filteringEnabled) {
    if (column.name === 'status') {
      const items = MemberStatusItems;
      return (
        <MaterialUI.TableCell classes={classes} style={{ paddingTop: '5px' }}>
          <Select
            data-cy={column.name ? `memberlist_table_${column.name.toLowerCase().replace(' ', '_')}}_select` : ''}
            id={column.name ? `memberlist_table_${column.name.toLowerCase().replace(' ', '_')}}_select` : ''}
            onChange={e =>
              onFilter(e.target.value ? { value: e.target.value } : null)
            }
            name={'status'}
            items={items}            
            value={filter ? filter.value : ''}
          />
        </MaterialUI.TableCell>
      );
    } else if (
      column.name === 'date_printed' ||
      column.name === 'effectivity_date' ||
      column.name === 'termination_date'
    ) {
      return (
        <MaterialUI.TableCell
          style={{ padding: '0px 10px 8px 10px' }}
          classes={classes}
        >
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              data-cy={column.name ? `memberlist_table_${column.name.toLowerCase().replace(' ', '_')}_picker` : ''}
              id={column.name ? `memberlist_table_${column.name.toLowerCase().replace(' ', '_')}_picker` : ''}
              variant="dialog"
              format="MMMM DD, YYYY"
              inputVariant="outlined"
              className={datePickerClasses.keyboardDatePicker}
              value={filter ? filter.value : ''}
              invalidDateMessage=""
              onChange={date => {
                let formattedDate = '';
                if (!isNil(date)) {
                  const dateMoment = moment(date.toISOString());
                  if (!isNil(dateMoment) && dateMoment.isValid()) {
                    formattedDate = dateMoment.format('MMMM DD, YYYY');
                  }
                }
                onFilter({
                  value: formattedDate,
                });
              }}
            />
          </MuiPickersUtilsProvider>
        </MaterialUI.TableCell>
      );
    } else {
      return (
        <MaterialUI.TableCell
          style={{ padding: '0px 10px 8px 10px' }}
          classes={classes}
        >
          <MaterialUI.OutlinedInput
            data-cy={column.name ? `memberlist_table_${column.name.toLowerCase().replace(' ', '_')}_field` : ''}
            id={column.name ? `memberlist_table_${column.name.toLowerCase().replace(' ', '_')}_field` : ''}
            classes={classes}
            style={{ width: '100%' }}
            value={filter ? filter.value : ''}
            labelWidth={0}
            onChange={e =>
              onFilter(e.target.value ? { value: e.target.value } : null)
            }
            placeholder=""
          />
        </MaterialUI.TableCell>
      );
    }
  } else {
    // return null;
    return (
      <MaterialUI.TableCell
        style={{ padding: '0px 10px 8px 10px' }}
        classes={classes}
      ></MaterialUI.TableCell>
    );
  }
};

