const DateFormatter = ({ value }: any) => {
  let text = value;
  if (text) {
    try {
      text = moment(value).format('MMM DD, YYYY');
    } catch (error) {
      text = value;
    }
  } else {
    text = '';
  }

  return text;
};

const DateCellDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={DateFormatter} {...props} />
);

const TimeFormatter = ({ value }: any) => {
  let text = value;
  if (text) {
    try {
      text = moment(value).format('hh:mm:ss A');
    } catch (error) {
      text = value;
    }
  } else {
    text = '';
  }

  return text;
};

const TimeCellDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={TimeFormatter} {...props} />
);

