import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions, Grid,
    Button, IconButton} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

interface ErrorWithListComponentProps {
    isModalOpen: boolean;
    id: string;
    message: string;
    title: string;
    data: any;
    onClose: () => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'100%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
    },
    dialogContentText :{
        fontSize: '0.875rem'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        width: '350px'
    },
    dialogTitle: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600
    },
    dialogAction: {
        marginBottom: theme.spacing(2),
        marginTop: theme.spacing(2),
        justifyContent:'center'
    },
    dialogContentContainer: {
        width: '100%'
    },
    boldText:{
        fontWeight: 'bold'
    },
    cancelButton: {
        width:115,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    list:{
        textAlign: "center"
    }
}));
  

export const ErrorWithListComponent: React.FC<ErrorWithListComponentProps> = (
	props: ErrorWithListComponentProps,
): JSX.Element => {
    const {
        isModalOpen,
        id
    } = props;
    const classes = useStyles();
    return (
        <Dialog
            id={id}
            open={isModalOpen}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs>
                        <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                            {props.title}
                            <IconButton aria-label="close" className={classes.closeButton} onClick={props.onClose}>
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                    </Grid>
                    <Grid item xs className={classes.dialogContentContainer}>
                        <DialogContent className={classes.dialogContent} style={{padding: '0px 20px 10px 20px'}}>
                            <DialogContentText className={classes.dialogContentText}>
                                <Grid container direction={"column"} item xs={12}>
                                    <Grid item xs>
                                        {props.message}
                                    </Grid>
                                    { props.data.map((data:string, idx: number) => (
                                        <Grid className={classes.list} item xs={12} key={idx}>
                                            {data}
                                        </Grid>
                                    ))}
                                </Grid>
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            <Button className={classes.cancelButton} variant="contained" color="secondary" onClick={props.onClose}>
                                Okay
                            </Button>
                        </DialogActions>
                    </Grid>
                </Grid>
            </div>
        </Dialog>
    )
}

ErrorWithListComponent.defaultProps = {
    isModalOpen: false,
    id: 'modal_id'
};
