import * as React from 'react';

import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    IconButton,
    Grid,
    Typography,
    FormControlLabel,
    Switch,
    InputLabel,
    Button,
    TextField,
    Radio,
    RadioGroup
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/core/styles';
import {
    KeyboardDatePicker,
    MuiPickersUtilsProvider,
} from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import { CustomAutocomplete } from 'Components/UI/CustomAutocomplete';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons';
import moment from 'moment';
import { Utils } from '@hims/core';


const useStyles = makeStyles(theme => ({
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    dialog: {
        align: 'center',
        justify: 'center',
        width: '95%',
        fontFamily: 'usual',
    },
    dialogContent: {
        paddingTop: '0px',
        width: '100%',
        height: 400,
    },
    dialogTitle: {
        paddingTop: theme.spacing(4),
    },
    dialogFirstTitle: {
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px',
    },
    dialogAction: {
        paddingTop: theme.spacing(8),
        paddingBottom: theme.spacing(4),
    },
    cancelButton: {
        minWidth: 129,
        marginRight: 25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: '#FFFFFF',
    },
    nextMemberButton: {
        minWidth: 129,
        backgroundColor: '#3AB77D',
    },
    terminateButton: {
        minWidth: 129,
        backgroundColor: '#FD5451',
        '&:hover': {
            backgroundColor: '#fd2724',
        },
    },
    labelRemarks: {
        fontSize: '12px',
        paddingBottom: theme.spacing(1),
        color: '#272E4C',
    },
    labelSource: {
        fontSize: '12px',
        color: '#272E4C',
        padding: '2.75px 20px 0 0',
    },
    memberName: {
        fontSize: '13px',
        paddingBottom: theme.spacing(2),
        fontWeight: 600,
    },
    prevButtonContainer: {
        display: 'flex',
        alignItems: 'center',
        '&:hover': {
            cursor: 'pointer',
            textDecoration: 'underline'
        }
    },
    prevIcon: {
    },
    prevButton: {
        fontSize: '12px',
        fontWeight: 600,
        paddingLeft: '10px'
    }
}));

interface MemberTerminateBatchesModalProps {
    id: string;
    isModalOpen: boolean;
    memberData: any[];
    onSubmit: (payload) => void;
    onClose: () => void;
}

export const MemberTerminateBatchesModal: React.FC<MemberTerminateBatchesModalProps> = (
    props: MemberTerminateBatchesModalProps
): JSX.Element => {
    const classes = useStyles();
    const [checkedSwitch, setCheckedSwitch] = React.useState<boolean>(true);
    const [activeMemberIndex, setActiveMemberIndex] = React.useState<number>(0);
    const [payload, setPayload] = React.useState<any[]>([])
    const [terminateData, setTerminateData] = React.useState<any>({
        termination_date: moment().toISOString(),
        endorsed_date: moment().toISOString(),
        endorsed_by: '',
        endorser_name: '',
        endorser_email: '',
        channel: '',
        reason: '',
        ticket_no: '',
        source: 'Internal',
        terminated_by: ''
    })
    console.log('memberData', props.memberData);
    React.useEffect(() => {
        Utils.StorageService('user_data', 'full_name').then((data: any) => {
            setTerminateData({
                ...terminateData,
                terminated_by: data.result
            })
		}).catch((err) => {
			console.log(err);
			return false;
		})
    },[])


    const handleMemberData = (currentIndex: number, nextIndex: any) => {
        let payload_data: any[] = payload;

        if (payload_data.length > 0 && currentIndex + 1 <= payload_data.length) {
            payload_data.map((data, index) => {
                console.log(data);
                if (index === currentIndex) {
                    payload_data.splice(index, 1);
                    payload_data.splice(index, 0, terminateData);
                }
            })
        } else {
            payload_data.push(terminateData);
        }

        setPayload(payload_data);

        if (nextIndex === 'submit') {
            handleTerminateData(payload_data); //Submit form
        } else {
            if (payload_data[nextIndex]) {
                setTerminateData(payload_data[nextIndex]);
            } else {
                setTerminateData({
                    ...terminateData,
                    termination_date: moment().toISOString(),
                    endorsed_date: moment().toISOString(),
                    endorsed_by: '',
                    endorser_name: '',
                    endorser_email: '',
                    channel: '',
                    reason: '',
                    ticket_no: '',
                    source: 'Internal',       
                })
            }
        }
       
    }

    const handleTerminateData = (data: any) => {
        let payload_data = {
            data: data,
            principal: []
        };
        let memberIds: any  = [];
        let principal: any = [];

        payload_data.data.map(item => {
            item['channel'] = item.channel ? item.channel.label : ''
        })

        props.memberData.length > 0 && props.memberData.map((member: any) => {
            memberIds.push(member.id);
            principal.push({
                name: member.member_name,
                id: member.id,
            });
        }) 

        payload_data['principal'] = principal;

        if (checkedSwitch) {
            payload_data.data = data[activeMemberIndex]
            payload_data.data['memberIds'] = memberIds;
            payload_data.data = [payload_data.data]
        }
        console.log(payload_data);
        props.onSubmit(payload_data)
        props.onClose()
    }

    return (
        <Dialog
            id={props.id}
            maxWidth={'xs'}
            open={props.isModalOpen}
            scroll={'paper'}
            aria-labelledby="modal-title"
            className={classes.dialog}
        >
            <DialogTitle
                className={classes.dialogTitle}
                disableTypography={true}
                id="modal-title"
            >
                <div className={classes.dialogFirstTitle}>Termination Details</div>
                <IconButton
                    id='client_close_button'
                    data-cy='client_close_button'
                    aria-label="close"
                    className={classes.closeButton}
                    onClick={() => { props.onClose() }}
                >
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid container spacing={2}>
                    {
                        props.memberData && props.memberData.length > 1 ?
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={checkedSwitch}
                                            onChange={(e: any) => setCheckedSwitch(e.target.checked)}
                                            color="primary"
                                        />
                                    }
                                    label="Apply to all selected members"
                                />
                            </Grid> : null
                    }
                    {
                        !checkedSwitch || props.memberData && props.memberData.length === 1 ?
                            <Grid item xs={12}>
                                <Typography className={classes.memberName}>
                                    {props.memberData && props.memberData[activeMemberIndex].member_name}
                                </Typography>
                            </Grid> : null
                    }
                </Grid>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <RadioGroup
                            row
                            value={terminateData.source}
                            onChange={(e: any) => {
                                setTerminateData({
                                    ...terminateData,
                                    source: e.target.value
                                })
                            }}
                        >
                            <Typography className={classes.labelSource}>Source</Typography>
                            <FormControlLabel value="Internal" control={<Radio color={'primary'} />} label="Internal" />
                            <FormControlLabel value="External" control={<Radio color={'primary'} />} label="External" />
                        </RadioGroup>
                    </Grid>
                    <Grid item xs={6}>
                        <InputLabel className={classes.labelRemarks}>Date of Termination</InputLabel>
                        <MuiPickersUtilsProvider utils={MomentUtils}>
                            <KeyboardDatePicker
                                autoOk
                                value={terminateData.termination_date}
                                error={false}
                                format={'MMMM DD, YYYY'}
                                variant='dialog'
                                inputVariant={'outlined'}
                                invalidDateMessage=""
                                minDate={new Date()}
                                // maxDate={maxDate}
                                fullWidth
                                onChange={(e: any) => {
                                    setTerminateData({
                                        ...terminateData,
                                        termination_date: moment(e).toISOString(),
                                    })
                                }}
                            />
                        </MuiPickersUtilsProvider>
                    </Grid>
                    <Grid item xs={6}>
                        <InputLabel className={classes.labelRemarks}>Endorsed Date</InputLabel>
                        <MuiPickersUtilsProvider utils={MomentUtils}>
                            <KeyboardDatePicker
                                autoOk
                                value={terminateData.endorsed_date}
                                error={false}
                                format={'MMMM DD, YYYY'}
                                variant='dialog'
                                inputVariant={'outlined'}
                                invalidDateMessage=""
                                fullWidth
                                onChange={(e: any) => {
                                    setTerminateData({
                                        ...terminateData,
                                        endorsed_date: moment(e).toISOString(),
                                    })
                                }}
                            />
                        </MuiPickersUtilsProvider>
                    </Grid>
                    <Grid item xs={6}>
                        {
                            terminateData.source === 'Internal' ? (
                                <>
                                    <InputLabel className={classes.labelRemarks}>Endorsed by</InputLabel>
                                    <TextField
                                        value={terminateData.endorsed_by ? terminateData.endorsed_by.label : ''}
                                        error={false}
                                        variant={'outlined'}
                                        fullWidth
                                        onChange={(e: any) => {
                                            setTerminateData({
                                                ...terminateData,
                                                endorsed_by: e.target.value
                                            })
                                        }}
                                    />
                                </>
                            ) : (<CustomAutocomplete
                                id={'client_member_data_endorsedby'}
                                labelSize="small"
                                options={[
                                    { value: 'Client', label: 'Client' },
                                    { value: 'Broker', label: 'Broker' },
                                ]}
                                label={'Endorsed by'}
                                value={terminateData.endorsed_by ? terminateData.endorsed_by : ''}
                                onChange={(option: any) => {
                                    setTerminateData({
                                        ...terminateData,
                                        endorsed_by: option.value
                                    })
                                }}
                                error={false}
                                error_message={''}
                            />)
                        }
                    </Grid>
                    <Grid item xs={6}>
                        <CustomAutocomplete
                            id={'client_member_data_sentvia'}
                            labelSize="small"
                            options={[
                                { value: 'Email', label: 'Email' },
                                { value: 'Ticket', label: 'Ticket' },
                                { value: 'Portal', label: 'Portal' },
                                { value: 'Fax', label: 'Fax' },
                            ]}
                            label={'Sent via'}
                            value={terminateData.channel ? terminateData.channel : ''}
                            onChange={(option: any) => {
                                console.log(option)
                                setTerminateData({
                                    ...terminateData,
                                    channel: option
                                })
                            }}
                            error={false}
                            error_message={''}
                        />
                    </Grid>
                    {
                        terminateData.source === 'External' ?
                            <>
                                <Grid item xs={6}>
                                    <InputLabel className={classes.labelRemarks}>Person who endorsed</InputLabel>
                                    <TextField
                                        value={terminateData.endorser_name}
                                        error={false}
                                        variant={'outlined'}
                                        fullWidth
                                        onChange={(e: any) => {
                                            setTerminateData({
                                                ...terminateData,
                                                endorser_name: e.target.value
                                            })
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <InputLabel className={classes.labelRemarks}>Endorser email</InputLabel>
                                    <TextField
                                        value={terminateData.endorser_email}
                                        error={false}
                                        variant={'outlined'}
                                        fullWidth
                                        onChange={(e: any) => {
                                            setTerminateData({
                                                ...terminateData,
                                                endorser_email: e.target.value
                                            })
                                        }}
                                    />
                                </Grid>
                            </> : null
                    }
                    <Grid item xs={6}>
                        <CustomAutocomplete
                            id={'client_member_data_reason'}
                            labelSize="small"
                            options={[
                                { value: 'Death', label: 'Death' },
                                { value: 'Resignation', label: 'Resignation' },
                                { value: 'Non - affiliation', label: 'Non - affiliation' },
                                { value: 'Enrollment to other HMO', label: 'Enrollment to other HMO', },
                                { value: 'Other', label: 'Other' },
                            ]}
                            label={'Reason'}
                            value={terminateData.reason ? terminateData.reason : ''}
                            onChange={(option: any) => {
                                setTerminateData({
                                    ...terminateData,
                                    reason: option.value
                                })
                            }}
                            error={false}
                            error_message={''}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <InputLabel className={classes.labelRemarks}>Ticket Number</InputLabel>
                        <TextField
                            value={terminateData.ticket_no ? terminateData.ticket_no : ''}
                            error={false}
                            variant={'outlined'}
                            fullWidth
                            onChange={(e: any) => {
                                setTerminateData({
                                    ...terminateData,
                                    ticket_no: e.target.value
                                })
                            }}
                        />
                    </Grid>
                </Grid>
                {
                    activeMemberIndex !== 0 ?
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <Typography 
                                    className={classes.prevButtonContainer}
                                    onClick={() => {
                                        handleMemberData(activeMemberIndex, activeMemberIndex - 1)
                                        setActiveMemberIndex(activeMemberIndex - 1);
                                    }}
                                >
                                    <FontAwesomeIcon size={'1x'} icon={faChevronLeft} className={classes.prevIcon} />
                                    <span className={classes.prevButton}>Prev Member</span>
                                </Typography>
                            </Grid>
                        </Grid> : null
                }

            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <Button
                            className={classes.cancelButton}
                            variant="contained"
                            color="primary"
                            onClick={() => props.onClose()}
                        >Cancel</Button>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        {
                            checkedSwitch || props.memberData && props.memberData.length === 1 || activeMemberIndex + 1 === props.memberData.length ?
                                (
                                    <Button
                                        className={classes.terminateButton}
                                        variant="contained"
                                        color="primary"
                                        onClick={() => handleMemberData(activeMemberIndex, 'submit')}
                                    >Submit</Button>
                                ) : (
                                    <Button
                                        className={classes.nextMemberButton}
                                        variant="contained"
                                        color="primary"
                                        onClick={() => {
                                            handleMemberData(activeMemberIndex, activeMemberIndex + 1)
                                            setActiveMemberIndex(activeMemberIndex + 1);
                                        }}
                                    >Next Member</Button>
                                )
                        }
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    )
}