import 
    // React, 
    {
     useState, 
     useEffect 
    } from "react";
import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Grid,
  Button,
  IconButton,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import clsx from 'clsx';

function useOnlineStatus() {
  const [online, setOnline] = useState(true);

  useEffect(() => {
    window.addEventListener("online", handler);
    window.addEventListener("offline", handler);

    // cleanup
    return () => {
      window.removeEventListener("online", handler);
      window.removeEventListener("offline", handler);
    }
  });

  function handler(event) {
    setOnline(navigator.onLine);

    if (event.type === "online") {
      // handle stuffs when browser resume online
    } else {
      // handle stuffs when browser goes offline
    }
  }

  return { online }
}


const useStyles = makeStyles(theme => ({
    root: {
      flexWrap: 'wrap',
    },
    dialog: {
      align: 'center',
      justify: 'center',
      width: '100%',
      fontFamily: 'usual',
    },
    dialogContent: {
      paddingTop: '0px',
    },
    dialogContentText: {
      fontSize: '0.875rem',
    },
    container: {
      paddingRight: '15px',
      paddingLeft: '15px',
      maxWidth: '380px',
      maxHeight: '250px'
    },
    dialogTitle: {
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(2),
      fontSize: '16px',
      color: '#272E4C',
      fontWeight: 600,
    },
    dialogAction: {
      marginBottom: theme.spacing(2),
      marginTop: theme.spacing(2),
      justifyContent: 'center',
    },
    boldText: {
      fontWeight: 'bold',
    },
    cancelButton: {
      marginRight: 25,
      paddingLeft: '24px',
      paddingRight: '24px',
      border: '1px solid #3AB77D',
      color: '#3AB77D',
      backgroundColor: '#FFFFFF',
    },
    submitButton: {
      marginLeft: 25,
      paddingLeft: '24px',
      paddingRight: '24px',
      textTransform: 'none',
    },
    primary: {
      border: '1px solid #3AB77D',
      color: '#3AB77D',
      backgroundColor: '#FFFFFF',
    },
    danger: {
      color: '#FFFFFF',
      border: '1px solid #fd5451',
      backgroundColor: '#fd5451',
      '&:hover': {
        backgroundColor: '#fd2724',
      },
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  }));
  
  interface LostInternetModalProps {
    id: string;
    isModalOpen: boolean;
    // modalTitle: string;
    // modalMessage: string;
    variant?: 'primary' | 'danger' | undefined;
    closeText?: string;
    confirmText?: string;
    onClose: () => void;
    // onConfirm: () => void;
  }
  
  export const LostInternetModal: React.FC<LostInternetModalProps
  > = (props: LostInternetModalProps): JSX.Element => {
    const { isModalOpen, id } = props;
    const classes = useStyles();
    const [variant, setVariant] = React.useState('');
    const status = useOnlineStatus();
  
    React.useEffect(() => {
      if (props.variant) {
        setVariant(props.variant);
      }
    }, [props.variant]);
  
    return (
      <Dialog
        id={id}
        open={isModalOpen && !status.online}
        aria-labelledby="modal-title"
        className={classes.dialog}
        style={{ zIndex: 9999 }}
      >
        <div>
          <Grid
            container
            className={classes.container}
            justify="flex-start"
            alignItems="flex-start"
            direction="column"
          >
            <Grid item xs>
              <DialogTitle
                className={classes.dialogTitle}
                disableTypography={true}
                id="modal-title"
              >
                Connection Error
                <IconButton
                  aria-label="close"
                  className={classes.closeButton}
                  onClick={props.onClose}
                >
                  <CloseIcon />
                </IconButton>
              </DialogTitle>
            </Grid>
            <Grid item xs>
              <DialogContent
                className={classes.dialogContent}
                style={{ padding: '0px 15px 0px 15px' }}
              >
                <DialogContentText className={classes.dialogContentText}>
                  Slow or no internet connection. Please check your internet setting.
                </DialogContentText>
              </DialogContent>
              <DialogActions className={classes.dialogAction}>
                <Button
                  className={clsx(classes.submitButton, classes[variant])}
                  variant="contained"
                  onClick={props.onClose}
                >
                  OK
                </Button>
              </DialogActions>
            </Grid>
          </Grid>
        </div>
      </Dialog>
    );
  };
  
  LostInternetModal.defaultProps = {
    id: 'confirm-modal-id',
    isModalOpen: false,
    // modalTitle: '',
    // modalMessage: '',
    variant: 'primary',
  };
  