import React from 'react';

import { IClientsProfilePage } from './IClientsProfilePage';
import { ClientsProfileComponent } from './Component/ClientsProfileComponent';
import { API } from 'Pages/API';
import { LostInternetModal } from 'Components/UI'
// import { Components } from '@hims/core';

import { Loader } from './../../Components/UI/LoadingIndicator';
export class ClientsProfilePage extends React.Component<
  IClientsProfilePage.IProps,
  IClientsProfilePage.IState
> {
  constructor(props: IClientsProfilePage.IProps) {
    super(props);
    this.state = {
      client_info: [],
      loading: false,
      check_internet_flag: false,
    };
  }

  componentDidMount() {
    this.getClientsInfoAPI(this.props.match.params.client_id);
  }
  componentWillMount() {
    this.getClientsInfoAPI(this.props.match.params.client_id);
  }

  handleCloseLostInternetConnectionModal = () =>{
    this.setState({
        check_internet_flag: false
    })
  }
  getClientsInfoAPI = (client_id: string) => {
    this.setState({
      loading: true,
      check_internet_flag: true,
    });
    API.getClientsInfo(client_id)
      .then(response => {
        console.log('client info response', response);
        this.setState({
          client_info: response.client,
          loading: false,
          check_internet_flag: false,
        });
      }).catch(error => {
        console.log('client info response error', error);
        this.setState({
          client_info: [],
          loading: false,
          check_internet_flag: false,
        });
      })
  };

  public render(): JSX.Element {
    return (
      <div>
        {this.state.loading ? <Loader /> : null}
        <ClientsProfileComponent data={this.state.client_info} />
        <LostInternetModal
            id="Lost Internet Modal"
            isModalOpen={this.state.check_internet_flag}
            onClose={this.handleCloseLostInternetConnectionModal} 
        />
      </div>
    );
  }
}
