import React from 'react';
import { Link } from 'react-router-dom';
import { bindActionCreators, Dispatch } from 'redux';
import {
  Avatar,
  Badge,
  Box,
  Grid,
  List,
  ListItem,
  ListItemText,
  Typography,
} from '@material-ui/core/';
import EditIcon from '@material-ui/icons/Edit';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircle } from '@fortawesome/free-solid-svg-icons';
import { lowerCase } from 'lodash';
import clsx from 'clsx';

import { IPersonProfile } from 'Pages/PersonProfile/IPersonProfile';
import { PersonProfileActions } from 'Pages/PersonProfile/PersonProfileActions';
import { Information } from 'Pages/PersonProfile/Information';
import { API } from 'Pages/API';
import { Store } from 'Components/Stores/IStore';
import { Loader } from 'Components/UI/LoadingIndicator';
import { ModalComponent } from 'Components/UI/ModalComponent';
import { Components } from '@hims/core';

import './style.scss';

export class PersonProfile extends React.Component<
  IPersonProfile.IProps,
  IPersonProfile.IState
> {
  constructor(props: IPersonProfile.IProps) {
    super(props);

    this.state = {
      check_internet_flag: false,
      memberDetails: {},
      isLoading: false,
      sideNav: 'information',
      isModalOpen: false,
      modalTitle: '',
      modalMessage: '',
    };
  }

  componentDidMount() {
    this.getMemberData();
  }

  getMemberData = () => {
    this.setState({
      isLoading: true,
      check_internet_flag: true,
    });

    API.getMemberData(this.props.match.params.member_id)
      .then(response => {
        if (response !== null && response !== undefined) {
          this.setState({
            check_internet_flag: false,
            isLoading: false,
            memberDetails: response,
          });
        } else {
          this.setState({
            isLoading: false,
            isModalOpen: true,
            modalTitle: 'Person Profile Error',
            modalMessage: "Unable to retrieve current member's person profile.",
          });
        }
      })
      .catch(() => {
        this.setState({
          isLoading: false,
          isModalOpen: true,
          modalTitle: 'Person Profile Error',
          modalMessage: "Unable to retrieve current member's person profile.",
        });
      });
  };

  getStatusColor = (status: string) => {
    switch (lowerCase(status)) {
      case 'for validation':
      case 'pending':
        return '#FAB435';
      case 'terminated':
      case 'cancelled':
        return '#BC2206';
      case 'suspended':
        return '#FD5451';
      case 'disapproved':
        return '#8034AD';
      case 'expired':
        return 'expired';
      default:
        return '#3AB77D';
    }
  };

  setSideNav = tab => {
    const disabledTabs = ['history', 'utilization', 'tickets', 'audit_logs'];
    if (disabledTabs.indexOf(tab) === -1) {
      this.setState({
        sideNav: tab,
      });
    }
  };

  closeModal = () => {
    this.setState({
      isModalOpen: false,
    });
  };

  public render(): JSX.Element {
    const memberId = this.props.match.params.member_id;
    const {
      isLoading,
      sideNav,
      memberDetails,
      isModalOpen,
      modalTitle,
      modalMessage,
    } = this.state;

    let memberName = '_';
    let isVip = false;
    if (memberDetails) {
      const { first_name, middle_name, last_name, is_vip } = memberDetails;
      const firstName = first_name ? first_name : '';
      const mi = middle_name ? middle_name.charAt(0) + '.' : '';
      const lastName = last_name ? last_name : '';

      memberName = `${firstName} ${mi} ${lastName}`;
      isVip = !!is_vip;
    }

    const breadcrumbItems = [
      {
        label: 'Members',
        url: '#/membership/members',
      },
      {
        label: memberName,
        url: '#/membership/view-member/' + memberId,
      },
      {
        label: 'Person Profile',
        url: '',
      },
    ];

    const BlankTemplate = () => {
      return (
        <Grid container>
          <Grid item xs={12}>
            <Typography>Content</Typography>
          </Grid>
        </Grid>
      );
    };

    return (
      <div className={clsx('PersonProfile')}>
        {isLoading ? <Loader /> : null}
        <Grid container>
          <Grid item xs={12}>
            <Grid container className={clsx('page-header')}>
              <Grid item xs={10}>
                <Grid container>
                  <Grid item xs={12}>
                    <Components.UI.BreadcrumbsComponent
                      items={breadcrumbItems}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Typography className={clsx('header-member-name')}>
                      {memberName}
                      {memberDetails !== undefined &&
                      memberDetails !== null &&
                      memberDetails.member_status !== undefined ? (
                        <span className={clsx('member-status-span')}>
                          <FontAwesomeIcon
                            className="fa"
                            icon={faCircle}
                            style={{
                              color: this.getStatusColor(
                                memberDetails.member_status,
                              ),
                              fontSize: '12px',
                              marginBottom: '1px',
                            }}
                          />
                          <span className={clsx('member-status-text')}>
                            {' '}
                            {memberDetails.member_status}{' '}
                          </span>
                        </span>
                      ) : null}
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={2}>
                <Box
                  display="flex"
                  alignItems="center"
                  justifyContent="flex-end"
                >
                  <Typography
                    data-cy={'person_profile_edit_member_btn'}
                    id={'person_profile_edit_member_btn'}
                    className={clsx('header-edit-member-btn')}
                  >
                    Edit Member
                  </Typography>
                </Box>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={12}>
            <Grid container className={clsx('page-content')}>
              <Grid item xs={12} md={2} className={clsx('side-menu')}>
                <Box>
                  <Badge
                    badgeContent={
                      <EditIcon
                        style={{
                          color: 'FFFFFF',
                          backgroundColor: '#3AB77D',
                          height: 24,
                          width: 24,
                          padding: 4,
                          borderRadius: 12,
                          marginLeft: -32,
                          marginTop: -32,
                        }}
                      />
                    }
                    anchorOrigin={{
                      horizontal: 'right',
                      vertical: 'bottom',
                    }}
                  >
                    <Avatar
                      src="temp_member_photo.png"
                      alt="avatar"
                      className={clsx(
                        isVip
                          ? 'side-menu-member-photo-vip'
                          : 'side-menu-member-photo',
                      )}
                    />
                  </Badge>
                </Box>
                <Box style={{ marginTop: 16 }}>
                  <Link
                    data-cy={'person_profile_view_member_link'}
                    id={'person_profile_view_member_link'}
                    to={`/membership/view-member/${this.props.match.params.member_id}`}
                    className={clsx('view-profile-link')}
                  >
                    View Member Profile
                  </Link>
                </Box>
                <Box style={{ marginTop: 32 }}>
                  <List className={clsx('side-menu-list')}>
                    <ListItem>
                      <ListItemText
                        data-cy={'person_profile_information_item'}
                        id={'person_profile_information_item'}
                        className={clsx(
                          sideNav === 'information'
                            ? 'side-menu-list-item-selected'
                            : 'side-menu-list-item',
                        )}
                        onClick={() => {
                          this.setSideNav('information');
                        }}
                      >
                        Information
                      </ListItemText>
                    </ListItem>
                    <ListItem disabled>
                      <ListItemText
                        data-cy={'person_profile_acc_history_item'}
                        id={'person_profile_acc_history_item'}
                        className={clsx(
                          sideNav === 'history'
                            ? 'side-menu-list-item-selected'
                            : 'side-menu-list-item',
                        )}
                        onClick={() => {
                          this.setSideNav('history');
                        }}
                      >
                        Accounts History
                      </ListItemText>
                    </ListItem>
                    <ListItem disabled>
                      <ListItemText
                        data-cy={'person_profile_utilization_item'}
                        id={'person_profile_utilization_item'}
                        className={clsx(
                          sideNav === 'utilization'
                            ? 'side-menu-list-item-selected'
                            : 'side-menu-list-item',
                        )}
                        onClick={() => {
                          this.setSideNav('utilization');
                        }}
                      >
                        Utilization
                      </ListItemText>
                    </ListItem>
                    <ListItem disabled>
                      <ListItemText
                        data-cy={'person_profile_tickets_item'}
                        id={'person_profile_tickets_item'}
                        className={clsx(
                          sideNav === 'tickets'
                            ? 'side-menu-list-item-selected'
                            : 'side-menu-list-item',
                        )}
                        onClick={() => {
                          this.setSideNav('tickets');
                        }}
                      >
                        Tickets
                      </ListItemText>
                    </ListItem>
                    <ListItem disabled>
                      <ListItemText
                        data-cy={'person_profile_audit_logs_item'}
                        id={'person_profile_audit_logs_item'}
                        className={clsx(
                          sideNav === 'audit_logs'
                            ? 'side-menu-list-item-selected'
                            : 'side-menu-list-item',
                        )}
                        onClick={() => {
                          this.setSideNav('audit_logs');
                        }}
                      >
                        Audit Logs
                      </ListItemText>
                    </ListItem>
                  </List>
                </Box>
              </Grid>
              <Grid item xs={12} md={10} style={{ paddingBottom: 32 }}>
                {sideNav === 'information' ? (
                  <Information memberDetails={memberDetails} />
                ) : sideNav === 'history' ? (
                  <BlankTemplate />
                ) : sideNav === 'utilization' ? (
                  <BlankTemplate />
                ) : sideNav === 'tickets' ? (
                  <BlankTemplate />
                ) : sideNav === 'audit_logs' ? (
                  <BlankTemplate />
                ) : null}
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <ModalComponent
          id="person_info_modal"
          isModalOpen={isModalOpen}
          title={modalTitle}
          message={modalMessage}
          onClose={this.closeModal}
        />
      </div>
    );
  }
}

const mapStateToProps = (state: Store) => state.home;
const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(PersonProfileActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
