  public static searchMembers(
    limit: number,
    offset: number,
    query: string,
    filters: any,
    sorting: any,
    clientId?: any,
  ) {
    let CLIENT_URL =
      API_URL +
      `members/search?query=${query}&filters=${JSON.stringify(
        filters,
      )}&sorting=${JSON.stringify(sorting)}`;

    if (limit !== 0) {
      CLIENT_URL += `&limit=${limit}&offset=${offset}`;
    }

    if (!isNil(clientId)) {
      CLIENT_URL += `&clientId=${clientId}`;
    }

    return fetch(CLIENT_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
      });
  }

  public static getMemberListReport(query: string, filters: any, sorting: any) {
    const CLIENT_URL =
      API_URL +
      `members/generate-csv?query=${query}&filters=${JSON.stringify(
        filters,
      )}&sorting=${JSON.stringify(sorting)}`;

    return fetch(CLIENT_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => {
        const responseBlob = response.blob();
        const responseJSON = response.json();
        return responseJSON
          .then(res => {
            if (res.error !== undefined) {
              return responseJSON;
            } else {
              return responseBlob;
            }
          })
          .catch(res => {
            console.log('downloadExceptionReport error: ', res);
            return responseBlob;
          });
      })
      .catch(error => {
        console.log('downloadExceptionReport error: ', error);
      });
  }

