import React, { useState, useEffect } from 'react';
import { IconButton, Snackbar, SnackbarContent } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { amber } from '@material-ui/core/colors';
import CloseIcon from '@material-ui/icons/Close';

import PropTypes from 'prop-types';
import clsx from 'clsx';

const makeSnackbarStyles = makeStyles(theme => ({
  success: {
    backgroundColor: '#E6F5F6',
    borderColor: '#E6F5F6',
  },
  error: {
    backgroundColor: theme.palette.error.light,
    borderColor: theme.palette.error.light,
  },
  info: {
    backgroundColor: theme.palette.primary.main,
  },
  warning: {
    backgroundColor: amber[700],
  },
  icon: {
    fontSize: 20,
    color: '#363636',
  },
  iconVariant: {
    opacity: 0.9,
    marginRight: theme.spacing(1),
  },
  message: {
    color: '#272E4C',
    display: 'flex',
    alignItems: 'center',
  },
}));

function MySnackbarContentWrapper(props) {
  const classes = makeSnackbarStyles();
  const { className, message, onClose, variant, ...other } = props;

  return (
    <SnackbarContent
      className={clsx(classes[variant], className)}
      aria-describedby="client-snackbar"
      message={
        <span id="client-snackbar" className={classes.message}>
          {message}
        </span>
      }
      action={[
        <IconButton
          key="close"
          aria-label="close"
          color="inherit"
          onClick={onClose}
        >
          <CloseIcon className={classes.icon} />
        </IconButton>,
      ]}
      {...other}
    />
  );
}

MySnackbarContentWrapper.propTypes = {
  className: PropTypes.string,
  message: PropTypes.string,
  onClose: PropTypes.func,
  variant: PropTypes.oneOf(['error', 'info', 'success', 'warning']).isRequired,
};

interface SnackbarNotificationProps {
  isOpen: boolean;
  message: string;
  variant: string;
  onSnackbarClose: () => void;
}

export const SnackbarNotification: React.FC<SnackbarNotificationProps> = (
  props: SnackbarNotificationProps,
): JSX.Element => {
  const [isSnackbarOpen, setIsSnackbarOpen] = useState(props.isOpen);
  const [variant, setVariant] = useState(props.variant);
  const [snackbarMessage, setSnackbarMessage] = useState(props.message);

  useEffect(() => {
    setIsSnackbarOpen(props.isOpen);
    setVariant(props.variant);
    setSnackbarMessage(props.message);
  }, [props.isOpen, props.variant, props.message]);

  function onSnackbarClose() {
    if (props.onSnackbarClose) {
      props.onSnackbarClose();
    }
  }

  return (
    <Snackbar
      open={isSnackbarOpen}
      onClose={onSnackbarClose}
      autoHideDuration={6000}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'right',
      }}
    >
      <MySnackbarContentWrapper
        variant={variant}
        message={snackbarMessage}
        onClose={onSnackbarClose}
      />
    </Snackbar>
  );
};

SnackbarNotification.defaultProps = {
  isOpen: false,
  message: '',
  variant: 'success',
};
