import * as React from 'react';
import { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Typography,
  Grid,
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import { Components } from '@hims/core';

const InputTextField = Components.UI.InputTextField.InputTextField;



const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
    minWidth: '250px',
    width: '45vw',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '680px',
    width: 'auto',
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
  },
  mnameLabel: {
    textTransform: 'uppercase',
    fontSize: 12,
    fontWeight: 400,
    color: 'rgba(39, 49, 76, 0.6)',
    paddingBottom: 4,
  },
  mnameValue: {
    fontSize: 14,
    fontWeight: 600,
    color: 'rgba(39, 49, 76, 1)',
  },
  ticketNoLabel: {
    fontSize: 14,
    fontWeight: 400,
    color: 'rgba(39, 49, 76, 1)',
    paddingBottom: 4,
  },
  tblTitle: {
    fontSize: 14,
    fontWeight: 600,
    color: 'rgba(39, 49, 76, 1)',
  },
  tblSubtitle: {
    fontSize: 12,
    fontWeight: 400,
    color: 'rgba(39, 49, 76, 1)',
  },
  tblbox: {
    minHeight: 65,
    borderRadius: 0,
    boxShadow: '0px 3px 6px #272E4C19',
    padding: 0,
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    minWidth:129,
    marginRight:25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: "#FFFFFF"
  },
  submitButton: {
    minWidth:129,
    backgroundColor: '#3AB77D'
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
}));



interface ReactivateDependentModalProps {
  isModalOpen: boolean;
  id: string;
  memberName?: string;
  principalData?: any[];
  onClose: () => void;
  onSubmit: (data: any) => void;
}

export const ReactivateDependentModal: React.FC<ReactivateDependentModalProps> = (
  props: ReactivateDependentModalProps,
): JSX.Element => {
  
  const { 
    isModalOpen, 
    id,
    memberName,
    principalData,
  } = props;

  const classes = useStyles();

  const [ticketNum, setTicketNum] = useState<string>('');
  
  const handleOnClose = () => {
    props.onClose();
    setTicketNum('');
  }
  
  const handleSubmit = () => {
    props.onSubmit({
      ticketNum: ticketNum,
    });
    setTicketNum('');
  }

  function handleChangeTicketNum(value: string) {
    setTicketNum(value);
  }

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>
          Re-activation Details
        </div>
        <div className={classes.dialogSecondTitle}>
        </div>
        <IconButton
          data-cy={'reactive_dependent_modal_close_btn'}
          id={'reactive_dependent_modal_close_btn'}
          aria-label="close"
          className={classes.closeButton}
          onClick={props.onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          <Grid item xs={12} md={6}>
            <Typography className={classes.mnameLabel}>
              Dependent
            </Typography>
            <Typography className={classes.mnameValue}>
              {memberName}
            </Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography className={classes.ticketNoLabel}>
              Ticket Number
            </Typography>
            <InputTextField
              id={props.id+'-ticketno'}
              onChange={handleChangeTicketNum}
              error={false}
              errorMessage={''}
              value={ticketNum}
            />
          </Grid>
          <Grid item xs={12} style={{marginTop: 32, marginBottom: 16}}>
            <Typography className={classes.tblTitle}>
              Principal
            </Typography>
            <Typography className={classes.tblSubtitle}>
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Paper className={classes.tblbox}>
              <Table 
                data-cy={'reactive_dependent_modal_principal_table'}
                id={'reactive_dependent_modal_principal_table'}
                size="small"
              >
                <TableHead>
                  <TableRow>
                    <TableCell style={{width: '40%'}}>Member Name</TableCell>
                    <TableCell style={{width: '30%'}}>Relationship</TableCell>
                    <TableCell style={{width: '30%'}}>Status</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                {principalData && principalData.length > 0 ? (
                    principalData.map((row, index) => (
                      <TableRow key={index}>
                        <TableCell component="th" scope="row" style={{width: '40%'}}>
                          {
                            row['first_name'] +
                            ' ' +
                            (row['middle_name']
                              ? row['middle_name'].charAt(0) + '. '
                              : '') +
                            row['last_name']
                          }
                        </TableCell>
                        <TableCell style={{width: '30%'}}>
                          {row['relationship_to_principal']}
                        </TableCell>
                        <TableCell style={{width: '30%'}}>
                          {row['member_status']}
                        </TableCell>
                      </TableRow>
                    ))
                  ) : (
                    <TableRow>
                      <TableCell
                        component="th"
                        scope="row"
                        style={{ textAlign: 'center' }}
                        colSpan={3}
                      >
                        No enrolled principal
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </Paper>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid 
          container
          direction="row"
          justify="center"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{textAlign:'center'}}>
            <Button
              data-cy={'reactive_dependent_modal_cancel_btn'}
              id={'reactive_dependent_modal_cancel_btn'}
              className={classes.cancelButton}
              variant="contained"
              color="primary"
              onClick={handleOnClose}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{textAlign:'center'}}>
            <Button
              data-cy={'reactive_dependent_modal_btn'}
              id={'reactive_dependent_modal_btn'}
              className={classes.submitButton}
              variant="contained"
              color="primary"
              onClick={handleSubmit}
            >
              Reactivate
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

ReactivateDependentModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
  memberName: '',
  principalData: [],
};
