import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Button,
  IconButton,
  TextField,
  InputLabel,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import { InformationLabel } from 'Components/UI/InformationLabel';
import { CustomAutocomplete } from 'Components/UI/CustomAutocomplete';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
    paddingLeft: '24px',
    paddingRight: '24px',
    width: '30rem',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    // minWidth: '680px',
    // width: 'auto',
    // width: '30rem',
    // height: '30rem'
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
    marginRight: '64px',
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
    paddingTop: theme.spacing(2),
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    width: 115,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  approveButton: {
    minWidth: 129,
    marginRight: 25,
    color: '#FFFFFF',
    backgroundColor: '#3AB77D',
    // '&:hover': {
    //   background: '#3AB77D',
    // },
  },
  label: {
    fontSize: '14px',
    color: '#272E4C',
    fontWeight: 600,
    paddingTop: theme.spacing(2),
  },
  previousButton: {
    minWidth: 120,
    fontWeight: 600,
  },
  nextButton: {
    minWidth: 130,
    fontWeight: 600,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  tab: {
    textAlign: 'left',
    alignItems: 'left',
  },
}));

export type ActionData = 'Approve' | 'Disapprove';

export interface ApproveDependentProps {
  isModalOpen: boolean;
  memberName: string;
  remarks: string;
  possiblePrincipals: any[];
  id: string;
  row: number;
  action: ActionData;
  onAction: (
    row: number,
    selectedTempId: string,
    action: ActionData,
    remarks: string,
  ) => void;
  onClose: (row: number) => void;
}

export const ApproveDependentModal: React.FC<ApproveDependentProps> = (
  props: ApproveDependentProps,
): JSX.Element => {
  const { id } = props;
  const [isModalOpen, setIsModalOpen] = useState(props.isModalOpen);
  const [dropdownChoice, setDropdownChoice] = useState({
    value: '',
    temp_id: '',
    first_name: '',
    middle_name: '',
    last_name: '',
    suffix: '',
    date_of_birth: '',
    plan_type: '',
    civil_status: '',
    gender: '',
    batch_name: '',
    isValue: true,
  });
  const classes = useStyles();
  const [remarks, setRemarks] = useState('');

  useEffect(() => {
    setIsModalOpen(props.isModalOpen);
    if (props.isModalOpen) {
      setDropdownChoice({
        value: '',
        temp_id: '',
        first_name: '',
        middle_name: '',
        last_name: '',
        suffix: '',
        date_of_birth: '',
        plan_type: '',
        civil_status: '',
        gender: '',
        batch_name: '',
        isValue: true,
      });
      setRemarks('');
    }
  }, [props.isModalOpen]);

  const onApprove = () => {
    if (props.onAction) {
      props.onAction(props.row, dropdownChoice.temp_id, props.action, remarks);
    }
  };

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div
          className={classes.dialogFirstTitle}
        >{`${props.action} Member (Dependent)`}</div>
        <IconButton
          id="close_button"
          data-cy='close_button'
          aria-label="close"
          className={classes.closeButton}
          onClick={() => props.onClose(props.row)}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          <Grid item xs={12}>
            <InformationLabel
              name={'DEPENDENT NAME'}
              value={props.memberName}
            />
            <InformationLabel name={'CONFLICT'} value={props.remarks} />
            <div style={{ paddingTop: '2rem' }} />
            <CustomAutocomplete
              id={'add_details_contract'}
              data-cy='add_details_contract'
              options={props.possiblePrincipals}
              label={'Choose Principal'}
              value={dropdownChoice}
              error={false}
              error_message={''}
              formatOptionLabel={({
                first_name,
                middle_name,
                last_name,
                suffix,
                date_of_birth,
                plan_type,
                civil_status,
                gender,
                batch_name,
                isValue,
              }) => (
                  <div>
                    <div style={{ padding: '2px' }}>
                      {`${GlobalFunction.toNameFormat(
                        first_name,
                        middle_name,
                        last_name
                      )} ${suffix}`}
                    </div>
                    {!isValue ? (
                      <>
                        <div style={{ fontSize: '11px', padding: '2px' }}>
                          {`${GlobalFunction.toCommonDateString(
                            new Date(date_of_birth),
                          )}, ${plan_type}`}
                        </div>
                        <div style={{ fontSize: '11px', padding: '2px' }}>
                          {`${civil_status}, ${gender}, Batch ${batch_name}`}
                        </div>
                      </>
                    ) : null}
                  </div>
                )}
              onChange={(value: any) => {
                setDropdownChoice({
                  ...value,
                  isValue: true,
                });
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <InputLabel
              className={classes.label}
              htmlFor="action_modal_remarks_tf"
            >
              Remarks
            </InputLabel>
            <TextField
              style={{ marginTop: '8px', width: '100%' }}
              id="action_modal_remarks_tf"
              data-cy='action_modal_remarks_tf'
              margin="normal"
              variant="outlined"
              multiline={true}
              rows={10}
              value={remarks}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const { value } = e.target;
                setRemarks(value);
              }}
              inputProps={{ 'aria-label': 'bare' }}
            />
            <div style={{ paddingTop: '4rem' }} />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              id="cancel_button"
              data-cy='cancel_button'
              className={classes.cancelButton}
              variant="contained"
              color="secondary"
              onClick={() => props.onClose(props.row)}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              id="action_button"
              data-cy='action_button'
              className={classes.approveButton}
              style={
                props.action === 'Disapprove'
                  ? { background: '#FD5451' }
                  : undefined
              }
              variant="contained"
              color="primary"
              disabled={
                dropdownChoice.value === '' && props.action === 'Approve'
              }
              onClick={onApprove}
            >
              {props.action}
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

ApproveDependentModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
};
