import * as React from 'react';
import './style.scss';
import MomentUtils from '@date-io/moment';
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
  MaterialUiPickersDate,
} from '@material-ui/pickers';
import { InputLabel, FormHelperText } from '@material-ui/core/';

interface IProps {
  placeholder: string;
  value: any;
  id: string;
  onChange: (value: Date | null) => void;
  error: boolean;
  error_message: string;
  minDate?: Date | null;
  maxDate?: Date;
  minDateMessage?: string | null;
  isRequired?: boolean;
}

export class DatePicker extends React.Component<IProps> {
  constructor(props: IProps) {
    super(props);
  }

  render(): JSX.Element {
    return (
      <div className="date-picker">
        <InputLabel className="label" htmlFor={this.props.id}>
          {this.props.placeholder} {this.props.isRequired === true ? 
          <span style={{color: 'red'}}>&nbsp;*</span> : null}
        </InputLabel>
        <MuiPickersUtilsProvider utils={MomentUtils}>
          <KeyboardDatePicker
            autoOk
            id={this.props.id}
            className="text-field"
            variant="inline"
            format="MMM DD, YYYY"
            value={this.props.value}
            minDate={this.props.minDate ? this.props.minDate : undefined}
            maxDate={this.props.maxDate}
            minDateMessage={
              this.props.minDateMessage ? this.props.minDateMessage : undefined
            }
            onChange={(date: MaterialUiPickersDate | null) => {
              this.props.onChange(date ? date.toDate() : null);
            }}
            style={{fontSize: '16px', fontWeight: 'bold'}}
          />
        </MuiPickersUtilsProvider>
        {this.props.error ? (
          <FormHelperText
            id={this.props.id + '_error'}
            style={{ color: '#E53935' }}
          >
            {this.props.error_message}
          </FormHelperText>
        ) : null}
      </div>
    );
  }
}
