  /******* handles changes in global search *******/
  handleSearch(e: React.ChangeEvent<HTMLInputElement>) {
    const { value } = e.target;
    const { default_filters } = this.state;
    if (this.state.typingTimeout < -1) {
      if (value === '' && default_filters && default_filters.length === 0) {
        const table = this.generateDataForTable([]);
        this.setState({
          member_list: [],
          member_data_all: table,
          member_data: table,
          global_filter: value,
          totalCount: 0,
        });

        return;
      }
    }


    if (this.state.typingTimeout) {
      clearTimeout(this.state.typingTimeout);
    }
    this.setState({
      global_filter: value,
      page: 0,
      limit: 10,
      typing: false,
      typingTimeout: setTimeout(() => {
        this.searchMembers();
      }, 2000),
    });
  }

  handlePageChange = (currentPage: number) => {
    this.setState(
      {
        page: currentPage,
      },
      () => {
        this.searchMembers();
      },
    );
  };

  handlePageSizeChange = value => {
    const { totalCount } = this.state;
    const pageLimit = 1000;

    if (value === 0 && totalCount !== 0 && totalCount > pageLimit) {
      this.setState({
        isPageSizeModal: true,
      });
    } else {
      this.setState(
        {
          limit: value,
        },
        () => {
          this.searchMembers();
        },
      );
    }
  };

  // handle api filter by column
  handleFilterByColumn = (filter: any) => {
    const { global_filter } = this.state;

    filter = filter.filter(fltr => fltr.value !== '');
    console.log('filter', filter)

    if (this.state.typingTimeout < -1) {
      if (global_filter === '' && filter && filter.length === 0) {
        const table = this.generateDataForTable([]);
        this.setState({
          member_list: [],
          member_data_all: table,
          member_data: table,
          default_filters: filter,
          totalCount: 0,
        });

        return;
      }
    }


    if (this.state.typingTimeout) {
      clearTimeout(this.state.typingTimeout);
    }

    this.setState({
      default_filters: filter,
      page: 0,
      typing: false,
      typingTimeout: setTimeout(() => {
        this.searchMembers();
      }, 2000),
    });

    // TODO add filter api call
  };

