  searchMembers = () => {
    this.setState(
      {
        loading_state: true,
        check_internet_flag: true,
      },
      () => {
        const filters = cloneDeep(this.state.default_filters);
        for (const filterIdx in filters) {
          const filter = filters[filterIdx];
          if (
            !isNil(filter) &&
            filter.columnName === 'status' &&
            filter.value === 'All'
          ) {
            filter.value = '';
            filters[filterIdx] = filter;
            break;
          }
        }

        API.searchMembers(
          this.state.limit,
          this.state.limit * this.state.page,
          this.state.global_filter,
          filters,
          this.state.sorting,
          this.props.data._id,
        ).then(response => {
          console.log('searchMembers', response);
          if (response && response.error === undefined) {
            const table = this.generateDataForTable(response.data);
            this.setState({
              check_internet_flag: false,
              member_list: response,
              member_data_all: table,
              member_data: table,
              loading_state: false,
              totalCount: response.totalCount,
              statusPanelCount: response.statusCount,
              forPrintingCount: get(response, 'forPrintingCount', 0),
            });
          } else {
            const table = this.generateDataForTable([]);
            this.setState({
              member_list: [],
              member_data_all: table,
              member_data: table,
              loading_state: false,
              isModalOpen: true,
              modalTitle: 'Loading failed',
              modalMessage:
                'An error occured while loading the list of members.',
            });
          }
          console.log(this.state.statusPanelCount['Suspended'], 'suspended');
        });
      },
    );
  };

