  modal2Next = (data: any, files = []) => {
    const inititalPayload = {
      member_id: "",
      member_name:'',
      corporation: '',
      type:'',
      previous_status_to_restore:'',
      is_external: true,
      date_termination: new Date(),
      date_endorsed: new Date(),
      sent_via: "",
      ticket_no: "",
      endorsed_by: "",
      remarks: "",
      selectedDependent:[],
      documents: []
    }
    
    const newData = Object.assign(inititalPayload,data)
    console.log('modal2next',newData)
    const newPayload = [...this.state.reactivateMemberData, newData];
    const newFiles = [...this.state.reactivateMemberFiles, ...files.map((file: any) => {
      return {
        member_id:data.member_id,
        file:{
          name: file.name,
          type: file.type,
          file: file,
          field: 'OR'
        }
      }
    })];
    console.log('newFiles',newFiles)
    
    this.setState({
      reactivateMemberData:newPayload,
      reactivateMemberFiles:newFiles,
      isRMModal:false,
    })

    if (this.state.RMCount+1 < this.state.memberStatusActionArrays.forReactivation.length) {
      setTimeout(() => {
        this.setState({
          RMCount: this.state.RMCount+1, 
          isRMModal: true,
        });
      }, 500);
    } else {
      this.setState({
        isRMModal: false,
        isCRMModalOpen:true,
      });
    }
  };

  handleCloseConfirmReactivateModal = () => {
    this.setState({
      isCRMModalOpen:false
    })
  }

  handleConfirmReactivate = () =>{
      const filesPayload = new FormData();

      let filesUploadedPayload: any = [];
      this.state.reactivateMemberFiles.map(file => {
        filesUploadedPayload.push(file.file.file);
      });

      this.state.reactivateMemberFiles.map(file => {
        filesPayload.append(`${file.file.field}`, file.file.file);
      });

      if ( this.state.reactivateMemberFiles.length > 0) {
        let filecount:number = 0
        this.state.reactivateMemberFiles.forEach(item=>{
          API.uploadMemberDocument(item.member_id,'others',item.file.file)
          .then(response=>{
            console.log('uploadmember',response)
            const indx =  this.state.reactivateMemberData.findIndex(item2=>item2.member_id===item.member_id)
            this.state.reactivateMemberData[indx]['documents'].push(response)
            filecount++
            if(filecount=== this.state.reactivateMemberFiles.length){
              API.batchPrinting( this.state.reactivateMemberData)
              .then(response2 => {
                console.log(response2);
              })
              .catch(error => {
                console.log('error in batch print', error);
              });
            }
          })
          .catch(error=>{
            console.log('upload error',error)
          })
        })
      } else {
        API.batchPrinting( this.state.reactivateMemberData)
          .then(response => {
            console.log(response);
          })
          .catch(error => {
            console.log('error in batch print', error);
          });
      }
  }

