const Select = ({ id, value, items, disabled }: any) => {
  const cellSelectClasses = cellSelectStyles();
  return (
    <MaterialUI.Select
      id={id}
      data-cy={id}
      classes={cellSelectClasses}
      value={value}
      disabled={disabled}
      style={{ width: '100%', backgroundColor: 'transparent' }}
      renderValue={(selected: any) => {
        if (selected !== null) {
          const value: any = items.find((item: any) => {
            return item.value === selected;
          });
          if (value) {
            return value.name;
          } else {
            return '';
          }
        }
        return '';
      }}
    >
      {items.map((item: any) => {
        return(
          <MaterialUI.MenuItem key={item.value} value={item.value}>
            {item.name}
          </MaterialUI.MenuItem>
        )
      })}
    </MaterialUI.Select>
  );
};

const TableFilterCell = (props: any) => {
  const datePickerClasses = datePickerStyles();
  const {
    column,
    filter,
    onFilter,
    filteringEnabled,
    classes,
  } = props;
  if (filteringEnabled) {
    if(column.name === 'revision_date' || 
      column.name === 'date_of_birth' ||
      column.name === 'effectivity_date'){
      return (
        <MaterialUI.TableCell
          style={{ padding: '0px 10px 8px 10px' }}
          classes={classes}
        >
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              id='filter_datepicker'
              data-cy='filter_datepicker'
              variant="dialog"
              format="MMMM DD, YYYY"
              inputVariant="outlined"
              className={datePickerClasses.keyboardDatePicker}
              value={filter ? filter.value : ''}
              invalidDateMessage=""
              onChange={date => {
                let formattedDate = '';
                if (!isNil(date)) {
                  const dateMoment = moment(date.toISOString());
                  if (!isNil(dateMoment) && dateMoment.isValid()) {
                    formattedDate = dateMoment.format('MMM DD, YYYY');
                  }
                }
                onFilter({
                  value: formattedDate,
                });
              }}
            />
          </MuiPickersUtilsProvider>
        </MaterialUI.TableCell>
      );
    } else {
      return (
        <MaterialUI.TableCell
          style={{ padding: '0px 10px 8px 10px' }}
          classes={classes}
        >
          <MaterialUI.OutlinedInput
            id='filter_input'
            data-cy='filter_input'
            classes={classes}
            style={{ width: '100%' }}
            value={filter ? filter.value : ''}
            labelWidth={0}
            onChange={e =>
              onFilter(e.target.value ? { value: e.target.value } : null)
            }
            placeholder=""
          />
        </MaterialUI.TableCell>
      );
    }
  } else {
    // return null;
    return (
      <MaterialUI.TableCell
        style={{ padding: '0px 10px 8px 10px' }}
        classes={classes}
      />
    );
  }
};

