const UploadFileFormatter = ({ column, row }: any) => {
  const id: string = column['name'] + '_' + row['id'];
  if (row['files'] === '') {
    return (
      <div id={id}>
        <MaterialUI.Typography
          color="textPrimary"
          style={{ paddingTop: '5px' }}
        >
          <span>
            <span style={{ paddingRight: '10px', fontSize: '15px' }}>
              <FontAwesomeIcon
                icon={faFileUpload}
                style={{ color: '#0D5E40' }}
              />
            </span>
            <span style={{ textDecoration: 'underline', color: '#0D5E40' }}>
              Upload File
            </span>
          </span>
        </MaterialUI.Typography>
      </div>
    );
  } else {
    if (selected_tab < 2) {
      const linkClasses = LinkStyles();
      return (
        <div id={id}>
          <MaterialUI.Typography>
            <span>
              <Link
                key={'view'}
                id='link_view'
                data-cy='link_view'
                className={linkClasses.link}
                onClick={() => {
                  globalImageViewerOpen = true;
                  globalSelectedFiles = row.filearray;
                }}
              >
                View
              </Link>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <Link
                key={'download'}
                id='link_download'
                data-cy='link_download'
                className={linkClasses.link}
                onClick={() => {
                  downloadFiles = true;
                  globalSelectedFiles = row.filearray;
                }}
              >
                Download
              </Link>
            </span>
          </MaterialUI.Typography>
        </div>
      );
    } else {
      return <div />;
    }
  }
};

