const PrincipalDropdown = ({ row, value }: any) => {
  // console.log('rows',row,row.member_details.possible_principals.length)
  let items:any[]=[]
  if(row.member_details && row.member_details.possible_principals){
    row.member_details.possible_principals.forEach(item=>{
      const name = `${item.first_name} ${item.last_name}`
      if(items.findIndex((item2)=>item2.value===name)===-1){
        items.push({
          value:name,
          name:name
        })
      }      
    })
  }
  
  if(row.principal_name !== undefined && row.principal_name !== ""){
    return (
      <Select id='principal_select' data-cy='principal_select' items={items}  
        disabled={false} value={value} />
    );
  } else {
    return(
      <MaterialUI.Select
        value={'No principals found'}
        disabled={false}
        style={{ width: '100%', backgroundColor: 'transparent' }}
        renderValue={() => {
          return 'No principals found';
        }}
      >
        <MaterialUI.MenuItem value={''}>
          {'No principals found'}
        </MaterialUI.MenuItem>
      </MaterialUI.Select>
    )
  }
}; 

const PrincipalDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={PrincipalDropdown} {...props} />
);


interface TableComponentProps {
  rows: any[];
  columns: any[];
  columnFilteringEnabled?: boolean;
  intgFltrColumnExtensions?: any[];
  columnExtensions?: any[];
  filterExtensions?: any[];
  formattedColumns?: any;
  columnBands?: any[] | null;
  singleSelect?: boolean;
  disableSearch?: boolean;
  disablePaging?: boolean;
  disableSelect?: boolean;
  disableFilter?: boolean;
  disableSort?: boolean;
  showSelectionColumn?: boolean;
  principalItem?:any[];
  toolbarType?: string | null;
  id: string;
  message: string;
  defaultFilter?: any[];
  onClickRow: (row: number[]) => void;
  onSelectedRadio?: (row: number, value: string) => void;
  onFilterChange?: (response: any) => void;
  onSortingChange?: (response: any) => void;
  onClickCell?: (row: any) => void;
  onClickView?: (data: any[]) => void;
  onClickDownload?: (data: any[]) => void;

  currentPage?: number;
  setCurrentPage?: (page: number) => void;

  pageSize?: number;
  onPageSizeChange?: (pageSize: number) => void;

  selectedTab?: number;
  defaultSort?: any[];
  query?: string;

  handleClickEdit?: (anchorEl: HTMLAnchorElement | null, data: any) => void;
  handleClickUpload?: (isUpload: boolean) => void;
  // verifyMemberTab?: number;
}

export const TableComponent: React.FC<TableComponentProps> = (
  props: TableComponentProps,
): JSX.Element => {
  const {
    columns,
    formattedColumns,
    singleSelect,
    disablePaging,
    disableSearch,
    disableSelect,
    disableSort,
    disableFilter,
    toolbarType,
    columnExtensions,
    columnBands,
    id,
    showSelectionColumn,
    query,
  } = props;
  

  const [selection, setSelection] = useState<number[]>([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [pageSize, setPageSize] = useState(5);
  const [pageSizes] = useState({
    standardTablePageSizes: [5, 10, 15, 0],
    uploadTablePageSizes: [10, 25, 50, 100]
  });
  const [rows, setRows] = useState(props.rows);
  const [selectedFiles, setSelectedFiles] = useState<any[]>([]);
  const [imageViewerOpen, setImageViewerOpen] = useState(false);
  useEffect(() => {
    setRows(props.rows);
  }, [props.rows]);

  useEffect(() => {
    if (props.pageSize !== undefined && props.pageSize !== null) {
      // console.log('pageSize from "props": '+props.pageSize)
      setPageSize(props.pageSize);
    }
  }, [props.pageSize]);

  const onPageSizeChange = (mPageSize: number) => {
    if (props.onPageSizeChange) {
      props.onPageSizeChange(mPageSize);
    } else {
      setPageSize(mPageSize);
    }
  };

  const dataTypeProviders: any[] = [];

  if (props.selectedTab !== undefined) {
    selected_tab = props.selectedTab;
  }

  function handleSelectionChange(newSelection: any[]) {
    if (props.onClickRow) {
      if (
        !radioSelected &&
        !clickCell &&
        !globalImageViewerOpen &&
        !downloadFiles &&
        !editSelected && 
        !uploadSelected
      ) {
        props.onClickRow(newSelection);
      } else if (radioSelected) {
        if (props.onSelectedRadio) {
          props.onSelectedRadio(newSelection[0], selectedValue);
        }
        radioSelected = false;
      } else if (
        clickCell &&
        !globalImageViewerOpen &&
        !downloadFiles &&
        !editSelected
      ) {
        if (props.onClickCell) {
          props.onClickCell(clickValue);
        }
        clickCell = false;
        clickValue = {};
      } else if (globalImageViewerOpen && !downloadFiles && !editSelected) {
        handleClickView(globalSelectedFiles);
        globalImageViewerOpen = false;
        globalSelectedFiles = [];
      } else if (downloadFiles && !editSelected) {
        if (props.onClickDownload) {
          props.onClickDownload(globalSelectedFiles);
        }
        downloadFiles = false;
        globalSelectedFiles = [];
      } else if (editSelected) {
        if (props.handleClickEdit) {
          props.handleClickEdit(anchorEl, editData);
        }
        anchorEl = null;
        editData = null;
        editSelected = false;
      } else if(uploadSelected){
        if(props.handleClickUpload){
          props.handleClickUpload(uploadSelected);
        }
        uploadSelected = false;
      }
    } else {
      if (singleSelect) {
        const lastSelected = newSelection.find(
          (selected: number) => selection.indexOf(selected) === -1,
        );

        if (lastSelected !== undefined) {
          setSelection([lastSelected]);
        }
      } else {
        setSelection(newSelection);
      }
    }
  }
  if (
    formattedColumns !== undefined &&
    Object.keys(formattedColumns).length > 0
  ) {
    if (formattedColumns['number'] !== undefined) {
      dataTypeProviders.push(
        <NumberDataTypeProvider for={formattedColumns.number} />,
      );
    }
    if (formattedColumns['text'] !== undefined) {
      dataTypeProviders.push(
        <TextDataTypeProvider for={formattedColumns.text} />,
      );
    }
    if (formattedColumns['system_name'] !== undefined) {
      dataTypeProviders.push(
        <SystemNameDataTypeProvider for={formattedColumns.system_name} />,
      );
    }
    if (formattedColumns['data_name'] !== undefined) {
      dataTypeProviders.push(
        <DataDataTypeProvider for={formattedColumns.data_name} />,
      );
    }
  
    if (formattedColumns['principal_select'] !== undefined) {
      dataTypeProviders.push(
        <PrincipalDataTypeProvider for={formattedColumns.principal_select} />,
      );
    }
    if (formattedColumns['value_name'] !== undefined) {
      dataTypeProviders.push(
        <ValuesNameDataTypeProvider for={formattedColumns.value_name} />,
      );
    }
    if (formattedColumns['required_columns'] !== undefined) {
      dataTypeProviders.push(
        <EmptyCellDataTypeProvider for={formattedColumns.required_columns} />,
      );
    }
    if (formattedColumns['radio'] !== undefined) {
      dataTypeProviders.push(
        <RadioDataTypeProvider for={formattedColumns.radio} />,
      );
    }
    if (formattedColumns['incomplete_radio'] !== undefined) {
      dataTypeProviders.push(
        <IncompleteRadioDataTypeProvider
          for={formattedColumns.incomplete_radio}
        />,
      );
    }
    if (formattedColumns['disapprove_action'] !== undefined) {
      dataTypeProviders.push(
        <DisapproveRadioDataTypeProvider
          for={formattedColumns.disapprove_action}
        />,
      );
    }
    if (formattedColumns['required_checkbox'] !== undefined) {
      dataTypeProviders.push(
        <RequiredCheckboxDataTypeProvider
          for={formattedColumns.required_checkbox}
        />,
      );
    }
    if (formattedColumns['upload_file'] !== undefined) {
      dataTypeProviders.push(
        <UploadFileDataTypeProvider
          for={formattedColumns.upload_file}
          {...props}
        />,
      );
    }
    if (formattedColumns['required_cell'] !== undefined) {
      dataTypeProviders.push(
        <RequiredDataTypeProvider
          for={formattedColumns.required_cell}
          {...props}
        />,
      );
    }
    if (formattedColumns['date'] !== undefined) {
      dataTypeProviders.push(
        <DateCellDataTypeProvider for={formattedColumns.date} />,
      );
    }
    if (formattedColumns['time'] !== undefined) {
      dataTypeProviders.push(
        <TimeCellDataTypeProvider for={formattedColumns.time} />,
      );
    }
  }

  const toolbar = DefaultTableToolbar;

  useEffect(() => {
    setSelectedFiles([]);
    setImageViewerOpen(false);
  }, []);

  const handleClickView = (images: any[]) => {
    if (images !== undefined) {
      setSelectedFiles(images);
      setImageViewerOpen(true);
    }
  };

  const handleCloseImageViewer = () => {
    setImageViewerOpen(false);
  };

  // console.log('Table ID: '+id);

  return (
    <Paper id={id}>
      <Grid rows={rows} columns={columns}>
        {dataTypeProviders}
        {/* Need to initialize paging state before paging panel */}
        {!disablePaging ? (
          <PagingState
            currentPage={props.currentPage ? props.currentPage : currentPage}
            onCurrentPageChange={
              props.setCurrentPage ? props.setCurrentPage : setCurrentPage
            }
            pageSize={pageSize}
            onPageSizeChange={onPageSizeChange}
          />
        ) : null}

        {!disablePaging ? <IntegratedPaging /> : null}

        {!disableSearch || query ? <SearchState value={query} /> : null}

        {!disableSort && props.defaultSort !== undefined ? (
          <SortingState
            defaultSorting={[]}
            sorting={props.defaultSort !== undefined ? props.defaultSort : []}
            onSortingChange={e => {
              if (props.onSortingChange) {
                props.onSortingChange(e);
              }
              if (props.onFilterChange) {
                props.onFilterChange(props.defaultFilter);
              }
            }}
          />
        ) : !disableSort ? (
          <SortingState
            defaultSorting={[]}
            onSortingChange={e => {
              if (props.onSortingChange) {
                props.onSortingChange(e);
              }
              if (props.onFilterChange) {
                props.onFilterChange(props.defaultFilter);
              }
            }}
          />
        ) : (
          <SortingState />
        )}

        {!disableSort ? <IntegratedSorting /> : null}

        {!disableSelect || !showSelectionColumn ? (
          <SelectionState
            selection={selection}
            onSelectionChange={handleSelectionChange}
          />
        ) : null}
        {!disableSelect ? <IntegratedSelection /> : null}

        {!disableFilter ? (
          <FilteringState
            defaultFilters={[]}
            filters={
              props.defaultFilter !== undefined ? props.defaultFilter : []
            }
            columnFilteringEnabled={props.columnFilteringEnabled}
            columnExtensions={props.filterExtensions}
            onFiltersChange={props.onFilterChange}
          />
        ) : null}

        {!disableFilter ? (
          <IntegratedFiltering
            columnExtensions={props.intgFltrColumnExtensions}
          />
        ) : null}

        <Table
          columnExtensions={columnExtensions}
          messages={{ noData: props.message }}
        />

        {!disableSelect && !showSelectionColumn ? (
          <TableSelection
            showSelectAll={!singleSelect ? true : false}
            cellComponent={!singleSelect ? TableCheckBox : TableRadio}
            headerCellComponent={TableCheckBox}
          />
        ) : !showSelectionColumn ? (
          <TableSelection
            selectByRowClick
            showSelectionColumn={false}
            cellComponent={!singleSelect ? TableCheckBox : TableRadio}
            headerCellComponent={TableCheckBox}
          />
        ) : null}
        <TableHeaderRow
          showSortingControls={!disableSort}
          sortLabelComponent={TableSortLabel}
        />
        {!disableFilter ? (
          <TableFilterRow cellComponent={TableFilterCell} />
        ) : null}
        {columnBands ? <TableBandHeader columnBands={columnBands} /> : null}

        {!disableSearch || toolbarType ? (
          <Toolbar rootComponent={toolbar} />
        ) : null}
        {!disableSearch ? (
          <SearchPanel inputComponent={TableSearchPanel} />
        ) : null}

          {/* Use default page size for other tables */}
        {!disablePaging && id !== 'upload_file_table' ? <PagingPanel pageSizes={pageSizes.standardTablePageSizes} /> : null}

          {/* Use this page size for upload tables to prevent overloading of > 100 data*/}
        {!disablePaging && id === 'upload_file_table' ? <PagingPanel pageSizes={pageSizes.uploadTablePageSizes} /> : null}
      </Grid>
      {imageViewerOpen ? (
        <ImageViewer
          images={selectedFiles}
          index={0}
          onClose={handleCloseImageViewer}
        />
      ) : null}
    </Paper>
  );
};

TableComponent.defaultProps = {
  rows: [],
  columns: [],
  columnExtensions: [],
  filterExtensions: [],
  columnBands: null,
  formattedColumns: {},
  singleSelect: false,
  disableSearch: false,
  disablePaging: false,
  disableSelect: false,
  disableFilter: false,
  disableSort: false,
  toolbarType: null,
  defaultFilter: [],
  showSelectionColumn: false,
  id: `table-id-${new Date().getTime()}`,
  columnFilteringEnabled: true,
  message: 'No available data',
  query: '',
};
