import * as React from 'react';
import { Grid, Tabs, Tab } from '@material-ui/core/';
import { useState, useEffect } from 'react';

import { TableComponent } from 'Components/UI/TableComponent';
import { ViewDocuments } from './ViewDocuments';
// import { ImageViewer } from '../../../Components/UI/ImageViewer'; 

interface DocumentsProps {
  memberId: string;
  all_documents_table: any;
  uploaded_documents_table: any;
  missing_documents_table: any;
  documents_tab: any;
  all_data: any;
  all_filter: any;
  all_sort: any;
  uploaded_filter: any;
  uploaded_sort: any;
  missing_filter: any;
  missing_sort: any;
  showHideLoader: (flag: boolean) => void;
  updateMemberDocuments: (uploadedDocs: any) => void;
  generateDocumentData: (data: any, disabled: boolean) => void;
  handleClickCell: (data: any) => void;
  setFieldValue: (fieldName: string, value: any) => void;
  applySortFilter: (params: any, tab: number, type: string) => void;
  handleOpenLostInternetConnectionModal: () => void;
  handleCloseLostInternetConnectionModal: () => void;
}

export const Documents: React.FC<DocumentsProps> = (
  props: DocumentsProps,
): JSX.Element => {
  const [selectedTab, setSelectedTab] = useState(0);
  const [showDrawer, setShowDrawer] = useState(false);
  const [selectedRow, setSelectedRow] = useState('');
  const [selectedRowData, setSelectedRowData] = useState<any>({});
  const [memberId, setMemberId] = useState(props.memberId);

  useEffect(() => {
    setMemberId(props.memberId);
  }, [props.memberId]);

  function handleChange(event: any, newValue: number) {
    console.log('event >>>', event);
    setSelectedTab(newValue);
  }

  function handleClickRow2(row: any) {
    let row_temp: any = {};
    if (selectedTab === 0) {
      const selected_row = props.all_documents_table['rows'][row[0]]['id'];
      row_temp = props.all_data.find(item => {
        return item['_id'] === selected_row;
      });
      setSelectedRow(selected_row);
    } else if (selectedTab === 1) {
      const selected_row = props.uploaded_documents_table['rows'][row[0]]['id'];
      row_temp = props.all_data.find(item => {
        return item['_id'] === selected_row;
      });
      setSelectedRow(selected_row);
    } else if (selectedTab === 2) {
      const selected_row = props.missing_documents_table['rows'][row[0]]['id'];
      row_temp = props.all_data.find(item => {
        return item['_id'] === selected_row;
      });
      setSelectedRow(selected_row);
    }
    setSelectedRowData(row_temp);
    setShowDrawer(true);
  }

  function handleFilter(filter: any) {
    props.applySortFilter(filter, selectedTab, 'filter');
  }

  function handleSort(sorting: any) {
    props.applySortFilter(sorting, selectedTab, 'sort');
  }

  function handleShowDrawer(value: boolean) {
    setShowDrawer(value);
  }

  const handleDownloadFiles = (files : any[]) =>{
    console.log('DOWNLOAD FILES', files)
    if(files !== undefined && files.length > 0){
      files.forEach(file => {
        if(file.name !== undefined && file.url){
          let a = document.createElement('a');
          a.href = file['url'];
          a.download = file['name'];
          a.click();
        }
      });
    }
  }

  return (
    <Grid container>
      <Grid item xs={12}>
        <Tabs
          value={selectedTab}
          onChange={handleChange}
          aria-label="documents tab"
        >
          {props.documents_tab.map(data => (
            <Tab
              data-cy={'view_member_documents_tab_' + data.label}
              id={'view_member_documents_tab_' + data.label}
              key={'view_member_documents_tab_' + data.label}
              label={data.label + ' (' + data.count + ')'}
              aria-controls={'verify-member-tabpanel-' + data.count}
            />
          ))}
        </Tabs>
      </Grid>
      <Grid item xs={12} style={{ paddingLeft: '24px' }}>
        {selectedTab === 0 ? (
          <TableComponent
            data-cy={'document_all_table'}
            id={'document_all_table'}
            rows={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['rows']
                : []
            }
            columns={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['columns']
                : []
            }
            message="No available data"
            onClickRow={handleClickRow2}
            selectedTab={selectedTab}
            disableSelect
            disableSearch
            columnFilteringEnabled={false}
            formattedColumns={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['formattedColumns']
                : {}
            }
            columnExtensions={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['columnExtensions']
                : []
            }
            filterExtensions={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['filterExtensions']
                : []
            }
            onClickCell={props.handleClickCell}
            // onClickView={handleClickView}
            onClickDownload={handleDownloadFiles}
            onFilterChange={handleFilter}
            onSortingChange={handleSort}
            defaultFilter={props.all_filter}
            defaultSort={props.all_sort}
          />
        ) : selectedTab === 1 ? (
          <TableComponent            
            data-cy={'document_uploaded_table'}
            id={'document_uploaded_table'}
            rows={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['rows']
                : []
            }
            columns={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['columns']
                : []
            }
            message="No available data"
            onClickRow={handleClickRow2}
            // onClickView={handleClickView}
            onClickDownload={handleDownloadFiles}
            selectedTab={selectedTab}
            disableSelect
            disableSearch
            columnFilteringEnabled={false}
            formattedColumns={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['formattedColumns']
                : {}
            }
            columnExtensions={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['columnExtensions']
                : []
            }
            filterExtensions={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['filterExtensions']
                : []
            }
            onClickCell={props.handleClickCell}
            onFilterChange={handleFilter}
            onSortingChange={handleSort}
            defaultFilter={props.uploaded_filter}
            defaultSort={props.uploaded_sort}
          />
        ) : selectedTab === 2 ? (
          <TableComponent
            data-cy={'document_missing_table'}
            id={'document_missing_table'}            
            rows={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['rows']
                : []
            }
            columns={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['columns']
                : []
            }
            message="No available data"
            onClickRow={handleClickRow2}
            selectedTab={selectedTab}
            disableSelect
            disableSearch
            columnFilteringEnabled={false}
            formattedColumns={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['formattedColumns']
                : {}
            }
            columnExtensions={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['columnExtensions']
                : []
            }
            filterExtensions={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['filterExtensions']
                : []
            }
            onClickCell={props.handleClickCell}
            onFilterChange={handleFilter}
            onSortingChange={handleSort}
            defaultFilter={props.missing_filter}
            defaultSort={props.missing_sort}
          />
        ) : null}
      </Grid>
      <Grid item xs={12}>
        <ViewDocuments
          open={showDrawer}
          showHideDrawer={handleShowDrawer}
          showHideLoader={props.showHideLoader}
          memberId={memberId}
          row_data={selectedRowData}
          row_id={selectedRow}
          updateMemberDocuments={props.updateMemberDocuments}
          generateDocumentData={props.generateDocumentData}
          all_data={props.all_data}
          handleOpenLostInternetConnectionModal={props.handleOpenLostInternetConnectionModal}
          handleCloseLostInternetConnectionModal={props.handleCloseLostInternetConnectionModal}
        />
      </Grid>
      {/* {imageViewerOpen ?
          <ImageViewer
          images={selectedFiles}
          index={0}
          onClose={handleCloseImageViewer}
          />
          : null
      } */}
    </Grid>
  );
};

Documents.defaultProps = {
  all_data: {},
  all_documents_table: {},
  uploaded_documents_table: {},
  missing_documents_table: {},
  documents_tab: [],
  all_filter: [],
  uploaded_filter: [],
  missing_filter: [],
  all_sort: [],
  uploaded_sort: [],
  missing_sort: [],
};
