import React from 'react';
//import { useState } from 'react';
import { 
    Grid, 
    Typography, 
    Box,
    Checkbox, 
} from '@material-ui/core/';
import { 
    makeStyles,
} from '@material-ui/core/styles';

const sicheckboxStyle = {
    size: 12,
    padding: 0,
    color: '#1E2071',
    '&$checked': {
        color: '#1E2071',
    },
};

const sicbitemStyles = makeStyles({
    disabledsiname: {
      fontSize: '0.875rem',
      color: '#272E4C',
      marginLeft: 16,
      paddingTop: 2,
      paddingBottom: 4,
    },
    enabledsiname: {
        fontSize: '0.875rem',
        color: '#272E4C',
        marginLeft: 16,
        paddingTop: 2,
        paddingBottom: 4,
    },
});

interface SICheckboxItemProps {
    label?: string;
    checked?: boolean;
    disabled?: boolean;
    handleChange? : any;
}

export const SICheckboxItem: React.FC<SICheckboxItemProps> = (
	props: SICheckboxItemProps,
): JSX.Element => {

    const classes = sicbitemStyles(props);

    return(
        <Grid container alignItems="center" style={{paddingTop: 4, paddingBottom: 4}}>
            <Grid item xs={12}>
                <Box display="flex" flexDirection="row">
                    <Box>
                        <Checkbox 
                            style={sicheckboxStyle} 
                            disabled={props.disabled} 
                            checked={props.checked} 
                            onChange={() => {
                                props.handleChange(!props.checked);
                            }} />
                    </Box>
                    <Box>
                        <Typography className={props.disabled ? classes.disabledsiname : classes.enabledsiname}>
                            {props.label}
                        </Typography>
                    </Box>
                    
                </Box>
            </Grid>
        </Grid>
    );
}

SICheckboxItem.defaultProps = {
    disabled: false,
    label: '',
    checked: false,
    handleChange: function() {}
}