import React, { useState, useEffect } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from '@material-ui/core/';

import moment from 'moment';

import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles(theme => ({
    dialog: {
        align: 'center',
        justify: 'center',
        fontFamily: 'usual',
    },
    dialogContent: {
        paddingTop: '0px',
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 500,
    },
    dialogFirstTitle: {
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px',
    },
    dialogSecondTitle: {
        fontSize: '14px',
        color: '#272E4C',
        paddingBottom: '1rem'
    },
    tblPaper: {
        minHeight: 175,
        borderRadius: 0,
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
    },
    boldText: {
        fontWeight: 'bold',
    },
    cancelButton: {
        minWidth: 129,
        marginRight: 25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: '#FFFFFF',
    },
    liftSuspensionButton: {
        minWidth: 129,
        backgroundColor: '#3AB77D',
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
}));

const LiftSuspensionButton = withStyles({
    root: {
        color: '#FFFFFF',
        backgroundColor: '#3AB77D',
        '&:hover': {
            backgroundColor: '#2e9565',
        },
    },
})(Button);

const CancelButton = withStyles({
    root: {
        '&:hover': {
            backgroundColor: '#3AB77D',
            color: '#FFFFFF',
        },
    },
})(Button);

interface ConfirmLiftSuspensionModalProps {
    id: string;
    isModalOpen: boolean;
    isApplyToAll: boolean;
    memberData: any[];
    onConfirmAction: (data: any[]) => void;
    onClose: () => void;
}

export const ConfirmLiftSuspensionModal: React.FC<ConfirmLiftSuspensionModalProps> = (
    props: ConfirmLiftSuspensionModalProps
): JSX.Element => {
    const classes = useStyles();
    const { id, isModalOpen, isApplyToAll, memberData} = props;

    const [memberDataPayload, setMemberDataPayload] = useState<any>([]);
    const [memberDataForDisplay, setMemberDataForDisplay] = useState<any>([]);
    const [memberCount, setMemberCount] = useState<number>(0);

    useEffect(() => {
        if(isModalOpen === true){
            if(memberData.length > 0){
                if(isApplyToAll === true){ 
                    memberData.forEach(allMemberData => {
                        let memberIds = allMemberData['memberIdsForDisplay'];
                        let memberNames = allMemberData['memberNamesForDisplay'];
                        let memberCorporates = allMemberData['memberCorporatesForDisplay'];
                        let memberSuspensionDates = allMemberData['memberSuspensionDatesForDisplay'];

                        setMemberCount(memberIds.length);
    
                        let memberDataArray = memberIds.map((id,index) => {
                            const data = {
                                member_id: id,
                                member_name: memberNames[index],
                                corporates: memberCorporates[index],
                                suspension_start_date: moment(memberSuspensionDates[index]).format('MMM DD, YYYY'),
                                suspension_lift_date: moment(allMemberData['suspension_lift_date']).format('MMM DD, YYYY')
                            }

                            return data;
                        });
                        
                        setMemberDataForDisplay(memberDataArray);

                        delete allMemberData['memberIdsForDisplay'];
                        delete allMemberData['memberNamesForDisplay'];
                        delete allMemberData['memberCorporatesForDisplay'];
                        delete allMemberData['memberSuspensionDatesForDisplay'];

                        setMemberDataPayload([allMemberData]);
                    })
                } else {
                    setMemberCount(memberData.length);

                    let memberDataArray =  memberData.map(eachMemberData => {
                        let memberIds = eachMemberData['memberIdsForDisplay'];
                        let memberNames = eachMemberData['memberNamesForDisplay'];
                        let memberCorporates = eachMemberData['memberCorporatesForDisplay'];

                        const data = {
                            member_id: memberIds[0],
                            member_name: memberNames[0],
                            corporates: memberCorporates[0],
                            suspension_start_date: moment(eachMemberData['suspension_start_date']).format('MMM DD, YYYY'),
                            suspension_lift_date: moment(eachMemberData['suspension_lift_date']).format('MMM DD, YYYY')
                        }

                        return data;
                    })

                    setMemberDataForDisplay(memberDataArray);

                    let finalDataArray = memberData.map(finalData => {
                        delete finalData['memberIdsForDisplay'];
                        delete finalData['memberNamesForDisplay'];
                        delete finalData['memberCorporatesForDisplay'];

                        return finalData;
                    })

                    setMemberDataPayload(finalDataArray);
                }
            } else {
                console.log('no data from Lift Suspension Modal');
            }
        }
    },[isModalOpen])

    useEffect(() => {
        // if(isModalOpen === true){
        //     console.log(memberDataForDisplay)
        // }
    },[memberDataForDisplay])

    useEffect(() => {
        // if(isModalOpen === true){
        //     console.log(memberDataPayload)
        // }
    },[memberDataPayload])

    function handleLiftSuspensionMember() {
        if(props.onConfirmAction){
            props.onConfirmAction(memberDataPayload);
            handleOnClose();
        }
    }

    function handleOnClose() {
        if(props.onClose){
            props.onClose();
        }
    }

    return (
        <Dialog
            id={id}
            maxWidth={'md'}
            open={isModalOpen}
            scroll={'paper'}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{ zIndex: 9999 }}
        >
            <DialogTitle
                className={classes.dialogTitle}
                disableTypography={true}
                id="modal-title"
            >
                <div className={classes.dialogFirstTitle}>
                    Confirm Lifting of Suspension
                </div>
                <IconButton
                    data-cy={'lift_suspension_conmod_close_btn'}
                    id={'lift_suspension_conmod_close_btn'}
                    aria-label="close"
                    className={classes.closeButton}
                    onClick={handleOnClose}
                >
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography className={classes.dialogSecondTitle}>
                        Are you sure you want to lift the suspension of the following{' '}
                        <span className={classes.boldText}>
                            {memberCount} member{memberCount > 1 ? 's' : null}
                        </span>
                        ? Member(s) will have their coverage re-activated on the set date.
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className={classes.tblPaper}>
                            <Table 
                                data-cy={'lift_suspension_conmod_dependents_table'}
                                id={'lift_suspension_conmod_dependents_table'}
                                size="small"
                            >
                                <TableHead>
                                    <TableRow>
                                        <TableCell style={{ width: '25%' }}>
                                            Member ID
                                        </TableCell>
                                        <TableCell style={{ width: '25%' }}>
                                            Member Name
                                        </TableCell>
                                        <TableCell style={{ width: '20%' }}>
                                            Corporation
                                        </TableCell>
                                        <TableCell style={{ width: '15%' }}>
                                            Effective Start of Suspension
                                        </TableCell>
                                        <TableCell style={{ width: '15%' }}>
                                            Effective Date of Lifting
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {memberDataForDisplay.length > 0 ? memberDataForDisplay.map(
                                        (row, index) => {
                                            return (
                                                <TableRow key={index}>
                                                    <TableCell style={{ width: '25%' }}>
                                                        {row['member_id']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '25%' }}>
                                                        {row['member_name']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '20%' }}>
                                                        {row['corporates']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '15%' }}>
                                                        {row['suspension_start_date']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '15%' }}>
                                                        {row['suspension_lift_date']}
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        }
                                    ): null}
                                </TableBody>
                            </Table>
                        </Paper>
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <CancelButton
                            data-cy={'lift_suspension_conmod_cancel_btn'}
                            id={'lift_suspension_conmod_cancel_btn'}
                            className={classes.cancelButton}
                            variant="contained"
                            color="primary"
                            onClick={handleOnClose}
                        >
                            Cancel
                        </CancelButton>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <LiftSuspensionButton
                            data-cy={'lift_suspension_conmod_btn'}
                            id={'lift_suspension_conmod_btn'}
                            className={classes.liftSuspensionButton}
                            variant="contained"
                            color="primary"
                            onClick={handleLiftSuspensionMember}
                        >
                            Lift Suspension
                        </LiftSuspensionButton>
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    );
};

ConfirmLiftSuspensionModal.defaultProps = {
    id: 'confirm_lift_suspension_modal',
    isModalOpen: false,
};
