    generateMemberListReport(filters ? : any) {
      this.setState({
        loading_state: true,
        check_internet_flag: true,
      });

      const { global_filter, default_filters, sorting } = this.state;

      const reportFilter = filters ? filters : default_filters;

      API.getMemberListReport(global_filter, reportFilter, sorting)
        .then(response => {
          this.setState({
            loading_state: false,
          });
          if (response !== undefined) {
            if (response.type === 'application/json') {
              this.setState({
                check_internet_flag: false,
                isModalOpen: true,
                modalTitle: 'Generate Report Error',
                modalMessage: 'Failed to get list of members.',
              });
            } else {
              const filename = `LIST_OF_MEMBERS${moment().format(
              'MMM-DD-YYYY',
            )}.csv`;
              const file = new File([response], filename, {
                type: 'text/csv;charset=utf-8',
              });
              saveAs(file);
            }
          } else {
            this.setState({
              isModalOpen: true,
              modalTitle: 'Generate Report Error',
              modalMessage: 'Failed to get list of members.',
            });
          }
        })
        .catch(() => {
          this.setState({
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Generate Report Error',
            modalMessage: 'Failed to get list of members.',
          });
        });
    }

