    /******* handles changes in global search *******/
    handleSearch(e: React.ChangeEvent < HTMLInputElement > ) {
      const { value } = e.target;
      const { default_filters } = this.state;

      if (value === '' && default_filters && default_filters.length === 0) {
        const table = this.generateDataForTable([]);
        this.setState({
          member_list: [],
          member_data_all: table,
          member_data: table,
          global_filter: value,
          clientList: [],
          totalCount: 0,
        });


        return;
      }


      if (this.state.typingTimeout) {
        clearTimeout(this.state.typingTimeout);
      }
      this.setState({
        global_filter: value,
        page: 0,
        limit: 10,
        typing: false,
        typingTimeout: setTimeout(() => {
          if (this.state.global_filter.length > 0) {
            this.searchMembers();
          }
        }, 2000)

      });

    }

    handlePageChange = (currentPage: number) => {
      this.setState({
          page: currentPage,
        },
        () => {
          this.searchMembers();
        },
      );
    };

    handlePageSizeChange = value => {
      const { totalCount } = this.state;
      const pageLimit = 1000;

      if (value === 0 && totalCount !== 0 && totalCount > pageLimit) {
        this.setState({
          isPageSizeModal: true,
        });
      } else {
        this.setState({
            limit: value,
          },
          () => {
            this.searchMembers();
          },
        );
      }
    };

