    /******* handles filters in table columns *******/
    handleFilterChange = (filter: any) => {
      const { member_data_all, member_data, global_filter } = this.state;
      let data = Object.assign({}, member_data);
      let filtered: any[] = [];
      let filter_member_id: string = '';
      let filter_member_name: string = '';
      let filter_member_corporate: string = '';
      let filter_member_effectivity_date: string = '';
      let filter_member_termination_date: string = '';
      let filter_member_date_status: string = '';

      filter.forEach(item => {
        if (item.columnName === 'member_id')
          filter_member_id = item.value.toLowerCase();
        if (item.columnName === 'member_name')
          filter_member_name = item.value.toLowerCase();
        if (item.columnName === 'corporates')
          filter_member_corporate = item.value.toLowerCase();
        if (item.columnName === 'effectivity_date')
          filter_member_effectivity_date = item.value.toLowerCase();
        if (item.columnName === 'termination_date')
          filter_member_termination_date = item.value.toLowerCase();
        if (item.columnName === 'status')
          filter_member_date_status = item.value.toLowerCase();
      });

      if (
        filter.length > 0 &&
        global_filter !== '' &&
        global_filter !== undefined &&
        global_filter !== null
      ) {
        member_data_all.rows.forEach(item => {
          if (
            (item.member_id &&
              item.member_id !== undefined &&
              item.member_id !== null &&
              item.member_id
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.member_name &&
              item.member_name !== undefined &&
              item.member_name !== null &&
              item.member_name
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.corporates &&
              item.corporates !== undefined &&
              item.corporates !== null &&
              item.corporates
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.effectivity_date &&
              item.effectivity_date !== undefined &&
              item.effectivity_date !== null &&
              item.effectivity_date
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.termination_date &&
              item.termination_date !== undefined &&
              item.termination_date !== null &&
              item.termination_date
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.status &&
              item.status !== undefined &&
              item.status !== null &&
              item.status.toLowerCase().includes(global_filter.toLowerCase()))
          )
            if (
              item.member_id !== undefined &&
              item.member_id !== null &&
              item.member_id.toLowerCase().includes(filter_member_id) &&
              (item.member_name !== undefined &&
                item.member_name !== null &&
                item.member_name.toLowerCase().includes(filter_member_name)) &&
              (item.corporates !== undefined &&
                item.corporates !== null &&
                item.corporates
                .toLowerCase()
                .includes(filter_member_corporate)) &&
              (item.effectivity_date !== undefined &&
                item.effectivity_date !== null &&
                item.effectivity_date
                .toLowerCase()
                .includes(filter_member_effectivity_date)) &&
              (item.termination_date !== undefined &&
                item.termination_date !== null &&
                item.termination_date
                .toLowerCase()
                .includes(filter_member_termination_date)) &&
              (item.status !== undefined &&
                item.status !== null &&
                item.status.toLowerCase().includes(filter_member_date_status))
            )
              filtered.push(item);
        });
        data.rows = filtered;
        this.setState({
          member_data: data,
          default_filters: filter,
        });
      } else if (
        global_filter !== '' &&
        global_filter !== undefined &&
        global_filter !== null
      ) {
        member_data_all.rows.forEach(item => {
          if (
            (item.member_id &&
              item.member_id !== undefined &&
              item.member_id !== null &&
              item.member_id
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.member_name &&
              item.member_name !== undefined &&
              item.member_name !== null &&
              item.member_name
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.corporates &&
              item.corporates !== undefined &&
              item.corporates !== null &&
              item.corporates
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.effectivity_date &&
              item.effectivity_date !== undefined &&
              item.effectivity_date !== null &&
              item.effectivity_date
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.termination_date &&
              item.termination_date !== undefined &&
              item.termination_date !== null &&
              item.termination_date
              .toLowerCase()
              .includes(global_filter.toLowerCase())) ||
            (item.status &&
              item.status !== undefined &&
              item.status !== null &&
              item.status.toLowerCase().includes(global_filter.toLowerCase()))
          ) {
            filtered.push(item);
          }
        });
        data.rows = filtered;
        this.setState({
          member_data: data,
          default_filters: filter,
        });
      } else if (filter.length > 0) {
        if (member_data_all !== undefined && member_data_all.rows.length > 0) {
          member_data_all.rows.forEach(item => {
            if (
              item.member_id !== undefined &&
              item.member_id !== null &&
              item.member_id.toLowerCase().includes(filter_member_id) &&
              (item.member_name !== undefined &&
                item.member_name !== null &&
                item.member_name.toLowerCase().includes(filter_member_name)) &&
              (item.corporates !== undefined &&
                item.corporates !== null &&
                item.corporates
                .toLowerCase()
                .includes(filter_member_corporate)) &&
              (item.effectivity_date !== undefined &&
                item.effectivity_date !== null &&
                item.effectivity_date
                .toLowerCase()
                .includes(filter_member_effectivity_date)) &&
              (item.termination_date !== undefined &&
                item.termination_date !== null &&
                item.termination_date
                .toLowerCase()
                .includes(filter_member_termination_date)) &&
              (item.status !== undefined &&
                item.status !== null &&
                item.status.toLowerCase().includes(filter_member_date_status))
            ) {
              filtered.push(item);
            }
          });
          data.rows = filtered;
          this.setState({
            member_data: data,
            default_filters: filter,
          });
        }
      } else if (
        filter.length === 0 ||
        global_filter === '' ||
        global_filter === undefined ||
        global_filter === null
      ) {
        data.rows = [];
        this.setState({
          member_data: data,
          default_filters: filter,
        });
      }
    };

