    searchMembers = () => {
      this.setState({
          loading_state: true,
          check_internet_flag: true,
        },
        () => {
          API.searchMembers(
            this.state.limit,
            this.state.limit * this.state.page,
            this.state.global_filter,
            this.state.default_filters,
            this.state.sorting,
          ).then(response => {
            console.log('search member', response)
            if (response && response.error === undefined) {
              console.log(response.data)
              const table = this.generateDataForTable(response.data);
              this.setState({
                check_internet_flag: false,
                member_list: response,
                member_data_all: table,
                member_data: table,
                loading_state: false,
                totalCount: response.totalCount,
                clientList: response.clients,
              });
            } else if (this.state.global_filter === '' || this.state.global_filter.trim() === '') {
              const table = this.generateDataForTable([]);
              this.setState({
                member_list: [],
                member_data_all: table,
                member_data: table,
                clientList: [],
                totalCount: 0,
                loading_state: false
              });
            }
          });
        },
      );
    };

