import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper,
  Select,
  MenuItem,
} from '@material-ui/core';

interface TableSelectProps {
  rows: any[];
  columns: any[];
  id: string;
  empty_message: string;
  name: string;
  maxHeight: string;
  handleChange: (value: string, row: string, table_name: string) => void;
}

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  tableWrapper: props => ({
    overflow: 'auto',
    maxHeight: props.maxHeight ? props.maxHeight : 'auto',
  }),
  tableHeader: {
    backgroundColor: '#272E4C',
  },
  select: {
    backgroundColor: 'transparent',
  },
  table: {
    '& th:not(:last-child)': {
      borderRight: '1px solid rgba(60, 57, 74, 0.3);',
    },
    '& td:not(:last-child)': {
      borderRight: '1px solid rgba(60, 57, 74, 0.3);',
    },
  },
  cell: {
    padding: '10px 40px 10px 16px',
  },
});

export const TableSelect: React.FC<TableSelectProps> = (
  props: TableSelectProps,
): JSX.Element => {
  const [id, setId] = React.useState(props.id);
  const [rows, setRows] = React.useState(props.rows);
  const [columns, setColumns] = React.useState(props.columns);

  React.useEffect(() => {
    setId(props.id);
    setRows(props.rows);
    setColumns(props.columns);
  }, [props.id, props.rows, props.columns]);

  const classes = useStyles(props);

  function onChange(
    event: React.ChangeEvent<{ name?: any | undefined; value: any }>,
  ) {
    if (props.handleChange) {
      props.handleChange(event.target.value, event.target.name, props.name);
    }
  }

  function getCell(col: any, row: any, row_idx: number, col_idx: number) {
    let table_cell: any | null;
    const value = row[col.name];
    switch (col.type) {
      case 'select':
        if (col.options) {
          let options: any = [];
          if (
            props.name === 'column names' ||
            props.name === 'add_new_data_map'
          ) {
            const selected_sheet_name: any[] = rows.map(item => {
              if (item['sheet_name'] !== '') {
                return item['sheet_name'];
              }
            });

            for (const i in col.options) {
              const opt = col.options[i];
              if (
                opt['value'] !== '' &&
                (selected_sheet_name.indexOf(opt['value']) === -1 ||
                  opt['value'] === value)
              ) {
                options.push(opt);
              }
            }

            if (options.length >= 1) {
              options.unshift({
                id: '',
                value: '',
                label: '',
              });
            }

            if (options.length === 0) {
              options.push({
                id: '',
                value: 'no_data_available',
                label: 'No data available',
              });
            }
          } else {
            const newOptions: any[] = [...col.options];
            newOptions.push({
              id: row.values,
              value: `${row.values} - For Validation`,
              label: 'For Validation',
            });
            options = newOptions;
          }
          table_cell = (
            <TableCell className={classes.cell} key={col_idx} align={col.align}>
              <Select
                id={col.id}
                className={classes.select}
                value={value}
                name={row_idx.toString()}
                onChange={onChange}
                style={{ width: '100%', backgroundColor: 'transparent' }}
              >
                {options.map((item: any, item_idx: number) => (
                  <MenuItem
                    key={item_idx}
                    value={item.value}
                    disabled={item.value === 'no_data_available'}
                  >
                    {item.label}
                  </MenuItem>
                ))}
              </Select>
            </TableCell>
          );
        } else {
          table_cell = (
            <TableCell className={classes.cell} key={col_idx} align={col.align}>
              {col.format && typeof value === 'number'
                ? col.format(value)
                : value}
            </TableCell>
          );
        }
        break;
      default:
        table_cell = (
          <TableCell className={classes.cell} key={col_idx} align={col.align}>
            {col.format && typeof value === 'number'
              ? col.format(value)
              : value}
          </TableCell>
        );
    }
    return table_cell;
  }

  return (
    <Paper className={classes.tableWrapper}>
      <Table id={id} stickyHeader className={classes.table}>
        <TableHead className={classes.tableHeader}>
          <TableRow>
            {columns.map((column: any, col_idx: number) => (
              <TableCell
                className={classes.cell}
                key={col_idx}
                align={column.align}
                style={{ minWidth: column.minWidth }}
              >
                {column.title}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row: any, row_idx: number) => {
            return (
              <TableRow
                role="checkbox"
                tabIndex={-1}
                key={row.code}
                className={row.className ? row.className : ''}
              >
                {columns.map((column: any, col_idx: number) => {
                  const value = row[column.name];
                  const is_required = row['required'] ? row['required'] : false;
                  if (column.type) {
                    return getCell(column, row, row_idx, col_idx);
                  } else {
                    return (
                      <TableCell
                        className={classes.cell}
                        key={col_idx}
                        align={column.align}
                      >
                        {column.format && typeof value === 'number'
                          ? column.format(value)
                          : value
                        }
                        {
                          is_required ? ' *' : ''
                        }
                      </TableCell>
                    );
                  }
                })}
              </TableRow>
            );
          })}
          {rows.length === 0 ? (
            <TableRow role="checkbox" tabIndex={-1}>
              <TableCell
                className={classes.cell}
                align={'center'}
                colSpan={columns.length}
              >
                {props.empty_message}
              </TableCell>
            </TableRow>
          ) : null}
        </TableBody>
      </Table>
    </Paper>
  );
};

TableSelect.defaultProps = {
  rows: [],
  columns: [],
};
