import * as React from 'react';
import { Grid, Typography, Stepper, Step,
    StepLabel, StepConnector} from '@material-ui/core/';
import { makeStyles, withStyles } from '@material-ui/core/styles';

interface PageHeaderComponentProps {
    label: string;
    steps: any[];
    activeStep: number;
    id:string;
}

const headerStyle = makeStyles({
    headerLabel: {
        paddingTop: '20px'
    },
    title: {
        fontSize: '1.5rem',
        alignItems: 'flex-start',
        fontWeight: 600
    },
    label:{
        fontWeight: 600
    }
});

const StepConnectorStyle = withStyles({
    alternativeLabel: {
        top: 10,
        left: 'calc(-50% + 12px)',
        right: 'calc(50% + 12px)',
    },
    active: {
      '& $line': {
        borderColor: '#3AB77D',
      },
    },
    completed: {
      '& $line': {
        borderColor: '#3AB77D',
      },
    },
    line: {
      borderColor: '#eaeaf0',
      borderTopWidth: 3,
      borderRadius: 1,
    },
})(StepConnector);

export const PageHeaderComponent: React.FC<PageHeaderComponentProps> = (
	props: PageHeaderComponentProps,
): JSX.Element => {
    const {
        label,
        steps,
        activeStep
    } = props;
    const classes = headerStyle()
    return (
        <Grid container justify="flex-start" alignItems="flex-start">
            <Grid item xs={6} className={classes.headerLabel}>
                <Typography className={classes.title} color="textPrimary">
                    {label}
                </Typography>
            </Grid>
            { steps.length > 0 ?
                <Grid item xs={6}>
                    <Stepper
                        data-cy={props.id ? `page_header_${props.id.toLowerCase().replace(' ','_')}` : ''}
                        id={props.id ? `page_header_${props.id.toLowerCase().replace(' ','_')}` : ''}
                        alternativeLabel 
                        activeStep={activeStep} 
                        connector={<StepConnectorStyle />}
                    >
                        {steps.map(data => (
                            <Step key={data}>
                                <StepLabel className={classes.label}>{data}</StepLabel>
                            </Step>
                        ))}
                    </Stepper>
                </Grid>: null
            }
        </Grid>
    )
}

PageHeaderComponent.defaultProps = {
    label: "Page",
    id: "stepper_1",
    steps: [],
    activeStep: 0
}