import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Grid,
  Button,
  IconButton,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

interface ModalComponentProps {
  id: string;
  isModalOpen: boolean;
  message: string;
  title: string;
  isPrimary?: boolean;
  onClose: () => void;
}

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '100%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '350px',
  },
  dialogTitle: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
  },
  dialogAction: {
    marginBottom: theme.spacing(2),
    marginTop: theme.spacing(2),
    justifyContent: 'center',
  },
  dialogContentContainer: {
    width: '100%',
  },
  boldText: {
    fontWeight: 'bold',
  },
  submitButton: {
    width: 115,
    border: '1px solid #3AB77D',
    backgroundColor: '##3AB77D',
    color: '#FFFFFF',
  },
  cancelButton: {
    width: 115,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
}));

export const ModalComponent: React.FC<ModalComponentProps> = (
  props: ModalComponentProps,
): JSX.Element => {
  const { isModalOpen, id } = props;
  const classes = useStyles();
  return (
    <Dialog
      id={id}
      open={isModalOpen}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <div>
        <Grid
          container
          className={classes.container}
          justify="flex-start"
          alignItems="flex-start"
          direction="column"
        >
          <Grid item xs>
            <DialogTitle
              className={classes.dialogTitle}
              disableTypography={true}
              id="modal-title"
            >
              {props.title}
              <IconButton
                data-cy={'modal_component_close_btn'}
                id={'modal_component_close_btn'}
                aria-label="close"
                className={classes.closeButton}
                onClick={props.onClose}
              >
                <CloseIcon />
              </IconButton>
            </DialogTitle>
          </Grid>
          <Grid item xs className={classes.dialogContentContainer}>
            <DialogContent
              className={classes.dialogContent}
              style={{ padding: '0px 20px 10px 20px' }}
            >
              <DialogContentText className={classes.dialogContentText}>
                {props.message}
              </DialogContentText>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
              <Button
                data-cy={'modal_component_okay_btn'}
                id={'modal_component_okay_btn'}
                className={
                  props.isPrimary ? classes.submitButton : classes.cancelButton
                }
                variant="contained"
                color={props.isPrimary ? 'primary' : 'secondary'}
                onClick={props.onClose}
              >
                Okay
              </Button>
            </DialogActions>
          </Grid>
        </Grid>
      </div>
    </Dialog>
  );
};

ModalComponent.defaultProps = {
  id: 'modal_id',
  isModalOpen: false,
  isPrimary: false,
};
