import React from 'react';

import {
  Typography,
  List,
  ListItem,
  ListItemText
} from '@material-ui/core/';
import { makeStyles } from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faStopCircle,
  faLock
} from '@fortawesome/free-solid-svg-icons';

const MemberStatusActionButtonsStyles = makeStyles(() => ({
  listRoot: {
    display: 'block',
    overflow: 'hidden',
    backgroundColor: '#fdfdfd',
    padding: '1rem 0'
  },
  listItem: {
    display: 'inline',
    padding: '0 1rem'
  },
  listSelectedCount: {
    display: 'inline'
  },
  listStatus: {
    display: 'inline'
  },
  textSelectedCount: {
    display: 'inline',
    color: '#1e2071',
    fontWeight: 'bold'
  },
  textRedStatus: {
    display: 'inline',
    color: '#ff1400',
    textDecoration: 'underline',
    cursor: 'pointer',
    fontSize: '.9em'
  },
  textBlueStatus: {
    display: 'inline',
    color: '#00a1d6',
    textDecoration: 'underline',
    cursor: 'pointer',
    fontSize: '.9em'
  },
  textGreenStatus: {
    display: 'inline',
    color: '#04843f',
    textDecoration: 'underline',
    cursor: 'pointer',
    fontSize: '.9em'
  },
}))

interface MemberStatusActionButtonsProps {
  selectedCount: number,
  statusCount: any,
  onStatusActionClick: (statusAction: string) => void
}

export const MemberStatusActionButtons: React.FC<MemberStatusActionButtonsProps> = (
  props: MemberStatusActionButtonsProps
): JSX.Element => {
  const classes = MemberStatusActionButtonsStyles(props);
  let selectedCount = props.selectedCount;

  let activeCount = props.statusCount.active;
  let markedSuspendCount = props.statusCount.markedForSuspension;
  let markedTerminateCount = props.statusCount.markedForTermination;
  let suspendedCount = props.statusCount.suspended;
  let terminatedCount = props.statusCount.terminated;
  let othersCount = props.statusCount.others;

  const handleMemberStatusAction = (statusAction: string) => {
    if(props.onStatusActionClick){
      props.onStatusActionClick(statusAction);
    }
  }

  return(
    <List className={classes.listRoot}>
      <ListItem className={classes.listItem}>
        <ListItemText
          disableTypography
          primary={
            <Typography className={classes.textSelectedCount}>
              {selectedCount === 0 ? 'NO ITEM SELECTED' : 
                  selectedCount === 1 ? `${selectedCount} ITEM SELECTED` : 
                  `${selectedCount} ITEMS SELECTED`
              }
            </Typography>
          }
          className={classes.listSelectedCount}
        />          
      </ListItem>

      {selectedCount > 0 ? 
        othersCount === 0 ? 
          markedSuspendCount === 0 && suspendedCount === 0 && terminatedCount === 0 ?
            activeCount > 0 || markedTerminateCount > 0 ? (
              <ListItem className={classes.listItem}>
                <ListItemText
                  disableTypography
                  primary={
                    <Typography className={classes.textRedStatus}>
                        <FontAwesomeIcon
                          icon={faLock}
                          style={{marginRight:'8px'}}
                        />
                        Suspend 
                    </Typography>
                  }
                  className={classes.listStatus}
                  onClick={() => {handleMemberStatusAction('Suspend')}}
                />
              </ListItem>
            ) : null
          : null 
        : null 
      : null}

      {/* COMMENTED OUT AS OF APR 23, 2020. Pls do not remove */}
      {/* {selectedCount > 0 ? 
        othersCount === 0 ?
          activeCount === 0 && markedSuspendCount === 0 && markedTerminateCount === 0 && suspendedCount === 0 ?
            terminatedCount > 0 ?
              <ListItem className={classes.listItem}>
                  <ListItemText 
                      disableTypography
                      primary={
                        <Typography className={classes.textGreenStatus}>
                          <FontAwesomeIcon
                            icon={faStopCircle}
                            style={{marginRight:'8px'}}
                          />
                          Re-activate  
                        </Typography>
                      }
                      className={classes.listStatus}
                      onClick={() => {handleMemberStatusAction('Reactivate')}}
                  />
              </ListItem>
            : null
          : null
        : null
      : null} */}

      {selectedCount > 0 ? 
        othersCount === 0 ?
          markedTerminateCount === 0 && terminatedCount === 0 ?
            activeCount > 0 || markedSuspendCount > 0 || suspendedCount > 0 ? (
              <ListItem className={classes.listItem}>
                  <ListItemText 
                      disableTypography
                      primary={                      
                        <Typography className={classes.textRedStatus}>
                          <FontAwesomeIcon
                            icon={faStopCircle}
                            style={{marginRight:'8px'}}
                          />
                          Terminate
                        </Typography>
                      }
                      className={classes.listStatus}
                      onClick={() => {handleMemberStatusAction('Terminate')}}
                  />
              </ListItem>
            ) : null 
          : null 
        : null 
      : null}

      {selectedCount > 0 ?
        othersCount === 0 ?
          activeCount === 0 && markedSuspendCount === 0 && suspendedCount === 0 && terminatedCount === 0 ? 
            markedTerminateCount > 0 ? (
            <ListItem className={classes.listItem}>
              <ListItemText
                disableTypography
                primary={
                  <Typography className={classes.textGreenStatus}>
                    <FontAwesomeIcon
                      icon={faStopCircle}
                      style={{marginRight:'8px'}}
                    />
                    Cancel Termination
                  </Typography>
                }
                className={classes.listStatus}
                onClick={() => {handleMemberStatusAction('Cancel Termination')}}
              />
            </ListItem>
          ) : null
          : null
        : null
      : null}

      {selectedCount > 0 ? 
        othersCount === 0 ?
          activeCount === 0 && markedSuspendCount === 0 && markedTerminateCount === 0 && terminatedCount === 0?
            suspendedCount > 0 ? (
              <ListItem className={classes.listItem}>
                  <ListItemText 
                      disableTypography
                      primary={
                        <Typography className={classes.textBlueStatus}>
                            <FontAwesomeIcon
                              icon={faLock}
                              style={{marginRight:'8px'}}
                            />
                            Lift Suspension
                        </Typography>
                      }
                      className={classes.listStatus}
                      onClick={() => {handleMemberStatusAction('Lift Suspension')}}
                  />
              </ListItem>
            ) : null 
          : null
        : null
      : null}

      {selectedCount > 0 ? 
        othersCount === 0 ?
          activeCount === 0 && markedTerminateCount === 0 && suspendedCount === 0 && terminatedCount === 0 ? 
            markedSuspendCount > 0 ? (
              <ListItem className={classes.listItem}>
                <ListItemText
                  disableTypography
                  primary={                  
                    <Typography className={classes.textGreenStatus}>
                        <FontAwesomeIcon
                          icon={faLock}
                          style={{marginRight:'8px'}}
                        />
                        Cancel Suspension
                    </Typography>
                  }
                  className={classes.listStatus}
                  onClick={() => {handleMemberStatusAction('Cancel Suspension')}}
                />
              </ListItem>
            ) : null
          : null
        : null
      : null}
    </List>
  )
}