// React imports
import React, { useState,useEffect } from 'react';
// Mui imports
import { Grid, Typography, Box, Checkbox, Button, Radio, RadioGroup, FormControl, FormControlLabel,Switch } from '@material-ui/core/';
// Relative imports
import { Modal } from '../../../ProcessBatches/Components/Modal';
import MomentUtils from '@date-io/moment';
import { BasicSelect, BasicTextField } from 'Components/UI'
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import {
  STYLE_CONST,
  SELECT_SENT_VIA,
  SELECT_REASON,
} from '../../../ProcessBatches/ProcessBatches.const';
import Dropzone from 'react-dropzone';
import {
  faFileUpload,
} from '@fortawesome/free-solid-svg-icons';
import { MaterialUiPickersDate } from '@material-ui/pickers';
// Core imports
// Thirdparty imports
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { ReactivateTable } from '../ReactivateTable'
import { Loader } from 'Components/UI/LoadingIndicator';
import { API } from 'Pages/API';
interface MemberReactivateModalProps {
  modalVisibility: boolean;
  modalClose: any;
  modalNext: any;
  isExternal: boolean;
  applyToAll:boolean;
  setApplyToAll:(apply:boolean)=>void;
  setIsExternal: any;
  currentMember: any;
  currentCount:number;
  overalllCount:number;
  lastMember:boolean;
}

export const MemberReactivateModal: React.FC<MemberReactivateModalProps> = (
  props: MemberReactivateModalProps,
): JSX.Element => {
  const classes = STYLE_CONST.batchesWrapper();
  const [dateTermination, setDateTermination] = useState<any>(new Date());
  const [dateTerminationError, setDateTerminationError] = useState<boolean>(false);
  const [dateEndorsed, setDateEndorsed] = useState<any>(new Date());
  const [dateEndorsedError, setDateEndorsedError] = useState<boolean>(false);
  const [sentVia, setSentVia] = useState<string>('');
  const [sentViaError, setSentViaError] = useState<boolean>(false);
  const [remarks, setRemarks] = useState<string>('');
  const [remarksError, setRemarksError] = useState<boolean>(false);
  // const [senderEmail, setSenderEmail] = useState<string>('');
  const [ticketNumber, setTicketNumber] = useState<string>('');
  const [reason, setReason] = useState<string>('');
  const [reasonError, setReasonError] = useState<boolean>(false);
  const [reasonOthers, setReasonOthers] = useState<string>('');
  const [personWhoEndorsed, setPersonWhoEndorsed] = useState<string>('');
  const [personWhoEndorsedError, setPersonWhoEndorsedError] = useState<boolean>(false);
  const [endorsedBy, setEndorsedBy] = useState<string>('');
  const [endorsedByError, setEndorsedByError] = useState<boolean>(false);
  const [cardReturned, setCardReturned] = useState<boolean>(false);
  const [generateNewCard, setGenerateNewCard] = useState<boolean>(false);
  const [uploadedFile, setUploadedFile] = useState<any[]>([])
  //@ts-ignore
  const [dependentRows, setDependentRows] = useState<any[]>([])
  const [selection, setSelection] = useState<any[]>([])
  const [selectAll , setSelectAll] = useState(false)
  const [selectedDependent, setSelectedDependent] = useState<any[]>([]);
  const [loading, setLoading] = useState(false)
  const reset = () => {
    setDateTermination(new Date());
    setDateTerminationError(false);
    setDateEndorsed(new Date());
    setDateEndorsedError(false);
    setSelection([])
    setSelectAll(false)
    setSentVia('');
    setSentViaError(false);
    setRemarks('');
    setRemarksError(false);
    setTicketNumber('');
    setReason('');
    setReasonError(false);
    setReasonOthers('');
    setPersonWhoEndorsed('');
    setPersonWhoEndorsedError(false);
    setEndorsedBy('');
    setEndorsedByError(false);
    setCardReturned(false);
    setGenerateNewCard(false);
    setUploadedFile([]);
  };

  useEffect(()=>{
    
    if(props.currentMember.id){
      console.log('check',props.currentMember.id)
      setLoading(true)
      API.getMemberData(props.currentMember.id)
      .then((response)=>{
        console.log('memberd ata response',response)
        API.getMemberDependents(response.temp_id)
        .then((response2)=>{
          console.log('dependent response',response2)
          const rowlist:any[] = []
          response2.forEach((item)=>{
            rowlist.push({
              ...item,
              member_name:item.first_name+' '+item.middle_name+' '+item.last_name,
              relationship:item.relationship_to_principal,
              principal:item.principal_first_name+' '+item.principal_middle_name+' '+item.principal_last_name,
              status:item.member_status,
            })
          })
          setDependentRows(rowlist)
          setLoading(false)
        })
        .catch((error2)=>{
          console.log('error getting dependents',error2)
        })
      })
      .catch((error)=>{
        console.log('error getting dependents',error)
      })
    }
    
  },[props.currentMember])

  const handleClose = () => {
    reset();
    props.modalClose();
  }

  const handleNext = () => {
    let errors = 0;

    console.log(uploadedFile);

    if (sentVia.length === 0) {
      setSentViaError(true);
      console.log('error sentVia')
      errors++;
    }

    if (dateTermination === null) {
      setDateTerminationError(true);
      console.log('error sentVia')
      errors++;
    }

    if (dateEndorsed === null) {
      setDateEndorsedError(true);
      console.log('error sentVia')
      errors++;
    }

    if (props.isExternal) {
      if (endorsedBy.length === 0) {
        setEndorsedByError(true);
        console.log('error endorsedBy')
        errors++;
      }
    } else {
      if (remarks.length === 0) {
        setRemarksError(true);
        console.log('error remarks')
        errors++;
      }
    }
    console.log('errors',errors)
    if(errors === 0){
      let data: object = {
        id:props.currentMember.id ? props.currentMember.id :'',
        member_id:props.currentMember.member_id ? props.currentMember.member_id :'', 
        member_name:props.currentMember.member_name ? props.currentMember.member_name :'',
        corporation: props.currentMember.corporates ? props.currentMember.corporates :'',
        type:props.currentMember.member_type ? props.currentMember.member_type :'',
        previous_status_to_restore:'active',
        sent_via: sentVia,
        date_termination: dateTermination,
        date_endorsed: dateEndorsed,
        is_external: props.isExternal,
        selectedDependent:selectedDependent,
        endorsed_by:endorsedBy,
      };

      if (ticketNumber.length > 0) {
        data['ticket_no'] = ticketNumber;
      }

      if (props.isExternal) {
        if (personWhoEndorsed.length > 0) {
          data['person_who_endorsed'] = personWhoEndorsed; 
        } 
      } else {
        if (remarks.length > 0) {
          data['remarks'] = remarks;
        }
        if (reasonOthers.length > 0) {
          data['reason_others'] = reasonOthers;
        }
      }

      reset();
      props.modalNext(data, uploadedFile);
    }
    // reset();
  }

  const member = props.currentMember;

  let files = uploadedFile && uploadedFile.length  ? 
    uploadedFile.map((file: any) => (
      <li key={file.path}>
        {!file.name ? file.path : file.name}
      </li>
    )) : [];
  
    const dependentColumn = [
      {
        name: 'member_name',
        title: 'Member Name',
      },
      {
        name: 'relationship',
        title: 'Relationship',
      },
      {
        name: 'principal',
        title: 'Principal',
      },
      {
        name: 'status',
        title: 'Status',
      },
    ];
    console.log('check current member',member)
    const onRowSelect = selected => {
      const added = selected.filter(sel => !selection.includes(sel));
      console.log('selected',selected)
      let currselected = selectedDependent;
  
      if (added.length > 0) {
        added.forEach(item => {
          const chk = dependentRows.find(item2 => item === item2._id);
          if (chk !== undefined) {
            currselected.push(chk); 
          }
        });
      }
      const removed = selection.filter(sel => !selected.includes(sel));
      if (removed.length > 0) {
        removed.forEach(item => {
          currselected = currselected.filter(item2 => item !== item2._id);
        });
      }
      const rowchkr:boolean[]=[]
      dependentRows.forEach(item=>{
        rowchkr.push(selected.includes(item._id))
      })
      
      setSelectedDependent(currselected);
      setSelection(selected);
      if(rowchkr.includes(false)){
        setSelectAll(false);
      } else {
        setSelectAll(true)
      }
      
    };

    const handleSelectAll = (value: boolean) => {
      let selected: string[] = [];
      let currselected: any[] = [];
      if (value) {
        selected = selection;
        const added: string[] = [];
        const filteredtickets = dependentRows;
        filteredtickets.map(item => {
          if (
            selected.findIndex(item2 => {
              return item2 === item._id;
            }) === -1
          ) {
            selected.push(item._id);
          }
          if (selectedDependent.findIndex(item2 => item2._id === item._id) === -1) {
            added.push(item._id);
          }
        });
  
        console.log('select all', added, selected, selection);
        currselected = selectedDependent;
  
        if (added.length > 0) {
          added.forEach(item => {
            const chk = dependentRows.find(item2 => item === item2._id);
            if (chk !== undefined) {
              currselected.push(chk);
            }
          });
        }
      }
  
      setSelectedDependent(currselected);
      setSelection(selected);
      setSelectAll(value);
    };
  
  return (
    <Modal visibility={props.modalVisibility} close={handleClose}>
      {loading ? <Loader /> : null}
      <Typography className={classes.subHeading}>
        Re-activation Details
      </Typography>
      
      <Grid container>
          <Grid item sm={8}>
            <Typography className={classes.modalInfo}>
              {member['member_name'] ? member['member_name'] + ' ' : 'First '}
              
            </Typography>
          </Grid>
          <Grid item sm={4}>
                <Grid container>
                  <Grid item sm={12}
                    direction="row"
                    justify="flex-end"
                    alignItems="stretch"
                  >
                    <Typography style={{fontSize: 14,
                      fontWeight: 300,
                      marginLeft: '5px',
                      textAlign:'right'}}>
                      {`${props.currentCount}/${props.overalllCount} members to reactivate`}
                    </Typography>
                  </Grid>
                </Grid>
          </Grid>
          <Grid item sm={8}>
            <FormControl component="fieldset">
              <RadioGroup name="source" className={classes.radioGroup}>
                <Typography className={classes.radioInfo}>Source</Typography>
                <FormControlLabel
                  className={classes.formRadioLabel}
                  control={ 
                        <Radio 
                          id='radio_internal'
                          data-cy='radio_internal'
                          name="internal"
                          className={classes.radio}
                          checked={props.isExternal === false}
                          onClick={(e) => {
                            e.stopPropagation();
                            reset();
                            props.setIsExternal(false);
                          }}
                        />
                      }
                  label="Internal"
                />
                <FormControlLabel
                  className={classes.formRadioLabel}
                  control={ 
                        <Radio 
                          id='radio_external'
                          data-cy='radio_external'
                          name="external"
                          className={classes.radio}
                          checked={props.isExternal === true}
                          onClick={(e) => {
                            e.stopPropagation();
                            reset();
                            props.setIsExternal(true);
                          }}
                        />
                      }
                  label="External"
                />
              </RadioGroup>
            </FormControl>
          </Grid>
          <Grid item sm={4} 
          >
            <Grid container>
                <Grid item sm={12}
                  style={{display:'block'}}
                >
                  <Box
                    display="flex"
                    justifyContent="flex-end"
                    alignItems="center"
                    m={1}
                    style={{ width: '100%', height: '100%' }}
                  >
                    <FormControl component="fieldset"
                        // style={{padding:'15px 0px'}}
                      >
                        <FormControlLabel
                            className={classes.formRadioLabel}
                            control={ 
                                  <Switch 
                                    id='switch_internal'
                                    data-cy='switch_internal'
                                    name="internal"
                                    className={classes.switchreactivate}
                                    
                                    color='default'
                                    checked={props.applyToAll}
                                    onChange={(e) => {
                                      // e.stopPropagation();
                                      props.setApplyToAll(e.target.checked);
                                    }}
                                  />
                                }
                            label="Apply to all selected members"
                          />
                      </FormControl>
                  </Box>
                    
                </Grid>
              </Grid>
            
          </Grid>
      </Grid>

      { !props.isExternal ? 
          <Grid container>
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Date of Termination:</Typography>
                <KeyboardDatePicker
                  id='date_termination'
                  data-cy='date_termination'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  maxDate={new Date(dateEndorsed)}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateTermination}
                  initialFocusedDate={dateTermination ? dateTermination : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateTermination(data);
                    setDateTerminationError(data === null);
                  }}
                  error={dateTerminationError}
                />
              </MuiPickersUtilsProvider>
            </Grid>
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Endorsed Date:</Typography>
                <KeyboardDatePicker
                  id='date_endorsed'
                  data-cy='date_endorsed'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateEndorsed}
                  initialFocusedDate={dateEndorsed ? dateEndorsed : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateEndorsed(data);
                    setDateEndorsedError(data === null);
                  }}
                  error={dateEndorsedError}
                />
              </MuiPickersUtilsProvider>
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='ticket-number'
                  data-cy='ticket-number'
                  label='Ticket Number'
                  labelFontSize='14px'
                  value={ticketNumber}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setTicketNumber(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='endorsed-by'
                  data-cy='endorsed-by'
                  label='Endorsed By'
                  labelFontSize='14px'
                  value={endorsedBy}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setEndorsedBy(value);
                    setEndorsedByError(value.length === 0);
                  }}
                  error={endorsedByError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicSelect
                  marginTop='10px'
                  marginBottom='10px'
                  labelFontSize='14px'
                  id='sent-via'
                  data-cy='sent-via'
                  label='Sent via*'
                  menuItems={SELECT_SENT_VIA}
                  width="85%"
                  value={sentVia}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setSentVia(value);
                    setSentViaError(value.length === 0);
                  }}
                  error={sentViaError}
                />
            </Grid>
            
            <Grid item sm={4}>
                <BasicTextField
                  id='remarks'
                  data-cy='remarks'
                  label='Remarks'
                  labelFontSize='14px'
                  value={remarks}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setRemarks(value);
                    setRemarksError(value.length === 0);
                  }}
                  error={remarksError}
                  width="85%"
                />
            </Grid>
            
            <Grid item sm={12}>
                <ReactivateTable
                  id='dependent_table'
                  rows={dependentRows}
                  columns={dependentColumn}
                  onRowSelect={onRowSelect}
                  selection={selection}
                  selectall={selectAll}
                  onSelectAll={(value: boolean) => {
                    handleSelectAll(value);
                    setSelectAll(value);
                  }}
                />
            </Grid>
          </Grid> : null
      }

      { props.isExternal ? 
          <Grid container>
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Date Sent:</Typography>
                <KeyboardDatePicker
                  id='date_sent_input'
                  data-cy='date_sent_input'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  maxDate={new Date(dateEndorsed)}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateTermination}
                  initialFocusedDate={dateTermination ? dateTermination : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateTermination(data);
                    setDateTerminationError(data === null);
                  }}
                  error={dateTerminationError}
                />
              </MuiPickersUtilsProvider>
            </Grid> 
            <Grid item sm={4}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Typography className={classes.formLabel}>Date Recieved:</Typography>
                <KeyboardDatePicker
                  id='date_received_input'
                  data-cy='date_received_input'
                  className={classes.datePicker}
                  style={{      
                    marginTop: '10px',
                    marginBottom: '20px',
                  }}
                  minDate={new Date()}
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  invalidDateMessage=""
                  value={dateEndorsed}
                  initialFocusedDate={dateEndorsed ? dateEndorsed : new Date()}
                  onChange={(date: MaterialUiPickersDate | null) => {
                    const data =  date ? date.toDate() : null;
                    setDateEndorsed(data);
                    setDateEndorsedError(data === null);
                  }}
                  error={dateEndorsedError}
                />
              </MuiPickersUtilsProvider>
            </Grid> 
            <Grid item sm={4}>
                <BasicTextField
                  id='ticket-number'
                  data-cy='ticket-number'
                  label='Ticket Number (Optional)'
                  labelFontSize='14px'
                  value={ticketNumber}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setTicketNumber(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='person-endorse'
                  data-cy='person-endorse'
                  label='Person Who Endorsed*'
                  labelFontSize='14px'
                  value={personWhoEndorsed}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setPersonWhoEndorsed(value);
                    setPersonWhoEndorsedError(value.length === 0);
                  }}
                  error={personWhoEndorsedError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='endorsed-by'
                  data-cy='endorsed-by'
                  label='Endorsed By*'
                  labelFontSize='14px'
                  value={endorsedBy}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setEndorsedBy(value);
                    setEndorsedByError(value.length === 0);
                  }}
                  error={endorsedByError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicSelect
                  marginTop='10px'
                  marginBottom='10px'
                  labelFontSize='14px'
                  id='sent-via'
                  data-cy='sent-via'
                  label='Sent via*'
                  menuItems={SELECT_SENT_VIA}
                  width="85%"
                  value={sentVia}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setSentVia(value);
                    setSentViaError(value.length === 0);
                  }}
                  error={sentViaError}
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='endorsed-by'
                  data-cy='endorsed-by'
                  label='Endorsed By*'
                  labelFontSize='14px'
                  value={endorsedBy}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setEndorsedBy(value);
                    setEndorsedByError(value.length === 0);
                  }}
                  error={endorsedByError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicSelect
                  marginTop='10px'
                  marginBottom='10px'
                  labelFontSize='14px'
                  id='reason'
                  data-cy='reason'
                  label='Reason*'
                  menuItems={SELECT_REASON}
                  value={reason}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setReason(value);
                    setReasonError(value.length === 0);
                  }}
                  error={reasonError}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
                <BasicTextField
                  id='reason-others'
                  data-cy='reason-others'
                  label='If Others, please specify'
                  labelFontSize='14px'
                  value={reasonOthers}
                  onChange={(e: any) => {
                    const value = e.target.value;
                    setReasonOthers(value);
                  }}
                  width="85%"
                />
            </Grid>
            <Grid item sm={4}>
              <FormControl component="fieldset">
                <RadioGroup name="card">
                  <FormControlLabel
                    className={classes.formLabel}
                    control={ 
                          <Checkbox 
                            id='checkbox_card_returned'
                            data-cy='checkbox_card_returned'
                            name="card_returned"
                            className={classes.checkbox}
                            checked={cardReturned}
                            onChange={e => {
                              setCardReturned(e.target.checked);
                            }}
                          />
                        }
                    label="Card Returned"
                  />
                  <FormControlLabel
                    className={classes.formLabel}
                    control={ 
                          <Checkbox 
                            id='checkbox_generate_new_card'
                            data-cy='checkbox_generate_new_card'
                            name="generate_new_card"
                            className={classes.checkbox}
                            checked={generateNewCard}
                            onChange={e => {
                              setGenerateNewCard(e.target.checked);
                            }}
                          />
                        }
                    label="Generate New Card No."
                  />
                </RadioGroup>
              </FormControl>
            </Grid>
            <Grid item xs={8}>
              <Dropzone
                onDropAccepted={acceptedFiles => setUploadedFile(acceptedFiles)}
              >
                {({ getRootProps, getInputProps }) => (
                  <section>
                    <div className={classes.fileupload} {...getRootProps()}>
                      <input id="upload_file_member"
                            data-cy='upload_file_member' {...getInputProps()} />
                      <FontAwesomeIcon
                        className="fa-lg"
                        icon={faFileUpload}
                        style={{ color: 'rgba(39, 46, 76, 0.56)' }}
                      />
                      <p
                        style={{
                          color: 'rgba(39, 46, 76, 0.56)',
                          fontWeight: 400,
                          fontSize: '14px',
                        }}
                      >
                        Upload OR
                      </p>
                    </div>
                    <aside>
                      {files.length > 0 ? (
                        <div>
                          <Typography>Uploaded File</Typography>
                          <ul>{files}</ul>
                        </div>
                      ) : null}
                    </aside>
                  </section>
                )}
              </Dropzone>
            </Grid>
          </Grid> : null
      }

      <Grid container className={classes.printButtons} spacing={5}>
        <Grid className={classes.modalButtonContainerLeft} item sm={6}>
          <Button
            id='cancel_reprint_button'
            data-cy='cancel_reprint_button'
            className={classes.modalButtonLeft}
            style={{ marginRight: '10px' }}
            variant="outlined"
            color="secondary"
            size="small"
            onClick={handleClose}
          >
            {' '}
            Cancel{' '}
          </Button>
        </Grid>
        <Grid className={classes.modalButtonContainerRight} item sm={6}>
          <Button
            id='finish_member_button'
            data-cy='finish_member_button'
            className={classes.modalButtonRight}
            style={{ marginRight: '10px' }}
            variant="contained"
            color="secondary"
            size="small"
            onClick={handleNext}
          >
            {' '}
            {props.lastMember ? 'Submit' : 'Next Member'}
            
            {' '}
          </Button>
        </Grid>
      </Grid>
    </Modal>
  );
};
