interface LiftSuspensionMemberModalProps {
  isModalOpen: boolean;
  id: string;
  memberData: any;
  onClose: () => void;
  onLiftSuspension: (item) => void;
}

export const LiftSuspensionMemberModal: React.FC<
  LiftSuspensionMemberModalProps
> = (props: LiftSuspensionMemberModalProps): JSX.Element => {
  const classes = useStyles();
  const { onLiftSuspension, isModalOpen, memberData, id } = props;
  const [effectiveLiftingDate, setEffectiveLiftingDate] = React.useState<any>(
    null,
  );

  const handleLiftSuspensionMember = () => {
    if (effectiveLiftingDate !== null) {
      onLiftSuspension(effectiveLiftingDate);
      setEffectiveLiftingDate(null);
    } else {
      onLiftSuspension(
        memberData
          ? memberData.member_suspension
            ? memberData.member_suspension.suspension_lift_date
              ? moment(memberData.member_suspension.suspension_lift_date)
              : null
            : null
          : null,
      );
      setEffectiveLiftingDate(null);
    }
  };

  const getSuspensionDate = () => {
    const suspensionDate = get(
      memberData,
      'member_suspension.suspension_start_date',
      null,
    );

    if (!isNil(suspensionDate)) {
      const suspensionDateMoment: moment.Moment = moment(suspensionDate);
      if (!isNil(suspensionDateMoment) && suspensionDateMoment.isValid()) {
        return suspensionDateMoment.format('MMM DD, YYYY');
      }
    }

    return '-';
  };

  const onClose = () => {
    setEffectiveLiftingDate(null);
    props.onClose();
  }

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 1200 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Lift Suspension Details</div>
        <IconButton
          data-cy={'lift_suspension_modal_close_btn'}
          id={'lift_suspension_modal_close_btn'}
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container spacing={3} style={{ padding: '1.5em 0' }}>
          <Grid item xs={12}>
            <Typography className={classes.textLabel}>
              EFFECTIVE DATE OF SUSPENSION
            </Typography>
            <Typography className={classes.textValue}>
              {getSuspensionDate()}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography className={classes.textValue}>
              Effective Date of Lifting
            </Typography>
            <DatePicker
              data-cy={'lift_suspension_modal_lift_picker'}
              id={'lift_suspension_modal_lift_picker'}
              placeholder=""
              labelSize="small"
              value={
                effectiveLiftingDate !== null
                  ? effectiveLiftingDate
                  : memberData
                  ? memberData.member_suspension
                    ? memberData.member_suspension.suspension_lift_date
                      ? moment(
                          memberData.member_suspension.suspension_lift_date,
                        )
                      : null
                    : null
                  : null
              }
              error={false}
              error_message={''}
              onChange={(date: Date) => {
                setEffectiveLiftingDate(date);
              }}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <CancelButton
              data-cy={'lift_suspension_modal_cancel_btn'}
              id={'lift_suspension_modal_cancel_btn'}
              className={classes.cancelButton}
              variant="contained"
              color="primary"
              onClick={onClose}
            >
              Cancel
            </CancelButton>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <SubmitButton
              data-cy={'lift_suspension_modal_btn'}
              id={'lift_suspension_modal_btn'}
              className={classes.submitButton}
              // disabled={effectiveLiftingDate !== null ? false : true}
              variant="contained"
              color="primary"
              onClick={handleLiftSuspensionMember}
            >
              Lift Suspension
            </SubmitButton>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};
