const datePickerStyles = makeStyles({
	keyboardDatePicker: {
		// marginTop: '10px',
		// marginBottom: '20px',
		width: '100%',
	},
});

const Select = ({ items, ...restProps  }: any) => {
	return (
		<MaterialUI.Select
			id='clientstable_select_item'
			data-cy='clientstable_select_item'
			variant='outlined'
			style={{ width: '100%' }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value === selected;
					});
					if (value) {
						return value.label;
					} else {
						return '';
					}
				}
				return '';
			}}
			{...restProps}
		>
			{items.map((item: any, i: number) => (
				<MaterialUI.MenuItem id='clientstable_menu_item' data-cy='clientstable_menu_item' key={i} value={item.value}>
					{item.label}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};

