import * as React from 'react';
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Grid,
  Button,
  TextField,
  InputLabel,
  FormGroup,
  FormControlLabel,
  Checkbox,
  FormHelperText,
} from '@material-ui/core/';
import { find, lowerCase } from 'lodash';

import { TableSelect } from '../Table';
import '../../style.scss';

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '100%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '250px',
  },
  dialogTitle: {
    marginTop: theme.spacing(2),
    marginBottom: '5px',
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    padding: '16px 18px',
  },
  dialogAction: {
    marginBottom: theme.spacing(1),
    justifyContent: 'center',
    padding: '3px',
  },
  dialogContentContainer: {
    width: '100%',
  },
  cancelButton: {
    width: 115,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  saveButton: {
    width: 115,
    marginLeft: 25,
  },
  label: {
    fontSize: '1rem',
  },
  errorText: {
    marginTop: '0px',
    marginBottom: '5px',
    color: '#E53935',
  },
  errorText2: {
    marginTop: '10px',
    color: '#E53935',
  },
}));

interface AddNewDataMapModalProps {
  isModalOpen: boolean;
  id: string;
  title: string;
  account: string;
  data: any;
  onClose: () => void;
  setDataValues: (data: any, attr: string) => void;
  savingNewDataMap: (payload: any) => void;
}

export const AddNewDataMapModal: React.FC<AddNewDataMapModalProps> = (
  props: AddNewDataMapModalProps,
): JSX.Element => {
  const { isModalOpen, account, id } = props;
  const classes = useStyles();
  const tableName = 'add_new_data_map';
  const [name, setName] = useState('');
  const [is_default, setIsDefault] = useState(false);
  const [error_name, setErrorName] = useState(false);
  const [error_name_message, setErrorNameMessage] = useState('');
  const [mappingError, setMappingError] = useState(false);
  const [mapping_error_message, setMappingErrorMessage] = useState('');

  useEffect(() => {
    if (props.isModalOpen) {
      setName('');
      mapDataValues();
    }
  }, [props.isModalOpen]);

  function mapDataValues() {
    const tempData: any = props.data;
    const { rows, columns } = tempData;

    if (!rows || !columns) {
      return;
    }

    const sheetOptions = find(columns, column => {
      if (column.type && lowerCase(column.type) === 'select') {
        return true;
      }
      return false;
    });

    if (!sheetOptions || !sheetOptions.options) {
      return;
    }

    const { options } = sheetOptions;
    const rowsLen = rows.length;
    for (let idx = 0; idx < rowsLen; idx++) {
      const { system_name } = rows[idx];
      const matched = find(options, option => {
        return lowerCase(system_name) === lowerCase(option.value);
      });

      if (matched) {
        tempData['rows'][idx]['sheet_name'] = matched.value;
      }
    }

    props.setDataValues(tempData, tableName);
  }

  function handleValuesChange(value: string, row: string, table_name: string) {
    const temp_table: any = props.data;
    temp_table['rows'][parseInt(row)]['sheet_name'] = value;
    props.setDataValues(temp_table, table_name);
    setMappingError(false);
    setMappingErrorMessage('');
  }

  function handleName(e: React.ChangeEvent<HTMLInputElement>) {
    const { value } = e.target;
    setName(value);
    setErrorName(false);
    setErrorNameMessage('');
  }

  function handleDefault(e: React.ChangeEvent<HTMLInputElement>) {
    setIsDefault(e.target.checked);
  }

  function onSave() {
    /******* validate name *****/
    let error_cnt = 0;
    if (name === '' || name === undefined) {
      setErrorName(true);
      setErrorNameMessage('Name is required.');
      error_cnt = error_cnt + 1;
    }
    /****** validate mapping *****/
    let mapping_flag = false;
    for (const row_cnt in props.data['rows']) {
      const row = props.data['rows'][row_cnt];
      if (row['sheet_name'] !== '' && row['sheet_name'] !== undefined) {
        mapping_flag = true;
        break;
      }
    }
    if (!mapping_flag) {
      error_cnt = error_cnt + 1;
      setMappingError(true);
      setMappingErrorMessage('Please set mapping.');
    }

    if (error_cnt === 0) {
      props.onClose();
      let payload: any = {
        client_id: '',
        name: name,
        is_default: is_default,
        mapping: [],
      };
      for (const row_cnt in props.data['rows']) {
        const row = props.data['rows'][row_cnt];
        if (row['sheet_name'] !== '' && row['sheet_name'] !== undefined) {
          payload['mapping'].push({
            system_name: row['system_name'],
            sheet_name: row['sheet_name'],
          });
        }
      }
      props.savingNewDataMap(payload);
    }
  }

  return (
    <Dialog
      id={id}
      open={isModalOpen}
      aria-labelledby="modal-title"
      className={classes.dialog}
    >
      <div>
        <Grid
          container
          className={classes.container}
          justify="flex-start"
          alignItems="flex-start"
          direction="column"
        >
          <Grid item xs>
            <DialogTitle
              className={classes.dialogTitle}
              disableTypography={true}
              id="modal-title"
            >
              {props.title}
            </DialogTitle>
          </Grid>
          <Grid item xs className={classes.dialogContentContainer}>
            <DialogContent
              className={classes.dialogContent}
              style={{ padding: '0px 20px 10px 20px' }}
            >
              <DialogContentText className={classes.dialogContentText}>
                <Grid container>
                  <Grid item xs={12}>
                    <InputLabel
                      className={classes.label}
                      htmlFor="add_new_data_map_field"
                    >
                      Name
                    </InputLabel>
                    <TextField
                      data-cy={'add_new_data_map_field'}
                      id={'add_new_data_map_field'}
                      style={{ marginTop: '8px', width: '100%' }}
                      margin="normal"
                      variant="outlined"
                      value={name}
                      onChange={handleName}
                      error={error_name}
                      inputProps={{ 'aria-label': 'bare' }}
                    />
                    {error_name === true ? (
                      <FormHelperText
                        id="add_new_data_map_field_error"
                        className={classes.errorText}
                      >
                        {error_name_message}
                      </FormHelperText>
                    ) : null}
                  </Grid>
                  <Grid item xs={12}>
                    <TableSelect
                      data-cy={'add_new_data_map_table'}
                      id={'add_new_data_map_table'}
                      rows={
                        Object.keys(props.data).length > 0
                          ? props.data['rows']
                          : []
                      }
                      columns={
                        Object.keys(props.data).length > 0
                          ? props.data['columns']
                          : []
                      }
                      empty_message={'No data available.'}
                      name={tableName}
                      handleChange={handleValuesChange}
                      maxHeight={'300px'}
                    />
                    {mappingError === true ? (
                      <FormHelperText
                        id="add_new_data_map_field_error"
                        className={classes.errorText2}
                      >
                        {mapping_error_message}
                      </FormHelperText>
                    ) : null}
                  </Grid>
                  <Grid item xs={12} style={{ paddingTop: '5px' }}>
                    <FormGroup>
                      <FormControlLabel
                        control={
                          <Checkbox
                            data-cy={'add_new_data_map_set_default'}
                            id={'add_new_data_map_set_default'}
                            checked={is_default}
                            style={{ padding: '5px' }}
                            color="primary"
                            onChange={handleDefault}
                          />
                        }
                        label={`Set as template default for ${account}`}
                      />
                    </FormGroup>
                  </Grid>
                </Grid>
              </DialogContentText>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
              <Button
                data-cy={'cancel_new_data_map_btn'}
                id={'cancel_new_data_map_btn'}
                className={classes.cancelButton}
                variant="contained"
                color="secondary"
                onClick={props.onClose}
              >
                Cancel
              </Button>
              <Button
                data-cy={'save_new_data_map_btn'}
                id={'save_new_data_map_btn'}
                className={classes.saveButton}
                variant="contained"
                color="primary"
                onClick={onSave}
              >
                Save
              </Button>
            </DialogActions>
          </Grid>
        </Grid>
      </div>
    </Dialog>
  );
};

AddNewDataMapModal.defaultProps = {
  isModalOpen: false,
  id: 'modal_id',
};
