import moment from 'moment';

export class GlobalFunction {
  static formatNumber(num: number) {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
  }

  static toNameFormat(fname: string, mname: string, lname: string) {
    if (fname === '' && mname === '' && lname === '')
      return '';
    return `${fname} ${GlobalFunction.getIntials(mname)} ${lname}`;
}

  static getIntials(text: string) {
    var initials = '';
    var wordArray = text.split(' ');
    for (var i = 0; i < wordArray.length; i++) {
      initials += wordArray[i].substring(0, 1).toUpperCase() + '.';
    }
    return initials;
  }

  static toCommonDateString(date: Date) {
    if (date) {
      const monthNames = [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
      ];
      const day = date.getDate();
      const monthIndex = date.getMonth();
      const year = date.getFullYear();
      return monthNames[monthIndex] + ' ' + day + ', ' + year;
    }

    return '';
  }

  static getAge(birthDate: any) {
    if (birthDate) {
      var ageInMillis = Date.now() - birthDate;
      return Math.abs(new Date(ageInMillis).getUTCFullYear() - 1970);
    }

    return null;
  }

  static getRandomValue() {
    return Math.random()
      .toString(36)
      .substr(2, 6);
  }

  static toShortDateString(date: Date | null) {
    if (date === null) {
      return '';
    }

    try {
      const momentDate = moment(date);
      if (momentDate && momentDate.isValid()) {
        return momentDate.format('MMM DD, YYYY');
      }
    } catch (error) {
      return '';
    }

    return '';
  }

  static getDateFromString(dateString: string) {
    const mmDdYyyy = moment(dateString, 'MM/DD/YYYY', true);
    if (mmDdYyyy.isValid()) {
      return mmDdYyyy.toDate();
    }

    const yyyyMmDd = moment(dateString, 'YYYY/MM/DD', true);
    if (yyyyMmDd.isValid()) {
      return yyyyMmDd.toDate();
    }

    const date = new Date(dateString);
    if (date instanceof Date && !isNaN(date.getTime())) {
      return date;
    }

    return null;
  }

  static isValidDate(date: string) {
    const d = new Date(date);
    return d instanceof Date && !isNaN(d.getTime());
  }
}
