import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography
} from '@material-ui/core/';


import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles(theme => ({
    dialog: {
        align: 'center',
        justify: 'center',
        fontFamily: 'usual',
    },
    dialogContent: {
        paddingTop: '0px',
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 500,
    },
    dialogFirstTitle: {
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px',
    },
    dialogSecondTitle: {
        fontSize: '14px',
        color: '#272E4C',
        paddingBottom: '1rem'
    },
    tblPaper: {
        minHeight: 175,
        borderRadius: 0,
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
    },
    boldText: {
        fontWeight: 'bold',
    },
    cancelButton: {
        minWidth: 129,
        marginRight: 40,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: '#FFFFFF',
    },
    suspendButton: {
        minWidth: 129,
        backgroundColor: '#FD5451',
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
}));

const SuspendButton = withStyles({
    root: {
        color: '#FFFFFF',
        backgroundColor: '#24B070',
    },
})(Button);

const CancelButton = withStyles({
    root: {
        '&:hover': {
            backgroundColor: '#3AB77D',
            color: '#FFFFFF',
        },
    },
})(Button);

interface ConfirmReactivateModalProps {
    id: string;
    isModalOpen: boolean;
    reactivateMember: any[];
    onClose: () => void;
    onConfirm: () => void;
}

export const ConfirmReactivateModal: React.FC<ConfirmReactivateModalProps> = (
    props: ConfirmReactivateModalProps
): JSX.Element => {
    const classes = useStyles();
    const { id, isModalOpen, reactivateMember, onClose, onConfirm} = props;

    return (
        <Dialog
            id={id}
            maxWidth={'lg'}
            open={isModalOpen}
            scroll={'paper'}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{ zIndex: 9999 }}
        >
            <DialogTitle
                className={classes.dialogTitle}
                disableTypography={true}
                id="modal-title"
            >
                <div className={classes.dialogFirstTitle}>
                    Confirm Reactivation of Members
                </div>
                <IconButton
                    data-cy={'member_suspension_conmod_close_btn'}
                    id={'member_suspension_conmod_close_btn'}
                    aria-label="close"
                    className={classes.closeButton}
                    onClick={onClose}
                >
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography>
                        {`Are you sure you want to cancel the termination of the following ${reactivateMember.length} members? Member (s) will continue to have their benefits coverage.`}
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className={classes.tblPaper}>
                            <Table 
                                data-cy={'member_suspension_conmod_dependents_table'}
                                id={'member_suspension_conmod_dependents_table'}
                                size="small"
                            >
                                <TableHead>
                                    <TableRow>
                                        <TableCell style={{ width: '25%' }}>
                                            Member ID
                                        </TableCell>
                                        <TableCell style={{ width: '25%' }}>
                                            Member Name
                                        </TableCell>
                                        <TableCell style={{ width: '20%' }}>
                                            Corporation
                                        </TableCell>
                                        <TableCell style={{ width: '15%' }}>
                                            Type
                                        </TableCell>
                                        <TableCell style={{ width: '15%' }}>
                                            Previous Status To Restore
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {reactivateMember.length > 0 ? reactivateMember.map(
                                        (row, index) => {
                                            return (
                                                <TableRow key={index}>
                                                    <TableCell style={{ width: '25%' }}>
                                                        {row['member_id']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '25%' }}>
                                                        {row['member_name']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '20%' }}>
                                                        {row['corporation']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '15%' }}>
                                                        {row['type']}
                                                    </TableCell>
                                                    <TableCell style={{ width: '15%' }}>
                                                        {row['previous_status_to_restore']}
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        }
                                    ): null}
                                </TableBody>
                            </Table>
                        </Paper>
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={12} style={{ textAlign: 'center' }}>
                        <CancelButton
                            data-cy={'member_suspension_conmod_cancel_btn'}
                            id={'member_suspension_conmod_cancel_btn'}
                            className={classes.cancelButton}
                            variant="contained"
                            color="primary"
                            onClick={onClose}
                        >
                            Cancel
                        </CancelButton>
                        <SuspendButton
                            data-cy={'member_suspension_conmod_btn'}
                            id={'member_suspension_conmod_btn'}
                            className={classes.suspendButton}
                            variant="contained"
                            color="primary"
                            onClick={onConfirm}
                        >
                            Reactivate
                        </SuspendButton>
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    );
};
