interface DocumentUpload {
  name: string;
  type: string;
  url: string;
}

interface ApproveMemberModalProps {
  isModalOpen: boolean;
  id: string;
  row: number;
  onClose: () => void;
  onApprove: (row: number, data: any, remarks: string) => void;
}

export const ApproveMemberModal: React.FC<ApproveMemberModalProps> = (
  props: ApproveMemberModalProps,
): JSX.Element => {
  const { id, row } = props;
  // const [tab, setSelectedTab] = useState(0);
  // const [partialcount, setPartialCount] = useState(0);
  // const [selectedpartial, setSelectedPartial] = useState<any>(data[0]);
  // const [status, setStatus] = useState(data[0]['status']);

  // console.log('data',data);

  // const [check1, setCheck1] = useState(false);
  // const [uploaded1] = useState(false);
  // const [selected1] = useState('');
  const [uploadedDocuments, setUploadedDocuments] = useState<DocumentUpload[]>(
    [],
  );
  const [isModalOpen, setIsModalOpen] = useState(props.isModalOpen);
  const [updata, setData] = useState({});
  const [remarks, setRemarks] = useState('');
  const classes = useStyles();

  useEffect(() => {
    setIsModalOpen(props.isModalOpen);
    if (props.isModalOpen) {
      setUploadedDocuments([]);
      setRemarks('');
    }
  }, [props.isModalOpen]);

  const handleSaveData = (data: any) => {
    setData(data);
  };

  const onDocumentUploaded = (document: DocumentUpload) => {
    const uploadedDocumentList = [...uploadedDocuments, document];
    setUploadedDocuments(uploadedDocumentList);
  };

  const handleApprove = () => {
    props.onApprove(row, updata, remarks);
  };

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Approve Member</div>
        <div className={classes.dialogSecondTitle}>
          Check which document(s) to upload to support the approval of the
          member.
        </div>
        <IconButton
          id='close_button'
          data-cy='close_button'
          aria-label="close"
          className={classes.closeButton}
          onClick={props.onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          <Grid item xs={12}>
            <ModalContent
              isModalOpen={true}
              onApprove={data => handleSaveData(data)}
              onUpload={onDocumentUploaded}
            />
          </Grid>
          <Grid item xs={12}>
            <InputLabel
              className={classes.label}
              htmlFor="disapprove_remarks_tf"
            >
              Remarks
            </InputLabel>
            <TextField
              style={{ marginTop: '8px', width: '100%' }}
              id="disapprove_remarks_tf"
              data-cy='disapprove_remarks_tf'
              margin="normal"
              variant="outlined"
              multiline={true}
              rows={10}
              value={remarks}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const { value } = e.target;
                setRemarks(value);
              }}
              inputProps={{ 'aria-label': 'bare' }}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              id="cancel_button"
              data-cy='cancel_button'
              className={classes.cancelButton}
              variant="contained"
              color="secondary"
              onClick={props.onClose}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              id="approve_button"
              data-cy='approve_button'
              className={classes.approveButton}
              variant="contained"
              color="primary"
              onClick={handleApprove}
            >
              Approve
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

ApproveMemberModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
};
