import React, {useState, useEffect} from 'react';
import { 
    InputLabel,
    Typography,
    Grid 
} from '@material-ui/core';


interface IBasicInformationProps {
    data: any,
    classes?: any,
}

export const BasicInformation: React.FC<IBasicInformationProps> = (props: IBasicInformationProps) => {
    const { data, classes }  = props;
    const [salesChannelArr,setSalesChannelArr] = useState(['','','']);
    console.log(data);    
    
    useEffect(() => {
        if(data !== undefined && data.sales_channel !== undefined){
            let salesChannel = data.sales_channel;
            let salesArr = [''];
            let salesProp;

            salesArr.shift();
            for(salesProp in salesChannel){
                salesArr.push(salesChannel[salesProp])
            }

            console.log(salesChannel)
            setSalesChannelArr(salesArr);
        }
    },[data])

    useEffect(() => {
        console.log(salesChannelArr)
    },[salesChannelArr])

    return (
        <>
             <Grid container xs={12} spacing={3}>
                <Grid item xs={12}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">CORPORATE ACCOUNT NO.</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.corporate_account_no}</Typography>   
                </Grid> 
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">REGISTERED COMPANY NAME</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.registered_name}</Typography>
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="status">TRADE COMPANY NAME</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.brand_name}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">INDUSTRY</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.industry}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">OWNERSHIP</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.ownership}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="primary_module">CORPORATE TIN</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.tin_no}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="primary_module">SEC REGISTRATION NO.</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.sec_reg_no}</Typography>  
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">E-VAT NO.</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.evat_no}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">ESTABLISHED (YEARS)</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.years_of_existence}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">AUTHORIZED SIGNATORY</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.authorized_signatory}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">BROKER/BDO</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && salesChannelArr[1]}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">BROKER COMPANY</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && salesChannelArr[2]}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">PREVIOUS HMO</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.prev_hmo_provider}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">CONTACT PERSON</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.authorized_signatory}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">POSITION</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.authorized_signatory_designation}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">CONTACT NO.</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.authorized_signatory_contact_no}</Typography>   
                </Grid>
                <Grid item xs={3}>
                    <InputLabel className={classes.inputLabel} htmlFor="description">CONTACT EMAIL</InputLabel>
                    <Typography className={classes.viewText}>{data !== undefined && data.authorized_signatory_email_address}</Typography>   
                </Grid>
            </Grid> 
        </>
    )
}
