import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Button,
  IconButton,
  TextField,
  InputLabel,
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles(theme => ({
  root: {
    flexWrap: 'wrap',
  },
  dialog: {
    align: 'center',
    justify: 'center',
    width: '95%',
    fontFamily: 'usual',
  },
  dialogContent: {
    paddingTop: '0px',
    paddingLeft: '24px',
    paddingRight: '24px',
  },
  dialogContentText: {
    fontSize: '0.875rem',
  },
  container: {
    paddingRight: '15px',
    paddingLeft: '15px',
    minWidth: '680px',
    width: 'auto',
  },
  dialogTitle: {
    paddingTop: theme.spacing(5),
    marginRight: '64px',
  },
  dialogFirstTitle: {
    fontSize: '16px',
    color: '#272E4C',
    fontWeight: 600,
    marginBottom: '5px',
  },
  dialogSecondTitle: {
    fontSize: '14px',
    color: '#272E4C',
    paddingTop: theme.spacing(2),
  },
  dialogAction: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  boldText: {
    fontWeight: 'bold',
  },
  cancelButton: {
    width: 115,
    marginRight: 25,
    border: '1px solid #3AB77D',
    color: '#3AB77D',
    backgroundColor: '#FFFFFF',
  },
  disapproveButton: {
    textTransform: 'none',
    minWidth: 129,
    marginRight: 25,
    color: '#FFFFFF',
    backgroundColor: '#FF5555',
    '&:hover': {
      background: '#FF7777',
    },
  },
  label: {
    fontSize: '14px',
    color: '#272E4C',
    fontWeight: 600,
    paddingTop: theme.spacing(2),
  },
  previousButton: {
    minWidth: 120,
    fontWeight: 600,
  },
  nextButton: {
    minWidth: 130,
    fontWeight: 600,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  tab: {
    textAlign: 'left',
    alignItems: 'left',
  },
}));

interface DisapproveMemberModalProps {
  isModalOpen: boolean;
  id: string;
  row: number;
  onClose: () => void;
  onDisapprove: (row: number, data: any) => void;
}

export const DisapproveMemberModal: React.FC<DisapproveMemberModalProps> = (
  props: DisapproveMemberModalProps,
): JSX.Element => {
  const { id, row } = props;
  const [isModalOpen, setIsModalOpen] = useState(props.isModalOpen);
  const [remarks, setRemarks] = useState('');
  const classes = useStyles();

  useEffect(() => {
    setIsModalOpen(props.isModalOpen);
    if (props.isModalOpen) {
      setRemarks('');
    }
  }, [props.isModalOpen]);

  const onDisapprove = () => {
    if (props.onDisapprove) {
      props.onDisapprove(row, remarks);
    }
  };

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Disapprove Member</div>
        <div className={classes.dialogSecondTitle}>
          This entry will be disapproved. You may add your reason for
          disapproval below.
        </div>
        <IconButton
          id='close_button'
          data-cy='close_button'
          aria-label="close"
          className={classes.closeButton}
          onClick={props.onClose}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          <Grid item xs={12}>
            <InputLabel
              className={classes.label}
              htmlFor="disapprove_remarks_tf"
            >
              Remarks
            </InputLabel>
            <TextField
              style={{ marginTop: '8px', width: '100%' }}
              id="disapprove_remarks_tf"
              data-cy='disapprove_remarks_tf'
              margin="normal"
              variant="outlined"
              multiline={true}
              rows={10}
              value={remarks}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const { value } = e.target;
                setRemarks(value);
              }}
              inputProps={{ 'aria-label': 'bare' }}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              id="cancel_button"
              data-cy='cancel_button'
              className={classes.cancelButton}
              variant="contained"
              color="secondary"
              onClick={props.onClose}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              id="disapprove_button"
              data-cy='disapprove_button'
              className={classes.disapproveButton}
              variant="contained"
              color="default"
              onClick={onDisapprove}
            >
              Disapprove
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

DisapproveMemberModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
};
