import * as React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Grid, FormControlLabel, Typography } from '@material-ui/core/';
import Checkbox, { CheckboxProps } from '@material-ui/core/Checkbox';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileUpload, faTimesCircle } from '@fortawesome/free-solid-svg-icons';
import { replace } from 'lodash';

import { Loader } from 'Components/UI/LoadingIndicator';
import { SnackbarNotification } from 'Components/UI/SnackbarNotification';
import { IApproveMemberModal } from './IApproveMemberModal';
import { API, BASE_API_URL } from '../../API';

const GreenCheckbox = withStyles({
  root: {
    color: '#272E4C33',
    '&$checked': {
      color: '#1E2071',
    },
  },
  checked: {},
})((props: CheckboxProps) => <Checkbox color="default" {...props} />);

interface DocumentUpload {
  name: string;
  type: string;
  url: string;
}

interface ModalContentProps {
  isModalOpen: boolean;
  onApprove: (value: string) => void;
  onUpload: (file: DocumentUpload) => void;
}

export class ModalContent extends React.Component<
  ModalContentProps,
  IApproveMemberModal.IState
> {
  constructor(props: ModalContentProps) {
    super(props);

    const data = {
      birth_certificate: {
        label: 'Birth Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      baptismal_certificate: {
        label: 'Baptismal Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      marriage_certificate: {
        label: 'Marriage Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      solo_parent_id: {
        label: 'Solo Parent ID',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      proof_of_parentage: {
        label: 'Other proof of Parentage',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      certificate_no_marriage: {
        label: 'Certificate of No Marriage',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      annulment_papers: {
        label: 'Annulment Papers',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      passport: {
        label: 'Passport',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      scanned_hmo_card: {
        label: 'Scanned HMO Card',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      death_certificate: {
        label: 'Death Certificate',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      employee_id: {
        label: 'Employee ID',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      proof_overseas_employment: {
        label: 'Proof of Overseas Employment',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
      others: {
        label: 'Others',
        checked: false,
        uploaded: false,
        uploaded_file: null,
        uploaded_filename: '',
      },
    };

    this.state = {
      updata: data,
      isLoading: false,
      isSnackbarNotifOpen: false,
      snackbarNotifMsg: 'File successfully uploaded.',
      snackbarNotifVariant: 'success',
    };
  }

  setFieldValue = (fieldName: string, value: any) => {
    this.setState(state => ({
      ...state,
      [fieldName]: value,
    }));

    if (fieldName === 'updata') {
      this.props.onApprove(value);
    }
  };

  setIsSnackbarNotifOpen = (isOpen: boolean) => {
    this.setState({
      isSnackbarNotifOpen: isOpen,
    });
  };

  handleChange = (name: string, checked: boolean) => {
    const temp = this.state.updata;
    temp[name].checked = checked;
    if (!checked) {
      temp[name].uploaded = false;
      temp[name].uploaded_file = null;
      temp[name].uploaded_filename = '';
    }
    this.setFieldValue('updata', temp);
    console.log('name: ', name);
  };

  handleDeleteFile = (name: string) => {
    console.log('event: ', name);
    const temp = this.state.updata;

    temp[name].uploaded = false;
    temp[name].uploaded_file = null;
    temp[name].uploaded_filename = '';
    this.setFieldValue('updata', temp);

    console.log('name: ', name);
  };

  handleUploadFile = (name: string) => {
    const temp = this.state.updata;

    temp[name].uploaded = false;
    this.setFieldValue('updata', temp);

    const inputElement = document.getElementById(name)!;
    if (inputElement) {
      inputElement.click();
    }
  };

  handleUploadFileChange = (type: string, event: any) => {
    const file = event.target.files[0];
    if (file) {
      this.uploadDocument(type, file);
    }
  };

  uploadDocument = (type: string, file: File) => {
    this.setState({
      isLoading: true,
    });
    API.uploadMemberDocument('verify-members', type, file)
      .then((uploadRes: any) => {
        if (uploadRes && uploadRes.url) {
          const apiBaseUrl = BASE_API_URL || '';
          const newUrl = replace(uploadRes.url, 'public/', apiBaseUrl);
          uploadRes.url = newUrl;

          const temp = this.state.updata;

          temp[type].uploaded = true;
          temp[type].uploaded_filename = uploadRes.name;
          temp[type].uploaded_file = uploadRes;

          console.log('uploadMemberDocumentAPI:')
          console.log(temp)

          this.setFieldValue('updata', temp);
          this.setState({
            isLoading: false,
          });
          this.setIsSnackbarNotifOpen(true);
        }
      })
      .catch(e => {
        console.log('uploadDocument Error', e);

        this.setState({
          isLoading: false,
        });
      });
  };

  public render(): JSX.Element {
    const {
      updata,
      isLoading,
      isSnackbarNotifOpen,
      snackbarNotifMsg,
      snackbarNotifVariant,
    } = this.state;

    return (
      <Grid
        container
        direction="column"
        justify="space-evenly"
        alignItems="stretch"
      >
        {Object.keys(updata).map((key, value) => {
          return (
            <Grid container direction="row" justify="space-between" key={key}>
              <FormControlLabel
                control={
                  <GreenCheckbox
                    checked={updata[key].checked}
                    onChange={e => {
                      this.handleChange(key, e.target.checked);
                      console.log('event key: ', key);
                      console.log('event value: ', value);
                    }}
                    value={key}
                  />
                }
                label={updata[key].label}
              />

              {updata[key].uploaded ? (
                <Typography color="textPrimary" style={{ paddingTop: '5px' }}>
                  <span>
                    <span style={{ textDecoration: 'underline' }}>
                      {updata[key].uploaded_filename}
                    </span>
                    <span
                      style={{ paddingLeft: '10px', fontSize: '15px' }}
                      onClick={() => {
                        this.handleDeleteFile(key);
                      }}
                    >
                      <FontAwesomeIcon
                        icon={faTimesCircle}
                        style={{ color: '#B5B8C1' }}
                      />
                    </span>
                  </span>
                </Typography>
              ) : updata[key].checked ? (
                <Typography color="textPrimary" style={{ paddingTop: '5px' }}>
                  <span>
                    <span
                      style={{ paddingRight: '10px', fontSize: '15px' }}
                      onClick={() => {
                        this.handleUploadFile(key);
                      }}
                    >
                      <FontAwesomeIcon
                        icon={faFileUpload}
                        style={{ color: '#0D5E40' }}
                      />
                    </span>
                    <span
                      style={{ textDecoration: 'underline', color: '#0D5E40' }}
                      onClick={() => {
                        this.handleUploadFile(key);
                      }}
                    >
                      Upload File
                    </span>
                    <input
                      id={key}
                      hidden
                      type="file"
                      onChange={e => {
                        this.handleUploadFileChange(key, e);
                      }}
                    />
                  </span>
                </Typography>
              ) : null}
            </Grid>
          );
        })}
        {isLoading ? <Loader /> : null}
        <SnackbarNotification
          isOpen={isSnackbarNotifOpen}
          message={snackbarNotifMsg}
          variant={snackbarNotifVariant}
          onSnackbarClose={() => {
            this.setIsSnackbarNotifOpen(false);
          }}
        />
      </Grid>
    );
  }
}
