import * as React from 'react';
import { Table, TableBody, TableHead, TableRow, TablePagination, TableCell, Grid } from '@material-ui/core';
import './component-style.scss';
import { LabelDisplayedRowsArgs } from '@material-ui/core/TablePagination';
import { API } from '../API';
import { TextButton } from './TextButton';
import { InputField } from './InputField';
import { faSort, faSortUp, faSortDown } from '@fortawesome/free-solid-svg-icons';
import { IconButton } from './IconButton';
import { NumberField } from './NumberField';
import { DatePicker } from './DatePicker';
import { cloneDeep } from 'lodash'

interface IProps {

    searchBarQuery: string;
    isMembershipModuleManager: boolean;
    userData: any;

    className?: string;
    paginationClass?: string;

    /* Normal Mode Feature */
    rows: any[];
    renderHeader?: () => JSX.Element;
    renderRow: (rowData: any, index: number) => JSX.Element;
    noRowsMessage?: string | JSX.Element;

    /* Simple Mode Feature */
    simpleModeOption?: SimpleModeOption;

    /* Additional Features */
    stickyHeader?: boolean;
    canOverflow?: boolean;

    /* Basic Pagination Features */
    rowsPerPageOptions?: number[];
    labelRowsPerPage?: string;
    onChangeInfo?: (info: LabelDisplayedRowsArgs) => string;
    onChangeState?: (page: number, rowsPerPage: number) => void;

    /* Server Side Paginatio Feature */
    serverSideOption?: ServerSideOption;

    statusTab: string; // chosen status tab, not necessarily ticket status (tab ALL)

    setLoading: (loading: boolean) => void;

    resetSearchBarQuery: () => void;
}

interface SimpleModeOption {
    columns: HeaderData[];
}

export interface HeaderData {
    style?: React.CSSProperties;
    name?: string;
    key?: string;
    filterType?: 'string' | 'number' | 'date';
    canSort?: boolean;
    renderHeader?: JSX.Element;
}

interface ServerSideOption {
    getUrl: (offset: number, limit: number, filter: any, order: string, count: boolean, filter_count: any) => string;
    onSuccess: (response: any) => void;
    onFail?: (error: any) => void;
    getCount: (response: any) => number;
}

interface IStates {
    page: number;
    rowsPerPage: number;
    count: number;
    pmakerUID: string;
    filterData: any;
    sortingStates: any;
}

const emptySortingState = {
    urgency: '',
    status: '',
    ticket_id: '',
    due_date: '',
    ticket_type: '',
    batch_no: '',
    client: '',
    assignee:'',
};

export class CustomTable extends React.PureComponent<IProps, IStates> {

    timeOut?: any; 

    constructor(props: IProps) {
        super(props);

        this.state = {
            page: 0,
            rowsPerPage: 0,
            count: 0,
            pmakerUID: '',
            filterData: {},
            sortingStates: cloneDeep(emptySortingState),
        }
    }

    componentDidMount() {
        if (this.props.rowsPerPageOptions) {
            this.setState({rowsPerPage: this.props.rowsPerPageOptions[0]}, () => {
                if (this.props.serverSideOption && this.props.userData) {
                    this.apiCall(this.props.serverSideOption, false, true);
                }
            });
        }
    }

    componentDidUpdate(prevProps) {
        if(prevProps.statusTab !== this.props.statusTab){
            this.props.resetSearchBarQuery()
            this.setState({filterData: {}}, () => {if(this.props.serverSideOption) this.apiCall(this.props.serverSideOption, false);})
        }
        if(prevProps.userData !== this.props.userData ||
            prevProps.isMembershipModuleManager !== this.props.isMembershipModuleManager){
            if(this.props.serverSideOption) this.apiCall(this.props.serverSideOption, false);
        }
        if(prevProps.searchBarQuery !== this.props.searchBarQuery && this.props.serverSideOption)
            this.apiCall(this.props.serverSideOption);
    }

    resetPage = () => {
        this.setState({page: 0});
    }

    getSlicedData = (rows: any[]) => {
        if (this.props.rowsPerPageOptions && !this.props.serverSideOption) {
            const startingItem = this.state.page * this.state.rowsPerPage;
            return rows.slice(startingItem, startingItem + this.state.rowsPerPage);
        } else {
            return rows;
        }
    }

    getFilteredRows = () => {
        const filteredRows = this.props.rows.filter(row => {
            var isFound = false;
            Object.keys(row).map(rowKey => {
                if (!isFound && (
                    row[rowKey] == this.state.filterData[rowKey] || 
                    this.state.filterData[rowKey] === undefined)) {
                    isFound = true;
                }
            });
            return isFound;
        });

        return this.getSlicedData(filteredRows);
    }

    constructFilterForCount = (count_filter: any) => {

        const open_count_filter = cloneDeep(count_filter)
        const closed_count_filter = cloneDeep(count_filter)
        const unassigned_count_filter = cloneDeep(count_filter)
        open_count_filter.push({status: 'OPEN'})
        closed_count_filter.push({status: 'CLOSED'})
        unassigned_count_filter.push({status: 'UNASSIGNED'})
        const open_final_filter = {and: open_count_filter}
        const closed_final_filter = {and: closed_count_filter}
        const unassigned_final_filter = {and: unassigned_count_filter}
        return {where: [open_final_filter, closed_final_filter, unassigned_final_filter]}
        
    }

    apiCall = (serverSideOption: ServerSideOption, resetPage?: boolean, getTabsCount?: boolean) => {
        if(this.props.setLoading) this.props.setLoading(true);

        const sort = this.state.sortingStates
        const offset = !resetPage ? this.state.page * this.state.rowsPerPage : 0;
        const limit = this.state.rowsPerPage;



        // CONSTRUCT AND FILTER


        // status tab filter
        let filter: any = {and: [{status : undefined}]};
        if(this.props.statusTab.toUpperCase() !== 'ALL'){
            filter['and'][0]['status'] = this.props.statusTab.toUpperCase();
        }


        // table filters
        let filter_and = filter['and']
        let count_filter : any = [];
        Object.keys(this.state.filterData).map(key => {
            if (this.state.filterData[key] && this.state.filterData[key].length > 0) {
                const table_filters = {[key]: {like: this.state.filterData[key], options: 'i'}}
                filter_and.push(table_filters)
                if(getTabsCount) count_filter.push(table_filters);
            }
        });

        // search bar filter
        if(this.props.searchBarQuery){
            const search_filter = {ticket_id: {like: this.props.searchBarQuery, options: 'i'}}
            filter_and.push(search_filter)
            if(getTabsCount) count_filter.push(search_filter);
        }

        
        // covers all conditions of non-Membership Module Manager for tabs:
        // OPEN, PENDING, RESOLVED, CLOSED, AND ALL...
        // the filtering for ALL here only includes ticket_types for specific users,
        // filtering for assignee pmaker id is accomplished on the OR filters

        if(!this.props.isMembershipModuleManager && 
            this.props.statusTab){
                if(['OPEN', 'PENDING', 'RESOLVED', 'CLOSED'].includes(this.props.statusTab.toUpperCase())){
                    if(this.props.userData && this.props.userData.group && this.props.userData.group.name){

                        if(this.props.userData.group.name === 'APD_ENCODER GROUP'){
                            const encoder_filter = {ticket_type: {inq: ['Enroll Memberlist', 'Enroll Member', 'Add Member']}}
                            filter_and.push(encoder_filter)
                            if(getTabsCount) count_filter.push(encoder_filter);
                        }
                        if(this.props.userData.group.name === 'APD_VERIFIER GROUP'){
                            const verifier_filter = {ticket_type: {inq: ['Verify Memberlist', 'Verify Member']}}
                            filter_and.push(verifier_filter)
                            if(getTabsCount) count_filter.push(verifier_filter);
                        }
                        if(this.props.userData.group.name === 'APD_PRINTING_STAFF'){
                            const printing_filter = {ticket_type: {inq: ['Print Card IDs', 'Claimed Print Card IDs']}}
                            filter_and.push(printing_filter)
                            if(getTabsCount) count_filter.push(printing_filter);
                        }
                    }
                    if(this.props.userData && this.props.userData.pmakerUid){
                        const pmaker_filter = {assignee_pmaker_uid: this.props.userData.pmakerUid}
                        filter_and.push(pmaker_filter)
                        if(getTabsCount) count_filter.push(pmaker_filter);
                    }
                }else if(this.props.statusTab.toUpperCase() === 'ALL'){
                    if(this.props.userData && this.props.userData.group && this.props.userData.group.name){
                        if(this.props.userData.group.name === 'APD_ENCODER GROUP'){
                            filter_and.push({ticket_type: {inq: ['Enroll Memberlist', 'Enroll Member', 'Add Member']}})
                        }
                        if(this.props.userData.group.name === 'APD_VERIFIER GROUP'){
                            filter_and.push({ticket_type: {inq: ['Verify Memberlist', 'Verify Member']}})
                        }
                        if(this.props.userData.group.name === 'APD_PRINTING_STAFF'){
                            filter_and.push({ticket_type: {inq: ['Print Card IDs', 'Claimed Print Card IDs']}})
                        }
                    }
                }
        }
        




        // CONSTRUCT OR FILTER INSIDE AND FILTER
        // covers conditions for non-MMM in tabs: ALL (only sets pmaker filter),
        // UNASSIGNED, sets condition pmaker_id = ticket.assignee_pmaker_id

        if(this.props.statusTab && ['UNASSIGNED', 'ALL'].includes(this.props.statusTab.toUpperCase()) &&
            this.props.userData && this.props.userData.group.name){
            if(['APD_ENCODER GROUP', 'APD_VERIFIER GROUP', 'APD_PRINTING_STAFF']
                .includes(this.props.userData.group.name) || !this.props.isMembershipModuleManager){
                let or_filter : any[] = [];
                if(this.props.userData.group.name === 'APD_ENCODER GROUP'){
                    or_filter.push(
                        {and: [
                            {status: 'UNASSIGNED'},
                            {ticket_type: 'Enroll Memberlist'}
                        ]})
                    or_filter.push(
                        {and: [
                            {status: 'UNASSIGNED'},
                            {ticket_type: 'Enroll Member'}
                        ]})
                    or_filter.push(
                        {and: [
                            {status: 'UNASSIGNED'},
                            {ticket_type: 'Add Member'}
                        ]})
                }else if(this.props.userData.group.name === 'APD_VERIFIER GROUP'){
                    or_filter.push(
                        {and: [
                            {status: 'UNASSIGNED'},
                            {ticket_type: 'Verify Memberlist'}
                        ]})
                    or_filter.push(
                        {and: [
                            {status: 'UNASSIGNED'},
                            {ticket_type: 'Verify Member'}
                        ]})
                }else if(this.props.userData.group.name === 'APD_PRINTING_STAFF'){
                    or_filter.push(
                        {and: [
                            {status: 'UNASSIGNED'},
                            {ticket_type: 'Print Card IDs'}
                        ]})
                    or_filter.push(
                        {and: [
                            {status: 'UNASSIGNED'},
                            {ticket_type: 'Claimed Print Card IDs'}
                        ]})
                }
                let count_or_filter = or_filter
                if(this.props.statusTab.toUpperCase() === 'ALL'){
                    or_filter.push(
                        {and: [
                            {assignee_pmaker_uid: this.props.userData.pmakerUid},
                            {status: {nin: ['UNASSIGNED']}}
                        ]})
                }
                if(or_filter.length){
                    filter_and.push({or: or_filter})
                }
                if(getTabsCount) count_filter.push({or: count_or_filter});
            }
            
        }


        let filter_for_count: any = {};
        if(getTabsCount){
            filter_for_count = this.constructFilterForCount(count_filter)
        }

        console.log('FILTER FOR COUNT', filter_for_count)

        // SORTING
        let order = ``
        Object.keys(sort).filter(key => sort[key]).forEach(item => {
            order = `${item} ${sort[item]}`
        })


        // API CALL
        filter['and'] = filter_and
        console.log('AND FILTER', filter_and)
        API.get(
            serverSideOption.getUrl(offset, limit, filter, order, getTabsCount ? getTabsCount : false, filter_for_count), 
            (response) => {
                if(response && response.error === undefined){
                    this.setState({count: response && response.count ? response.count : 0}, ()=>console.log('page',this.state.count));
                    serverSideOption.onSuccess(response);
                }else{
                    serverSideOption.onSuccess({tickets: []})
                    this.setState({count: 0});
                }
                
            }, 
            serverSideOption.onFail).catch(e => console.log(e));
        
        if(resetPage) this.setState({page: 0});
        if(this.props.setLoading) this.props.setLoading(false);
    }

    render(): JSX.Element {
        console.log('render page', this.state.count)
        return (
            <>
            <div style={{overflow: this.props.canOverflow ? 'auto' : 'none'}}>
                <Table 
                    className={`${(this.props.className ? this.props.className : '')} table`} 
                    stickyHeader={this.props.stickyHeader ? this.props.stickyHeader : false}>

                    <TableHead>
                        {this.props.simpleModeOption ? (
                            <TableRow>
                                {this.props.simpleModeOption.columns.map(column => {
                                    // @ts-ignore
                                    return this.renderHeaderColumn(column, this.props.serverSideOption);
                                })}
                            </TableRow>
                        ) : this.props.renderHeader ? (
                            <TableRow>
                                {this.props.renderHeader()}
                            </TableRow>
                        ) : null}
                    </TableHead>

                    <TableBody>
                        {this.props.renderRow ? (
                            this.props.rows.length > 0 ? (
                                this.getSlicedData(this.props.rows).map((row, j) => {
                                    return this.props.renderRow(row, j)
                                })  
                            ) : (
                                <TableRow>
                                    <TableCell colSpan={999} style={{textAlign: 'center'}}>
                                        {this.props.noRowsMessage ? this.props.noRowsMessage : 'No data found'}
                                    </TableCell>
                                </TableRow>
                            )
                        ) : null}
                    </TableBody>

                </Table>
            </div>

            {this.props.rowsPerPageOptions && !this.props.serverSideOption ? (
                <TablePagination
                    className={`${(this.props.paginationClass ? this.props.paginationClass : '')} pagination MuiTablePagination-root`}
                    rowsPerPageOptions={this.props.rowsPerPageOptions}
                    count={this.props.rows.length}
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    labelDisplayedRows={(info: LabelDisplayedRowsArgs) => {
                        return (
                            this.props.onChangeInfo ? (
                                this.props.onChangeInfo(info)
                            ) : (
                                `${info.from}-${info.to} of ${info.count}`
                            ));
                    }}
                    labelRowsPerPage={
                        this.props.labelRowsPerPage ?
                            this.props.labelRowsPerPage :
                            'Rows per page:'
                    }
                    onChangePage={(_event: unknown, page: number) => {
                        this.setState({page: page});
                    }}
                    onChangeRowsPerPage={(event: React.ChangeEvent<HTMLInputElement>) => {
                        this.setState({
                            rowsPerPage: parseInt(event.target.value, 10),
                            page: 0
                        });
                    }} />
            ) : null}

            {this.props.serverSideOption && this.props.rowsPerPageOptions ? (
                <ServerPagination
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    count={this.state.count}
                    onChangePage={(page: number) => {
                        this.setState({page: page}, () => {
                            if (this.props.serverSideOption) {
                                this.apiCall(this.props.serverSideOption, false);
                            }
                        });
                    }} />
            ) : null}
            </>
        );
    }

    renderHeaderColumn(header: HeaderData, option?: ServerSideOption): JSX.Element {
        const sort = this.state.sortingStates
        const style: React.CSSProperties = {
            padding: '8px'
        };
        console.log('header col',header)
        return (
            <TableCell style={{...header.style ? header.style : null, ...style}}>
                {header.name ? (
                    <span className='header-2'>{header.name}</span>
                ) : null}
                {header.canSort ? (
                    <div style={{float: 'right'}}>
                        <IconButton 
                            icon={
                                header && header.key && sort[header.key] === 'asc' ? faSortUp : 
                                header && header.key && sort[header.key] === 'desc' ? faSortDown : 
                                faSort
                            } 
                            onClick={() => {
                                if(header && header.key){
                                    const key = header.key
                                    const value = !sort[header.key] || sort[header.key] === 'desc' ? 'asc' : 'desc'
                                    let temp = cloneDeep(emptySortingState)
                                    temp[key] = value;
                                    this.setState({sortingStates: temp},()=>{
                                        if (this.props.serverSideOption) 
                                            this.apiCall(this.props.serverSideOption, false);
                                    })
                                } 
                            }} />
                    </div>
                ) : null}
                {header.filterType && header.name ? (
                    <>
                    {header.filterType === 'string' ? (
                        <InputField
                            id={`${header.key}_filter`}
                            value={
                                this.state[header.key ? header.key : header.name]
                                // this.state.filterData[header.key ? header.key : header.name]
                            }
                            onChange={(value: string) => {
                                // @ts-ignore
                                this.state.filterData[header.key ? header.key : header.name] = value;
                                this.setState({filterData: this.state.filterData});
                                if (option) {
                                    clearTimeout(this.timeOut);
                                    this.timeOut = setTimeout(() => {
                                        this.apiCall(option, true);
                                    }, 1000);
                                }
                            }} />
                    ) : header.filterType === 'number' ? (
                        <NumberField
                            id={`${header.key}_filter`}
                            value={
                                this.state[header.key ? header.key : header.name]
                                // this.state.filterData[header.key ? header.key : header.name]
                            }
                            onChange={(value: number) => {
                                // @ts-ignore
                                this.state.filterData[header.key ? header.key : header.name] = value;
                                this.setState({filterData: this.state.filterData});
                                if (option) {
                                    clearTimeout(this.timeOut);
                                    this.timeOut = setTimeout(() => {
                                        this.apiCall(option, true);
                                    }, 1000);
                                }
                            }} />
                    ) : header.filterType === 'date' ? (
                        <DatePicker
                            id={`${header.key}_filter`}
                            value={
                                this.state[header.key ? header.key : header.name]
                                // this.state.filterData[header.key ? header.key : header.name]
                            }
                            onChange={(value: Date | null) => {
                                // @ts-ignore
                                this.state.filterData[header.key ? header.key : header.name] = value;
                                this.setState({filterData: this.state.filterData});
                                if (option) {
                                    clearTimeout(this.timeOut);
                                    this.timeOut = setTimeout(() => {
                                        this.apiCall(option, true);
                                    }, 1000);
                                }
                            }} />
                    ) : null}
                    </>
                    
                ) : null}
            </TableCell>
        );
    }
}










interface ServerPaginationProps {
    rowsPerPage: number;
    page: number;
    count: number;
    onChangePage: (page: number) => void;
}

export class ServerPagination extends React.PureComponent<ServerPaginationProps> {

    getPagesList = () => {
        const list: string[] = [];
        const maxPage = Math.ceil(this.props.count / this.props.rowsPerPage);
        let alreadyHidden = false;

        for (var i = Math.max(0, this.props.page - 4); i < maxPage; i++) {
            if (maxPage - i > 2 && i - this.props.page >= 2) { 
                if (!alreadyHidden) {
                    list.push('...');
                    alreadyHidden = true;
                }
            } else {
                list.push('' + (i + 1));
            }
        }
        return list.slice(Math.max(list.length - 5, 0));
    }


    render(): JSX.Element {
        const pages = this.getPagesList();
        const style: React.CSSProperties = {
            textAlign: 'center'
        };
        
        return (
            <Grid container spacing={1} className='custom-pagination'>
                <Grid item xs={1} />
                <Grid item xs={4}>
                    {`${this.props.page * this.props.rowsPerPage + 1}-` +
                     `${Math.min((this.props.page + 1) * this.props.rowsPerPage, this.props.count)} out of ` +
                     `${this.props.count} items`}
                </Grid>
                <Grid item xs={7}>
                    <Grid container spacing={1}>
                        <Grid item xs />
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                label='First' 
                                onClick={() => {
                                    this.props.onChangePage(0);
                                }} />
                        </Grid>
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                label='Prev' 
                                onClick={() => {
                                    const prevPage = this.props.page - 1;
                                    if (prevPage >= 0) {
                                        this.props.onChangePage(prevPage);
                                    }
                                }} />
                        </Grid>
                        {pages.map(page => {
                            const pageNumber = parseInt(page) - 1;
                            const style: React.CSSProperties = {
                                fontWeight: this.props.page == pageNumber ? 'bold' : undefined,
                                cursor: !isNaN(pageNumber) ? 'pointer' : undefined,
                                textAlign: 'center'
                            };
                            return (
                                <Grid item xs={1}>
                                    <div 
                                        style={style}
                                        onClick={() => {
                                            if (!isNaN(pageNumber)) {
                                                this.props.onChangePage(pageNumber);
                                            }
                                        }}>
                                        {page}
                                    </div>
                                </Grid>
                            );
                        })}
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                label='Next' 
                                onClick={() => {
                                    const nextPage = this.props.page + 1;
                                    if (nextPage * this.props.rowsPerPage < this.props.count)
                                    this.props.onChangePage(nextPage);
                                }} />
                        </Grid>
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                label='Last'
                                onClick={() => {
                                    this.props.onChangePage(Math.floor(this.props.count / this.props.rowsPerPage));
                                }} />
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        );
    }
}

