import React, { useState, useEffect } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
//   FormControlLabel,
//   Switch,
  Checkbox,
  InputLabel,
  TextField
} from '@material-ui/core/';

import CloseIcon from '@material-ui/icons/Close';

import { API } from '../../../API';
import { Loader } from 'Components/UI/LoadingIndicator';

const dependentCheckboxStyle = {
    size: 12,
    padding: 0,
    color: '#272E4C',
    '&$checked': {
      color: '#272E4C',
    },
};

const useStyles = makeStyles(theme => ({
    dialog: {
        align: 'center',
        justify: 'center',
        fontFamily: 'usual',
    },
    dialogContent: {
        paddingTop: '0px',
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 500,
    },
    dialogFirstTitle: {
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px',
    },
    dialogSecondTitle: {
        fontSize: '14px',
        color: '#272E4C',
        paddingBottom: '1rem'
    },
    tblTitle: {
        fontSize: 14,
        fontWeight: 600,
        color: 'rgba(39, 49, 76, 1)',
    },
    tblPaperWithDeps: {
        minHeight: 175,
        borderRadius: 0,
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    tblPaperNoDeps: {
        minHeight: 'auto',
        borderRadius: 0,
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
    },
    boldText: {
        fontWeight: 'bold',
    },
    cancelButton: {
        minWidth: 129,
        marginRight: 25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: '#FFFFFF',
    },
    cancelSuspensionButton: {
        minWidth: 129,
        backgroundColor: '#3AB77D',
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    labelRemarks: {
        fontSize: '14px',
        color: '#272E4C',
        fontWeight: 600,
        paddingTop: theme.spacing(2),
    }
}));

const CancelSuspensionButton = withStyles({
    root: {
        color: '#FFFFFF',
        backgroundColor: '#3AB77D',
        '&:hover': {
            backgroundColor: '#2e9565',
        },
    },
})(Button);

const CancelButton = withStyles({
    root: {
        '&:hover': {
            backgroundColor: '#3AB77D',
            color: '#FFFFFF',
        },
    },
})(Button);

interface CancelSuspensionModalProps {
    id: string;
    isModalOpen: boolean;
    memberData: any[];
    onStatusAction: (payload: any) => void;
    onClose: () => void;
}

export const CancelSuspensionModal: React.FC<CancelSuspensionModalProps> = (
    props: CancelSuspensionModalProps
): JSX.Element => {
    const classes = useStyles();
    const { id, isModalOpen, memberData} = props;

    const [isLoaded, setIsLoaded] = useState<boolean>(false);
    const [user, setUser] = useState<any>({});
    // const [applyToAllSwitch, setApplyToAllSwitch] = useState<boolean>(false);
    const [fetchedDependents, setFetchedDependents] = useState<any>([]); 
    const [selectedDependents, setSelectedDependents] = useState<any[]>([]);
    const [remarks, setRemarks] = useState<string>('');
    const [memberCount, setMemberCount] = useState<number>(0);


    useEffect(() => {
        // reset components values on modal open
        if(props.isModalOpen) {
            resetData();
        }
    }, [props.isModalOpen]);

    useEffect(() => {
        async function getMemberDependents(){
            try {
                const userData = await API.getUserDataFromDb();
                setUser(userData);

                let dependentsArray: any[] = [];
                let promise = await memberData.map(data => API.getMemberStatusActionDependents(data.id)
                    .then(res => {
                        return res.map(res => {
                            res['principal_name'] = data.member_name;
                            return res;
                        })
                    })
                )
                const response = await Promise.all(promise);
                let filteredResponse = response.filter(res => res.length > 0)
                
                filteredResponse.forEach(filtRes => {
                    filtRes.forEach((dependent) => {
                        dependentsArray.push(dependent);
                    });
                })
                setFetchedDependents(dependentsArray);
                setIsLoaded(true);
            } catch(error){
                console.log(error)
                setIsLoaded(true);
            }
        }

        if(memberData.length > 0 && isModalOpen === true){
            setMemberCount(memberData.length);
            getMemberDependents();
        }         
    },[isModalOpen])


    const resetData = () => {
        setIsLoaded(false);
        setUser({});
        // setApplyToAllSwitch(false);
        setFetchedDependents([]);
        setSelectedDependents([]);
        setRemarks('');
        setMemberCount(0);
    }

    // function handleApplyToAllSwitchChange(){
    //     if(applyToAllSwitch === true){
    //         setApplyToAllSwitch(false);     
    //     } else {
    //         setApplyToAllSwitch(true);
    //     }
    // };

    function handleDependentsCheckboxChange(id: string) {
        if (selectedDependents.includes(id)) {
          setSelectedDependents(selectedDependents.filter(item => item !== id));
        } else {
          const newSelectedDependents = [...selectedDependents];
          newSelectedDependents.push(id);
          setSelectedDependents(newSelectedDependents);
        }
    }

    function handleStatusAction() {
        let memberIdsArray: any[] = [];
        memberData.forEach(member => {
            memberIdsArray.push(member.id)
        })

        if(selectedDependents.length > 0){
            selectedDependents.forEach(dependentId => {
                memberIdsArray.push(dependentId);
            })
        }

        const payload = {
            memberIds: memberIdsArray,
            remarks: remarks !== '' ? remarks : undefined,
            username: user.userName
        };

        if(props.onStatusAction){
            props.onStatusAction(payload);
            handleOnClose();
        }
    }

    function handleOnClose() {
        if(props.onClose){
            props.onClose();
        }
    }

    return (
        <div>
            <Dialog
                id={id}
                maxWidth={'sm'}
                open={isModalOpen}
                scroll={'paper'}
                aria-labelledby="modal-title"
                className={classes.dialog}
                // style={{ zIndex: 9999 }}
            >
                {isLoaded === false ? <Loader /> :
                    (<> 
                        <DialogTitle
                            className={classes.dialogTitle}
                            disableTypography={true}
                            id="modal-title"
                        >
                            <div className={classes.dialogFirstTitle}>
                                Confirm Cancelling of Suspension
                            </div>
                            <IconButton
                                data-cy={'cancel_suspension_modal_close_btn'}
                                id={'cancel_suspension_modal_close_btn'}
                                aria-label="close"
                                className={classes.closeButton}
                                onClick={handleOnClose}
                            >
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                        <DialogContent className={classes.dialogContent}>
                            <Grid container spacing={2}>
                                {/* <Grid item xs={12}>
                                    <FormControlLabel
                                        control={
                                            <Switch
                                                id={'members_apply_to_all'}
                                                data-cy={'members_apply_to_all'}
                                                checked={applyToAllSwitch}
                                                onChange={handleApplyToAllSwitchChange}
                                                color="primary"
                                                inputProps={{ 'aria-label': 'primary checkbox' }}
                                            />
                                        }
                                        label="Apply Remarks to all selected members"
                                    />
                                </Grid> */}
                                <Grid item xs={12}>
                                    <Typography className={classes.dialogSecondTitle}>
                                    Are you sure you want to cancel the suspension of {' '}
                                    <span className={classes.boldText}>
                                        {memberCount} member{memberCount > 1 ? 's' : null}
                                    </span>
                                    ? Member(s) will continue to have their benefits coverage.
                                    </Typography>
                                </Grid>
                                {fetchedDependents.length > 0 ?
                                    <> 
                                        <Grid item xs={12}>
                                            <Typography className={classes.tblTitle}>Dependents</Typography>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <Paper 
                                                className={fetchedDependents.length > 0 ? 
                                                    classes.tblPaperWithDeps : classes.tblPaperNoDeps
                                                }
                                            >
                                                <Table
                                                    data-cy={'cancel_suspension_dependents_table'}
                                                    id={'cancel_suspension_dependents_table'}
                                                    size="small"
                                                >
                                                    <TableHead>
                                                        <TableRow>
                                                            <TableCell style={{ width: '5%' }}>
                                                                {/* Checkbox Column */}
                                                            </TableCell>
                                                            <TableCell style={{ width: '25%' }}>
                                                                Name
                                                            </TableCell>
                                                            <TableCell style={{ width: '25%' }}>
                                                                Relationship
                                                            </TableCell>
                                                            <TableCell style={{ width: '25%' }}>
                                                                Principal
                                                            </TableCell>
                                                            <TableCell style={{ width: '20%' }}>
                                                                Status
                                                            </TableCell>
                                                        </TableRow>
                                                    </TableHead>
                                                    <TableBody>
                                                    {fetchedDependents.map(
                                                        (row, index) => {
                                                            return (
                                                                <TableRow key={index}>
                                                                    <TableCell style={{ width: '5%' }}>
                                                                        <Checkbox
                                                                            style={dependentCheckboxStyle}
                                                                            disabled={
                                                                                row['member_status'].toString().toLowerCase() === 'marked for suspension' ? 
                                                                                false : true
                                                                            }
                                                                            indeterminate={
                                                                                row['member_status'].toString().toLowerCase() === 'marked for suspension' ? 
                                                                                false : true
                                                                            }
                                                                            checked={selectedDependents.includes(
                                                                                row['_id']
                                                                            )}
                                                                            onChange={() => {
                                                                                handleDependentsCheckboxChange(row['_id']);
                                                                            }}
                                                                        />
                                                                    </TableCell>
                                                                    <TableCell style={{ width: '25%' }}>
                                                                        {`${row['first_name']} ${row['middle_name']} ${row['last_name']}`}                                                                    
                                                                    </TableCell>
                                                                    <TableCell style={{ width: '25%' }}>
                                                                        {row['relationship_to_principal']}
                                                                    </TableCell>
                                                                    <TableCell style={{ width: '25%' }}>
                                                                        {row['principal_name']}
                                                                    </TableCell>
                                                                    <TableCell style={{ width: '20%' }}>
                                                                        {row['member_status']}
                                                                    </TableCell>
                                                                </TableRow>
                                                            );
                                                        }
                                                    )}
                                                    </TableBody>
                                                </Table>
                                            </Paper>
                                        </Grid>
                                    </>
                                : null}
                                {/* {applyToAllSwitch === true ?  */}
                                    <Grid item xs={12}>
                                        <InputLabel 
                                            className={classes.labelRemarks} 
                                            htmlFor="cancel_suspension_remarks_field"
                                        >
                                            Remarks
                                        </InputLabel>
                                        <TextField
                                            data-cy={'cancel_suspension_remarks_field'}
                                            id={'cancel_suspension_remarks_field'}
                                            style={{ marginTop: '8px', width: '100%' }}
                                            margin="normal"
                                            variant="outlined"
                                            multiline={true}
                                            rows={10}
                                            value={remarks}
                                            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                const { value } = e.target;
                                                setRemarks(value);
                                            }}
                                            inputProps={{ 'aria-label': 'bare' }}
                                        />
                                    </Grid>
                                {/* : null} */}
                            </Grid>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            <Grid
                                container
                                direction="row"
                                justify="space-evenly"
                                alignItems="stretch"
                            >
                                <Grid item xs={4} style={{ textAlign: 'center' }}>
                                    <CancelButton
                                        data-cy={'cancel_suspension_modal_cancel_btn'}
                                        id={'cancel_suspension_modal_cancel_btn'}
                                        className={classes.cancelButton}
                                        variant="contained"
                                        color="primary"
                                        onClick={handleOnClose}
                                    >
                                        Cancel
                                    </CancelButton>
                                </Grid>
                                <Grid item xs={4} style={{ textAlign: 'center' }}>
                                    <CancelSuspensionButton
                                        data-cy={'cancel_suspension_modal_btn'}
                                        id={'cancel_suspension_modal_btn'}
                                        className={classes.cancelSuspensionButton}
                                        variant="contained"
                                        color="primary"
                                        onClick={handleStatusAction}
                                    >
                                        Cancel Suspension
                                    </CancelSuspensionButton>
                                </Grid>
                            </Grid>
                        </DialogActions>
                    </>)
                }
            </Dialog>
        </div>
    );
};

CancelSuspensionModal.defaultProps = {
    id: 'cancel_suspension_modal',
    isModalOpen: false,
};
