import React from 'react';
import {
    Dialog,
    Grid,
    // Typography, 
    DialogContent,
} from '@material-ui/core';
import {
    STYLE_CONST
} from '../../ProcessBatches.const';
import CloseIcon from '@material-ui/icons/Close';

interface ModalProps {
    visibility: boolean,
    children: any,
    title?:any,
    modalstyle?:any,
    noMaxWidth?:boolean,
    noCloseButton?:boolean,
    close: () => void
}

export const Modal: React.FC<ModalProps> = (props: ModalProps): JSX.Element => {
    const {
        visibility,
        children,
        close,
        title,
        modalstyle,
        noMaxWidth,
        noCloseButton
    } = props;
    const classes = STYLE_CONST.modalStyles();

    return (
        <Dialog onClose={close} 
        aria-labelledby="print-modal" 
        open={visibility}
        maxWidth={noMaxWidth ? false : 'md'}>
            <DialogContent style={modalstyle ? modalstyle : {}}>
            <Grid container>
                    <Grid item xs={!noCloseButton ? 10 : 12} className={classes.closeButton} 
                    style={{textAlign:'left',cursor:'auto'}}>
                        {title ? title : ''}
                    </Grid>
                    {!noCloseButton ? <Grid item xs={2} className={classes.closeButton}>
                        <CloseIcon onClick={close} />
                    </Grid> : null}
                    
            </Grid>
            <div className={classes.container}>
                {
                    children
                }
            </div>
            </DialogContent>
            
        </Dialog>
    )
}